import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { AvatarModule, BreadCrumbModule, UploadIconModule, UploadProfilePictureModule } from '@tei/material';
import { AdminPortalService } from '../../../../@services/admin-portal.service';
import { PortalSettingsLocalizationChipsModule } from '../../../../components/portal-settings-localization-chips/portal-settings-localization-chips.module';
import { PortalSettingsLocalizationPreferredCountyCodesModule } from '../../../../components/portal-settings-localization-preferred-county-codes/portal-settings-localization-preferred-county-codes.module';
import { ManagePortalSettingsComponent } from './manage-portal-settings.component';

const COMPONENTS = [ManagePortalSettingsComponent];
const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule];
const MATERIAL_MODULES = [MatIconModule, MatButtonModule, MatInputModule];
const TEI_MATERIAL_MODULES = [AvatarModule, UploadProfilePictureModule, TeiTextTranslatePipeModule, UploadIconModule, BreadCrumbModule];
const TEI_ADMIN_CENTER_MODULES = [
  PortalSettingsLocalizationPreferredCountyCodesModule,
  PortalSettingsLocalizationChipsModule
];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_ADMIN_CENTER_MODULES,
    ...TEI_MATERIAL_MODULES
  ],
  exports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_ADMIN_CENTER_MODULES,
    ...COMPONENTS
  ],
  providers: [
    { provide: 'IAdminPortalService', useClass: AdminPortalService }
  ]
})
export class ManagePortalSettingsModule { }
