import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetRolePermissionByRoleIDRequest, GetRolePermissionByRoleIDResponse } from '../@intermediate/models/get-role-permission-by-id.model';
import { PostRolePermissionRequest, PostRolePermissionResponse } from '../@intermediate/models/post-role-permission.model';
import { IRolePermissionService } from '../@intermediate/role-permission.interface';

@Injectable()
export class RolePermissionService implements IRolePermissionService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Security/UserManagement/RolePermission';
  }

  GetPermissionByRoleID(request: GetRolePermissionByRoleIDRequest): Observable<GetRolePermissionByRoleIDResponse>
  {
    const apiUrl = this.serviceUrl + '/GetPermissionByRoleID';
    return this.http.get<GetRolePermissionByRoleIDResponse>(apiUrl, { params: request as any });
  }

  PostRolesPermissions(request: PostRolePermissionRequest): Observable<PostRolePermissionResponse>
  {
    const apiUrl = this.serviceUrl + '/PostRolesPermissions';
    return this.http.post<PostRolePermissionResponse>(apiUrl, request);
  }
}
