import { Component, Inject, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ManageLoadingService } from '@tei/core';
import { BreadCrumb, Setting } from '@tei/material';
import { Subscription, finalize } from 'rxjs';
import { IAdminPortalService } from '../../@intermediate/admin-portal.interface';
import { IHostAdminCenterHelperService } from '../../@intermediate/host-admin-center-helper.interface';
import { GetPaymentGatewaySettingsRequest, GetPaymentGatewaySettingsResponse, gatewayCodeEnum } from '../../@intermediate/models/get-payment-gateway-settings.model';
import { ManagePortalTab, ManagePortalTabIdEnum } from '../../@intermediate/models/get-portal.model';
import { PortalGroupByID } from '../../@intermediate/portal-group/models/get-portal-group-by-id.model';
import { IPortalService } from '../../@intermediate/portal.interface';
@Component({
  selector: 'tei-admin-admin-center-payment-settings',
  templateUrl: './payment-settings.component.html',
  styleUrls: ['./payment-settings.component.scss']
})
export class PaymentSettingsComponent implements OnInit
{
  portalId: string;
  tabs: ManagePortalTab[] = [];
  selectedTab: ManagePortalTab;
  breadCrumbData: BreadCrumb[] = [];

  settings: Setting[] = [
    {
      link: ManagePortalTabIdEnum.GeneralSetup,
      title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.GENERAL_SETUP'),
      description: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.GENERAL_SETUP_DESCRIPTION')
    }
  ];

  achq: Setting = {
    link: ManagePortalTabIdEnum.ACHQSetup,
    title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.ACHQ_SETUP'),
    description: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.ACHQ_SETUP_DESCRIPTION')
  };

  converge: Setting = {
    link: ManagePortalTabIdEnum.ElavonConvergeSetup,
    title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.ELAVON_CONVERGE_SETUP'),
    description: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.ELAVON_CONVERGE_SETUP_DESCRIPTION')
  };

  noon: Setting = {
    link: ManagePortalTabIdEnum.NoonPaymentsSetup,
    title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.NOON_PAYMENTS_SETUP'),
    description: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.NOON_PAYMENTS_SETUP_DESCRIPTION')
  };


  parentPortalGroupId: string;
  parentPortalGroup: PortalGroupByID;
  subscription: Subscription = new Subscription();
  constructor(
    @Inject('IHostAdminCenterHelperService') private hostAdminCenterHelperService: IHostAdminCenterHelperService,
    @Inject('IAdminPortalService') private adminPortalService: IAdminPortalService,
    @Inject('IPortalService') private portalService: IPortalService,
    public loadingService: ManageLoadingService,
    private activatedRoute: ActivatedRoute,
    private translate: TranslateService,
  ) { }

  ngOnInit(): void
  {
    this.subscription.add(this.hostAdminCenterHelperService.currentPortalGroupDetails.subscribe(portalDetail =>
    {
      if (portalDetail)
      {
        this.fillBreadCrump(portalDetail);
        this.isLoad(portalDetail);
      }
    }));
  }

  fillBreadCrump(portalDetail): void
  {
    this.breadCrumbData = [
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.PORTAL_GROUPS.TITLE'),
        relativeUrl: '../../../../',
      },
      {
        title: `${ portalDetail.parentPortalGroup.PortalGroup.PortalGroupName }`,
        relativeUrl: `../../../`,
      },
      {
        title: `${ portalDetail.portal.PortalName }`,
        relativeUrl: '../',
      },
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.PAYMENT_SETTINGS'),
        relativeUrl: '',
      }
    ];
  }

  isLoad(portalDetail): void
  {
    const request: GetPaymentGatewaySettingsRequest = {
      PortalID: portalDetail.portal.PortalID
    };

    this.loadingService.startLoading();
    this.adminPortalService.GetPaymentGatewaySettings(request).pipe(finalize(() => this.loadingService.finishLoading())).subscribe({
      next: (response: GetPaymentGatewaySettingsResponse) =>
      {
        if (response?.IsACHQEnabled)
        {
          this.settings.push(this.achq);
        }
        if (response?.SelectedGateway?.GatewayCode === gatewayCodeEnum.Converge)
        {
          this.settings.push(this.converge);
        } else if (response?.SelectedGateway?.GatewayCode === gatewayCodeEnum.Noon)
        {
          this.settings.push(this.noon);
        }
      }
    });
  }
  ngOnDestroy(): void
  {
    this.subscription?.unsubscribe();
  }
}
