import { Component, EventEmitter, Inject, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { UntypedFormBuilder, UntypedFormGroup } from '@angular/forms';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { ResponsiveService } from '@tei/core';
import { Subscription } from 'rxjs';
import { GetPrinterCredentialByIdResponse } from '../../../@intermediate/models/get-printer-credential-by-id.model';
import { PostCreatePrinterCredentialResponse } from '../../../@intermediate/models/post-create-printer-credential.model';

@Component({
  selector: 'tei-admin-admin-center-device-details',
  templateUrl: './device-details.component.html',
  styleUrls: ['./device-details.component.scss']
})
export class DeviceDetailsComponent implements OnInit, OnDestroy
{
  @Output() close = new EventEmitter();
  @Input() set deviceDetails(value: PostCreatePrinterCredentialResponse)
  {
    if (value)
    {
      this.form.patchValue({
        UserName: value.UserName,
        Password: value.Password,
        McPrint3Url: value.McPrint3Url,
        SP742Url: value.SP742Url
      });
    }
  }
  isMobile = false;
  subscription: Subscription;
  form: UntypedFormGroup;
  constructor(private fb: UntypedFormBuilder, @Inject(MAT_DIALOG_DATA) public data: GetPrinterCredentialByIdResponse, private dialogRef: MatDialogRef<DeviceDetailsComponent>,
    private responsiveService: ResponsiveService) { }

  ngOnInit(): void
  {
    this.createFrom();
    this.setMobileSize();
  }

  setMobileSize(): void
  {
    this.subscription = this.responsiveService.mediaBreakpoint.subscribe(screenSize =>
    {
      this.isMobile = screenSize === 'xs' || screenSize === 'sm' || screenSize === 'md';
    });
  }

  createFrom(): void
  {
    this.form = this.fb.group({
      UserName: [''],
      Password: [''],
      McPrint3Url: [''],
      SP742Url: ['']
    });
    if (this.data)
    {
      this.form.patchValue({
        UserName: this.data?.UserName,
        Password: this.data?.Password,
        McPrint3Url: this.data?.McPrint3Url,
        SP742Url: this.data?.SP742Url
      });
    }
  }

  onClose(): void
  {
    this.data?.UserName ? this.dialogRef.close() : this.close.emit(true);
  }

  get formControls()
  {
    return this.form.controls;
  }

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }
}
