import { Component, Inject, OnInit, ViewChild } from '@angular/core';
import { MatStepper } from '@angular/material/stepper';
import { TranslateService } from '@ngx-translate/core';
import { BreadCrumb } from '@tei/material';
import { PostNavigationRequest } from '@tei/material-smart-components/lib/@intermediate/secure-navigation/model/post-secure-navigation.model';
import { ISecureNavigationService } from '@tei/material-smart-components/lib/@intermediate/secure-navigation/secure-navigation.interface';
import { ECommerceTransaction } from '@tei/retail-admin';
import { firstValueFrom } from 'rxjs';


@Component({
  selector: 'tei-portal-admin-center-payment-engine-settings-ecommerce-transaction-inquiry',
  templateUrl: './ecommerce-transaction-inquiry.component.html',
  styleUrls: ['./ecommerce-transaction-inquiry.component.scss']
})
export class ECommerceTransactionInquiryComponent implements OnInit
{
  isMobile = false;
  selectedECommerceTransactionUIDs: string[] = [];
  selectedECommerceLineUIDs: string[] = [];
  @ViewChild('stepper') stepper: MatStepper;
  constructor(
    private translate: TranslateService,
    @Inject('ISecureNavigationService') private secureNavigationService: ISecureNavigationService,
  ) { }
  async ngOnInit()
  {
    await this.loadSecureNavigation();
  }

  breadCrumbData: BreadCrumb[] = [

    {
      title: this.translate.instant('ADMIN_CENTER.HOST.SETTINGS.PORTAL_ADMIN_CENTER'),
      relativeUrl: ''
    },
    {
      title: this.translate.instant('ADMIN_CENTER.HOST.SETTINGS.SETUP'),
      relativeUrl: '../../../'
    },
    {
      title: this.translate.instant('ADMIN_CENTER.HOST.SETTINGS.PAYMENT_ENGINE'),
      relativeUrl: '../../'
    },
    {
      title: this.translate.instant('ADMIN_CENTER.HOST.SETTINGS.TRANSACTION_INQUIRY'),
      relativeUrl: '../'
    },
    {
      title: this.translate.instant('ADMIN_CENTER.TRANSACTION_INQUIRY.ECOMMERCE_TRANSACTION_INQUIRY_TITLE'),
      relativeUrl: ''
    },
  ];

  async loadSecureNavigation(): Promise<void>
  {
    const request: PostNavigationRequest = {
      WebItemGroupIdentifier: 'portal',
      WebItemIdentifiers: [{ Identifier: 'payment-settings' }]
    };
    await firstValueFrom(this.secureNavigationService.Post(request));
  }


  onECommerceTransaction(event: Array<ECommerceTransaction>): void
  {
    this.selectedECommerceTransactionUIDs = event?.map(t => t.TransactionUID);
    this.stepper.next();
  }

  onReturn(event: Array<any>): void
  {
    this.selectedECommerceLineUIDs = event?.map(t => t.LineUID);
    this.stepper.next();
  }
  goBack(event: any): void
  {
    this.stepper.previous();
  }
}
