import { Component, Inject, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { UntypedFormBuilder, UntypedFormGroup, Validators } from '@angular/forms';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { MatStepper } from '@angular/material/stepper';
import { ResponsiveService } from '@tei/core';
import { Subscription } from 'rxjs';
import { AppModeType, GetPaymentDeviceConfigurationRequest, GetPaymentDeviceConfigurationResponse, IntegrationTypeEnm } from '../../../@intermediate/models/get-payment-device-configurations.model';
import { PostPaymentDeviceRegistrationRequest, PostPaymentDeviceRegistrationResponse } from '../../../@intermediate/models/post-payment-device-registration.model';
import { IPaymentDevicesService } from '../../../@intermediate/payment-devices.interface';

@Component({
  selector: 'tei-admin-admin-center-new-payment-device',
  templateUrl: './new-payment-device.component.html',
  styleUrls: ['./new-payment-device.component.scss']
})
export class NewPaymentDeviceComponent implements OnInit, OnDestroy
{
  subscription: Subscription;
  AddDeviceForm: UntypedFormGroup;
  InfoDeviceForm: UntypedFormGroup;
  secretId: string;
  isLoading = false;
  isMobile = false;
  @ViewChild('stepper') stepper: MatStepper;
  paymentDeviceConfiguration: GetPaymentDeviceConfigurationResponse = null;
  formSubscription: Subscription;
  appModeType = AppModeType;
  
  fileName: string;
  integrationConfigJsonFile = null;

  get isWebPos() { return this.formControls?.AppMode?.value === AppModeType.WEBPOS; }
  get isPat() { return this.formControls?.AppMode?.value === AppModeType.PAT; }
  get isRetail() { return this.formControls?.AppMode?.value === AppModeType.RETAIL; }
  get isConverge() { return this.formControls?.IntegrationType?.value === IntegrationTypeEnm.Converge; }
  get isWebPos_Standalone() { return this.formControls?.AppMode?.value === AppModeType.WEBPPOS_Standalone; }

  constructor(private responsiveService: ResponsiveService,
    private dialogRef: MatDialogRef<NewPaymentDeviceComponent>,
    private fb: UntypedFormBuilder, @Inject('IPaymentDevicesService') private paymentDevicesService: IPaymentDevicesService,
    @Inject(MAT_DIALOG_DATA) public data: { PortalID: number, SecretID: string; }) { }

  ngOnInit(): void
  {
    this.createForm();
    this.getMobileSize();
    this.getConfigurations();
  }

  getMobileSize(): void
  {
    this.subscription = this.responsiveService.mediaBreakpoint.subscribe(screenSize =>
    {
      this.isMobile = screenSize === 'xs' || screenSize === 'sm' || screenSize === 'md';
    });
  }

  onAppModeChanges(): void
  {
    this.formSubscription = this.formControls.AppMode.valueChanges.subscribe(value =>
    {
      if (this.isWebPos || this.isWebPos_Standalone)
      {
        this.formControls.RecipientID.setValue(this.paymentDeviceConfiguration?.RecipientID);
        this.formControls.RecipientID.disable();
      } else
      {
        this.formControls.RecipientID.setValue('');
        this.formControls.RecipientID.enable();
      }

      if (value === this.appModeType.PAT)
      {
        this.formControls.RecipientID.disable();
        this.formControls.TerminalID.setValue('');        
        this.formControls.SubscriberID.setValue('');
        this.formControls.SubscriberID.disable();
      } else
      {
        this.formControls.TerminalID.enable();
        this.formControls.SubscriberID.enable();
      }
    });
  }

  getConfigurations(): void
  {
    this.isLoading = true;
    const request = new GetPaymentDeviceConfigurationRequest();
    request.PortalID = this.data?.PortalID;
    this.paymentDevicesService.GetConfiguration(request).subscribe({
      next: (response: GetPaymentDeviceConfigurationResponse) =>
      {
        this.isLoading = false;
        this.paymentDeviceConfiguration = response;
        this.fillInfoDeviceForm(response);
      },
      error: err => this.isLoading = false,
      complete: () => this.isLoading = false,
    });
  }

  fillInfoDeviceForm(paymentDeviceConfiguration: GetPaymentDeviceConfigurationResponse): void
  {
    this.InfoDeviceForm.patchValue({
      MessagingEndpoint: paymentDeviceConfiguration.MessagingEndpoint,
      BasePatEndpoint: paymentDeviceConfiguration.BasePatEndpoint,
      BaseAuthEndpoint: paymentDeviceConfiguration.BaseAuthEndpoint,
      GatewayEndpoint: paymentDeviceConfiguration.GatewayEndpoint,
      WebPosUrl: paymentDeviceConfiguration.WebPosUrl
    });
  }

  createForm(): void
  {
    this.AddDeviceForm = this.fb.group({
      DeviceDescription: ['', Validators.required],
      AppMode: [null, Validators.required],
      IntegrationType: [null, Validators.required],
      TerminalID: ['', Validators.required],
      Header: ['', Validators.required],
      SubHeader: ['', Validators.required],
      SubscriberID: ['', Validators.required],
      RecipientID: [this.paymentDeviceConfiguration?.RecipientID ?? '', Validators.required],      
      PortalID: [null],
      IntegrationConfigJsonFile: [null],
    });
    this.InfoDeviceForm = this.fb.group({
      MessagingEndpoint: [''],
      BasePatEndpoint: [''],
      BaseAuthEndpoint: [''],
      GatewayEndpoint: [''],
      WebPosUrl: ['']
    });
    this.InfoDeviceForm.disable();
    this.onAppModeChanges();
  }

  get formControls()
  {
    return this.AddDeviceForm.controls;
  }

  get infoDeviceFormControls()
  {
    return this.InfoDeviceForm.controls;
  }

  onConfirm(): void
  {
    this.formControls.PortalID.setValue(this.data?.PortalID);
    const request: PostPaymentDeviceRegistrationRequest = this.AddDeviceForm.getRawValue();
    const formData = new FormData();
    formData.append('DeviceDescription', request.DeviceDescription ?? '');
    formData.append('AppMode', request.AppMode?.toString() ?? '');
    formData.append('IntegrationType', request.IntegrationType?.toString() ?? '');
    formData.append('TerminalID', request.TerminalID ?? '');
    formData.append('Header', request.Header ?? '');
    formData.append('SubHeader', request.SubHeader ?? '');
    formData.append('SubscriberID', request.SubscriberID ?? '');
    formData.append('RecipientID', request.RecipientID ?? '');
    formData.append('PortalID', request.PortalID?.toString() ?? '');
    
    formData.append('IntegrationConfigJsonFile', this.integrationConfigJsonFile);
    
    this.isLoading = true;
    this.paymentDevicesService.Registration(formData).subscribe({
      next: (response: PostPaymentDeviceRegistrationResponse) =>
      {
        this.isLoading = false;
        this.secretId = response.SecretID;
        this.stepper.next();
      },
      error: err => this.isLoading = false,
      complete: () => this.isLoading = false
    });
  }

  close(state: boolean): void
  {
    this.dialogRef.close(state);
  }

  onChangeUploadZip(event): void
  {
    this.integrationConfigJsonFile = event.target.files[0];
    this.fileName = event.target.files[0].name;
  }

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
    this.formSubscription.unsubscribe();
  }
}
