import { Component, Inject, OnInit } from '@angular/core';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ManageLoadingService, ResponsiveService } from '@tei/core';
import { BreadCrumb, CardDialogComponent, ConfirmationDialogComponent, DialogDataModel } from '@tei/material';
import { Subscription } from 'rxjs';
import { finalize } from 'rxjs/operators';
import { ICustomerFacingDisplayService } from '../../@intermediate/customer-facing-display.interface';
import { DeleteCustomerFacingDisplayRequest, DeleteCustomerFacingDisplayResponse } from '../../@intermediate/models/delete-customer-facing-display.model';
import { CustomerFacingDisplaysTableMetadata } from '../../@intermediate/models/get-customer-display-facing.model';
import { PostCustomerFacingDisplayResponse } from '../../@intermediate/models/post-customer-facing-display.model';
import { PostResetCustomerFacingDisplayRequest, PostResetCustomerFacingDisplayResponse } from '../../@intermediate/models/post-reset-customer-facing-display.model';
import { PostCustomerFacingDisplaySearchRequest, PostCustomerFacingDisplaySearchResponse } from '../../@intermediate/models/post-search-customer-facing-display.model';
import { NewSecretIdDialogComponent } from './new-secret-id-dialog/new-secret-id-dialog.component';

@Component({
  selector: 'tei-admin-admin-center-customer-facing-displays',
  templateUrl: './customer-facing-displays.component.html',
  styleUrls: ['./customer-facing-displays.component.scss']
})
export class CustomerFacingDisplaysComponent implements OnInit
{
  metadata = new CustomerFacingDisplaysTableMetadata();
  dataSource = null;
  isMobileView = false;
  subscription = new Subscription();
  breadCrumbData: BreadCrumb[] = [
    {
      title: this.translate.instant('ADMIN_CENTER.DEVICES.PRINTERS.PORTAL_ADMIN_CENTER'),
      relativeUrl: ''
    },
    {
      title: this.translate.instant('ADMIN_CENTER.DEVICES.PRINTERS.SETUP'),
      relativeUrl: '../setup'
    },
    {
      title: this.translate.instant('ADMIN_CENTER.CUSTOMER_FACING_DISPLAYS.TITLE'),
      relativeUrl: ''
    },
  ];

  constructor(private responsiveService: ResponsiveService, private translate: TranslateService,
    @Inject('ICustomerFacingDisplayService') private customerFacingDisplayService: ICustomerFacingDisplayService,
    public manageLoadingService: ManageLoadingService, private router: Router,
    private dialog: MatDialog) { }

  ngOnInit(): void
  {
    this.getMobileSize();
    this.getCFDs();
  }

  getMobileSize(): void
  {
    this.subscription.add(this.responsiveService.mediaBreakpoint.subscribe(mediaBreakpoint =>
    {
      this.isMobileView = mediaBreakpoint === 'xs' || mediaBreakpoint === 'sm' || mediaBreakpoint === 'md';
    }));
  }

  getCFDs(): void
  {
    const request = new PostCustomerFacingDisplaySearchRequest();
    this.manageLoadingService.startLoading();
    this.customerFacingDisplayService.Search(request).pipe(finalize(() => this.manageLoadingService.finishLoading())).subscribe({
      next: (response: PostCustomerFacingDisplaySearchResponse) =>
      {
        this.dataSource = response.Devices;
      },
      error: err => { },
      complete: () => { }
    });
  }

  onDeleteDialog(deviceId: number): void
  {
    const dialogRef = this.dialog.open(CardDialogComponent, {
      panelClass: 'o-confirm-dialog',
      data: {
        component: ConfirmationDialogComponent,
        dialogData: {
          title: 'ADMIN_CENTER.PAYMENT_DEVICES.DELETE_DEVICE',
          content: 'ADMIN_CENTER.PAYMENT_DEVICES.DELETE_DEVICE_QUESTION',
          submitButtonLabel: 'ADMIN_CENTER.PAYMENT_DEVICES.DELETE',
          submitButtonColor: 'warn',
          cancelButtonLabel: 'ADMIN_CENTER.PAYMENT_DEVICES.CANCEL'
        } as DialogDataModel
      },
      width: '350px',
    });
    dialogRef.afterClosed().subscribe(result =>
    {
      if (result)
      {
        this.onDelete(deviceId);
      }
    });
  }

  onDelete(deviceId: number): void
  {
    const request: DeleteCustomerFacingDisplayRequest = {
      DeviceID: deviceId
    };
    this.manageLoadingService.startLoading();
    this.customerFacingDisplayService.Delete(request).pipe(finalize(() => this.manageLoadingService.finishLoading())).subscribe({
      next: (response: DeleteCustomerFacingDisplayResponse) =>
      {
        this.getCFDs();
      },
      error: err => { },
      complete: () => { }
    });
  }

  onResetDeviceDialog(deviceId: number): void
  {
    const dialogRef = this.dialog.open(CardDialogComponent, {
      panelClass: 'o-confirm-dialog',
      data: {
        component: ConfirmationDialogComponent,
        dialogData: {
          title: 'ADMIN_CENTER.CUSTOMER_FACING_DISPLAYS.RESET_DEVICE',
          content: 'ADMIN_CENTER.CUSTOMER_FACING_DISPLAYS.RESET_DEVICE_DESCRIPTION',
          submitButtonLabel: 'ADMIN_CENTER.CUSTOMER_FACING_DISPLAYS.RESET',
          submitButtonColor: 'warn',
          cancelButtonLabel: 'ADMIN_CENTER.PAYMENT_DEVICES.CANCEL'
        } as DialogDataModel
      },
      width: '350px',
    });
    dialogRef.afterClosed().subscribe(result =>
    {
      if (result)
      {
        this.onResetDevice(deviceId);
      }
    });
  }

  onResetDevice(deviceId: number): void
  {
    const request: PostResetCustomerFacingDisplayRequest = {
      DeviceID: deviceId
    };
    this.manageLoadingService.startLoading();
    this.customerFacingDisplayService.Reset(request).pipe(finalize(() => this.manageLoadingService.finishLoading())).subscribe({
      next: (response: PostResetCustomerFacingDisplayResponse) =>
      {
        this.generateSecretId(response);
      },
      error: err => { },
      complete: () => { }
    });
  }

  generateSecretId(data: PostCustomerFacingDisplayResponse): void
  {
    const dialog = this.dialog.open(NewSecretIdDialogComponent, {
      panelClass: ['o-dialog-sm-fullscreen', 'o-dialog-md-fullscreen', 'new-customer-facing-display-secret'],
      width: '460px',
      data: {
        data: data,
        isMobile: this.isMobileView
      }
    });

    dialog.afterClosed().subscribe(result =>
    {
      this.getCFDs();
    });
  }

}
