import { Injectable } from "@angular/core";
import { TranslateService } from "@ngx-translate/core";
import { Portal } from '../@intermediate/staff-user/models/get-manage-role.model';
import { ItemNode } from "../components/tree/tree.model";

@Injectable()
export class StaffUserGeneralService
{
  constructor(private translateService: TranslateService) { }

  getFullName(firstName: string, lastName: string, userName: string): string
  {
    firstName = firstName === 'NONE' ? '' : firstName;
    lastName = lastName === 'NONE' ? '' : lastName;

    let fullName = '';

    if (firstName)
    {
      fullName = firstName;
    }

    if (lastName)
    {
      fullName += ((fullName ? ' ' : '') + lastName);
    }

    if (!fullName)
    {
      fullName = userName;
    }

    return fullName;
  }

  fillRestaurantTreeData(portal: Portal): Array<ItemNode>
  {
    let treeData = [];
    if (portal?.Restaurants?.length > 0)
    {
      let restaurantItemNode = new ItemNode();
      restaurantItemNode.Item = {
        Id: "Restaurants",
        Key: "Restaurants",
        Title: this.translateService.instant("ADMIN_CENTER.INVITE_USERS_INITIATE.RESTAURANTS"),
        IsSelected: false
      };

      portal?.Restaurants?.forEach(item =>
      {
        restaurantItemNode.Children.push(
          {
            Item: {
              Id: item.SiteID.toString(),
              Key: item.SiteID.toString(),
              Title: item.SiteName,
              IsSelected: false
            },
            Children: []
          });

        var insertedItem = restaurantItemNode.Children[restaurantItemNode.Children.length - 1];
        item?.Terminals.forEach(terminal =>
        {
          insertedItem.Children.push(
            {
              Item: {
                Id: terminal.TerminalID,
                Key: terminal.TerminalID,
                Title: terminal.Description,
                IsSelected: terminal.TerminalIsSelected
              },
              Children: []
            });
        });
      });

      treeData.push(restaurantItemNode);
    }
    return treeData;
  }


  fillSiteTreeData(portal: Portal): Array<ItemNode>
  {
    let treeData = [];

    if (portal?.ProShops?.length > 0)
    {
      let retailItemNode = new ItemNode();

      retailItemNode.Item = {
        Id: "Retails",
        Key: "Retails",
        Title: this.translateService.instant("ADMIN_CENTER.INVITE_USERS_INITIATE.RETAILS"),
        IsSelected: false
      };

      portal?.ProShops?.forEach(item =>
      {
        retailItemNode.Children.push(
          {
            Item: {
              Id: item.SiteID.toString(),
              Key: item.SiteID.toString(),
              Title: item.SiteName,
              IsSelected: false
            },
            Children: []
          });

        var insertedItem = retailItemNode.Children[retailItemNode.Children.length - 1];
        item.Terminals.forEach(terminal =>
        {
          insertedItem.Children.push(
            {
              Item: {
                Id: terminal.TerminalID,
                Key: terminal.TerminalID,
                Title: terminal.Description,
                IsSelected: terminal.TerminalIsSelected
              },
              Children: []
            });
        });
      });
      treeData.push(retailItemNode);
    }

    return treeData;
  }
}
