import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetUserByEmailRequest, GetUserByEmailResponse } from '../@intermediate/user/models/get-user-by-email.model';
import { GetUserByIDRequest, GetUserByIDResponse } from '../@intermediate/user/models/get-user-by-user-id.model';
import { GetVerifyStaffEmailInPortalGroupRequest, GetVerifyStaffEmailInPortalGroupResponse } from '../@intermediate/user/models/get-verify-staff-email-in-portal-group.model';
import { IUserService } from '../@intermediate/user/user.interface';

@Injectable()
export class UserService implements IUserService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/User';
  }

  GetById(request: GetUserByIDRequest): Observable<GetUserByIDResponse>
  {
    const apiUrl = this.serviceUrl + '/GetById';
    return this.http.get<GetUserByIDResponse>(apiUrl, { params: request as any });
  }

  GetByEmail(request: GetUserByEmailRequest): Observable<GetUserByEmailResponse>
  {
    const apiUrl = this.serviceUrl + '/GetByEmail';
    return this.http.get<GetUserByEmailResponse>(apiUrl, { params: request as any });
  }

  public GetVerifyStaffEmailInPortalGroup(request: GetVerifyStaffEmailInPortalGroupRequest): Observable<GetVerifyStaffEmailInPortalGroupResponse>
  {
    const apiUrl = this.serviceUrl + '/GetVerifyStaffEmailInPortalGroup';
    return this.http.get<GetVerifyStaffEmailInPortalGroupResponse>(apiUrl, { params: request as any });
  }
}

