import { FormControl, FormGroup } from '@angular/forms';
import { GetGeneralSettingsResponse } from './get-general-settings.model';
export class PostGeneralSettingsRequest
{
  public EnablePayMethodCreditCard: boolean;
  public EnablePayMethodCashEQ: boolean;
  public EnablePayMethodECheck: boolean;
  public EnablePaymentMethodOnAccount: boolean;
  public SkipManageCart: boolean;
  public AutomationCategoryTemplateID: number;
  public MerchantPhone: string;
  public MerchantEmail: string;
  public MerchantAddress: string;
  public QuickPaymentMessage: string;
}

export class PostGeneralSettingsResponse
{

}
export class GeneralSettingsRequestForm extends FormGroup
{
  constructor(value: GetGeneralSettingsResponse)
  {
    super({
      EnablePayMethodCreditCard: new FormControl(value.EnablePayMethodCreditCard),
      EnablePayMethodCashEQ: new FormControl(value.EnablePayMethodCashEQ),
      EnablePayMethodECheck: new FormControl(value.EnablePayMethodECheck),
      EnablePaymentMethodOnAccount: new FormControl(value.EnablePaymentMethodOnAccount),
      SkipManageCart: new FormControl(value.SkipManageCart),
      AutomationCategoryTemplateID: new FormControl(value.AutomationCategoryTemplateID),
      MerchantPhone: new FormControl(value.MerchantPhone),
      MerchantEmail: new FormControl(value.MerchantEmail),
      MerchantAddress: new FormControl(value.MerchantAddress),
      QuickPaymentMessage: new FormControl(value?.QuickPaymentMessage),
      EnableTermsAndConditions: new FormControl(value?.EnableTermsAndConditions),
      TermsAndConditions: new FormControl(value?.TermsAndConditions),
    });
  }

  public value: PostGeneralSettingsRequest;

  public setValue(value: GetGeneralSettingsResponse)
  {
    const request = new PostGeneralSettingsRequest();
    super.setValue(request);
  }
}

