import { Component, Inject, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { SnackBarService } from '@tei/material';
import { Observable, firstValueFrom, tap } from 'rxjs';
import { GetAnonymousUserPermissionRequest, GetAnonymousUserPermissionResponse } from '../../../@intermediate/user-permissions/models/get-anonymous.model';
import { AnonymousUserPermissionForm } from '../../../@intermediate/user-permissions/models/post-anonymous.model';
import { IUserPermissionService } from '../../../@intermediate/user-permissions/user-permissions.interface';

@Component({
  selector: 'tei-admin-center-roles-anonymous-tab',
  templateUrl: './anonymous-tab.component.html',
  styleUrls: ['./anonymous-tab.component.scss']
})
export class RolesAnonymousTabComponent implements OnInit
{
  anonymousPermissions$: Observable<GetAnonymousUserPermissionResponse>;
  form: AnonymousUserPermissionForm;


  constructor(
    @Inject('IUserPermissionService') private userPermissionService: IUserPermissionService,
    private snackBarService: SnackBarService,
    private translate: TranslateService
  )
  {
  }

  ngOnInit(): void
  {
    const request: GetAnonymousUserPermissionRequest = {
    };

    this.anonymousPermissions$ = this.userPermissionService.GetAnonymous(request).pipe(
      tap((response) =>
      {
        this.form = new AnonymousUserPermissionForm(response);
      })
    );
  }

  async save(): Promise<void>
  {
    const request = this.form.value;

    try
    {
      await firstValueFrom(this.userPermissionService.PostAnonymous(request));
      this.snackBarService.showSuccess(this.translate.instant('ADMIN_CENTER.ROLES.ANONYMOUS.SETTINGS_SAVED'));
    } catch (error)
    {
    }
  }
}
