import { ClipboardModule } from '@angular/cdk/clipboard';
import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatDividerModule } from '@angular/material/divider';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacyTableModule as MatTableModule } from '@angular/material/legacy-table';
import { MatStepperModule } from '@angular/material/stepper';
import { RouterModule } from '@angular/router';
import { ResponsiveTableHeightModule } from '@tei/core';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { BreadCrumbModule, LoadingModule, TreeModule } from '@tei/material';
import { PrinterCredentialService } from '../../@services/printer-credential.service';
import { DeviceDetailsComponent } from './device-details/device-details.component';
import { NewDeviceComponent } from './new-device/new-device.component';
import { PrintersSettingComponent } from './printers-setting.component';

const BASE_MODULES = [CommonModule, RouterModule, FormsModule, ReactiveFormsModule];
const MATERIAL_MODULES = [MatTableModule, MatDividerModule, MatCardModule, MatButtonModule, MatStepperModule, MatFormFieldModule, MatInputModule, ClipboardModule];
const TEI_MATERIAL_MODULES = [BreadCrumbModule, LoadingModule, TreeModule];
const TEI_CORE_MODULES = [TeiTextTranslatePipeModule, ResponsiveTableHeightModule];
const ADMIN_MODULES = [];
const COMPONENTS = [PrintersSettingComponent, NewDeviceComponent];

@NgModule({
  declarations: [
    ...COMPONENTS,
    DeviceDetailsComponent,
  ],
  imports: [RouterModule.forChild([
    { path: '', component: PrintersSettingComponent },

  ]),
  ...BASE_MODULES, ...ADMIN_MODULES, ...TEI_CORE_MODULES, ...MATERIAL_MODULES, ...TEI_MATERIAL_MODULES],
  providers: [{ provide: 'IPrinterCredentialService', useClass: PrinterCredentialService }],
})
export class PrintersSettingModule { }
