import { Component, Inject } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { TranslateService } from '@ngx-translate/core';
import { BreadCrumb, SnackBarService } from '@tei/material';
import { Subscription } from 'rxjs';
import { IButterCMSSetupService } from '../../../@intermediate/butter-cms-setup.interface';
import { IHostAdminCenterHelperService } from '../../../@intermediate/host-admin-center-helper.interface';
import { ButterCMSSettingsModel } from '../../../@intermediate/models/butter-cms-settings.model';
import { portalGroupDetails } from '../../../@intermediate/models/host-admin-center-helper.model';

@Component({
  selector: 'tei-admin-admin-center-butter-cmssetup',
  templateUrl: './butter-cmssetup.component.html',
  styleUrls: ['./butter-cmssetup.component.css']
})
export class ButterCMSSetupComponent
{
  breadCrumbData: BreadCrumb[] = [];
  subscription: Subscription = new Subscription();

  hideReadPassword: boolean = true;
  hideWritePassword: boolean = true;
  portalDetail: portalGroupDetails;

  settingForm: FormGroup<ButterCMSSettingsModel> = new FormGroup<ButterCMSSettingsModel>({
    ButterCMSReadAPIToken: new FormControl('', Validators.required),
    ButterCMSWriteAPIToken: new FormControl('', Validators.required)
  });

  constructor(
    @Inject('IHostAdminCenterHelperService') private hostAdminCenterHelperService: IHostAdminCenterHelperService,
    @Inject("IButterCMSSetupService") private butterCMSSetupService: IButterCMSSetupService,
    private snackBar: SnackBarService,
    private translate: TranslateService)
  {

  }

  ngOnInit(): void
  {
    this.subscription.add(this.hostAdminCenterHelperService.currentPortalGroupDetails.subscribe(portalDetail =>
    {
      if (portalDetail)
      {
        this.portalDetail = portalDetail;
        this.fillBreadCrump(portalDetail);
        this.loadData(this.portalDetail?.portal?.PortalID);
      }
    }));

  }

  loadData(portalId: number): void
  {
    this.subscription.add(this.butterCMSSetupService.get(portalId).subscribe(result =>
    {
      this.settingForm.patchValue({
        ButterCMSReadAPIToken: result.ButterCMSReadAPIToken,
        ButterCMSWriteAPIToken: result.ButterCMSWriteAPIToken
      });
    }));
  }

  saveSetting(): void
  {
    this.butterCMSSetupService.post({
      ButterCMSReadAPIToken: this.settingForm.controls.ButterCMSReadAPIToken.value,
      ButterCMSWriteAPIToken: this.settingForm.controls.ButterCMSWriteAPIToken.value,
      PortalID: this.portalDetail?.portal?.PortalID
    }).subscribe(res =>
    {
      this.snackBar.showSuccess(this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.BUTTERCMS_SETUP_PAGE.SAVED_SUCCESSFULLY'), '', null, 'left');
    });
  }

  fillBreadCrump(portalDetail): void
  {
    this.breadCrumbData = [
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.PORTAL_GROUPS.TITLE'),
        relativeUrl: '../../../../../',
      },
      {
        title: `${ portalDetail.parentPortalGroup.PortalGroup.PortalGroupName }`,
        relativeUrl: `../../../../`,
      },
      {
        title: `${ portalDetail.portal.PortalName }`,
        relativeUrl: '../../../',
      },
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.ACCESS'),
        relativeUrl: '../../',
      },
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.INTEGRATION.TITLE'),
        relativeUrl: '../',
      },
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.BUTTERCMS_SETUP'),
        relativeUrl: '',
      }
    ];
  }
}
