import { Component, Inject, OnInit } from '@angular/core';
import { MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { GetInviteUserRequest, GetInviteUserResponse, InviteUserSelectedData } from '../../../../@intermediate/staff-user/models/get-invite-user.model';
import { PostInviteUserRequestForm } from '../../../../@intermediate/staff-user/models/post-invite-user.model';
import { IStaffUserService } from '../../../../@intermediate/staff-user/staff-user.interface';


@Component({
  selector: 'tei-admin-admin-center-invite-users',
  templateUrl: './invite-users.component.html',
  styleUrls: ['./invite-users.component.scss']
})
export class InviteUsersComponent implements OnInit
{
  inviteUserSelectedData: InviteUserSelectedData;
  model: GetInviteUserResponse;
  form: PostInviteUserRequestForm;
  isLoading: boolean = false;

  constructor(
    private dialogRef: MatDialogRef<InviteUsersComponent>,
    @Inject('IStaffUserService') private service: IStaffUserService
  ) { }

  ngOnInit(): void
  {
    this.form = new PostInviteUserRequestForm();
    this.load();
  }

  load(): void
  {
    if (this.model)
    {
      this.model = new GetInviteUserResponse();
    }

    const request = new GetInviteUserRequest();

    this.isLoading = true;
    this.service.GetInvite(request).subscribe({
      next: (response: GetInviteUserResponse) =>
      {
        this.model = response;
        this.isLoading = false;
      },
      error: () => this.isLoading = false
    });
  }

  onNext(inviteUserSelectedData: InviteUserSelectedData): void
  {
    this.inviteUserSelectedData = inviteUserSelectedData;
  }

  onClose(isChanged: boolean): void
  {
    this.dialogRef.close(isChanged);
  }
}
