import { SelectionModel } from '@angular/cdk/collections';
import { Component, Inject, OnDestroy, OnInit } from '@angular/core';
import { MatLegacySelectChange as MatSelectChange } from '@angular/material/legacy-select';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ResponsiveService } from '@tei/core';
import { SnackBarService } from '@tei/material';
import { Subscription, firstValueFrom } from 'rxjs';
import { GetPermissionRequest, Permission, PermissionGroup } from '../../../@intermediate/models/get-permission.model';
import { GetRolePermissionByRoleIDRequest } from '../../../@intermediate/models/get-role-permission-by-id.model';
import { GetRoleWithPermissionAccessRequest, GetRoleWithPermissionAccessResponse, Grouping, RoleWithPermissionAccess } from '../../../@intermediate/models/get-role-with-permission-access.model';
import { PostRolePermissionRequest, RolePermission } from '../../../@intermediate/models/post-role-permission.model';
import { IPermissionService } from '../../../@intermediate/permission.interface';
import { IRolePermissionService } from '../../../@intermediate/role-permission.interface';
import { IRolesService } from '../../../@intermediate/role.interface';

@Component({
  selector: 'tei-admin-center-permissions',
  templateUrl: './permissions.component.html',
  styleUrls: ['./permissions.component.scss']
})
export class PermissionsComponent implements OnInit, OnDestroy
{

  subscriptions: Subscription = new Subscription();
  roleId: number;
  panelOpenStates = {};
  isStaffLevel: boolean = false;
  roles: RoleWithPermissionAccess[];
  rolesByColumn: (Grouping[])[] = [];
  isMobileView: boolean = false;
  selectRoles: number[] = [0, 0, 0];
  permissionGroups: PermissionGroup[];
  selections: (SelectionModel<number>[])[] = [];
  columns: number[] = [0, 1, 2];
  loading: boolean = true;
  isChangedPermission: boolean;
  roleGroupings: Grouping[] = [];

  constructor(
    @Inject('IRolePermissionService') private rolePermissionService: IRolePermissionService,
    @Inject('IPermissionService') private permissionService: IPermissionService,
    @Inject('IRolesService') private rolesService: IRolesService,
    private translate: TranslateService,
    private snackbarService: SnackBarService,
    private activatedRoute: ActivatedRoute,
    private responsiveService: ResponsiveService,
    private router: Router,
  )
  {
  }

  async ngOnInit()
  {

    this.subscriptions.add(
      this.responsiveService.mediaBreakpoint.subscribe(mediaBreakpoint =>
      {
        this.isMobileView = mediaBreakpoint === 'xs' || mediaBreakpoint === 'sm' || mediaBreakpoint === 'md';
      })
    );

    this.subscriptions.add(this.activatedRoute.params.subscribe(params =>
    {
      this.roleId = +params['id'];
    }));

    try
    {
      let request = new GetRoleWithPermissionAccessRequest();

      const groupingResponse = await firstValueFrom(this.rolesService.GetRolesWithPermissionAccess(request));

      this.roleGroupings = [...groupingResponse.Groups];

      this.roles = this.getAllRoles(groupingResponse);

      this.columns.forEach((column) =>
      {
        this.rolesByColumn[column] = this.pleaseGetCopy(groupingResponse.Groups);
      });

      request = new GetPermissionRequest();

      const permissionResponse = await firstValueFrom(this.permissionService.Get(request));
      this.permissionGroups = permissionResponse.PermissionGroups || [];
      this.permissionGroups = this.permissionGroups.sort((a, b) => a.Name.localeCompare(b.Name));

      this.columns.forEach((column) =>
      {
        this.selections[column] = [];

        this.permissionGroups.forEach(() =>
        {
          this.selections[column].push(new SelectionModel<number>(true, []));
        });
      });

      this.loading = false;

      this.load();

    }
    catch (error)
    {
      this.loading = false;
    }
  }

  pleaseGetCopy(groups: Grouping[]): Grouping[]
  {
    return groups.map(group =>
    {
      return {
        GroupTitle: group.GroupTitle,
        Roles: Object.assign([], group.Roles)
      };
    });
  }

  getAllRoles(groupingResponse: GetRoleWithPermissionAccessResponse): RoleWithPermissionAccess[]
  {
    var roles: RoleWithPermissionAccess[] = [];
    groupingResponse.Groups.map(g =>
    {
      roles = [...roles, ...g.Roles];
    });
    return roles;
  }

  getRolesInGroup(allRoles: RoleWithPermissionAccess[], groupTitle: string)
  {
    allRoles.filter(f => f.RoleGroupingTitle === groupTitle);
  }

  load()
  {
    this.setRolePermissions(this.roleId, 0);
  }

  isReadOnly(roleId): boolean
  {
    var role = this.roles.find(role => role.RoleID === roleId);

    if (role === undefined || role === null)
    {
      return true;
    }
    else
    {
      return !!role.IsReadOnly;
    }
  }

  roleHasAccessToPermission(roleId: number, permission: Permission)
  {
    var hasAccess = this.roles.find(role => role.RoleID === roleId)?.PermissionAccessList
      .find(perm => perm === permission.PermissionID);

    if (hasAccess === undefined || hasAccess === null)
    {
      return false;
    }
    else
    {
      return !!hasAccess;
    }
  }

  getRoleTypeTitle(roleId): string
  {
    return this.roles.find(role => role.RoleID === roleId)?.RoleTypeTitle;
  }

  setRolePermissions(roleId: number, column: number): void
  {
    const request: GetRolePermissionByRoleIDRequest = {
      RoleID: roleId
    };
    this.subscriptions.add(this.rolePermissionService.GetPermissionByRoleID(request).subscribe(response =>
    {

      this.permissionGroups.forEach((permissionGroup, permissionGroupIndex) =>
      {
        this.selections[column][permissionGroupIndex].clear();

        const permissionGroupPermissionIDs: number[] = permissionGroup.Permissions.map(
          permission => permission.PermissionID
        );

        permissionGroupPermissionIDs.forEach(permissionID =>
        {
          if (response.PermissionIDs.indexOf(permissionID) > -1)
          {
            this.selections[column][permissionGroupIndex].select(permissionID);
          }
        });
      });
    }));

    this.selectRoles[column] = roleId;

    this.updateRolesByColumn();
  }

  updateRolesByColumn(): void
  {

    this.columns.forEach((column) =>
    {
      let otherColumnSelectedRoles: (number[])[] = [];

      otherColumnSelectedRoles[column] = [...this.selectRoles];
      otherColumnSelectedRoles[column].splice(column, 1);

      this.rolesByColumn[column] = this.pleaseGetCopy(this.roleGroupings).map(m =>
      {
        let roles = this.roles.filter(role => role.RoleGroupingTitle === m.GroupTitle && !otherColumnSelectedRoles[column].includes(role.RoleID));

        return { GroupTitle: m.GroupTitle, Roles: roles };
      });

    });
  }

  isAllSelected(column: number, permissionGroupIndex: number): boolean
  {
    const numSelected = this.selections[column][permissionGroupIndex].selected.length;
    const numRows = this.permissionGroups[permissionGroupIndex].Permissions.length;
    return numSelected === numRows;
  }

  masterToggle(column: number, permissionGroupIndex: number): void
  {
    this.isChangedPermission = true;
    if (this.isAllSelected(column, permissionGroupIndex))
    {
      this.selections[column][permissionGroupIndex].clear();
      return;
    }
    this.selections[column][permissionGroupIndex].select(
      ...this.permissionGroups[permissionGroupIndex].Permissions.map(permission => permission.PermissionID)
    );
  }
  childToggle(column: number, permissionGroupIndex: number, permissionID: number): void
  {
    this.isChangedPermission = true;
    this.selections[column][permissionGroupIndex].toggle(permissionID);
  }

  changeRoleComboBox(event: MatSelectChange, column: number): void
  {
    if (event.value)
    {
      this.setRolePermissions(event.value, column);
    }
    else
    {
      this.permissionGroups.forEach((permissionGroup, permissionGroupIndex) =>
      {
        this.selections[column][permissionGroupIndex].clear();
      });
    }
  }

  onPanelHeaderClick()
  {
  }

  saveChanges(): void
  {
    const rolePermissions: RolePermission[] = [];

    this.selections.forEach((selectionColumn, column) =>
    {
      const roleID = this.selectRoles[column];
      if (roleID && !this.isReadOnly(roleID))
      {
        const rolePermission: RolePermission = {
          RoleID: roleID,
          PermissionIDs: []
        };

        selectionColumn.forEach(permissionGroup =>
        {
          rolePermission.PermissionIDs.push(...permissionGroup.selected);
        });

        rolePermissions.push(rolePermission);
      }
    });

    const request: PostRolePermissionRequest = {
      RolePermissions: rolePermissions
    };

    this.subscriptions.add(this.rolePermissionService.PostRolesPermissions(request).subscribe({
      next: () =>
      {
        this.snackbarService.showSuccess(this.translate.instant('ADMIN_CENTER.PERMISSIONS.SUCCESS_MESSAGE'), '', null, 'left');
        this.isChangedPermission = false;
      },
      error: () =>
      {
        this.snackbarService.showError(this.translate.instant('ADMIN_CENTER.PERMISSIONS.ERROR_MESSAGE'), '', null, 'left');
      }
    })
    );
  }

  ngOnDestroy(): void
  {
    this.subscriptions.unsubscribe();
  }
}
