import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ManagePortalTabIdEnum } from '../../@intermediate/models/get-portal.model';
import { ConvergeSettingsComponent } from './converge-settings/converge-settings.component';
import { PaymentSettingsComponent } from './payment-settings.component';


const routes: Routes = [{
  path: '',
  component: PaymentSettingsComponent,
  children: [
    {
      path: ManagePortalTabIdEnum.ElavonConvergeSetup,
      component: ConvergeSettingsComponent
    },
  ]
}];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class PaymentSettingsRoutingModule
{
}
