import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { CreatePortalForm } from '../../../@intermediate/portal/post-portal.model';
import { Subscription } from 'rxjs';

@Component({
  selector: 'tei-host-admin-add-edit-portal-details',
  templateUrl: './portal-details.component.html',
  styleUrls: ['./portal-details.component.scss']
})
export class AddEditPortalDetailsComponent implements OnInit, OnDestroy
{
  @Input() form: CreatePortalForm;
  @Input() advancedSetting: boolean = false;

  subscription: Subscription = new Subscription();

  constructor() { }

  ngOnInit(): void {
    this.subscription.add(
      this.form.controls.PortalName.valueChanges.subscribe((value = "") =>
      {
        this.form.controls.PortalIdentifier.setValue(value.replace(/\s/g, ''));
      })
    );
  }

  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }
}
