import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetProductRequest, GetProductResponse } from '../@intermediate/models/get-product.model';
import { PostProductRequest, PostProductResponse } from '../@intermediate/models/post-product.model';
import { IProductService } from '../@intermediate/product.interface';

@Injectable({
  providedIn: 'root'
})
export class ProductService implements IProductService
{

  private readonly serviceUrl: string;
  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Admin/AdminCenter/Product';
  }

  Get(request: GetProductRequest): Observable<GetProductResponse>
  {
    const apiUrl = this.serviceUrl + '/GetAll';
    return this.http.get<GetProductResponse>(apiUrl, { params: request as any });
  }

  Post(request: PostProductRequest): Observable<PostProductResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.post<PostProductRequest>(apiUrl, request);
  }
}
