import { Component, Inject, OnInit, ViewChild } from '@angular/core';
import { AbstractControl, ValidationErrors, ValidatorFn } from '@angular/forms';
import { MatAutocomplete } from '@angular/material/autocomplete';
import { TranslateService } from '@ngx-translate/core';
import { BreadCrumb, SnackBarService } from '@tei/material';
import { debounceTime, distinctUntilChanged, map } from 'rxjs';
import { GetSurchargeSettingsRequest, GetSurchargeSettingsResponse, ISurchargeSettingsService, SurchargeSettingsRequestForm } from '../../../@intermediate/surcharge-settings';

@Component({
  selector: 'tei-portal-admin-center-payment-engine-settings-surcharge',
  templateUrl: './surcharge-settings.component.html',
  styleUrls: ['./surcharge-settings.component.scss']
})
export class SurchargeSettingsComponent implements OnInit
{

  surchargeItems = [];
  breadCrumbData: BreadCrumb[] = [
    {
      title: this.translate.instant('ADMIN_CENTER.HOST.SETTINGS.PORTAL_ADMIN_CENTER'),
      relativeUrl: ''
    },
    {
      title: this.translate.instant('ADMIN_CENTER.HOST.SETTINGS.SETUP'),
      relativeUrl: '../../'
    },
    {
      title: this.translate.instant('ADMIN_CENTER.HOST.SETTINGS.PAYMENT_ENGINE'),
      relativeUrl: '../'
    },
    {
      title: this.translate.instant('ADMIN_CENTER.HOST.SETTINGS.SURCHARGE_SETTINGS'),
      relativeUrl: ''
    },
  ];
  isPageLoading = false;
  form: SurchargeSettingsRequestForm;
  model: GetSurchargeSettingsResponse;

  pageSize = 1000;
  currentPage = 0;
  isLoading = false;
  isLastPage = false;
  filteredItems: any[] = [];
  displayedItems: any[] = [];

  @ViewChild('auto') matAutocomplete!: MatAutocomplete;

  constructor(private translate: TranslateService, private snackBar: SnackBarService,
    @Inject('ISurchargeSettingsService') private surchargeSettingsService: ISurchargeSettingsService)
  {
  }
  filterSurchargeItems(name: string)
  {
    return this.surchargeItems?.filter((state) => state.label.toLowerCase().indexOf(name.toLowerCase()) === 0);
  }

  async ngOnInit()
  {
    this.surchargeSettingsService.Get(new GetSurchargeSettingsRequest).subscribe(response =>
    {
      this.model = response;
      this.form = new SurchargeSettingsRequestForm(response);
      this.form.get('SurchargeItemID').addValidators(this.selectedItemValidator());

      this.filteredItems = [...this.model.SurchargeItems];

      this.form.get('SurchargeItemID')!.valueChanges.pipe(
        debounceTime(300),
        distinctUntilChanged(),
        map(value => this.filterItems(value || ''))
      ).subscribe(filtered =>
      {
        this.filteredItems = filtered;
        this.resetPagination();
        this.loadMoreItems();
      });

      setTimeout(() =>
      {
        this.matAutocomplete.opened.subscribe(() =>
        {
          setTimeout(() => this.attachScrollListener(), 200);
        });
      }, 1000);

      if (this.model.SurchargeItemID)
        this.form.get('SurchargeItemID')!.patchValue(this.model.SurchargeItemID);
      this.loadMoreItems();
    });
  }

  attachScrollListener()
  {
    if (!this.matAutocomplete.panel) return;
    const panel = this.matAutocomplete.panel.nativeElement;

    panel.addEventListener('scroll', () =>
    {
      const atBottom = panel.scrollTop + panel.clientHeight >= panel.scrollHeight - 10;
      if (atBottom && !this.isLoading)
      {
        this.loadMoreItems();
      }
    });
  }

  displayFn(itemId: string): string
  {
    if (!itemId || !this.model || !this.model.SurchargeItems)
    {
      return '';
    }
    const item = this.model.SurchargeItems.find(item => item.ItemID === itemId);
    return item ? item.Description : '';
  }

  filterItems(value: string): any[]
  {
    if (!this.model || !this.model.SurchargeItems)
    {
      return [];
    }

    // If value is empty, return all items
    if (!value)
    {
      return this.model.SurchargeItems;
    }

    // Check if value is an ItemID (which means an option was selected)
    const itemById = this.model.SurchargeItems.find(item => item.ItemID === value);
    if (itemById)
    {
      return [itemById];
    }

    // Otherwise filter by Description containing the typed value
    const filterValue = value.toLowerCase();
    return this.model.SurchargeItems.filter(item =>
      item.Description.toLowerCase().includes(filterValue)
    );
  }

  loadMoreItems()
  {
    if (this.isLoading || this.isLastPage) return;

    this.isLoading = true;
    const start = this.currentPage * this.pageSize;
    const end = start + this.pageSize;

    const itemsToShow = this.filteredItems.slice(start, end);

    if (itemsToShow.length === 0)
    {
      this.isLastPage = true; // No more items available
    } else
    {
      this.displayedItems = [...this.displayedItems, ...itemsToShow];
      this.currentPage++; // Increment the page number for the next set of items
    }

    this.isLoading = false;
  }

  resetPagination()
  {
    this.displayedItems = [];
    this.currentPage = 0;
    this.isLastPage = false;
  }

  selectedItemValidator(): ValidatorFn
  {
    return (control: AbstractControl): ValidationErrors | null =>
    {

      const value = control.value;

      const itemById = this.model.SurchargeItems.find(item => item.ItemID === value);
      if (itemById)
      {
        return null;
      }
      else
        return { hasItem: true };
    };
  }

  save(form: SurchargeSettingsRequestForm)
  {
    this.isPageLoading = true;
    this.surchargeSettingsService.Post(form.value).subscribe(
      (response) =>
      {
        this.isPageLoading = false;
        this.snackBar.showSuccess(this.translate.instant('PAYMENT_ENGINE.SURCHARGE_SETTINGS.SETTINGS_SAVED'));
      },
      (error) =>
      {
        this.isPageLoading = false;
      });
  }
}
