import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { BookingSetupRoutingModule } from './booking-setup-routing.module';
import { BookingSetupComponent } from './booking-setup.component';
import { SecureNavigationModule } from '@tei/material-smart-components';
import { BreadCrumbModule } from '@tei/material';
import { BookingFieldsComponent } from './booking-fields/booking-fields.component';


const TEI_MATERIAL_MODULES = [SecureNavigationModule, BreadCrumbModule];

@NgModule({
  declarations: [BookingSetupComponent, BookingFieldsComponent],
  imports: [
    CommonModule,
    BookingSetupRoutingModule,
    ...TEI_MATERIAL_MODULES
  ],
  exports: [BookingSetupComponent]
})
export class BookingSetupModule { }
