import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { BreadCrumbModule } from '@tei/material';
import { RouterModule } from '@angular/router';
import { ECommerceTransactionInquiryComponent } from './ecommerce-transaction-inquiry.component';
import { SecureNavigationService } from '@tei/material-smart-components';
import { ECommerceTransactionsLookupModule } from '@tei/retail-admin';
import { MatStepperModule } from '@angular/material/stepper';

const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule];

const TEI_MATERIAL_MODULES = [ BreadCrumbModule];
const MATERIAL_MODULES = [MatStepperModule];
const TEI_CORE_MODULES = [TeiTextTranslatePipeModule];
const COMPONENTS = [ECommerceTransactionInquiryComponent];
const TEI_RETAIL_ADMIN = [ECommerceTransactionsLookupModule];

@NgModule({
  declarations: [...COMPONENTS],
    imports: [
      RouterModule.forChild([
        { path: '', component: ECommerceTransactionInquiryComponent },
      ]),
      ...BASE_MODULES,
      ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
      ...TEI_CORE_MODULES,
      ...TEI_RETAIL_ADMIN
  ],
    providers: [
      { provide: 'ISecureNavigationService', useClass: SecureNavigationService }],
})

export class ECommerceTransactionInquiryModule { }
