import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { IPWAAppSetupService } from '../@intermediate/pwa-app-setup.interface';
import { GetPWAAppSetupRequest, GetPWAAppSetupResponse } from '../@intermediate/models/get-pwa-app-setup.model';
import { PostPWAAppSetupRequest, PostPWAAppSetupResponse } from '../@intermediate/models/post-pwa-app-setup.model';

@Injectable({
  providedIn: 'root'
})
export class PWAAppSetupService implements IPWAAppSetupService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Admin/AdminCenter/TenantWebAliasManifest';
  }

  public Get(request: GetPWAAppSetupRequest): Observable<GetPWAAppSetupResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.get<GetPWAAppSetupResponse>(apiUrl, { params: request as any });
  }

  public Post(request: FormData): Observable<PostPWAAppSetupResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.post<PostPWAAppSetupResponse>(apiUrl, request);
  }
}
