import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { AccessNavigationComponent } from './access-navigation.component';
import { RouterModule } from '@angular/router';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { TranslateModule } from '@ngx-translate/core';

import { SecureNavigationModule } from '@tei/material-smart-components';
import { BreadCrumbModule } from '@tei/material';
import { IntegrationsComponent } from '../../pages/integrations/integrations.component';
import { ManagePortalProductsComponent } from '../../pages/host-admin-center/portal-group/manage-portal-products/manage-portal-products.component';
import { AnalyticsComponent } from '../../pages/host-admin-center/analytics/analytics.component';


const COMPONENTS = [AccessNavigationComponent,];
const BASE_MODULES = [CommonModule, RouterModule];
const TEI_CORE_MODULES = [TeiTextTranslatePipeModule, TranslateModule, BreadCrumbModule];
const TEI_MATERIAL_SMART_COMPONENTS_MODULES = [
  SecureNavigationModule
];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    RouterModule.forChild([
      { path: '', component:  AccessNavigationComponent},
      {path: 'analytics', component: AnalyticsComponent},
      { path: 'integrations', component: IntegrationsComponent },
      {path: 'products', component: ManagePortalProductsComponent,},

    ]),
    ...BASE_MODULES,
    ...TEI_CORE_MODULES,
    ...TEI_MATERIAL_SMART_COMPONENTS_MODULES
  ]
})
export class AccessNavigationModule { }
