import { Component, Inject, OnDestroy, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { Subscription } from 'rxjs';
import { PortalSetupJobsTerminal } from '../../@intermediate/portal-setup-jobs/models/get-ecommerce-setup.model';
import { EcommerceSetupJobDialogModel } from './dialog-data.model';

@Component({
  selector: 'tei-ecommerce-setup-dialog',
  templateUrl: './ecommerce-setup-dialog.component.html',
  styleUrls: ['./ecommerce-setup-dialog.component.scss'],
})

export class EcommerceSetupDialogComponent implements OnInit, OnDestroy
{
  subscription = new Subscription();
  dialogData: EcommerceSetupJobDialogModel;
  form = new FormGroup({
    SiteID: new FormControl(null, Validators.required),
    TerminalID: new FormControl(null, Validators.required),
    AutomatedNotificationID: new FormControl(null, Validators.required),
  });

  constructor(public dialog: MatDialog,
    @Inject(MAT_DIALOG_DATA) public data)
  {
    this.dialogData = this.data.dialogData;
  }

  ngOnInit()
  {
    this.form.patchValue({
      SiteID: this.dialogData.options.Sites[0]?.SiteID,
      TerminalID: this.dialogData.options.Sites[0]?.Terminals[0]?.TerminalID,
      AutomatedNotificationID: this.dialogData.options.AutomationGroups[0]?.AutomationID ? `${ this.dialogData.options.AutomationGroups[0]?.AutomationID }` : null,
    });
    this.subscription.add(this.form.controls.SiteID.valueChanges.subscribe(() =>
    {
      this.form.get("TerminalID").setValue(null);
    }));
  }

  get terminals(): PortalSetupJobsTerminal[]
  {
    return this.form.controls.SiteID.value ? (this.dialogData.options.Sites.find(site => site.SiteID === this.form.controls.SiteID.value)?.Terminals || []) : [];
  }

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }
}


