import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatDividerModule } from '@angular/material/divider';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyMenuModule as MatMenuModule } from '@angular/material/legacy-menu';
import { MatLegacyTableModule as MatTableModule } from '@angular/material/legacy-table';
import { RouterModule } from '@angular/router';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { AvatarModule, BreadCrumbModule, QuickSearchFilterChipModule, SelectChipModule } from '@tei/material';
import { PortalGroupsService } from '../../../../@services/portal-groups.service';
import { PortalService } from '../../../../@services/portal.service';
import { TenantsService } from '../../../../@services/tenants.service';
import { AddEditPortalDialogModule } from './add-edit-portal/add-edit-portal.module';
import { ManagePortalGroupPortalsComponent } from './manage-portal-group-portals.component';

const COMPONENTS = [ManagePortalGroupPortalsComponent];
const BASE_MODULES = [CommonModule, RouterModule];
const MATERIAL_MODULES = [MatIconModule, MatButtonModule, MatDividerModule, MatMenuModule, MatTableModule];
const TEI_MATERIAL_MODULES = [
  AvatarModule,
  BreadCrumbModule,
  QuickSearchFilterChipModule,
  TeiTextTranslatePipeModule,
  SelectChipModule
];
const ADMIN_CENTER_MODULES = [AddEditPortalDialogModule];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...ADMIN_CENTER_MODULES,
    ...TEI_MATERIAL_MODULES
  ],
  exports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...ADMIN_CENTER_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...COMPONENTS
  ],
  providers: [
    { provide: 'IPortalGroupsService', useClass: PortalGroupsService },
    { provide: 'ITenantsService', useClass: TenantsService },
    { provide: 'IPortalService', useClass: PortalService }
  ]
})
export class ManagePortalGroupPortalsModule { }
