import { Component, Inject, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { BreadCrumb } from '@tei/material';
import { Subscription } from 'rxjs';
import { IAdminPortalGroupService } from '../../../../@intermediate/portal-group/admin-portal-groups.interface';
import { CreatePortalGroupForm, PostPortalGroupsRequest } from '../../../../@intermediate/portal-group/models/post-portal-groups.model';

@Component({
  selector: 'tei-host-admin-add-portal-group',
  templateUrl: './add-portal-group.component.html',
  styleUrls: ['./add-portal-group.component.scss']
})
export class AddPortalGroupComponent implements OnInit, OnDestroy
{
  breadCrumbData: BreadCrumb[] = [
    {
      title: this.translate.instant('ADMIN_CENTER.GROUP.PORTAL_GROUPS.TITLE'),
      relativeUrl: '../'
    },
    {
      title: this.translate.instant('ADMIN_CENTER.GROUP.NEW_PORTAL_GROUP.NEW_PORTAL_GROUP'),
      relativeUrl: ''
    }
  ];
  isLoading;
  form: CreatePortalGroupForm = new CreatePortalGroupForm();
  subscription: Subscription = new Subscription();

  constructor(
    @Inject('IAdminPortalGroupService') private hostAdminPortalGroupsService: IAdminPortalGroupService,
    private translate: TranslateService,
    private router: Router,
    private activatedRoute: ActivatedRoute,
  )
  {
  }

  ngOnInit()
  {
    this.subscription.add(
      this.form.controls.PortalGroupName.valueChanges.subscribe((value = "") =>
      {
        this.form.controls.PortalGroupIdentifier.setValue(value.replace(/\s/g, ''));
      })
    );
  }

  onSave()
  {
    const postRequest: PostPortalGroupsRequest = {
      PortalGroupName: this.form.value.PortalGroupName
    };

    this.hostAdminPortalGroupsService.Post(postRequest).subscribe({
      next: (response) =>
      {
        this.router.navigate(['/admin-center/host/portal-groups']);
      }
    });
  }

  ngOnDestroy(): void
  {
    this.subscription?.unsubscribe();
  }
}
