import { UntypedFormControl, UntypedFormGroup } from "@angular/forms";

export class PostPortalGroupsRequest
{
  ClientName?: string;
  PortalGroupName: string;
}

export class PostPortalGroupsResponse
{
  PortalGroupID: number;
}

export class CreatePortalGroupForm extends UntypedFormGroup
{
  constructor()
  {
    super({
      PortalGroupName: new UntypedFormControl(""),
      PortalGroupIdentifier: new UntypedFormControl({ value: "", disabled: true })
    });
  }

  public value: PostPortalGroupsRequest;
}
