import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetEmailSettingsRequest, GetEmailSettingsResponse } from '../@intermediate/models/get-email-settings.model';
import { PostEmailSettingsRequest, PostEmailSettingsResponse } from '../@intermediate/models/post-email-settings.model';
import { IEmailSettingsService } from '../@intermediate/email-settings.interface';

@Injectable()
export class EmailSettingsService implements IEmailSettingsService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Admin/AdminCenter/Portal/EmailSettings';
  }

  Get(request: GetEmailSettingsRequest): Observable<GetEmailSettingsResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.get<GetEmailSettingsResponse>(apiUrl, { params: { PortalID: request.PortalID } });
  }

  Post(request: PostEmailSettingsRequest): Observable<PostEmailSettingsResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.post<PostEmailSettingsRequest>(apiUrl, request);
  }
}
