import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { RouteData } from '@tei/core';
import { PortalAdminCenterSettingsComponent } from './portal-admin-center-settings.component';

const routes: Routes = [
  {
    path: '',
    component: PortalAdminCenterSettingsComponent,
    children: []
  },
  {
    path: 'google-analytics',
    loadChildren: () => import('../google-authenticator-setting/google-authenticator-setting.module').then(m => m.GoogleAuthenticatorSettingModule),
    data: { RouteData: new RouteData({ Title: 'google-authenticator' }) }
  },
  {
    path: 'printer-credentials',
    loadChildren: () => import('../printers-setting/printers-setting.module').then(m => m.PrintersSettingModule),
    data: { RouteData: new RouteData({ Title: 'printer-credentials' }) }
  },
  {
    path: 'payment-engine',
    loadChildren: () => import('../payment-engine-settings/portal-admin-center-payment-engine-settings.module').then(m => m.PortalAdminCenterPaymentEngineSettingsModule),
    data: { RouteData: new RouteData({ Title: 'payment-engine' }) }
  },
  {
    path: 'customer-facing-displays',
    loadChildren: () => import('../customer-facing-displays/customer-facing-displays.module').then(m => m.CustomerFacingDisplaysModule),
    data: { RouteData: new RouteData({ Title: 'customer-display-facing' }) },
  },
  {
    path: 'authentication-settings',
    loadChildren: () => import('../authentication-setup-settings/authentication-setup-settings.module').then(m => m.AuthenticationSetupSettingsModule),
    data: { RouteData: new RouteData({ Title: 'authentication-settings' }) }
  },
  {
    path: 'web-admin-center',
    loadChildren: () => import('@tei/common-module-settings').then(m => m.ModuleSettingsModule),
    data: { RouteData: new RouteData({ Title: 'web-admin-center' }) },
  },

  { path: '**', redirectTo: '' },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class PortalAdminCenterSettingsRoutingModule {
}
