import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { TranslateModule } from '@ngx-translate/core';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { BreadCrumbModule } from '@tei/material';
import { SecureNavigationModule } from '@tei/material-smart-components';
import { ManagePortalTabComponent } from './manage-portal-tab.component';

const COMPONENTS = [ManagePortalTabComponent];
const BASE_MODULES = [CommonModule, RouterModule];
const CORE_MODULES = [TeiTextTranslatePipeModule, TranslateModule, BreadCrumbModule];
const TEI_MATERIAL_SMART_COMPONENTS_MODULES = [
  SecureNavigationModule
];
@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...CORE_MODULES,
    ...TEI_MATERIAL_SMART_COMPONENTS_MODULES
  ],
  exports: [
    ...COMPONENTS,
    ...CORE_MODULES,
    ...TEI_MATERIAL_SMART_COMPONENTS_MODULES

  ]
})
export class ManagePortalTABModule { }
