import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatNativeDateModule } from '@angular/material/core';
import { MatDatepickerModule } from '@angular/material/datepicker';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacySlideToggleModule as MatSlideToggleModule } from '@angular/material/legacy-slide-toggle';
import { RouterModule } from '@angular/router';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { BreadCrumbModule, FormTimePickerModule, LoadingModule } from '@tei/material';
import { SettingLinkModule } from '../../components/setting-link/setting-link.module';
import { PortalAdminCenterSettingsComponent } from './portal-admin-center-settings.component';
import { PortalAdminCenterSettingsRoutingModule } from './portal-admin-center-settings-routing.module';
import { PrintersSettingModule } from '../printers-setting/printers-setting.module';
import { SecureNavigationModule } from '@tei/material-smart-components';

const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule, RouterModule];
const MATERIAL_MODULES = [MatIconModule, MatSlideToggleModule, MatFormFieldModule, MatInputModule, MatButtonModule];
const TEI_MATERIAL_MODULES = [FormTimePickerModule, MatDatepickerModule, MatNativeDateModule, LoadingModule, BreadCrumbModule, SecureNavigationModule];
const TEI_CORE_MODULES = [TeiTextTranslatePipeModule];
const ADMIN_MODULES = [SettingLinkModule, PrintersSettingModule];
const COMPONENTS = [PortalAdminCenterSettingsComponent];

@NgModule({
  declarations: [
    ...COMPONENTS
  ],
  imports: [RouterModule.forChild([
    { path: '', component: PortalAdminCenterSettingsComponent },

  ]), PortalAdminCenterSettingsRoutingModule,
  ...BASE_MODULES, ...ADMIN_MODULES, ...TEI_CORE_MODULES, ...MATERIAL_MODULES, ...TEI_MATERIAL_MODULES],
  providers: []
})
export class PortalAdminCenterSettingsModule { }
