export class ItemNode
{
  public Children: ItemNode[] = [];
  public Item: TreeNode;
}

export class ItemFlatNode
{
  public Item: TreeNode;
  public Level: number = 0;
  public Expandable: boolean = false;
}

export class TreeNode
{
  public Title: string;
  public Id: string;
  public Key: string;
  public IsSelected: boolean = false;
}
