import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { BreadCrumbModule, LoadingModule } from '@tei/material';
import { PortalSetupJobsService } from '../../@services/portal-setup-jobs.service';
import { AddEditPortalDatabaseModule } from '../../components/add-edit-portal-details/portal-database/portal-database.module';
import { AddEditPortalDetailsModule } from '../../components/add-edit-portal-details/portal-details/portal-details.module';
import { PortalSetupJobsComponent } from './portal-setup-jobs.component';
import { EcommerceSetupDialogModule } from '../../components/ecommerce-setup-dialog/ecommerce-setup-dialog.module';

const MATERIAL_MODULES = [
  MatButtonModule
];
const FORM_GROUP = [FormsModule, ReactiveFormsModule];
const TEI_MATERIAL_MODULES = [
  LoadingModule,
  BreadCrumbModule,
  EcommerceSetupDialogModule
];
const COMMON_MODULES = [CommonModule, TeiTextTranslatePipeModule];
const ADMIN_CENTER_MODULES = [
  AddEditPortalDatabaseModule,
  AddEditPortalDetailsModule
];
const COMPONENTS = [PortalSetupJobsComponent];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...FORM_GROUP,
    ...ADMIN_CENTER_MODULES,
    ...COMMON_MODULES
  ],
  exports: [
    ...COMPONENTS
  ],
  providers: [
    { provide: 'IPortalSetupJobsService', useClass: PortalSetupJobsService }
  ],
})
export class PortalSetupJobsModule
{ }
