import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacyRadioModule as MatRadioModule } from '@angular/material/legacy-radio';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { BreadCrumbModule, LoadingModule, SnackBarModule } from '@tei/material';
import { AdminPortalService } from '../../../../../@services/admin-portal.service';
import { PortalGroupsService } from '../../../../../@services/portal-groups.service';
import { AddEditPortalDatabaseModule } from '../../../../../components/add-edit-portal-details/portal-database/portal-database.module';
import { AddEditPortalDetailsModule } from '../../../../../components/add-edit-portal-details/portal-details/portal-details.module';
import { AddEditPortalDialogComponent } from './add-edit-portal.component';

const MATERIAL_MODULES = [
  MatInputModule,
  MatFormFieldModule,
  MatIconModule,
  MatRadioModule,
  MatButtonModule
];
const FORM_GROUP = [FormsModule, ReactiveFormsModule];
const TEI_MATERIAL_MODULES = [
  BreadCrumbModule,
  LoadingModule,
  SnackBarModule
];
const COMMON_MODULES = [CommonModule, TeiTextTranslatePipeModule];
const ADMIN_CENTER_MODULES = [
  AddEditPortalDatabaseModule,
  AddEditPortalDetailsModule
];
const COMPONENTS = [AddEditPortalDialogComponent];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...FORM_GROUP,
    ...ADMIN_CENTER_MODULES,
    ...COMMON_MODULES
  ],
  exports: [
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...FORM_GROUP,
    ...ADMIN_CENTER_MODULES,
    ...COMMON_MODULES,
    ...COMPONENTS
  ],
  providers: [
    { provide: 'IPortalGroupsService', useClass: PortalGroupsService },
    { provide: 'IAdminPortalService', useClass: AdminPortalService }
  ],
})
export class AddEditPortalDialogModule
{ }
