import { Component, Inject, OnDestroy, OnInit } from "@angular/core";
import { FormControl, FormGroup, Validators } from "@angular/forms";
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { ActivatedRoute, Router } from "@angular/router";
import { TranslateService } from "@ngx-translate/core";
import { ManageLoadingService } from "@tei/core";
import { BreadCrumb, ImageUploaderComponent, ImageUploaderModel, SnackBarService } from "@tei/material";
import { Subscription } from "rxjs";
import { IHostAdminCenterHelperService } from "../../@intermediate/host-admin-center-helper.interface";
import { displayEnum, GetPWAAppSetupRequest, GetPWAAppSetupResponse, orientationEnum, PWAAppSetupForm } from "../../@intermediate/models/get-pwa-app-setup.model";
import { portalGroupDetails } from "../../@intermediate/models/host-admin-center-helper.model";
import { PostPWAAppSetupRequest, PostPWAAppSetupResponse, SizedImageEnum } from "../../@intermediate/models/post-pwa-app-setup.model";
import { IPWAAppSetupService } from "../../@intermediate/pwa-app-setup.interface";

@Component({
  selector: 'tei-pwa-app-setup',
  templateUrl: './pwa-app-setup.component.html',
  styleUrls: ['./pwa-app-setup.component.scss'],
})
export class PWAAppSetupComponent implements OnInit, OnDestroy
{
  get formControls()
  {
    return this.form.controls;
  }
  file = null;
  PWAAppSetupResponse = new GetPWAAppSetupResponse();
  PWAAppSetupRequest = new PostPWAAppSetupRequest();
  isSmallScreen: boolean;
  isLoading = false;
  portalDetail: portalGroupDetails;
  subscription: Subscription = new Subscription();
  breadCrumbData: BreadCrumb[] = [];
  portalID: number;
  displayItems = [
    { name: 'Standalone', value: displayEnum.standalone },
    { name: 'Full screen', value: displayEnum.fullscreen },
    { name: 'Minimal UI', value: displayEnum.minimal_ui },
    { name: 'Browser', value: displayEnum.browser }
  ];
  orientationItems = [
    { name: 'Portrait', value: orientationEnum.portrait },
    { name: 'Landscape', value: orientationEnum.landscape },
    { name: 'Natural', value: orientationEnum.natural }
  ];
  form = new FormGroup<PWAAppSetupForm>({
    ShortName: new FormControl('', Validators.required),
    Name: new FormControl('', Validators.required),
    Description: new FormControl('', Validators.required),
    ThemeColor: new FormControl('', Validators.required),
    BackgroundColor: new FormControl('', Validators.required),
    Display: new FormControl(displayEnum.standalone, Validators.required),
    Orientation: new FormControl(orientationEnum.portrait, Validators.required),
    StartURL: new FormControl('', Validators.required),
    Icon_72_Url: new FormControl('', Validators.required),
    Icon_96_Url: new FormControl(''),
    Icon_128_Url: new FormControl(''),
    Icon_144_Url: new FormControl(''),
    Icon_152_Url: new FormControl(''),
    Icon_192_Url: new FormControl('', Validators.required),
    Icon_384_Url: new FormControl(''),
    Icon_512_Url: new FormControl('', Validators.required)
  });

  constructor(
    @Inject("IPWAAppSetupService") private service: IPWAAppSetupService,
    private translate: TranslateService,
    public manageLoadingService: ManageLoadingService,
    private router: Router,
    private route: ActivatedRoute,
    @Inject('IHostAdminCenterHelperService') private hostAdminCenterHelperService: IHostAdminCenterHelperService,
    private dialog: MatDialog,
    private snackBar: SnackBarService)
  { }

  ngOnInit(): void
  {
    this.loadCurrentPortalGroupDetails();
  }

  loadCurrentPortalGroupDetails(): void
  {
    this.subscription.add(this.hostAdminCenterHelperService.currentPortalGroupDetails.subscribe(portalDetail =>
    {
      if (portalDetail)
      {
        this.portalDetail = {
          parentPortalGroup: portalDetail.parentPortalGroup.PortalGroup,
          portal: portalDetail.portal
        };
        this.fillBreadCrump();
        this.loadData(portalDetail?.portal?.PortalID);
        this.portalID = portalDetail?.portal?.PortalID;
      }
    }));
  }

  private async fillBreadCrump(): Promise<void>
  {
    this.breadCrumbData = [];
    this.breadCrumbData = [
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.PORTAL_GROUPS.TITLE'),
        relativeUrl: '../../../../',
      },
      {
        title: `${ this.portalDetail.parentPortalGroup.PortalGroupName }`,
        relativeUrl: `../../../`,
      },
      {
        title: `${ this.portalDetail.portal.PortalName }`,
        relativeUrl: '../',
      },
      {
        title: this.translate.instant('ADMIN_CENTER.PWA_APP_SETUP.TITLE'),
        relativeUrl: '',
      }
    ];
  }

  loadData(PortalID: number): void
  {
    this.manageLoadingService.startLoading();
    const request = new GetPWAAppSetupRequest();
    request.PortalID = PortalID;

    this.subscription.add(this.service.Get(request).subscribe(
      (result: GetPWAAppSetupResponse) =>
      {
        this.form.patchValue(
          {
            ShortName: result.ShortName,
            Name: result.Name,
            Description: result.Description,
            ThemeColor: result.ThemeColor,
            BackgroundColor: result.BackgroundColor,
            Display: result.Display,
            Orientation: result.Orientation,
            StartURL: result.StartUrl,
            Icon_72_Url: result.Icon_72_Url,
            Icon_96_Url: result.Icon_96_Url,
            Icon_128_Url: result.Icon_128_Url,
            Icon_144_Url: result.Icon_144_Url,
            Icon_152_Url: result.Icon_152_Url,
            Icon_192_Url: result.Icon_192_Url,
            Icon_384_Url: result.Icon_384_Url,
            Icon_512_Url: result.Icon_512_Url
          });
        this.PWAAppSetupResponse = result;
        this.manageLoadingService.finishLoading();
      },
      (err) =>
      {
      }));
  }

  onChange512SizedImage(): void
  {
    this.onChangeUploadImage(SizedImageEnum.Size512X512);
  }

  onChange192SizedImage()
  {
    this.onChangeUploadImage(SizedImageEnum.Size192X192);
  }

  onChange128SizedImage(): void
  {
    this.onChangeUploadImage(SizedImageEnum.Size128X128);
  }

  onChange72SizedImage(): void
  {
    this.onChangeUploadImage(SizedImageEnum.Size72X72);
  }

  onChangeUploadImage(fieldName: SizedImageEnum): void
  {
    const imageFormats = '.png';
    const imagePermittedFileTypes = ['PNG'];
    const data: ImageUploaderModel = {
      round: false,
      maintainAspectRatio: false,
      aspectRatio: 4 / 2.5,
      acceptFormat: imageFormats,
      permittedFileTypes: imagePermittedFileTypes,
      maxSize: 512,
      imageServerUrl: this.returnImageURL(fieldName),
      origImage: this.file,
      croppedImage: this.returnImageURL(fieldName),
    };

    const imageUploaderDialog = this.dialog.open(ImageUploaderComponent, {
      width: '400px',
      panelClass: ['o-dialog-md-fullscreen'],
      data,
    });
    imageUploaderDialog.afterClosed().subscribe(result =>
    {
      if (result)
      {
        this.file = result.origImage;
        switch (fieldName)
        {
          case SizedImageEnum.Size72X72:
            this.PWAAppSetupResponse.Icon_72_Url = result?.croppedImage;
            this.formControls.Icon_72_Url.setValue(result?.origImage);
            break;
          case SizedImageEnum.Size128X128:
            this.PWAAppSetupResponse.Icon_128_Url = result?.croppedImage;
            this.formControls.Icon_128_Url.setValue(result?.origImage);
            break;
          case SizedImageEnum.Size192X192:
            this.PWAAppSetupResponse.Icon_192_Url = result?.croppedImage;
            this.formControls.Icon_192_Url.setValue(result?.origImage);
            break;
          case SizedImageEnum.Size512X512:
            this.PWAAppSetupResponse.Icon_512_Url = result?.croppedImage;
            this.formControls.Icon_512_Url.setValue(result?.origImage);
            break;
        }
      }
    });
  }

  returnImageURL(fieldName: SizedImageEnum): string
  {
    switch (fieldName)
    {
      case SizedImageEnum.Size72X72: return this.PWAAppSetupResponse.Icon_72_Url;
      case SizedImageEnum.Size128X128: return this.PWAAppSetupResponse.Icon_128_Url;
      case SizedImageEnum.Size192X192: return this.PWAAppSetupResponse.Icon_192_Url;
      case SizedImageEnum.Size512X512: return this.PWAAppSetupResponse.Icon_512_Url;
    }
  }

  saveSetting(): void
  {
    const formData = new FormData();
    formData.append('PortalID', this.portalID.toString());
    formData.append('ShortName', this.formControls.ShortName.value);
    formData.append('Name', this.formControls.Name.value);
    formData.append('Description', this.formControls.Description.value);
    formData.append('ThemeColor', this.formControls.ThemeColor.value);
    formData.append('BackgroundColor', this.formControls.BackgroundColor.value);
    formData.append('Display', this.formControls.Display.value);
    formData.append('Orientation', this.formControls.Orientation.value);
    formData.append('StartURL', this.formControls.StartURL.value);
    formData.append('Icon_72', this.formControls.Icon_72_Url.value);
    formData.append('Icon_192', this.formControls.Icon_192_Url.value);
    formData.append('Icon_512', this.formControls.Icon_512_Url.value);
    this.manageLoadingService.startLoading();

    this.subscription.add(this.service.Post(formData).subscribe(

      {
        next: (response: PostPWAAppSetupResponse) =>
        {
          if (response)
          {
            this.snackBar.showSuccess(this.translate.instant('ADMIN_CENTER.PWA_APP_SETUP.SAVED_SUCCESSFULLY'), '', null, 'left');
            this.manageLoadingService.finishLoading();
          }
        },
        error: err => this.manageLoadingService.finishLoading(),
        complete: () => this.manageLoadingService.finishLoading()
      }
    ));
  }

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }
}
