import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { RouteData } from '@tei/core';
import { PortalAdminCenterPaymentEngineSettingsComponent } from '../payment-engine-settings/portal-admin-center-payment-engine-settings.component';

const routes: Routes = [

  {
    path: '',
    component: PortalAdminCenterPaymentEngineSettingsComponent,
    data: { RouteData: new RouteData({ Title: 'payment-engine-settings' }) }
  },
  {
    path: 'general',
    loadChildren: () => import('./general-settings/general-settings.module').then(m => m.GeneralSettingsModule),
    data: { RouteData: new RouteData({ Title: 'payment-engine-general' }) }
  },
  {
    path: 'surcharges',
    loadChildren: () => import('./surcharge-settings/surcharge-settings.module').then(m => m.SurchargeSettingsModule),
    data: { RouteData: new RouteData({ Title: 'payment-engine-surcharges' }) }
  },
  {
    path: 'transaction-inquiry',
    loadChildren: () => import('./transaction-inquiry/transaction-inquiry.module').then(m => m.TransactionInquiryModule),
    data: { RouteData: new RouteData({ Title: 'payment-engine-transaction-inquiry' }) }
  },
  { path: '**', redirectTo: '' },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class PortalAdminCenterPaymentEngineSettingsRoutingModule
{
}
