import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCheckboxModule as MatCheckboxModule } from '@angular/material/legacy-checkbox';
import { MatLegacyOptionModule as MatOptionModule } from '@angular/material/legacy-core';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacySelectModule as MatSelectModule } from '@angular/material/legacy-select';
import { MatLegacySlideToggleModule as MatSlideToggleModule } from '@angular/material/legacy-slide-toggle';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { BreadCrumbModule, LoadingModule, SelectChipModule, SettingsListModule } from '@tei/material';
import { ManagePortalTABModule } from '../host-admin-center/manage-portal/manage-portal-tab/manage-portal-tab.module';
import { AchqPaymentSettingComponent } from './achq-payment-setting/achq-payment-setting.component';
import { ConvergeSettingsComponent } from './converge-settings/converge-settings.component';
import { GeneralPaymentSettingComponent } from './general-payment-setting/general-payment-setting.component';
import { NoonPaymentSettingComponent } from './noon-payment-setting/noon-payment-setting.component';
import { PaymentSettingsRoutingModule } from './payment-settings-routing.module';
import { PaymentSettingsComponent } from './payment-settings.component';


const ROUTING_MODULES = [PaymentSettingsRoutingModule];
const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule];
const MATERIAL_MODULES = [MatIconModule, MatButtonModule, MatInputModule, MatSlideToggleModule, MatSelectModule, MatFormFieldModule, MatOptionModule, MatCheckboxModule];
const TEI_MATERIAL_MODULES = [BreadCrumbModule, TeiTextTranslatePipeModule, SelectChipModule, SettingsListModule, LoadingModule];
const ADMIN_CENTER_MODULES = [ManagePortalTABModule];
const COMPONENTS = [PaymentSettingsComponent, ConvergeSettingsComponent, GeneralPaymentSettingComponent, NoonPaymentSettingComponent];
@NgModule({
  declarations: [COMPONENTS, AchqPaymentSettingComponent],
  imports: [
    ...ROUTING_MODULES,
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...ADMIN_CENTER_MODULES,
    ...TEI_MATERIAL_MODULES
  ],
  exports: [
    ...COMPONENTS,
    ...ROUTING_MODULES,
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...ADMIN_CENTER_MODULES,
    ...TEI_MATERIAL_MODULES]
})
export class PaymentSettingsModule { }
