import { Component, Inject, OnDestroy, OnInit } from '@angular/core';
import { AbstractControl, UntypedFormBuilder, UntypedFormGroup, ValidationErrors, ValidatorFn, Validators } from '@angular/forms';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ManageLoadingService, ResponsiveService } from '@tei/core';
import { BreadCrumb, ImageUploaderComponent, ImageUploaderModel } from '@tei/material';
import { Observable, Subscription, forkJoin } from 'rxjs';
import { finalize, map } from 'rxjs/operators';
import { ICustomerFacingDisplayService } from '../../../@intermediate/customer-facing-display.interface';
import { BGCFDType, GetCustomerFacingDisplayConfigurationRequest, GetCustomerFacingDisplayConfigurationResponse, Site, Terminal } from '../../../@intermediate/models/get-customer-facing-display-configuration.model';
import { PostCustomerFacingDisplayResponse } from '../../../@intermediate/models/post-customer-facing-display.model';
import { Device, PostCustomerFacingDisplaySearchRequest, PostCustomerFacingDisplaySearchResponse } from '../../../@intermediate/models/post-search-customer-facing-display.model';

@Component({
  selector: 'tei-admin-admin-center-customer-facing-displays-edit',
  templateUrl: './customer-facing-displays-edit.component.html',
  styleUrls: ['./customer-facing-displays-edit.component.scss']
})
export class CustomerFacingDisplaysEditComponent implements OnInit, OnDestroy
{
  subscription = new Subscription();
  form: UntypedFormGroup;
  isMobile = false;
  ActiveBackgroundImage = null;
  NoOrderBackgroundImage = null;
  BGCFDType = BGCFDType;
  CFDData: Device;
  configurations: GetCustomerFacingDisplayConfigurationResponse;
  sites: Site[] = [];
  terminals: Terminal[] = [];
  breadCrumbData: BreadCrumb[] = [
    {
      title: this.translate.instant('ADMIN_CENTER.DEVICES.PRINTERS.PORTAL_ADMIN_CENTER'),
      relativeUrl: ''
    },
    {
      title: this.translate.instant('ADMIN_CENTER.DEVICES.PRINTERS.SETUP'),
      relativeUrl: '../../setup'
    },
    {
      title: this.translate.instant('ADMIN_CENTER.CUSTOMER_FACING_DISPLAYS.TITLE'),
      relativeUrl: '../../'
    },
    {
      title: this.translate.instant('ADMIN_CENTER.CUSTOMER_FACING_DISPLAYS.MANAGE'),
      relativeUrl: ''
    },
  ];

  constructor(private responsiveService: ResponsiveService, private translate: TranslateService, private dialog: MatDialog, private fb: UntypedFormBuilder,
    @Inject('ICustomerFacingDisplayService') private customerFacingDisplayService: ICustomerFacingDisplayService,
    public manageLoadingService: ManageLoadingService, private route: ActivatedRoute, private router: Router) { }

  ngOnInit(): void
  {
    this.getMobileSize();
    this.createForm();
  }

  getRouteParam(): void
  {
    this.route.params.subscribe(param =>
    {
      this.manageLoadingService.startLoading();
      forkJoin({ CFDData: this.getCFDs(), configurations: this.getConfigurations(Number(param.id)) }).pipe(finalize(() => this.manageLoadingService.finishLoading())).subscribe({
        next: (response =>
        {
          this.fillCFDData(response.CFDData, param.id);
          this.fillConfigurations(response.configurations);
        })
      });
    });
  }

  fillCFDData(CFDData: PostCustomerFacingDisplaySearchResponse, deviceId: string): void
  {
    this.CFDData = CFDData.Devices.filter(item => item.DeviceID === Number(deviceId))[0];
    this.fillForm(this.CFDData);
  }

  fillConfigurations(configuration: GetCustomerFacingDisplayConfigurationResponse): void
  {
    this.configurations = configuration;
    this.formControls.AppMode.setValue(configuration.AppModes.filter(item => item.Value === this.CFDData.AppMode)[0]);
    this.formControls.Site.setValue(configuration.Sites.filter(item => item.Value === parseInt(this.CFDData.SiteID))[0]);
    this.formControls.TerminalID.setValue(configuration.Terminals.filter(item => item.Value === parseInt(this.CFDData.TerminalID))[0]);
  }

  getCFDs(): Observable<PostCustomerFacingDisplaySearchResponse>
  {
    const request = new PostCustomerFacingDisplaySearchRequest();
    return this.customerFacingDisplayService.Search(request).pipe(map(res =>
    {
      return res;
    }));
  }

  getConfigurations(deviceId: number): Observable<GetCustomerFacingDisplayConfigurationResponse>
  {
    const request = new GetCustomerFacingDisplayConfigurationRequest();
    request.DeviceID = deviceId;
    return this.customerFacingDisplayService.Configuration(request).pipe(map(res =>
    {
      return res;
    }));
  }

  getMobileSize(): void
  {
    this.subscription.add(this.responsiveService.mediaBreakpoint.subscribe(mediaBreakpoint =>
    {
      this.isMobile = mediaBreakpoint === 'xs' || mediaBreakpoint === 'sm' || mediaBreakpoint === 'md';
    }));
  }

  noWhitespaceValidator(): ValidatorFn
  {
    return (control: AbstractControl): ValidationErrors | null =>
    {
      const isWhitespace = (control.value || '').trim().length === 0;
      return isWhitespace ? { whitespace: true } : null;
    };
  }

  createForm(): void
  {
    this.form = this.fb.group({
      DeviceDescription: ['', [Validators.required, this.noWhitespaceValidator()]],
      AppMode: [null],
      Site: [null],
      TerminalID: [null, Validators.required],
      ActiveBackgroundImage: [null],
      NoOrderBackgroundImage: [null],
      MaskedDeviceSecretCode: ['']
    });
    this.subscription.add(this.formControls.AppMode.valueChanges.subscribe(appMode =>
    {
      this.sites = this.configurations.Sites.filter(item => item.AppMode === appMode.Value);
      this.terminals = [];
      this.formControls.TerminalID.setValue(null);
    }));
    this.subscription.add(this.formControls.Site.valueChanges.subscribe(site =>
    {
      this.terminals = this.configurations.Terminals.filter(item => item.SiteID === site.Value);
      this.formControls.TerminalID.setValue(null);
    }));
    this.getRouteParam();
  }

  fillForm(cfdData: Device): void
  {
    this.form.patchValue({
      DeviceDescription: cfdData.DeviceDescription,
      MaskedDeviceSecretCode: cfdData.MaskedDeviceSecretCode
    });
  }

  onChangeUpload(type: number): void
  {
    const cfdImageFormats = '.jpg,.jpeg,.jpe,.jif,.jfif,.jfi,.png,.gif,.bmp,.dib,.tiff,.tif,.heif,.heic';
    const cfdImagePermittedFileTypes = ['PNG', 'JPG', 'JPEG'];
    const data: ImageUploaderModel = {
      round: false,
      maintainAspectRatio: false,
      aspectRatio: 4 / 2.5,
      acceptFormat: cfdImageFormats,
      permittedFileTypes: cfdImagePermittedFileTypes,
      maxSize: 512,
      imageServerUrl: type === BGCFDType.ActiveBackgroundImage ? this.CFDData.ActiveBackgroundImageURL : this.CFDData.NoOrderBackgroundImageURL,
      origImage: type === BGCFDType.ActiveBackgroundImage ? this.formControls.ActiveBackgroundImage.value : this.formControls.NoOrderBackgroundImage.value,
      croppedImage: type === BGCFDType.ActiveBackgroundImage ? this.ActiveBackgroundImage : this.NoOrderBackgroundImage
    };

    const imageUploaderDialog = this.dialog.open(ImageUploaderComponent, {
      width: '400px',
      panelClass: ['o-dialog-md-fullscreen'],
      data,
    });

    imageUploaderDialog.afterClosed().subscribe(result =>
    {
      if (result)
      {
        if (type === BGCFDType.ActiveBackgroundImage)
        {
          this.CFDData.ActiveBackgroundImageURL = result?.croppedImage;
          this.ActiveBackgroundImage = result?.croppedImage;
          this.formControls.ActiveBackgroundImage.setValue(result?.origImage);
          if (!result.croppedImage)
          {
            this.formControls.ActiveBackgroundImage.setValidators([Validators.required]);
            this.formControls.ActiveBackgroundImage.updateValueAndValidity();
          }
        } else
        {
          this.CFDData.NoOrderBackgroundImageURL = result?.croppedImage;
          this.NoOrderBackgroundImage = result?.croppedImage;
          this.formControls.NoOrderBackgroundImage.setValue(result?.origImage);
          if (!result.croppedImage)
          {
            this.formControls.NoOrderBackgroundImage.setValidators([Validators.required]);
            this.formControls.NoOrderBackgroundImage.updateValueAndValidity();
          }
        }
      }
    });
  }

  onSave(): void
  {
    const formData = new FormData();
    formData.append('DeviceDescription', this.formControls.DeviceDescription.value);
    formData.append('TerminalID', this.formControls.TerminalID.value.Value);
    formData.append('ActiveBackgroundImage', this.formControls.ActiveBackgroundImage.value);
    formData.append('NoOrderBackgroundImage', this.formControls.NoOrderBackgroundImage.value);
    formData.append('DeviceID', this.CFDData.DeviceID.toString());
    this.manageLoadingService.startLoading();
    this.customerFacingDisplayService.Edit(formData).subscribe({
      next: (response: PostCustomerFacingDisplayResponse) =>
      {
        this.manageLoadingService.finishLoading();
        this.router.navigate(['../..'], { relativeTo: this.route });
      },
      error: err => this.manageLoadingService.finishLoading(),
      complete: () => this.manageLoadingService.finishLoading()
    });
  };

  get formControls()
  {
    return this.form.controls;
  }

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }
}
