import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { AvatarModule } from '@tei/material';
import { AccountUserComponent } from './account-user.component';

const BASE_MODULES = [CommonModule];
const MATERIAL_MODULES = [MatButtonModule];
const TEI_MATERIAL_MODULES = [AvatarModule];
const TEI_CORE_MODULES = [TeiTextTranslatePipeModule];
const COMPONENTS = [AccountUserComponent];

@NgModule({
  declarations: [
    ...COMPONENTS
  ],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_CORE_MODULES
  ],
  exports: [
    ...COMPONENTS,
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES
  ]
})
export class AccountUserModule { }
