import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatRippleModule } from '@angular/material/core';
import { MatDividerModule } from '@angular/material/divider';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCheckboxModule as MatCheckboxModule } from '@angular/material/legacy-checkbox';
import { MatLegacyTooltipModule as MatTooltipModule } from '@angular/material/legacy-tooltip';
import { RouterModule } from '@angular/router';
import { FormlyModule } from '@ngx-formly/core';
import { FormlyMaterialModule } from '@ngx-formly/material-legacy';
import { FormlyMatToggleModule } from '@ngx-formly/material-legacy/toggle';
import { TranslateModule } from '@ngx-translate/core';
import { TeiCurrencyTranslateModule, TeiTextEllipsisPipeModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { BreadCrumbModule } from '@tei/material';
import { AuthenticationSettingsService } from '../../@services/authentication-settings.service';
import { AuthenticationSettingComponent } from './authentication-setting.component';
import { MatLegacySlideToggleModule as MatSlideToggleModule } from '@angular/material/legacy-slide-toggle';
import { MatLegacySelectModule as MatSelectModule } from '@angular/material/legacy-select';

const COMPONENTS = [AuthenticationSettingComponent];
const CORE_MODULES = [TeiTextTranslatePipeModule, TranslateModule, BreadCrumbModule];
const MATERIAL_MODULES = [MatSlideToggleModule, FormlyMatToggleModule, MatIconModule, FormlyMaterialModule, MatDividerModule, MatTooltipModule,
  MatButtonModule, MatRippleModule, MatCheckboxModule, MatSelectModule];
const FORM_GROUP = [FormsModule, ReactiveFormsModule];
const COMMON_MODULES = [CommonModule, FormlyModule, RouterModule];
const TEI_CORE_MODULES = [TeiCurrencyTranslateModule, TeiTextEllipsisPipeModule];

export function requiredValidatorMessage()
{
  return `this field is required`;
}

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...CORE_MODULES, ...MATERIAL_MODULES, ...FORM_GROUP, ...COMMON_MODULES, ...TEI_CORE_MODULES,
    FormlyModule.forRoot({
      validationMessages: [{ name: 'required', message: requiredValidatorMessage }],
    })
  ],
  exports: [
    ...COMPONENTS
  ], providers: [{ provide: 'IAuthenticationSettingsService', useClass: AuthenticationSettingsService }]
})
export class AuthenticationSettingModule { }
