import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { IAdminPortalService } from '../@intermediate/admin-portal.interface';
import { GetAchqPaymentSettingsConfigRequest, GetAchqPaymentSettingsConfigResponse } from '../@intermediate/models/get-achq-payment-settings-config.model';
import { GetAchqPaymentSettingsRequest, GetAchqPaymentSettingsResponse } from '../@intermediate/models/get-achq-payment-settings.model';
import { GetCoverageSetupRequest, GetCoverageSetupResponse } from '../@intermediate/models/get-coverage-setup.model';
import { GetNoonPaymentRequest, GetNoonPaymentResponse } from '../@intermediate/models/get-noon-paymnet-setup.model';
import { GetPaymentGatewaySettingsRequest, GetPaymentGatewaySettingsResponse } from '../@intermediate/models/get-payment-gateway-settings.model';
import { GetPortalAdvancedSettingsRequest, GetPortalAdvancedSettingsResponse } from '../@intermediate/models/get-portal-advanced-settings.model';
import { GetPortalSettingsRequest, GetPortalSettingsResponse } from '../@intermediate/models/get-portal-settings.model';
import { PostAchqPaymentSettingsRequest, PostAchqPaymentSettingsResponse } from '../@intermediate/models/post-achq-payment-settings.model';
import { PostCoverageSetupRequest, PostCoverageSetupResponse } from '../@intermediate/models/post-coverage-setup.model';
import { PostNoonPaymentRequest, PostNoonPaymentResponse } from '../@intermediate/models/post-noon-paymnet-setup.model';
import { PostPaymentGatewaySettingsRequest, PostPaymentGatewaySettingsResponse } from '../@intermediate/models/post-payment-gateway-settings.model';
import { PostPortalSettingsResetRequest, PostPortalSettingsResetResponse } from '../@intermediate/models/post-portal-settings-reset.model';
import { PostPortalSettingsResponse } from '../@intermediate/models/post-portal-settings.model';
import { PostPortalRequest, PostPortalResponse } from '../@intermediate/portal/post-portal.model';


@Injectable({
  providedIn: 'root'
})
export class AdminPortalService implements IAdminPortalService
{
  private readonly serviceUrl: string;
  private readonly adminCenterServiceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Admin/AdminCenter/Portal';
    this.adminCenterServiceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Admin/AdminCenter';
  }

  GetSetting(request: GetPortalSettingsRequest): Observable<GetPortalSettingsResponse>
  {
    const apiUrl = this.serviceUrl + '/GetSetting';
    return this.http.get<GetPortalSettingsResponse>(apiUrl, { params: request as any });
  }

  UpdateSetting(request: FormData): Observable<PostPortalSettingsResponse>
  {
    const apiUrl = this.serviceUrl + '/UpdateSetting';
    return this.http.post<PostPortalSettingsResponse>(apiUrl, request);
  }

  ResetSetting(request: PostPortalSettingsResetRequest): Observable<PostPortalSettingsResetResponse>
  {
    const apiUrl = this.serviceUrl + '/ResetSetting';
    return this.http.post<PostPortalSettingsResetResponse>(apiUrl, request);
  }

  Post(request: PostPortalRequest): Observable<PostPortalResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.post<PostPortalResponse>(apiUrl, request);
  }

  AdvancedSetting(request: GetPortalAdvancedSettingsRequest): Observable<GetPortalAdvancedSettingsResponse>
  {
    const apiUrl = this.serviceUrl + '/AdvancedSetting';
    return this.http.get<GetPortalAdvancedSettingsResponse>(apiUrl, { params: request as any });
  }

  GetPaymentGatewaySettings(request: GetPaymentGatewaySettingsRequest): Observable<GetPaymentGatewaySettingsResponse>
  {
    const apiUrl = this.serviceUrl + '/PaymentGatewaySettings';
    return this.http.get<GetPaymentGatewaySettingsResponse>(apiUrl, { params: request as any });
  }

  PostPaymentGatewaySettings(request: PostPaymentGatewaySettingsRequest): Observable<PostPaymentGatewaySettingsResponse>
  {
    const apiUrl = this.serviceUrl + '/PaymentGatewaySettings';
    return this.http.post<PostPaymentGatewaySettingsResponse>(apiUrl, request);
  }

  GetCoverageSetup(request: GetCoverageSetupRequest): Observable<GetCoverageSetupResponse>
  {
    const apiUrl = this.adminCenterServiceUrl + '/CoverageSetup';
    return this.http.get<GetCoverageSetupResponse>(apiUrl, { params: request as any });
  }

  PostCoverageSetup(request: PostCoverageSetupRequest): Observable<PostCoverageSetupResponse>
  {
    const apiUrl = this.adminCenterServiceUrl + '/CoverageSetup';
    return this.http.post<PostCoverageSetupResponse>(apiUrl, request);
  }

  GetNoonPaymentSetup(request: GetNoonPaymentRequest): Observable<GetNoonPaymentResponse>
  {
    const apiUrl = this.adminCenterServiceUrl + '/NoonPayment';
    return this.http.get<GetNoonPaymentResponse>(apiUrl, { params: request as any });
  }

  PostNoonPaymentSetup(request: PostNoonPaymentRequest): Observable<PostNoonPaymentResponse>
  {
    const apiUrl = this.adminCenterServiceUrl + '/NoonPayment';
    return this.http.post<PostNoonPaymentResponse>(apiUrl, request);
  }

  AchqPaymentSettings(request: GetAchqPaymentSettingsRequest): Observable<GetAchqPaymentSettingsResponse>
  {
    const apiUrl = this.serviceUrl + '/AchqPaymentSettings';
    return this.http.get<GetAchqPaymentSettingsResponse>(apiUrl, { params: request as any });
  }

  AchqPaymentSettingsConfig(request: GetAchqPaymentSettingsConfigRequest): Observable<GetAchqPaymentSettingsConfigResponse>
  {
    const apiUrl = this.serviceUrl + '/AchqPaymentSettingsConfig';
    return this.http.get<GetAchqPaymentSettingsConfigResponse>(apiUrl);
  }

  UpdateAchqPaymentSetting(request: PostAchqPaymentSettingsRequest): Observable<PostAchqPaymentSettingsResponse>
  {
    const apiUrl = this.serviceUrl + '/AchqPaymentSettings';
    return this.http.post<PostAchqPaymentSettingsResponse>(apiUrl, request);
  }
}

