import { ChangeDetectorRef, Component, Inject, OnDestroy, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatCheckboxChange } from '@angular/material/checkbox';
import { ActivatedRoute } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ManageLoadingService, ResponsiveService } from '@tei/core';
import { SnackBarService } from '@tei/material';
import { Subscription, finalize } from 'rxjs';
import { IEmailSettingsService } from '../../../../@intermediate/email-settings.interface';
import { IHostAdminCenterHelperService } from '../../../../@intermediate/host-admin-center-helper.interface';
import { GetEmailSettingsRequest, GetEmailSettingsResponse } from '../../../../@intermediate/models/get-email-settings.model';
import { PostEmailSettingsRequest, PostEmailSettingsResponse } from '../../../../@intermediate/models/post-email-settings.model';
import { PortalGroupByID } from '../../../../@intermediate/portal-group/models/get-portal-group-by-id.model';

@Component({
  selector: 'tei-admin-email-settings',
  templateUrl: './email-settings.component.html',
  styleUrls: ['./email-settings.component.scss'],
})
export class EmailSettingsComponent implements OnInit, OnDestroy
{
  isLoading: boolean = false;
  isMobile = false;
  subscription: Subscription = new Subscription();
  form: FormGroup;
  portalDetail: { parentPortalGroup: PortalGroupByID; portal: any; };
  portalId: any;
  breadCrumbData = [];
  emailSettings: GetEmailSettingsResponse;
  emailTestRecipients: string[];
  chipIsRequired: boolean;
  chipDisabled: boolean;

  constructor(
    @Inject('IHostAdminCenterHelperService') private hostAdminCenterHelperService: IHostAdminCenterHelperService,
    @Inject('IEmailSettingsService') private service: IEmailSettingsService,
    public loadingService: ManageLoadingService,
    private activatedRoute: ActivatedRoute,
    private translate: TranslateService,
    private responsiveService: ResponsiveService,
    private fb: FormBuilder,
    private snackBar: SnackBarService,
    private cdr: ChangeDetectorRef
  ) { }

  ngOnInit(): void
  {
    this.getCurrentPortalGroupDetails();
    this.getMobileSize();
    this.createForm();
    this.getPortalDetail();
  }

  getPortalDetail(): void
  {
    this.subscription.add(
      this.activatedRoute.parent.params.subscribe((params) =>
      {
        this.portalId = params['id'];
        this.getEmailSettings();
      })
    );
  }

  getEmailSettings(): void
  {
    this.isLoading = true;
    const request = new GetEmailSettingsRequest();
    request.PortalID = parseInt(this.portalId);
    this.service
      .Get(request)
      .pipe(finalize(() => (this.isLoading = false)))
      .subscribe({
        next: (response: GetEmailSettingsResponse) =>
        {
          if (response)
          {
            this.emailSettings = response;
            this.chipDisabled = !this.emailSettings.EnableTestMode;
            this.fillEmailsData();
          }
        },
        error: () => { },
      });
  }

  getCurrentPortalGroupDetails(): void
  {
    this.subscription.add(
      this.hostAdminCenterHelperService.currentPortalGroupDetails.subscribe((portalDetail) =>
      {
        if (portalDetail)
        {
          this.portalDetail = {
            parentPortalGroup: portalDetail.parentPortalGroup.PortalGroup,
            portal: portalDetail.portal,
          };
          this.fillBreadCrump();
        }
      })
    );
  }

  getMobileSize(): void
  {
    this.subscription.add(
      this.responsiveService.mediaBreakpoint.subscribe((mediaBreakpoint) =>
      {
        this.isMobile = ['xs', 'sm', 'md'].includes(mediaBreakpoint);
      })
    );
  }

  onSave(): void
  {
    const request: PostEmailSettingsRequest = {
      EmailTestRecipients: this.form.controls['EmailTestRecipients'].value.join(', '),
      EnableTestMode: this.form.controls['EnableTestMode'].value,
      PortalID: parseInt(this.portalId)
    };
    this.loadingService.startLoading();
    this.service
      .Post(request)
      .pipe(finalize(() => this.loadingService.finishLoading()))
      .subscribe({
        next: (response: PostEmailSettingsResponse) =>
        {
          if (response)
          {
            this.snackBar.showSuccess(
              this.translate.instant('ADMIN_CENTER.PRODUCTS.SAVED_SUCCESSFULLY'),
              '',
              null,
              'left'
            );
          }
        },
      });
  }

  fillEmailsData(): void
  {
    this.form.patchValue({
      EnableTestMode: this.emailSettings.EnableTestMode,
      EmailTestRecipients: this.emailSettings.EmailTestRecipients
        ? this.emailSettings.EmailTestRecipients.split(',').map(email => email.trim())
        : []
    });
    if (this.emailSettings?.EmailTestRecipients)
    {
      this.emailTestRecipients = this.emailSettings.EmailTestRecipients.split(', ');
    }
  }

  addEmailChip(emails: string[]): void
  {
    const emailFormArray = this.form.get('EmailTestRecipients') as FormArray;

    emailFormArray.clear();

    emails.forEach(email =>
    {
      emailFormArray.push(this.fb.control(email));
    });
  }

  createForm(): void
  {
    this.form = this.fb.group({
      EnableTestMode: [false],
      EmailTestRecipients: this.fb.array([]),
    });
  }

  get chipControl(): FormArray
  {
    return this.form.get('EmailTestRecipients') as FormArray;
  }

  onTestModeChange(event: MatCheckboxChange): void
  {
    this.chipIsRequired = event.checked;
    this.updateChipValidators();
    this.cdr.detectChanges();
  }

  private updateChipValidators(): void
  {
    if (this.chipIsRequired)
    {
      this.chipControl.setValidators([Validators.required, this.validateArrayNotEmpty]);
      this.chipControl.enable();
    } else
    {
      this.chipControl.clearValidators();
      this.chipControl.setErrors(null);
      this.chipControl.disable();
    }
    this.chipControl.updateValueAndValidity();
  }

  private validateArrayNotEmpty(control: FormArray)
  {
    return control.value?.length === 0 ? { validateArrayNotEmpty: true } : null;
  }

  fillBreadCrump(): void
  {
    this.breadCrumbData = [
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.PORTAL_GROUPS.TITLE'),
        relativeUrl: '../../../../../',
      },
      {
        title: `${ this.portalDetail.parentPortalGroup.PortalGroupName }`,
        relativeUrl: `../../../../`,
      },
      {
        title: `${ this.portalDetail.portal.PortalName }`,
        relativeUrl: '../../',
      },
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.EMAIL_SETTINGS.TITLE'),
        relativeUrl: '',
      },
    ];
  }

  ngOnDestroy(): void
  {
    this.subscription?.unsubscribe();
  }
}
