import { Component, Inject, OnDestroy, OnInit, SkipSelf } from '@angular/core';
import {
  AbstractControl,
  FormArray,
  FormControl,
  FormGroup,
  ValidationErrors,
  ValidatorFn,
  Validators
} from '@angular/forms';
import { ErrorStateMatcher } from "@angular/material/core";
import { ActivatedRoute, Params, Router } from "@angular/router";
import { TranslateService } from "@ngx-translate/core";
import { ManageLoadingService } from "@tei/core";
import { BreadCrumb, SnackBarService } from "@tei/material";
import { Subscription } from "rxjs";
import { AliasType, GetAliasTypeRequest } from "../../../../../@intermediate/models/get-alias-type-model";
import { GetPortalDetailRequest, PortalDetail } from "../../../../../@intermediate/models/get-portal.model";
import { displayEnum, orientationEnum } from "../../../../../@intermediate/models/get-pwa-app-setup.model";
import { FormFactoEnum, TenantWebAliasManifestScreenshotResponse } from "../../../../../@intermediate/models/get-tenant-web-alias-manifest-screenshot.model";
import { GetWebAliasApplicationsRequest, TenantWebAliasApplication } from "../../../../../@intermediate/models/get-web-alias-applications.model";
import { GetAllManifestCategoriesRequest, ManifestCategory } from "../../../../../@intermediate/models/get-web-alias-categories.model";
import { GetWebAliasDetailRequest, GetWebAliasDetailResponse } from "../../../../../@intermediate/models/get-web-alias-detail.model";
import { AddEditPortalGroupAliasesForm, ManifestScreenShotsForm } from "../../../../../@intermediate/models/get-web-alias.model";
import { PostWebAliasResponse } from "../../../../../@intermediate/models/post-web-alias.model";
import { PutWebAliasResponse } from "../../../../../@intermediate/models/put-web-alias.model";
import { GetPortalGroupByIDRequest, PortalGroupByID } from "../../../../../@intermediate/portal-group/models/get-portal-group-by-id.model";
import { IPortalGroupsService } from '../../../../../@intermediate/portal-group/portal-groups.interface';
import { IPortalService } from "../../../../../@intermediate/portal.interface";
import { IWebAliasService } from "../../../../../@intermediate/web-alias.interface";

@Component({
  selector: 'tei-host-admin-add-edit-portal-group-aliases',
  templateUrl: './add-edit-portal-group-aliases.component.html',
  styleUrls: ['./add-edit-portal-group-aliases.component.scss']
})
export class AddEditPortalGroupAliasesComponent implements OnInit, OnDestroy
{
  file = null;
  ZipFile = null;
  webAliasDetailResponse = new GetWebAliasDetailResponse();
  isSmallScreen: boolean;
  isLoading = false;
  subscription: Subscription = new Subscription();
  breadCrumbData: BreadCrumb[] = [];
  aliasId: number;
  portal: PortalDetail;
  portalGroup: PortalGroupByID;
  tenantID: number;
  portalID: string;
  portalGroupID: string;
  allManifestCategoriesList: ManifestCategory[] = [];
  aliasTypeList: AliasType[] = [];
  webAliasApplicationsList: TenantWebAliasApplication[] = [];
  routeParams: Params;
  fileName: string;

  displayItems = [
    { name: 'Standalone', value: displayEnum.standalone },
    { name: 'Full screen', value: displayEnum.fullscreen },
    { name: 'Minimal UI', value: displayEnum.minimal_ui },
    { name: 'Browser', value: displayEnum.browser }
  ];
  orientationItems = [
    { name: 'Portrait', value: orientationEnum.portrait },
    { name: 'Landscape', value: orientationEnum.landscape },
    { name: 'Natural', value: orientationEnum.natural }
  ];
  form = new FormGroup<AddEditPortalGroupAliasesForm>({
    Alias: new FormControl('', [Validators.required, Validators.pattern(/^(?!www\.)([a-zA-Z0-9-]+\.)+[a-zA-Z]{2,}(\/[^\s]*)?$/)]),
    AliasTypeID: new FormControl(1, Validators.required),
    IsPrimary: new FormControl(false),
    Applications: new FormControl(null),
    ManifestName: new FormControl('Total e Integrated', Validators.required),
    ManifestShortName: new FormControl('TEI', Validators.required),
    ManifestThemeColor: new FormControl('#028eb5', Validators.required),
    ManifestBackgroundColor: new FormControl('#1f1f1f', Validators.required),
    ManifestDisplay: new FormControl(displayEnum.fullscreen, Validators.required),
    ManifestStartUrl: new FormControl('/auth/login/ ', Validators.required),
    ManifestDescription: new FormControl('Total e Integrateds progressive web app.', Validators.required),
    ManifestOrientation: new FormControl(orientationEnum.portrait, Validators.required),
    ManifestCategories: new FormControl([12]),
    ManifestIconsZipFile: new FormControl(null),
    ManifestScreenShots: new FormArray([]),
  });

  constructor(
    @Inject('IPortalGroupsService') private hostAdminPortalGroupsService: IPortalGroupsService,
    @Inject('IPortalService') private hostAdminPortalService: IPortalService,
    @Inject('IWebAliasService') private service: IWebAliasService,
    @SkipSelf() protected _errorStateMatcher: ErrorStateMatcher,
    public manageLoadingService: ManageLoadingService,
    private translate: TranslateService,
    private activatedRoute: ActivatedRoute,
    private router: Router,
    private snackBar: SnackBarService,
    private route: ActivatedRoute)
  {
  }

  ngOnInit(): void
  {
    this.loadAliasDetail();
  }

  get formControls()
  {
    return this.form.controls;
  }

  loadAliasDetail(): void
  {
    const params = this.activatedRoute.snapshot.params;
    this.aliasId = params?.aliasId;
    this.routeParams = params.portalGroupId ? params : this.activatedRoute.parent.snapshot.params;
    this.portalID = this.routeParams.id;
    this.portalGroupID = this.routeParams.portalGroupId;
    if (this.portalID)
    {
      this.getHostAdminPortal(this.portalID);
    } else
    {
      this.getHostAdminPortalGroups(this.portalGroupID);
    }
  }

  getHostAdminPortal(portalID: string): void
  {
    const request: GetPortalDetailRequest = {
      PortalID: portalID
    };
    this.subscription.add(this.hostAdminPortalService.GetByID(request).subscribe(response =>
    {
      this.portal = response;
      this.tenantID = response.TenantID;
      this.getAliasType();
      this.getAllManifestCategories();
      this.getWebAliasApplications(this.tenantID);
      this.getHostAdminPortalGroups(this.portalGroupID);
      if (this.aliasId)
      {
        this.loadData(this.aliasId);
      }
    }));
  }

  getHostAdminPortalGroups(portalGroupID: string): void
  {
    const request: GetPortalGroupByIDRequest = {
      PortalGroupID: portalGroupID
    };
    this.subscription.add(this.hostAdminPortalGroupsService.GetByID(request).subscribe(response =>
    {
      this.portalGroup = response?.PortalGroup;
      if (!this.portalID)
      {
        this.tenantID = this.portalGroup?.TenantID;
      }
      this.createBreadCrumb(this.aliasId);
      this.getAliasType();
      this.getAllManifestCategories();
      this.getWebAliasApplications(this.tenantID);
      if (this.aliasId)
      {
        this.loadData(this.aliasId);
      }
    }));
  }

  createBreadCrumb(aliasId: number): void
  {
    var relativeUrlForEdit = '../';
    if (!aliasId)
    {
      relativeUrlForEdit = '';
    }

    if (this.portalID)
    {
      this.breadCrumbData = [
        {
          title: this.translate.instant('ADMIN_CENTER.GROUP.PORTAL_GROUPS.TITLE'),
          relativeUrl: '../../../../../' + relativeUrlForEdit,
        },
        {
          title: `${ this.portalGroup.PortalGroupName }`,
          relativeUrl: '../../../../' + relativeUrlForEdit,
        },
        {
          title: `${ this.portal?.PortalName }`,
          relativeUrl: '../../' + relativeUrlForEdit,
        },
        {
          title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.ALIASES'),
          relativeUrl: `../` + relativeUrlForEdit,
        },
        {
          title: aliasId ? this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.EDIT_ALIAS') : this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.ADD_ALIAS'),
          relativeUrl: '',
        }
      ];
    }
    else
    {
      this.breadCrumbData = [
        {
          title: this.translate.instant('ADMIN_CENTER.GROUP.PORTAL_GROUPS.TITLE'),
          relativeUrl: '../../../' + relativeUrlForEdit,
        },
        {
          title: `${ this.portalGroup.PortalGroupName }`,
          relativeUrl: '../../' + relativeUrlForEdit,
        },
        {
          title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.ALIASES'),
          relativeUrl: `../` + relativeUrlForEdit,
        },
        {
          title: aliasId ? this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.EDIT_ALIAS') : this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.ADD_ALIAS'),
          relativeUrl: '',
        }
      ];
    }

  }

  loadData(aliasId: number): void
  {
    this.manageLoadingService.startLoading();
    const request = new GetWebAliasDetailRequest();
    request.TenantWebAliasID = aliasId;
    this.subscription.add(this.service.GetDetails(request).subscribe(
      (result: GetWebAliasDetailResponse) =>
      {
        this.form.patchValue(
          {
            Alias: result.WebAliasUrl,
            IsPrimary: result.IsPrimary,
            AliasTypeID: result.AliasTypeID,
            ManifestName: result.ManifestName,
            ManifestShortName: result.ManifestShortName,
            ManifestThemeColor: result.ManifestThemeColor,
            ManifestBackgroundColor: result.ManifestBackgroundColor,
            ManifestDisplay: result.ManifestDisplay,
            ManifestStartUrl: result.ManifestStartUrl,
            ManifestDescription: result.ManifestDescription,
            ManifestOrientation: result.ManifestOrientation,
            ManifestCategories: result.ManifestCategories?.map(p => p.ManifestCategoryID),
          });
        this.form.controls.ManifestScreenShots.clear();

        const screenshotsArray = this.form.get('ManifestScreenShots') as FormArray;
        result.Screenshots.forEach(item =>
        {
          screenshotsArray.push(this.createScreenshotGroup(item));
        });
        this.webAliasDetailResponse = result;
        this.webAliasApplicationsList = this.webAliasDetailResponse.Applications;
        this.manageLoadingService.finishLoading();
      },
      (error) =>
      {
      }));
  }

  createScreenshotGroup(item): FormGroup
  {
    return new FormGroup<ManifestScreenShotsForm>({
      Label: new FormControl(item.Label || ''),
      FormFactor: new FormControl(item.FormFactor || FormFactoEnum.NotSet),
      TenantWebAliasManifestScreenshotID: new FormControl(item.TenantWebAliasManifestScreenshotID || 0),
      TenantWebAliasID: new FormControl(item.TenantWebAliasID || null),
      ImageUID: new FormControl(item.ImageUID || null),
      ImageUrl: new FormControl(item.ImageUrl || null),
      ImageFile: new FormControl(item.ImageFile || null),
    });
  }

  getAllManifestCategories(): void
  {
    const request = new GetAllManifestCategoriesRequest();
    this.subscription.add(this.service.GetAllManifestCategories(request).subscribe((response) =>
    {
      this.allManifestCategoriesList = response.AllManifestCategories;
    }));
  }

  getAliasType(): void
  {
    const request = new GetAliasTypeRequest();
    this.subscription.add(this.service.GetAliasTypes(request).subscribe((response) =>
    {
      this.aliasTypeList = response.AliasTypes;
    }));
  }

  getWebAliasApplications(tenantID: number): void
  {
    if (this.aliasId)
    {
      return;
    }
    const request = new GetWebAliasApplicationsRequest();
    request.TenantID = tenantID;
    this.subscription.add(this.service.GetWebAliasApplications(request).subscribe((response) =>
    {
      this.webAliasApplicationsList = response.ApplicationList;
    }));
  }

  public valueById(ManifestCategoryID: number): string
  {
    return this.allManifestCategoriesList.find(c => c.ManifestCategoryID === ManifestCategoryID)?.Name;
  }

  onCategoryRemoved(ManifestCategoryID: number): void
  {
    var ManifestCategorieForm = (this.form.get('ManifestCategories') as FormControl);
    const toppings = ManifestCategorieForm.value as number[];
    ManifestCategorieForm.setValue(toppings.filter(obj => obj !== ManifestCategoryID));
  }

  onChangeUploadZip(event): void
  {
    this.ZipFile = event.target.files[0];
    this.fileName = event.target.files[0].name;
  }

  addScreenShot(): void
  {
    const screenshotsArray = this.form.get('ManifestScreenShots') as FormArray;
    var newScreenshot = new TenantWebAliasManifestScreenshotResponse();
    newScreenshot.FormFactor = FormFactoEnum.NotSet;
    newScreenshot.TenantWebAliasManifestScreenshotID = 0;
    screenshotsArray.push(this.createScreenshotGroup(newScreenshot));
  }

  removeScreenshot(index: number): void
  {
    var ManifestScreenShotsForm = (this.form.get('ManifestScreenShots') as FormArray);
    ManifestScreenShotsForm.removeAt(index);
  }

  saveSetting(aliasId: number): void
  {
    const formData = new FormData();
    if (aliasId)
    {
      formData.append('TenantWebAliasID', this.aliasId.toString());
    } else
    {
      formData.append('TenantID', this.tenantID.toString());
    }
    formData.append('WebAliasUrl', this.formControls.Alias.value);
    formData.append('AliasTypeID', this.formControls.AliasTypeID.value.toString());
    formData.append('IsPrimary', this.formControls.IsPrimary.value.toString());
    formData.append('ManifestShortName', this.formControls.ManifestShortName.value);
    formData.append('ManifestName', this.formControls.ManifestName.value);
    formData.append('ManifestDescription', this.formControls.ManifestDescription.value);
    formData.append('ManifestThemeColor', this.formControls.ManifestThemeColor.value);
    formData.append('ManifestBackgroundColor', this.formControls.ManifestBackgroundColor.value);
    formData.append('ManifestDisplay', this.formControls.ManifestDisplay.value);
    formData.append('ManifestOrientation', this.formControls.ManifestOrientation.value);
    formData.append('ManifestStartUrl', this.formControls.ManifestStartUrl.value);

    formData.append('ManifestIconsZipFile', this.ZipFile);
    this.webAliasApplicationsList.forEach((item, index) =>
    {
      formData.append(`Applications[${ index }].ApplicationID`, item?.ApplicationID?.toString());
      formData.append(`Applications[${ index }].Selected`, item?.Selected?.toString());
    });
    this.form.controls?.ManifestScreenShots?.value?.forEach((item, index) =>
    {
      formData.append(`ManifestScreenshots[${ index }].TenantWebAliasManifestScreenshotID`, item?.TenantWebAliasManifestScreenshotID?.toString());
      formData.append(`ManifestScreenshots[${ index }].Label`, item?.Label?.toString());
      formData.append(`ManifestScreenshots[${ index }].FormFactor`, item?.FormFactor?.toString());
      formData.append(`ManifestScreenshots[${ index }].ImageFile`, item?.ImageFile);
      formData.append(`ManifestScreenshots[${ index }].ImageUID`, item?.ImageUID);
    });
    this.form.controls?.ManifestCategories?.value?.forEach((item, index) =>
    {
      formData.append(`ManifestCategories[${ index }].ManifestCategoryID`, item?.toString());
    });

    this.manageLoadingService.startLoading();

    if (this.aliasId)
    {
      this.editAlias(formData);
    } else
    {
      this.saveNewAlias(formData);
    }
  }

  saveNewAlias(formData): void
  {
    this.subscription.add(this.service.Post(formData).subscribe(

      {
        next: (response: PostWebAliasResponse) =>
        {
          if (response)
          {
            this.snackBar.showSuccess(this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.PWA_APP_SETUP.SAVED_SUCCESSFULLY'), '', null, 'left');
            this.manageLoadingService.finishLoading();
            this.router.navigate(['..'], { relativeTo: this.route });
          }
        },
        error: err => this.manageLoadingService.finishLoading(),
        complete: () => this.manageLoadingService.finishLoading()
      }
    ));
  }

  editAlias(formData): void
  {
    this.subscription.add(this.service.Put(formData).subscribe(

      {
        next: (response: PutWebAliasResponse) =>
        {
          if (response)
          {
            this.snackBar.showSuccess(this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.PWA_APP_SETUP.EDIT_SUCCESSFULLY'), '', null, 'left');
            this.loadData(this.aliasId);
            this.manageLoadingService.finishLoading();
          }
        },
        error: err => this.manageLoadingService.finishLoading(),
        complete: () => this.manageLoadingService.finishLoading()
      }
    ));
  }

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }
}
