import { Component, Inject, OnDestroy, OnInit } from '@angular/core';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { TranslateService } from '@ngx-translate/core';
import { BreadCrumb, CardDialogComponent, ConfirmationDialogComponent } from '@tei/material';
import { firstValueFrom, Subscription } from 'rxjs';
import { IHostAdminCenterHelperService } from '../../@intermediate/host-admin-center-helper.interface';
import { portalGroupDetails } from '../../@intermediate/models/host-admin-center-helper.model';
import { EcommerceSetupStatusEnum, GetEcommerceSetupRequest, GetEcommerceSetupResponse } from '../../@intermediate/portal-setup-jobs/models/get-ecommerce-setup.model';
import { GetMyProfileSetupRequest, MyProfileSetupStatusEnum } from '../../@intermediate/portal-setup-jobs/models/get-my-profile-setup.model';
import { PortalSetupJobsModuleType } from '../../@intermediate/portal-setup-jobs/models/portal-setup-jobs.enum';
import { PostEcommerceSetupRequest } from '../../@intermediate/portal-setup-jobs/models/post-ecommerce-setup.model';
import { PostMyProfileSetupRequest, RunMyProfileSetupStatusEnum } from '../../@intermediate/portal-setup-jobs/models/post-my-profile-setup.model';
import { IPortalSetupJobsService } from '../../@intermediate/portal-setup-jobs/portal-setup-jobs.interface';
import { EcommerceSetupJobDialogModel, EcommerceSetupJobResult } from '../../components/ecommerce-setup-dialog/dialog-data.model';
import { EcommerceSetupDialogComponent } from '../../components/ecommerce-setup-dialog/ecommerce-setup-dialog.component';


@Component({
  selector: 'tei-host-admin-manage-portal-setup-jobs',
  templateUrl: './portal-setup-jobs.component.html',
  styleUrls: ['./portal-setup-jobs.component.scss']
})
export class PortalSetupJobsComponent implements OnInit, OnDestroy
{
  isLoading;
  isRunInitialProfileSetupActivated: boolean = false;
  isRunInitialEcommerceSetupActivated: boolean = false;
  breadCrumbData: BreadCrumb[] = [];
  portalDetail: portalGroupDetails;
  subscription: Subscription = new Subscription();
  myProfileSetupStatus: MyProfileSetupStatusEnum;
  eCommerceSetupResponse: GetEcommerceSetupResponse;
  constructor(
    @Inject('IPortalSetupJobsService') private portalSetupJobsService: IPortalSetupJobsService,
    @Inject('IHostAdminCenterHelperService') private hostAdminCenterHelperService: IHostAdminCenterHelperService,
    private translate: TranslateService,
    private dialog: MatDialog
  )
  {
  }

  ngOnInit(): void
  {
    this.subscription.add(this.hostAdminCenterHelperService.currentPortalGroupDetails.subscribe(async (portalDetail) =>
    {
      if (portalDetail)
      {
        this.portalDetail = {
          parentPortalGroup: portalDetail.parentPortalGroup.PortalGroup,
          portal: portalDetail.portal
        };
        this.fillBreadCrump();
        this.myProfileSetupStatus = await this.getMyProfileSetupStatus();
        this.eCommerceSetupResponse = await this.getEcommerceSetupStatus();
      }
    }));
  }

  async getMyProfileSetupStatus(): Promise<MyProfileSetupStatusEnum>
  {
    const request: GetMyProfileSetupRequest = {
      PortalID: this.portalDetail.portal.PortalID
    };

    try
    {
      this.isLoading = true;

      const response = await firstValueFrom(this.portalSetupJobsService.GetMyProfileSetup(request));

      if (response.Status == MyProfileSetupStatusEnum.New)
      {
        this.isRunInitialProfileSetupActivated = true;
      }

      return response.Status;
    } catch (error)
    {
    } finally
    {
      this.isLoading = false;
    }

  }

  async getEcommerceSetupStatus(): Promise<GetEcommerceSetupResponse>
  {
    const request: GetEcommerceSetupRequest = {
      PortalID: this.portalDetail.portal.PortalID
    };

    try
    {
      this.isLoading = true;

      const response = await firstValueFrom(this.portalSetupJobsService.GetEcommerceSetup(request));

      if (response.Status == EcommerceSetupStatusEnum.New)
      {
        this.isRunInitialEcommerceSetupActivated = true;
      }

      return response;
    } catch (error)
    {
    } finally
    {
      this.isLoading = false;
    }

  }

  showProfileProductNotEnabledDialog(module: PortalSetupJobsModuleType): void
  {
    let translateExt;
    switch (module)
    {
      case PortalSetupJobsModuleType.PROFILE:
        translateExt = "PROFILE_NOT_ENABLED";
        break;
      case PortalSetupJobsModuleType.ECOMMERCE:
        translateExt = "ECOMMERCE_NOT_ENABLED";
        break;
      default:
        break;
    }
    const title = this.translate.instant("ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.SETUP_JOBS.SETUP_FAILED");
    const content = this.translate.instant(`ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.SETUP_JOBS.${ translateExt }`);
    this.showSetupResultDialog(title, content);
  }

  showOngoingDialog(module: PortalSetupJobsModuleType): void
  {
    let translateExt;
    switch (module)
    {
      case PortalSetupJobsModuleType.PROFILE:
        translateExt = "SETUP_ONGOING_CONTENT";
        break;
      case PortalSetupJobsModuleType.ECOMMERCE:
        translateExt = "ECOMMERCE_SETUP_ONGOING_CONTENT";
        break;
      default:
        break;
    }
    const title = this.translate.instant("ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.SETUP_JOBS.SETUP_ONGOING");
    const content = this.translate.instant(`ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.SETUP_JOBS.${ translateExt }`);
    this.showSetupResultDialog(title, content);
  }

  showSuccessfulDialog(module: PortalSetupJobsModuleType): void
  {
    let translateExt = "SETUP_SUCCESSFUL_CONTENT";
    const title = this.translate.instant("ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.SETUP_JOBS.SETUP_SUCCESSFUL");
    const content = this.translate.instant(`ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.SETUP_JOBS.${ translateExt }`);
    this.showSetupResultDialog(title, content);
  }

  showSetupCompletedDialog(module: PortalSetupJobsModuleType): void
  {
    let translateExt;
    switch (module)
    {
      case PortalSetupJobsModuleType.PROFILE:
        translateExt = "SETUP_COMPLETED_CONTENT";
        break;
      case PortalSetupJobsModuleType.ECOMMERCE:
        translateExt = "ECOMMERCE_SETUP_COMPLETED_CONTENT";
        break;
      default:
        break;
    }
    const title = this.translate.instant("ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.SETUP_JOBS.SETUP_SUCCESSFUL");
    const content = this.translate.instant(`ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.SETUP_JOBS.${ translateExt }`);
    this.showSetupResultDialog(title, content);
  }


  showFaildDialog(module: PortalSetupJobsModuleType): void
  {
    let translateExt;
    switch (module)
    {
      case PortalSetupJobsModuleType.PROFILE:
        translateExt = "SETUP_FAILED_CONTENT";
        break;
      case PortalSetupJobsModuleType.ECOMMERCE:
        translateExt = "ECOMMERCE_SETUP_FAILED_CONTENT";
        break;
      default:
        break;
    }
    const title = this.translate.instant("ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.SETUP_JOBS.SETUP_FAILED");
    const content = this.translate.instant(`ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.SETUP_JOBS.${ translateExt }`);
    this.showSetupResultDialog(title, content);
  }

  fillBreadCrump(): void
  {
    this.breadCrumbData = [];
    this.breadCrumbData = [
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.PORTAL_GROUPS.TITLE'),
        relativeUrl: '../../../../',
      },
      {
        title: `${ this.portalDetail.parentPortalGroup.PortalGroupName }`,
        relativeUrl: `../../../`,
      },
      {
        title: `${ this.portalDetail.portal.PortalName }`,
        relativeUrl: '../',
      },
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.SETUP_JOBS.TITLE'),
        relativeUrl: '',
      }
    ];
  }

  async runMyProfileSetup(): Promise<void>
  {
    this.isRunInitialProfileSetupActivated = false;

    const request: PostMyProfileSetupRequest = {
      PortalID: this.portalDetail.portal.PortalID
    };

    try
    {
      this.isLoading = true;
      const response = await firstValueFrom(this.portalSetupJobsService.PostMyProfileSetup(request));

      if (response.Status == RunMyProfileSetupStatusEnum.ProfileProductNotEnabled)
      {
        this.showProfileProductNotEnabledDialog(PortalSetupJobsModuleType.PROFILE);
      }
      else if (response.Status == RunMyProfileSetupStatusEnum.Failed)
      {
        this.showFaildDialog(PortalSetupJobsModuleType.PROFILE);
      }
      else if (response.Status == RunMyProfileSetupStatusEnum.Successful)
      {
        this.showSuccessfulDialog(PortalSetupJobsModuleType.PROFILE);
      }
    } catch (error)
    {
    } finally
    {
      this.isLoading = false;
    }
  }

  async showMyProfileSetupStatus(): Promise<void>
  {
    this.myProfileSetupStatus = await this.getMyProfileSetupStatus();
    if (this.myProfileSetupStatus == MyProfileSetupStatusEnum.Successful)
    {
      this.showSetupCompletedDialog(PortalSetupJobsModuleType.PROFILE);
    }
    else if (this.myProfileSetupStatus == MyProfileSetupStatusEnum.Ongoing)
    {
      this.showOngoingDialog(PortalSetupJobsModuleType.PROFILE);
    }
    else if (this.myProfileSetupStatus == MyProfileSetupStatusEnum.Failed)
    {
      this.showFaildDialog(PortalSetupJobsModuleType.PROFILE);
    }
  }

  async runEcommerceSetup(result: EcommerceSetupJobResult): Promise<void>
  {
    this.isRunInitialEcommerceSetupActivated = false;

    const request: PostEcommerceSetupRequest = {
      ...result,
      PortalID: this.portalDetail.portal.PortalID,
      SiteName: this.eCommerceSetupResponse.Sites.find(site => site.SiteID == result.SiteID).SiteName,
      AutomatedNotificationID: +result.AutomatedNotificationID
    };

    try
    {
      this.isLoading = true;
      await firstValueFrom(this.portalSetupJobsService.PostEcommerceSetup(request));
      this.showEcommerceSetupStatus();
    } catch (error)
    {
    } finally
    {
      this.isLoading = false;
    }
  }

  async showEcommerceSetupStatus(): Promise<void>
  {
    this.eCommerceSetupResponse = await this.getEcommerceSetupStatus();
    if (this.eCommerceSetupResponse.Status == EcommerceSetupStatusEnum.Successful)
    {
      this.showSetupCompletedDialog(PortalSetupJobsModuleType.ECOMMERCE);
    }
    else if (this.eCommerceSetupResponse.Status == EcommerceSetupStatusEnum.Ongoing)
    {
      this.showOngoingDialog(PortalSetupJobsModuleType.ECOMMERCE);
    }
    else if (this.eCommerceSetupResponse.Status == EcommerceSetupStatusEnum.Failed)
    {
      this.showFaildDialog(PortalSetupJobsModuleType.ECOMMERCE);
    }
    else if (this.eCommerceSetupResponse.Status == EcommerceSetupStatusEnum.NotSupported)
    {
      this.showProfileProductNotEnabledDialog(PortalSetupJobsModuleType.ECOMMERCE);
    }
  }

  showSetupResultDialog(title: string, content: string): void
  {
    this.dialog.open(CardDialogComponent, {
      panelClass: 'o-confirm-dialog',
      data: {
        component: ConfirmationDialogComponent,
        dialogData: {
          title,
          content,
          cancelButtonLabel: this.translate.instant("ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.SETUP_JOBS.CLOSE")
        }
      },
      width: '300px',
    });
  }

  showEcommerceSetupDialog(): void
  {
    const ref = this.dialog.open(CardDialogComponent, {
      panelClass: 'o-confirm-dialog',
      data: {
        component: EcommerceSetupDialogComponent,
        dialogData: {
          options: this.eCommerceSetupResponse
        } as EcommerceSetupJobDialogModel
      },
      width: '400px',
    });

    this.subscription.add(ref.afterClosed().subscribe(result =>
    {
      if (result)
      {
        this.runEcommerceSetup(result);
      }
    }));
  }

  get isMyProfileSetupCompleted(): boolean
  {
    return this.myProfileSetupStatus === MyProfileSetupStatusEnum.Successful;
  }

  get isEcommerceSetupCompleted(): boolean
  {
    return this.eCommerceSetupResponse?.Status === EcommerceSetupStatusEnum.Successful;
  }

  ngOnDestroy(): void
  {
    this.subscription?.unsubscribe();
  }
}
