import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AdminCenterComponent } from './admin-center.component';
import { GroupAdminCenterComponent } from './pages/group-admin-center/group-admin-center.component';
import { StaffUserComponent } from './pages/group-admin-center/staff-user/staff-user.component';
import { HostAdminCenterComponent } from './pages/host-admin-center/host-admin-center.component';
import { ManagePortalGroupAliasesComponent } from './pages/host-admin-center/manage-portal-group/manage-portal-group-aliases/manage-portal-group-aliases.component';
import { AddEditPortalDialogComponent } from './pages/host-admin-center/manage-portal-group/manage-portal-group-portals/add-edit-portal/add-edit-portal.component';
import { ManagePortalGroupPortalsComponent } from './pages/host-admin-center/manage-portal-group/manage-portal-group-portals/manage-portal-group-portals.component';
import { ManagePortalGroupSettingsComponent } from './pages/host-admin-center/manage-portal-group/settings/manage-portal-group-settings.component';
import { AddPortalGroupComponent } from './pages/host-admin-center/portal-group/add-portal-group/add-portal-group.component';
import { EditPortalGroupComponent } from './pages/host-admin-center/portal-group/edit-portal-group/edit-portal-group.component';
import { PortalGroupComponent } from './pages/host-admin-center/portal-group/portal-group.component';
import { PagesComponent } from './pages/pages.component';
import { PermissionsComponent } from './pages/roles/permissions/permissions.component';
import { RolesComponent } from './pages/roles/roles.component';
import { AddEditPortalGroupAliasesComponent } from './pages/host-admin-center/manage-portal-group/manage-portal-group-aliases/add-edit-portal-group-aliases/add-edit-portal-group-aliases.component';

const routes: Routes = [{
  path: '',
  component: AdminCenterComponent,
  children: [
    {
      path: '',
      component: PagesComponent,
      children: [
        {
          path: 'group',
          component: GroupAdminCenterComponent,
          children: [
            {
              path: 'users',
              component: StaffUserComponent
            },
            {
              path: 'roles/:id/permissions',
              component: PermissionsComponent
            },
            {
              path: 'roles',
              component: RolesComponent
            }
          ]
        },
        {
          path: 'host',
          component: HostAdminCenterComponent,
          children: [
            {
              path: 'portal-groups/:portalGroupId/portals/new-portal',
              component: AddEditPortalDialogComponent
            },
            {
              path: 'portal-groups/:portalGroupId/portals/:id',
              loadChildren: () => import('./pages/host-admin-center/manage-portal/manage-portal.module').then(m => m.ManagePortalModule)
            },
            {
              path: 'portal-groups/new',
              component: AddPortalGroupComponent
            },
            {
              path: 'portal-groups/:portalGroupId',
              component: ManagePortalGroupSettingsComponent
            },
            {
              path: 'portal-groups/:portalGroupId/details',
              component: EditPortalGroupComponent
            },
            {
              path: 'portal-groups/:portalGroupId/portals',
              component: ManagePortalGroupPortalsComponent
            },
            {
              path: 'portal-groups/:portalGroupId/aliases',
              component: ManagePortalGroupAliasesComponent
            },
            {
              path: 'portal-groups/:portalGroupId/aliases/add',
              component: AddEditPortalGroupAliasesComponent
            },
            {
              path: 'portal-groups/:portalGroupId/aliases/edit/:aliasId',
              component: AddEditPortalGroupAliasesComponent
            },
            {
              path: 'portal-groups',
              component: PortalGroupComponent
            }
          ]
        }
      ]
    },
    { path: '**', redirectTo: '' },
  ],
}];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class AdminCenterRoutingModule
{
}
