import { Component, Input, OnInit, ViewChild } from '@angular/core';
import { UntypedFormControl, UntypedFormGroup } from '@angular/forms';
import { MatLegacySelect as MatSelect } from '@angular/material/legacy-select';

@Component({
  selector: 'tei-host-admin-manage-portal-settings-localization-chips',
  templateUrl: './portal-settings-localization-chips.component.html',
  styleUrls: ['./portal-settings-localization-chips.component.scss'],
})
export class PortalSettingsLocalizationChipsComponent implements OnInit
{

  @Input('teiChipData') teiChipData: TeiChipsModel;
  @Input('chipsFormGroup') formGroup = new UntypedFormGroup({});
  @Input('chipFormControlName') chipFormControlName = '';
  @ViewChild(MatSelect) matSelect!: MatSelect;
  
  constructor()
  {
  }

  ngOnInit(): void
  {

  }


  onToppingRemoved(topping: number)
  {
    const toppings = this.internalFormControl.value as number[];
    this.removeFirst(toppings, topping);
    this.internalFormControl.setValue(toppings);
    if (this.matSelect?.options)
    {
      const matOption = this.matSelect.options.filter(x => x.value == topping)[0];
      if (matOption)
          matOption.deselect();
    }
  }

  private removeFirst<T>(array: T[], toRemove: T): void
  {
    const index = array.indexOf(toRemove);
    if (index !== -1)
    {
      array.splice(index, 1);
    }
  }

  public get internalFormControl(): UntypedFormControl
  {
    return (this.formGroup.get(this.chipFormControlName) as UntypedFormControl);
  }

  public valueById(id: number): string
  {
    return this.teiChipData.allChips.find(c => c.id === id)?.name;
  }
}



export class TeiChipsModel
{
  allChips: { id, name, active; }[];
}
