import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ManagePortalTabIdEnum } from '../../../@intermediate/models/get-portal.model';
import { AuthenticationSettingComponent } from '../../../components/authentication-setting/authentication-setting.component';
import { ButterCMSSetupComponent } from '../../integrations/butter-cmssetup/butter-cmssetup.component';
import { IntegrationsComponent } from '../../integrations/integrations.component';
import { RangeMachineIntegrationComponent } from '../../integrations/range-machine-integration/range-machine-integration.component';
import { PaymentDevicesComponent } from '../../payment-devices/payment-devices.component';
import { AchqPaymentSettingComponent } from '../../payment-settings/achq-payment-setting/achq-payment-setting.component';
import { ConvergeSettingsComponent } from '../../payment-settings/converge-settings/converge-settings.component';
import { GeneralPaymentSettingComponent } from '../../payment-settings/general-payment-setting/general-payment-setting.component';
import { NoonPaymentSettingComponent } from '../../payment-settings/noon-payment-setting/noon-payment-setting.component';
import { PaymentSettingsComponent } from '../../payment-settings/payment-settings.component';
import { PortalSetupJobsComponent } from '../../portal-setup-jobs/portal-setup-jobs.component';
import { PWAAppSetupComponent } from '../../pwa-app-setup/pwa-app-setup.component';
import { AddEditPortalGroupAliasesComponent } from '../manage-portal-group/manage-portal-group-aliases/add-edit-portal-group-aliases/add-edit-portal-group-aliases.component';
import { ManagePortalGroupAliasesComponent } from '../manage-portal-group/manage-portal-group-aliases/manage-portal-group-aliases.component';
import { ManagePortalProductsComponent } from '../portal-group/manage-portal-products/manage-portal-products.component';
import { ManagePortalAdvancedSettingsComponent } from './advanced-settings/manage-portal-advanced-settings.component';
import { ManagePortalSettingsComponent } from './manage-portal-settings/manage-portal-settings.component';
import { ManagePortalTabComponent } from './manage-portal-tab/manage-portal-tab.component';
import { ManagePortalComponent } from './manage-portal.component';
import { EmailSettingsComponent } from './email-settings/email-settings.component';


const routes: Routes = [{
  path: '',
  component: ManagePortalComponent,
  children: [
    {
      path: '',
      component: ManagePortalTabComponent
    },
    {
      path: ManagePortalTabIdEnum.Settings,
      component: ManagePortalSettingsComponent
    },
    {
      path: ManagePortalTabIdEnum.Access,
      loadChildren: () => import('../../../components/access-navigation/access-navigation.module').then(m => m.AccessNavigationModule),

    },
    {
      path: ManagePortalTabIdEnum.AdvancedSettings,
      component: ManagePortalAdvancedSettingsComponent
    },
    {
      path: ManagePortalTabIdEnum.Authentication,
      component: AuthenticationSettingComponent
    },
    {
      path: ManagePortalTabIdEnum.Aliases,
      component: ManagePortalGroupAliasesComponent
    },
    {
      path: ManagePortalTabIdEnum.Aliases + '/add',
      component: AddEditPortalGroupAliasesComponent
    },
    {
      path: ManagePortalTabIdEnum.Aliases + '/edit/:aliasId',
      component: AddEditPortalGroupAliasesComponent
    },
    {
      path: ManagePortalTabIdEnum.PaymentDevice,
      component: PaymentDevicesComponent
    },
    {
      path: ManagePortalTabIdEnum.PaymentSettings,
      component: PaymentSettingsComponent,
    },
    {
      path: ManagePortalTabIdEnum.PWAAppSetup,
      component: PWAAppSetupComponent,
    },
    {
      path: ManagePortalTabIdEnum.EmailSettings,
      component: EmailSettingsComponent,
    },
    {
      path: ManagePortalTabIdEnum.PortalSetupJobs,
      component: PortalSetupJobsComponent,
    },
    {
      path: `${ ManagePortalTabIdEnum.Access }/${ ManagePortalTabIdEnum.Integrations }`,
      component: IntegrationsComponent,
    },
    {
      path: `${ ManagePortalTabIdEnum.Access }/${ ManagePortalTabIdEnum.Integrations }/${ ManagePortalTabIdEnum.ButterCMSSetup }`,
      component: ButterCMSSetupComponent,
    },
    {
      path: `${ ManagePortalTabIdEnum.Access }/${ ManagePortalTabIdEnum.Integrations }/${ ManagePortalTabIdEnum.RangeMachineIntegration }/${ ManagePortalTabIdEnum.RangeExpressSetup }`,
      loadChildren: () => import('../../integrations/range-express-api-setup/range-express-api-setup.module').then(m => m.RangeExpressApiSetupModule),
    },
    {
      path: `${ ManagePortalTabIdEnum.Access }/${ ManagePortalTabIdEnum.Integrations }/${ ManagePortalTabIdEnum.RangeMachineIntegration }`,
      component: RangeMachineIntegrationComponent,
    },
    {
      path: `${ ManagePortalTabIdEnum.Access }/${ ManagePortalTabIdEnum.Integrations }/${ ManagePortalTabIdEnum.RangeMachineIntegration }/${ ManagePortalTabIdEnum.ERangeSetup }`,
      loadChildren: () => import('../../integrations/range-device-setup/range-device-setup.module').then(m => m.RangeDeviceSetupModule)
    },
    {
      path: `${ ManagePortalTabIdEnum.Access }/${ ManagePortalTabIdEnum.Integrations }/${ ManagePortalTabIdEnum.RangeMachineIntegration }/${ ManagePortalTabIdEnum.SelectPiSetup }`,
      loadChildren: () => import('../../integrations/select-pi-setup/select-pi-setup.module').then(m => m.SelectPiSetupModule),
    },
    {
      path: `${ ManagePortalTabIdEnum.Access }/${ ManagePortalTabIdEnum.Integrations }/${ ManagePortalTabIdEnum.RangeMachineIntegration }/${ ManagePortalTabIdEnum.RangeMachineClassIDS }`,
      loadChildren: () => import('../../integrations/range-machine-class-ids/range-machine-class-ids.module').then(m => m.RangeMachineClassIDSModule),

    },
    {
      path: `${ ManagePortalTabIdEnum.Access }/${ ManagePortalTabIdEnum.Products }`,
      component: ManagePortalProductsComponent,
    },
    {
      path: `${ ManagePortalTabIdEnum.PaymentSettings }/${ ManagePortalTabIdEnum.GeneralSetup }`,
      component: GeneralPaymentSettingComponent
    },
    {
      path: `${ ManagePortalTabIdEnum.PaymentSettings }/${ ManagePortalTabIdEnum.ElavonConvergeSetup }`,
      component: ConvergeSettingsComponent
    },
    {
      path: `${ ManagePortalTabIdEnum.PaymentSettings }/${ ManagePortalTabIdEnum.NoonPaymentsSetup }`,
      component: NoonPaymentSettingComponent
    },
    {
      path: `${ ManagePortalTabIdEnum.PaymentSettings }/${ ManagePortalTabIdEnum.ACHQSetup }`,
      component: AchqPaymentSettingComponent
    },
  ]
}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class ManagePortalRoutingModule
{
}
