import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyCheckboxModule as MatCheckboxModule } from '@angular/material/legacy-checkbox';
import { RouterModule } from '@angular/router';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { UserPermissionService } from '../../../@services/user-permission.service';
import { RolesAnonymousTabComponent } from './anonymous-tab.component';

const COMPONENTS = [RolesAnonymousTabComponent];
const BASE_MODULES = [CommonModule, RouterModule, FormsModule, ReactiveFormsModule];
const CORE_MODULES = [TeiTextTranslatePipeModule];
const MATERIAL_MODULES = [
  MatCheckboxModule,
  MatCardModule,
  MatButtonModule
];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...CORE_MODULES,
    ...MATERIAL_MODULES
  ],
  exports: [
    ...COMPONENTS,
    ...CORE_MODULES,
    ...MATERIAL_MODULES
  ],
  providers: [
    { provide: 'IUserPermissionService', useClass: UserPermissionService }
  ]
})
export class RolesAnonymousTabModule { }
