import { UntypedFormArray, UntypedFormControl, UntypedFormGroup } from "@angular/forms";
import { GetAnonymousUserPermissionResponse } from "./get-anonymous.model";

export class AnonymousUserPermissionForm extends UntypedFormGroup
{
  constructor(model: GetAnonymousUserPermissionResponse)
  {
    const form = {
      UserPermissionsItems: new UntypedFormArray(
        model.AnonymousUserPermission.map(x => new UntypedFormGroup({
          PermissionID: new UntypedFormControl(x.PermissionID),
          AllowAnonymous: new UntypedFormControl(x.TenantAllowAnonymous)
        }))
      )
    };
    super(form);
  }

  public value: PostAnonymousUserPermissionRequest;
}


export class PostAnonymousUserPermissionRequest
{
  UserPermissionsItems: UserPermissionsItem[];
}

class UserPermissionsItem
{
  PermissionID: number;
  AllowAnonymous: boolean;
}

export class PostAnonymousUserPermissionResponse
{
  Message: string;
  Success: boolean;
}
