import { Component, Inject, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { BreadCrumb, SnackBarService } from '@tei/material';
import { Subscription } from 'rxjs';
import { IHostAdminCenterHelperService } from '../../../../@intermediate/host-admin-center-helper.interface';
import { GetProductRequest, GetProductResponse, ProductCategory } from '../../../../@intermediate/models/get-product.model';
import { portalGroupDetails } from '../../../../@intermediate/models/host-admin-center-helper.model';
import { PostProductRequest, PostProductResponse } from '../../../../@intermediate/models/post-product.model';
import { PortalGroupByID } from '../../../../@intermediate/portal-group/models/get-portal-group-by-id.model';
import { IProductService } from '../../../../@intermediate/product.interface';

@Component({
  selector: 'tei-admin-admin-center-manage-portal-products',
  templateUrl: './manage-portal-products.component.html',
  styleUrls: ['./manage-portal-products.component.scss']
})
export class ManagePortalProductsComponent implements OnInit
{
  isLoading: boolean = false;
  productCategories: ProductCategory[];
  parentPortalGroup: PortalGroupByID;
  subscription: Subscription = new Subscription();
  portalDetail: portalGroupDetails;
  breadCrumbData: BreadCrumb[] = [];
  constructor(
    @Inject('IProductService') private productService: IProductService,
    @Inject('IHostAdminCenterHelperService') private hostAdminCenterHelperService: IHostAdminCenterHelperService,
    private snackBarService: SnackBarService,
    private activatedRoute: ActivatedRoute,
    private translate: TranslateService,
  ) { }

  ngOnInit(): void
  {
    this.subscription.add(this.hostAdminCenterHelperService.currentPortalGroupDetails.subscribe(portalDetail =>
    {
      if (portalDetail)
      {
        this.portalDetail = portalDetail;
        this.fillBreadCrump(portalDetail);
        this.load();
      }
    }));
  }

  fillBreadCrump(portalDetail): void
  {
    this.breadCrumbData = [
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.PORTAL_GROUPS.TITLE'),
        relativeUrl: '../../../../',
      },
      {
        title: `${ portalDetail.parentPortalGroup.PortalGroup.PortalGroupName }`,
        relativeUrl: `../../../`,
      },
      {
        title: `${ portalDetail.portal.PortalName }`,
        relativeUrl: '../../',
      },
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.ACCESS'),
        relativeUrl: '../',
      },
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.Products'),
        relativeUrl: '',
      }
    ];
  }

  load(): void
  {
    const request = new GetProductRequest();
    request.TenantID = this.portalDetail.portal.TenantID;
    this.isLoading = true;
    this.productService.Get(request).subscribe({
      next: (response: GetProductResponse) =>
      {
        this.isLoading = false;
        this.productCategories = response.ProductCategories;
        this.updateSelectedProductCategories();
      },
      error: () => this.isLoading = false
    });
  }

  updateSelectedProductCategories(): void
  {
    this.productCategories.forEach((productCategory) =>
    {
      productCategory.Selected = productCategory.Products.every(product => product.Selected);
      productCategory.Indeterminate = !productCategory.Selected && productCategory.Products.some(product => product.Selected && !product.IsComplementary);
    });
  }

  updateSelectedProductCategory(productCategoryId: number): void
  {
    const productCategory = this.productCategories.find((productCategory => productCategory.CategoryID === productCategoryId));
    productCategory.Selected = productCategory.Products.every(product => product.Selected);
    productCategory.Indeterminate = !productCategory.Selected && productCategory.Products.some(product => product.Selected && !product.IsComplementary);
  }

  setAll(checked: boolean, productCategoryId: number)
  {
    const productCategory = this.productCategories.find((productCategory => productCategory.CategoryID === productCategoryId));
    productCategory.Products.forEach((product) =>
    {
      if (!product.IsComplementary)
      {
        product.Selected = checked;
      }
    });
  }

  save(): void
  {
    const request = new PostProductRequest();
    request.TenantID = this.portalDetail.portal.TenantID;
    request.ProductIds = [];
    this.productCategories.forEach((productCategory) =>
    {
      productCategory.Products.forEach((product) =>
      {
        if (!product.IsComplementary && product.Selected)
        {
          request.ProductIds.push(product.ProductID);
        }
      });
    });
    this.isLoading = true;
    this.productService.Post(request).subscribe({
      next: (response: PostProductResponse) =>
      {
        this.isLoading = false,
          this.snackBarService.showSuccess(this.translate.instant('ADMIN_CENTER.PRODUCTS.SAVED_SUCCESSFULLY'), '', null, 'left');
      },
      error: () => this.isLoading = false,
    });
  }
}
