import { Component, Inject, OnInit } from '@angular/core';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { TranslateService } from '@ngx-translate/core';
import { ResponsiveService } from '@tei/core';
import { BreadCrumb, CardDialogComponent, ConfirmationDialogComponent, DialogDataModel, SnackBarService } from '@tei/material';
import { Subscription, firstValueFrom } from 'rxjs';
import { finalize } from 'rxjs/operators';
import { ClientIdentifiersInformation, GetLinkedSiteRequest, GetLinkedSiteResponse, LinkedSiteMetadata } from '../../@intermediate/models/get-linked-sites.model';
import { DeletePrinterCredentialRequest } from '../../@intermediate/models/post-delete-printer-credential.model';
import { PostResetPrinterCredentialRequest, PostResetPrinterCredentialResponse } from '../../@intermediate/models/post-reset-printer-credential.model';
import { IPrinterCredentialService } from '../../@intermediate/printer-credential.interface';
import { DeviceDetailsComponent } from './device-details/device-details.component';
import { NewDeviceComponent } from './new-device/new-device.component';

@Component({
  selector: 'tei-admin-admin-center-printers-setting',
  templateUrl: './printers-setting.component.html',
  styleUrls: ['./printers-setting.component.scss']
})
export class PrintersSettingComponent implements OnInit
{
  metadata = new LinkedSiteMetadata();
  dataSource: Array<ClientIdentifiersInformation>;
  isMobileView: boolean = false;
  subscription = new Subscription();
  isLoading = false;
  breadCrumbData: BreadCrumb[] = [
    {
      title: this.translate.instant('ADMIN_CENTER.DEVICES.PRINTERS.PORTAL_ADMIN_CENTER'),
      relativeUrl: ''
    },
    {
      title: this.translate.instant('ADMIN_CENTER.DEVICES.PRINTERS.SETUP'),
      relativeUrl: '../setup'
    },
    {
      title: this.translate.instant('ADMIN_CENTER.DEVICES.PRINTERS.PRINTER_CREDENTIALS'),
      relativeUrl: ''
    },
  ];

  constructor(private responsiveService: ResponsiveService, private translate: TranslateService,
    @Inject('IPrinterCredentialService') private printerCredentialService: IPrinterCredentialService, private dialog: MatDialog, private snackBarService: SnackBarService) { }

  ngOnInit(): void
  {
    this.subscription.add(this.responsiveService.mediaBreakpoint.subscribe(mediaBreakpoint =>
    {
      this.isMobileView = mediaBreakpoint === 'xs' || mediaBreakpoint === 'sm' || mediaBreakpoint === 'md';
    }));
    this.load();
  }

  async load(): Promise<void>
  {
    const request = new GetLinkedSiteRequest();
    this.isLoading = true;
    const response: GetLinkedSiteResponse = await firstValueFrom(this.printerCredentialService.LinkedSites(request).pipe(finalize(() => this.isLoading = false)));
    this.dataSource = response ? response?.SiteClientIdentifiersInformation : [];
  }

  newDevice(): void
  {
    const dialog = this.dialog.open(NewDeviceComponent, {
      panelClass: ['o-dialog-sm-fullscreen', 'o-dialog-md-fullscreen', 'new-printer-device'],
      width: '420px',
      data: {
        isEdit: false,
        clientIdentifiersInformation: []
      }
    });
    dialog.afterClosed().subscribe(result =>
    {
      if (result)
      {
        this.load();
      }
    });
  }

  onResetDialog(id: number): void
  {
    const dialogRef = this.dialog.open(CardDialogComponent, {
      panelClass: 'o-confirm-dialog',
      data: {
        component: ConfirmationDialogComponent,
        dialogData: {
          title: 'ADMIN_CENTER.DEVICES.RESET_PRINTER',
          content: 'ADMIN_CENTER.DEVICES.RESET_PRINTER_MESSAGE',
          submitButtonLabel: 'ADMIN_CENTER.DEVICES.PRINTERS.RESET',
          submitButtonColor: 'warn',
          cancelButtonLabel: 'ADMIN_CENTER.DEVICES.CANCEL'
        } as DialogDataModel
      },
      width: '350px',
    });
    dialogRef.afterClosed().subscribe(result =>
    {
      if (result)
      {
        this.onResetDevice(id);
      }
    });
  }

  onResetDevice(id: number): void
  {
    const request: PostResetPrinterCredentialRequest = {
      ID: id,
    };
    this.isLoading = true;
    this.printerCredentialService.Reset(request).subscribe({
      next: (response: PostResetPrinterCredentialResponse) =>
      {
        this.isLoading = false;
        this.dialog.open(DeviceDetailsComponent, {
          panelClass: ['o-dialog-sm-fullscreen', 'o-dialog-md-fullscreen', 'new-printer-device-details'],
          width: '420px',
          data: response,
        });
      },
      error: err => { this.isLoading = false; },
      complete: () => { }
    });
  }

  onDeleteDialog(id: number): void
  {
    const dialogRef = this.dialog.open(CardDialogComponent, {
      panelClass: 'o-confirm-dialog',
      data: {
        component: ConfirmationDialogComponent,
        dialogData: {
          title: 'ADMIN_CENTER.DEVICES.DELETE_SITE',
          content: 'ADMIN_CENTER.DEVICES.DELETE_SITE_QUESTION',
          submitButtonLabel: 'ADMIN_CENTER.DEVICES.DELETE',
          submitButtonColor: 'warn',
          cancelButtonLabel: 'ADMIN_CENTER.DEVICES.CANCEL'
        } as DialogDataModel
      },
      width: '350px',
    });
    dialogRef.afterClosed().subscribe(result =>
    {
      if (result)
      {
        this.onDelete(id);
      }
    });
  }

  onDelete(id: number): void
  {
    const request: DeletePrinterCredentialRequest = {
      ClientIdentifierID: id
    };
    this.isLoading = true;
    this.printerCredentialService.Delete(request).subscribe({
      next: (response: DeletePrinterCredentialRequest) =>
      {
        this.isLoading = false;
        this.snackBarService.showSuccess(this.translate.instant('ADMIN_CENTER.DEVICES.DELETED'), '', null, 'left');
        this.load();
      },
      error: err => { this.isLoading = false; },
      complete: () => { }
    });
  }

  onEditDialog(clientIdentifiersInformation: ClientIdentifiersInformation): void
  {
    const dialog = this.dialog.open(NewDeviceComponent, {
      panelClass: ['o-dialog-sm-fullscreen', 'o-dialog-md-fullscreen', 'new-printer-device'],
      width: '420px',
      data: {
        clientIdentifiersInformation,
        isEdit: true,
      }
    });
    dialog.afterClosed().subscribe(result =>
    {
      if (result)
      {
        this.load();
      }
    });
  }

}
