import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { IDesignationsService } from '../@intermediate/designation.interface';
import { GetDesignationRequest, GetDesignationResponse } from '../@intermediate/models/get-designation.model';

@Injectable({
  providedIn: 'root'
})
export class DesignationsService implements IDesignationsService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Designation';
  }

  public Get(request: GetDesignationRequest): Observable<GetDesignationResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.get<GetDesignationResponse>(apiUrl, { params: request as any });
  }
}
