import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { DeleteWebAliasRequest, DeleteWebAliasResponse } from '../@intermediate/models/delete-web-alias.model';
import { GetWebAliasRequest, GetWebAliasResponse } from '../@intermediate/models/get-web-alias.model';
import { PostWebAliasSetToPrimaryRequest, PostWebAliasSetToPrimaryResponse } from '../@intermediate/models/post-web-alias-set-to-primary.model';
import { PostWebAliasRequest, PostWebAliasResponse } from '../@intermediate/models/post-web-alias.model';
import { PutWebAliasRequest, PutWebAliasResponse } from '../@intermediate/models/put-web-alias.model';
import { IWebAliasService } from '../@intermediate/web-alias.interface';
import { GetWebAliasDetailRequest, GetWebAliasDetailResponse } from '../@intermediate/models/get-web-alias-detail.model';
import { GetWebAliasApplicationsRequest, GetWebAliasApplicationsResponse } from '../@intermediate/models/get-web-alias-applications.model';
import { GetAllManifestCategoriesRequest, GetAllManifestCategoriesResponse } from '../@intermediate/models/get-web-alias-categories.model';
import { GetAliasTypeRequest, GetAliasTypeResponse } from '../@intermediate/models/get-alias-type-model';


@Injectable({
  providedIn: 'root'
})
export class WebAliasService implements IWebAliasService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Admin/AdminCenter/WebAlias';
  }

  Get(request: GetWebAliasRequest): Observable<GetWebAliasResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.get<GetWebAliasResponse>(apiUrl, { params: request as any });
  }

  GetDetails(request: GetWebAliasDetailRequest): Observable<GetWebAliasDetailResponse>
  {
    const apiUrl = this.serviceUrl + '/GetDetail';
    return this.http.get<GetWebAliasDetailResponse>(apiUrl, { params: request as any });
  }

  GetNew(request: GetWebAliasDetailRequest): Observable<GetWebAliasDetailResponse>
  {
    const apiUrl = this.serviceUrl + '/GetDetail';
    return this.http.get<GetWebAliasDetailResponse>(apiUrl, { params: request as any });
  }

  GetAllManifestCategories(request: GetAllManifestCategoriesRequest): Observable<GetAllManifestCategoriesResponse>
  {
    const apiUrl = this.serviceUrl + '/GetAllManifestCategories';
    return this.http.get<GetAllManifestCategoriesResponse>(apiUrl, { params: request as any });
  }

  GetWebAliasApplications(request: GetWebAliasApplicationsRequest): Observable<GetWebAliasApplicationsResponse>
  {
    const apiUrl = this.serviceUrl + '/GetWebAliasApplication';
    return this.http.get<GetWebAliasApplicationsResponse>(apiUrl, { params: request as any });
  }

  GetAliasTypes(request: GetAliasTypeRequest): Observable<GetAliasTypeResponse>
  {
    const apiUrl = this.serviceUrl + '/GetAliasTypes';
    return this.http.get<GetAliasTypeResponse>(apiUrl, { params: request as any });
  }


  Post(request: PostWebAliasRequest): Observable<PostWebAliasResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.post<PostWebAliasResponse>(apiUrl, request);
  }

  Put(request: PutWebAliasRequest): Observable<PutWebAliasResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.put<PutWebAliasResponse>(apiUrl, request);
  }

  Delete(request: DeleteWebAliasRequest): Observable<DeleteWebAliasResponse>
  {
    
    const apiUrl = this.serviceUrl;
    return this.http.delete<DeleteWebAliasResponse>(apiUrl, { params: request as any });

  }

  SetToPrimary(request: PostWebAliasSetToPrimaryRequest): Observable<PostWebAliasSetToPrimaryResponse>
  {
    const apiUrl = this.serviceUrl + '/SetToPrimary';
    return this.http.post<PostWebAliasSetToPrimaryResponse>(apiUrl, request);
  }


}

