import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatDividerModule } from '@angular/material/divider';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacyMenuModule as MatMenuModule } from '@angular/material/legacy-menu';
import { MatLegacyTableModule as MatTableModule } from '@angular/material/legacy-table';
import { MatLegacyTabsModule as MatTabsModule } from '@angular/material/legacy-tabs';
import { MatLegacyTooltipModule as MatTooltipModule } from '@angular/material/legacy-tooltip';
import { TeiTextEllipsisPipeModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { AvatarModule } from '@tei/material';
import { RolesService } from '../../@services/role.service';
import { RolesAnonymousTabModule } from '../../components/roles/anonymous-tab/anonymous-tab.module';
import { AddEditRoleDialogModule } from './add-edit-role-dialog/add-edit-role-dialog.module';
import { PermissionsModule } from './permissions/permissions.module';
import { RolesComponent } from './roles.component';
import { MatSortModule } from '@angular/material/sort';
const BASE_MODULES = [CommonModule];
const MATERIAL_MODULES = [MatTabsModule, MatFormFieldModule, MatInputModule, MatCardModule, MatIconModule, MatButtonModule, MatDividerModule, MatMenuModule, MatTableModule, MatSortModule];
const TEI_MATERIAL_MODULES = [AvatarModule, TeiTextTranslatePipeModule, TeiTextEllipsisPipeModule, MatTooltipModule];
const TEI_ADMIN_CENTER = [
  RolesAnonymousTabModule,
  PermissionsModule,
  AddEditRoleDialogModule
];
const COMPONENTS = [RolesComponent];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_ADMIN_CENTER
  ],
  exports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_ADMIN_CENTER,
    ...COMPONENTS
  ],
  providers: [
    { provide: 'IRolesService', useClass: RolesService }
  ],
})
export class RolesModule { }
