import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCheckboxModule as MatCheckboxModule } from '@angular/material/legacy-checkbox';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { BreadCrumbModule, LoadingModule, MultiChipModule, SelectChipModule } from '@tei/material';
import { EmailSettingsService } from '../../../../@services/email-settings.service';
import { EmailSettingsComponent } from './email-settings.component';

const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule];
const MATERIAL_MODULES = [MatButtonModule, MatInputModule, MatFormFieldModule, MatCheckboxModule];
const TEI_MATERIAL_MODULES = [BreadCrumbModule, TeiTextTranslatePipeModule, SelectChipModule, LoadingModule];
const COMPONENTS = [EmailSettingsComponent];
@NgModule({
  declarations: [COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    MultiChipModule
  ],
  exports: [
    ...COMPONENTS
  ],
  providers: [
    { provide: 'IEmailSettingsService', useClass: EmailSettingsService },
  ]
})
export class EmailSettingsModule { }
