import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { MatIconModule } from '@angular/material/icon';
import { RouterModule } from '@angular/router';
import { TranslateModule } from '@ngx-translate/core';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { SettingLinkComponent } from './setting-link.component';

const COMPONENTS = [SettingLinkComponent];
const BASE_MODULES = [CommonModule, RouterModule];
const CORE_MODULES = [TeiTextTranslatePipeModule, TranslateModule];
const MATERIAL_MODULES = [MatIconModule];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES, ...CORE_MODULES, ...MATERIAL_MODULES
  ],
  exports: [
    ...COMPONENTS
  ]
})
export class SettingLinkModule { }
