import { UntypedFormControl, UntypedFormGroup, Validators } from "@angular/forms";
import { PortalDetail } from "../models/get-portal.model";

export class PostPortalRequest
{
  PortalGroupID: number;
  PortalID?: number;
  PortalName: string;
  PortalIdentifier: string;
  PortalAbbreviation: string;
  DataSource: string;
  DataBase: string;
  UserName: string;
  Password: string;
  IsPlay: string;
}

export class PostPortalResponse
{
  PortalID: number;
}

export class CreatePortalForm extends UntypedFormGroup
{
  constructor(portalGroupID: number)
  {
    super({
      PortalGroupID: new UntypedFormControl(portalGroupID),
      PortalName: new UntypedFormControl(""),
      PortalIdentifier: new UntypedFormControl(""),
      PortalAbbreviation: new UntypedFormControl("", [Validators.required, Validators.maxLength(3)]),
      PortalID: new UntypedFormControl(""),
      IsPlay: new UntypedFormControl(true),
      DataSource: new UntypedFormControl(""),
      DataBase: new UntypedFormControl(""),
      UserName: new UntypedFormControl(""),
      Password: new UntypedFormControl("")
    });
  }

  public value: PostPortalRequest;

  public setValue(value: PortalDetail)
  {
    const request = new PostPortalRequest();
    request.PortalGroupID = value.PortalGroupID;
    request.PortalName = value.PortalName;
    request.PortalIdentifier = value.PortalIdentifier;
    request.PortalAbbreviation = value.PortalAbbreviation;
    request.PortalID = value.PortalGroupID;
    request.IsPlay = value.PortalIdentifier;
    request.DataSource = value.WebAliasUrl;
    request.DataBase = value.ImageUrl;
    request.UserName = value.PortalName;
    request.Password = value.TenantUID;
    super.setValue(request);
  }
}
