import { FormControl, FormGroup, Validators } from "@angular/forms";
import { GetEditableAuthenticationSettingsResponse, TokenStorageType } from "./get-editable-authentication-settings.model";

export class PostAuthenticationSettingsRequest
{
  public PortalID: number;
  public AllowMember: boolean;
  public AllowActivation : boolean;
  public AllowPublic : boolean;
  public AllowRegistration : boolean;
  public AllowSSOLogin : boolean;
  public PasswordLessLogin : boolean;
  public TokenStorageType: TokenStorageType;
}

export class PostAuthenticationSettingsResponse
{

}

export class PostAuthenticationSettingsRequestForm extends FormGroup
{
  constructor()
  {
    super(
      {
      PortalID: new FormControl(0),
      AllowMember: new FormControl(Boolean),
      AllowActivation : new FormControl(Boolean),
      AllowPublic: new FormControl(Boolean),
      AllowRegistration :new FormControl(Boolean),
      AllowSSOLogin : new FormControl(Boolean),
      PasswordLessLogin : new FormControl(Boolean),
      TokenStorageType: new FormControl(Boolean),
    });
  }

  public value: PostAuthenticationSettingsRequest;

  public setValue(value: GetEditableAuthenticationSettingsResponse)
  {
    const request = new PostAuthenticationSettingsRequest();
    request.PortalID = 0;
    request.AllowMember = value.AllowMember;
    request.AllowActivation = value.AllowActivation;
    request.AllowPublic = value.AllowPublic;
    request.AllowRegistration = value.AllowRegistration;
    request.AllowSSOLogin = value.AllowSSOLogin;
    request.PasswordLessLogin = value.PasswordLessLogin;
    request.TokenStorageType = value.TokenStorageType;
    super.setValue(request);
  }
}
