import { Component, Inject, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { UntypedFormBuilder, UntypedFormGroup, Validators } from '@angular/forms';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { MatStepper } from '@angular/material/stepper';
import { TranslateService } from '@ngx-translate/core';
import { ResponsiveService } from '@tei/core';
import { ItemFlatNode, ItemNode, SnackBarService } from '@tei/material';
import { Subscription } from 'rxjs';
import { ClientIdentifiersInformation } from '../../../@intermediate/models/get-linked-sites.model';
import { GetRestaurantAndRetailSiteRequest, GetRestaurantAndRetailSiteResponse, Site, Sites } from '../../../@intermediate/models/get-sites-model';
import { PostCreatePrinterCredentialRequest, PostCreatePrinterCredentialResponse } from '../../../@intermediate/models/post-create-printer-credential.model';
import { PutPrinterCredentialRequest } from '../../../@intermediate/models/put-printer-credential.model';
import { IPrinterCredentialService } from '../../../@intermediate/printer-credential.interface';
import { PrinterCredentialService } from '../../../@services/printer-credential.service';

@Component({
  selector: 'tei-admin-admin-center-new-device',
  templateUrl: './new-device.component.html',
  providers: [{ provide: 'IPrinterCredentialService', useClass: PrinterCredentialService }],
  styleUrls: ['./new-device.component.scss']
})
export class NewDeviceComponent implements OnInit, OnDestroy
{

  subscription: Subscription;
  isMobile = false;
  treeData: ItemNode[] = [];
  selectedSiteIDs: number[] = [];
  isLoading: boolean = false;
  form: UntypedFormGroup;
  deviceDetails: PostCreatePrinterCredentialResponse;
  sites = Sites;
  @ViewChild('stepper') stepper: MatStepper;
  constructor(private responsiveService: ResponsiveService,
    private dialogRef: MatDialogRef<NewDeviceComponent>,
    @Inject(MAT_DIALOG_DATA) public dialogData: { isEdit: boolean, clientIdentifiersInformation?: ClientIdentifiersInformation; },
    @Inject('IPrinterCredentialService') private printerCredentialService: IPrinterCredentialService, private translate: TranslateService, private snackBarService: SnackBarService,
    private fb: UntypedFormBuilder) { }

  ngOnInit(): void
  {
    this.setMobileSize();
    this.load();
    this.createForm();
  }

  setMobileSize(): void
  {
    this.subscription = this.responsiveService.mediaBreakpoint.subscribe(screenSize =>
    {
      this.isMobile = screenSize === 'xs' || screenSize === 'sm' || screenSize === 'md';
    });
  }

  createForm(): void
  {
    this.form = this.fb.group({
      Name: ['', Validators.required]
    });
  }

  load(): void
  {
    const request = new GetRestaurantAndRetailSiteRequest();
    this.isLoading = true;
    this.printerCredentialService.Sites(request).subscribe({
      next: (response: GetRestaurantAndRetailSiteResponse) =>
      {
        if (this.dialogData?.isEdit)
        {
          this.formControls?.Name.setValue(this.dialogData?.clientIdentifiersInformation?.Description);
          this.formControls?.Name.disable();
        }
        this.createTree(response);
        this.isLoading = false;
      },
      error: err => { this.isLoading = false; },
      complete: () => { }
    });
  }

  createTree(data: GetRestaurantAndRetailSiteResponse): void
  {
    Object.keys(data).map((item, index) =>
    {
      const siteItemNode = new ItemNode();
      siteItemNode.Item = {
        Id: index.toString(),
        IsDisabled: false,
        IsSelected: false,
        Key: null,
        Title: this.sites[item],
      };
      data[item].map((child: Site) =>
      {
        const foundSite = this.dialogData?.clientIdentifiersInformation?.Sites?.filter(item => item.SiteID === child.SiteID);
        if (foundSite?.length > 0)
        {
          this.selectedSiteIDs.push(foundSite[0].SiteID);
        }
        const childSiteItemNode = new ItemNode;
        childSiteItemNode.Item = {
          Id: child.SiteID.toString(),
          IsDisabled: false,
          IsSelected: foundSite?.length > 0,
          Key: null,
          Title: child.SiteName,
        };
        siteItemNode.Children.push({
          Item: childSiteItemNode.Item,
          Children: []
        });
      });
      this.treeData.push(siteItemNode);
    });
  };

  selectTreeItem(event: ItemFlatNode): void
  {
    if (event.Level === 0)
    {
      const selectedNodeParent = this.treeData.filter(treeItem => treeItem.Item.Id === event.Item.Id)[0];
      selectedNodeParent.Children.map(child =>
      {
        if (event.Item.IsSelected)
        {
          this.selectedSiteIDs = this.selectedSiteIDs.filter(siteId => siteId !== Number(child.Item.Id));
          child.Item.IsSelected = false;
        } else
        {
          if (this.selectedSiteIDs.findIndex(siteId => siteId === Number(child.Item.Id)) === -1)
          {
            this.selectedSiteIDs.push(Number(child.Item.Id));
            child.Item.IsSelected = true;
          }
        }
      });
      event.Item.IsSelected = !event.Item.IsSelected;
    } else
    {
      if (event.Item.IsSelected)
      {
        this.selectedSiteIDs = this.selectedSiteIDs.filter(siteId => siteId !== Number(event.Item.Id));
        this.treeData.map(item =>
        {
          if (item.Children.findIndex(child => child.Item.Id === event.Item.Id) > -1)
          {
            item.Item.IsSelected = false;
          }
        });
      } else
      {
        this.selectedSiteIDs.push(Number(event.Item.Id));
      }
      event.Item.IsSelected = !event.Item.IsSelected;
    }
  }

  onConfirm(): void
  {
    this.dialogData.isEdit ? this.edit() : this.create();
  }

  create(): void
  {
    const request: PostCreatePrinterCredentialRequest = {
      Name: this.formControls?.Name?.value,
      SiteIDs: this.selectedSiteIDs,
    };
    this.isLoading = true;
    this.printerCredentialService.PrinterCredential(request).subscribe({
      next: (response: PostCreatePrinterCredentialResponse) =>
      {
        this.snackBarService.showSuccess(this.translate.instant('ADMIN_CENTER.DEVICES.SAVED_SUCCESSFULLY'), '', null, 'left');
        this.deviceDetails = response;
        this.stepper.next();
        this.isLoading = false;
      },
      error: err => { this.isLoading = false; },
      complete: () => { this.isLoading = false; }
    });
  }

  edit(): void
  {
    const request: PutPrinterCredentialRequest = {
      ClientIdentifierID: this.dialogData.clientIdentifiersInformation.ClientIdentifierID,
      SiteIDs: this.selectedSiteIDs
    };
    this.isLoading = true;
    this.printerCredentialService.Edit(request).subscribe({
      next: (response: PostCreatePrinterCredentialResponse) =>
      {
        this.isLoading = false;
        this.snackBarService.showSuccess(this.translate.instant('ADMIN_CENTER.DEVICES.EDIT_SUCCESSFULLY'), '', null, 'left');
        this.close(true);
      },
      error: err => { this.isLoading = false; },
      complete: () => { this.isLoading = false; }
    });
  }

  get formControls()
  {
    return this.form.controls;
  }

  close(state: boolean): void
  {
    this.dialogRef.close(state);
  }

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }

}
