import { Component, Inject, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { BreadCrumb } from '@tei/material';
import { SecureNavigation } from '@tei/material-smart-components';
import { GetPortalGroupByIDRequest } from '../../../../@intermediate/portal-group/models/get-portal-group-by-id.model';
import { IPortalGroupsService } from '../../../../@intermediate/portal-group/portal-groups.interface';
@Component({
  selector: 'tei-host-admin-manage-portal-group-settings',
  templateUrl: './manage-portal-group-settings.component.html',
  styleUrls: ['./manage-portal-group-settings.component.scss']
})
export class ManagePortalGroupSettingsComponent implements OnInit
{
  breadCrumbData: BreadCrumb[] = [];

  secureNavigation: SecureNavigation = {
    groupPermissionIdentifier: 'portal-groups',
    sections: [
      {
        title: '',
        items: [
          {
            permissionIdentifier: "portal-group-details",
            link: "details",
            title: this.translate.instant("ADMIN_CENTER.GROUP.SETTINGS.DETAILS"),
            description: this.translate.instant("ADMIN_CENTER.GROUP.SETTINGS.GENERAL_INFORMATION")
          },
          {
            permissionIdentifier: "portals",
            link: "portals",
            title: this.translate.instant("ADMIN_CENTER.GROUP.SETTINGS.PORTALS"),
            description: this.translate.instant("ADMIN_CENTER.GROUP.SETTINGS.CONFIGURE_THE_PORTALS")
          },
          {
            permissionIdentifier: "aliases",
            link: "aliases",
            title: this.translate.instant("ADMIN_CENTER.GROUP.SETTINGS.ALIASES"),
            description: this.translate.instant("ADMIN_CENTER.GROUP.SETTINGS.CONFIGURE_THE_ALIASES")
          }
        ]
      }
    ]
  };

  constructor(
    @Inject('IPortalGroupsService') private hostAdminPortalGroupsService: IPortalGroupsService,
    private activatedRoute: ActivatedRoute,
    private translate: TranslateService
  )
  {
  }

  ngOnInit()
  {
    const request: GetPortalGroupByIDRequest = {

      PortalGroupID: this.activatedRoute.snapshot.params.portalGroupId
    };

    this.hostAdminPortalGroupsService.GetByID(request).subscribe(res =>
    {
      this.breadCrumbData = [
        {
          title: this.translate.instant('ADMIN_CENTER.GROUP.PORTAL_GROUPS.TITLE'),
          relativeUrl: '../'
        },
        {
          title: `${ res.PortalGroup?.PortalGroupName }`,
          relativeUrl: ''
        }
      ];
    });
  }
}
