import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { RouteData } from '@tei/core';
import { BookingFieldsComponent } from './booking-fields/booking-fields.component';
import { BookingSetupComponent } from './booking-setup.component';

const routes: Routes = [
  { path: '', component: BookingSetupComponent },

  {
    path: "booking-fields/field-groups", loadChildren: () => import('@tei/common-field-management').then(m => m.FieldGroupsModule),
  },
  {
    path: 'booking-fields/field-groups/manage/:field-group-id',
    loadChildren: () => import('@tei/common-field-management').then(m => m.ManageFieldGroupModule),
    data: { RouteData: new RouteData({ Title: 'Manage Field Group', CanGoBack: true, BackRoute: 'field-groups' }) }
  },
  {
    path: 'booking-fields/field-groups/manage',
    loadChildren: () => import('@tei/common-field-management').then(m => m.ManageFieldGroupModule),
    data: { RouteData: new RouteData({ Title: 'Manage Field Group', CanGoBack: true, BackRoute: 'field-groups' }) }
  },
  {
    path: "booking-fields/master-field-list",
    loadChildren: () => import('@tei/common-field-management').then(m => m.MasterListModule)
  },
  {
    path: 'booking-fields/master-field-list/manage',
    redirectTo: 'booking-fields/master-field-list/manage/'
  },
  {
    path: 'booking-fields/master-field-list/manage/:master-field-id',
    loadChildren: () => import('@tei/common-field-management').then(m => m.ManageMasterFieldModule),
    data: { RouteData: new RouteData({ Title: 'Manage Master Field', CanGoBack: true, BackRoute: 'booking-fields/master-field-list' }) }
  },
  {
    path: 'booking-fields/master-field-list/manage/:master-field-id',
    loadChildren: () => import('@tei/common-field-management').then(m => m.ManageMasterFieldModule),
    data: { RouteData: new RouteData({ Title: 'Manage Master Field', CanGoBack: true, BackRoute: 'booking-fields/master-field-list' }) }
  },
  {
    path: 'booking-fields/master-field-list/customize/:master-field-id/:field-group-id',
    loadChildren: () => import('@tei/common-field-management').then(m => m.ManageMasterFieldModule),
    data: { RouteData: new RouteData({ Title: 'Customize Master Field', CanGoBack: true, BackRoute: 'booking-fields/field-groups/manage/:field-group-id' }) }
  },
  {
    path: 'booking-fields', component: BookingFieldsComponent,
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class BookingSetupRoutingModule
{
}
