import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { DeletePaymentDeviceRequest, DeletePaymentDeviceResponse } from '../@intermediate/models/delete-payment-device.model';
import { GetPaymentDeviceConfigurationRequest, GetPaymentDeviceConfigurationResponse } from '../@intermediate/models/get-payment-device-configurations.model';
import { GetPaymentDevicesRequest, GetPaymentDevicesResponse } from '../@intermediate/models/get-payment-devices.model';
import { PostPaymentDeviceRegistrationResponse } from '../@intermediate/models/post-payment-device-registration.model';
import { PostPaymentDeviceResetRequest, PostPaymentDeviceResetResponse } from '../@intermediate/models/post-payment-device-reset.model';
import { IPaymentDevicesService } from '../@intermediate/payment-devices.interface';

@Injectable({
  providedIn: 'root'
})
export class PaymentDevicesService implements IPaymentDevicesService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/DeviceIntegration/PaymentDevice';
  }

  GetPaymentDevices(request: GetPaymentDevicesRequest): Observable<GetPaymentDevicesResponse>
  {
    const apiUrl = this.serviceUrl + '/Search';
    return this.http.post<GetPaymentDevicesResponse>(apiUrl, request);
  }

  GetConfiguration(request: GetPaymentDeviceConfigurationRequest): Observable<GetPaymentDeviceConfigurationResponse>
  {
    const apiUrl = this.serviceUrl + '/Configuration';
    return this.http.get<GetPaymentDeviceConfigurationResponse>(apiUrl, { params: request as any });
  }

  Reset(request: PostPaymentDeviceResetRequest): Observable<PostPaymentDeviceResetResponse>
  {
    const apiUrl = this.serviceUrl + '/Reset';
    return this.http.post<PostPaymentDeviceResetResponse>(apiUrl, request);
  }

  Registration(request: FormData): Observable<PostPaymentDeviceRegistrationResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.post<PostPaymentDeviceRegistrationResponse>(apiUrl, request);
  }

  Delete(request: DeletePaymentDeviceRequest): Observable<DeletePaymentDeviceResponse>
  {
    const options = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      }),
      body: request
    };
    const apiUrl = this.serviceUrl;
    return this.http.delete<DeletePaymentDeviceResponse>(apiUrl, options);
  }

}
