import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacyRadioModule as MatRadioModule } from '@angular/material/legacy-radio';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { AvatarModule, BreadCrumbModule, LoadingModule, UploadIconModule, UploadProfilePictureModule } from '@tei/material';
import { AdminPortalGroupService } from '../../../../@services/admin-portal-group.service';
import { EditPortalGroupComponent } from './edit-portal-group.component';

const MATERIAL_MODULES = [
  MatInputModule,
  MatFormFieldModule,
  MatIconModule,
  MatRadioModule,
  MatButtonModule
];
const FORM_GROUP = [FormsModule, ReactiveFormsModule];
const TEI_MATERIAL_MODULES = [
  AvatarModule,
  BreadCrumbModule,
  LoadingModule,
  UploadProfilePictureModule,
  UploadIconModule
];
const COMMON_MODULES = [CommonModule, TeiTextTranslatePipeModule];
const COMPONENTS = [EditPortalGroupComponent];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...FORM_GROUP,
    ...COMMON_MODULES
  ],
  exports: [
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...FORM_GROUP,
    ...COMMON_MODULES,
    ...COMPONENTS
  ],
  providers: [
    { provide: 'IAdminPortalGroupService', useClass: AdminPortalGroupService }
  ],
})
export class EditPortalGroupModule
{ }
