import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyAutocompleteModule as MatAutocompleteModule } from '@angular/material/legacy-autocomplete';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacySelectModule as MatSelectModule } from '@angular/material/legacy-select';
import { MatLegacySlideToggleModule as MatSlideToggleModule } from '@angular/material/legacy-slide-toggle';

import { MatLegacyCheckboxModule as MatCheckboxModule } from '@angular/material/legacy-checkbox';
import { RouterModule } from '@angular/router';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { BreadCrumbModule, LoadingModule } from '@tei/material';
import { SurchargeSettingsService } from '../../../@services/surcharge-settings.service';
import { SurchargeSettingsComponent } from './surcharge-settings.component';

const BASE_MODULES = [
  CommonModule,
  FormsModule,
  ReactiveFormsModule,
];
const MATERIAL_MODULES = [MatInputModule, MatCheckboxModule, MatSlideToggleModule, MatAutocompleteModule, MatSelectModule
];
const TEI_MATERIAL_MODULES = [
  BreadCrumbModule,
  LoadingModule,
];
const TEI_CORE_MODULES = [TeiTextTranslatePipeModule];
const COMPONENTS = [SurchargeSettingsComponent];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    RouterModule.forChild([
      { path: '', component: SurchargeSettingsComponent },
    ]),
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_CORE_MODULES
  ],
  providers: [{ provide: 'ISurchargeSettingsService', useClass: SurchargeSettingsService }],
})
export class SurchargeSettingsModule { }
