import { CommonModule } from "@angular/common";
import { NgModule } from "@angular/core";
import { FormsModule, ReactiveFormsModule } from "@angular/forms";
//mat modules
import { MatDividerModule } from "@angular/material/divider";
import { MatIconModule } from "@angular/material/icon";
import { MatLegacyButtonModule as MatButtonModule } from "@angular/material/legacy-button";
import { MatLegacyDialogModule as MatDialogModule } from "@angular/material/legacy-dialog";
import { MatLegacyFormFieldModule as MatFormFieldModule } from "@angular/material/legacy-form-field";
import { MatLegacyInputModule as MatInputModule } from "@angular/material/legacy-input";
import { MatLegacySelectModule as MatSelectModule } from '@angular/material/legacy-select';
import { SafeHtmlPipeModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { CardDialogModule } from "@tei/material";
//components
import { EcommerceSetupDialogComponent } from "./ecommerce-setup-dialog.component";


const BASE_MODULES = [CommonModule];
const TEI_MATERIAL_MODULE = [CardDialogModule];
const COMPONENTS = [EcommerceSetupDialogComponent];
const MATERIAL_MODULES = [MatDividerModule, MatButtonModule, MatDialogModule, MatIconModule, MatInputModule, FormsModule, ReactiveFormsModule, MatFormFieldModule, MatSelectModule];
const TEI_CORE_MODULES = [TeiTextTranslatePipeModule, SafeHtmlPipeModule];


@NgModule({
  declarations: [...COMPONENTS],
  imports: [...BASE_MODULES, ...MATERIAL_MODULES, ...TEI_MATERIAL_MODULE, ...TEI_CORE_MODULES],
})
export class EcommerceSetupDialogModule { }
