import { Component, Inject, OnDestroy, OnInit } from '@angular/core';
import { AbstractControl, UntypedFormBuilder, UntypedFormGroup, ValidationErrors, ValidatorFn, Validators } from '@angular/forms';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ManageLoadingService, ResponsiveService } from '@tei/core';
import { BreadCrumb, ImageUploaderComponent, ImageUploaderModel } from '@tei/material';
import { Subscription } from 'rxjs';
import { finalize } from 'rxjs/operators';
import { ICustomerFacingDisplayService } from '../../../@intermediate/customer-facing-display.interface';
import { BGCFDType, GetCustomerFacingDisplayConfigurationRequest, GetCustomerFacingDisplayConfigurationResponse, Site, Terminal } from '../../../@intermediate/models/get-customer-facing-display-configuration.model';
import { PostCustomerFacingDisplayResponse } from '../../../@intermediate/models/post-customer-facing-display.model';
import { NewSecretIdDialogComponent } from '../new-secret-id-dialog/new-secret-id-dialog.component';

@Component({
  selector: 'tei-admin-admin-center-customer-facing-displays-add',
  templateUrl: './customer-facing-displays-add.component.html',
  styleUrls: ['./customer-facing-displays-add.component.scss']
})
export class CustomerFacingDisplaysAddComponent implements OnInit, OnDestroy
{
  subscription = new Subscription();
  form: UntypedFormGroup;
  isMobile = false;
  ActiveBackgroundImage;
  NoOrderBackgroundImage;
  BGCFDType = BGCFDType;
  configurations: GetCustomerFacingDisplayConfigurationResponse;
  sites: Site[] = [];
  terminals: Terminal[] = [];
  breadCrumbData: BreadCrumb[] = [
    {
      title: this.translate.instant('ADMIN_CENTER.DEVICES.PRINTERS.PORTAL_ADMIN_CENTER'),
      relativeUrl: ''
    },
    {
      title: this.translate.instant('ADMIN_CENTER.DEVICES.PRINTERS.SETUP'),
      relativeUrl: '../setup'
    },
    {
      title: this.translate.instant('ADMIN_CENTER.CUSTOMER_FACING_DISPLAYS.TITLE'),
      relativeUrl: '../'
    },
    {
      title: this.translate.instant('ADMIN_CENTER.CUSTOMER_FACING_DISPLAYS.ADD'),
      relativeUrl: ''
    },
  ];

  constructor(private responsiveService: ResponsiveService, private translate: TranslateService, private dialog: MatDialog, private fb: UntypedFormBuilder,
    @Inject('ICustomerFacingDisplayService') private customerFacingDisplayService: ICustomerFacingDisplayService,
    public manageLoadingService: ManageLoadingService, private router: Router, private route: ActivatedRoute) { }

  ngOnInit(): void
  {
    this.getMobileSize();
    this.getConfigurations();
    this.createForm();
  }

  getMobileSize(): void
  {
    this.subscription.add(this.responsiveService.mediaBreakpoint.subscribe(mediaBreakpoint =>
    {
      this.isMobile = mediaBreakpoint === 'xs' || mediaBreakpoint === 'sm' || mediaBreakpoint === 'md';
    }));
  }

  noWhitespaceValidator(): ValidatorFn
  {
    return (control: AbstractControl): ValidationErrors | null =>
    {
      const isWhitespace = (control.value || '').trim().length === 0;
      return isWhitespace ? { whitespace: true } : null;
    };
  }

  createForm(): void
  {
    this.form = this.fb.group({
      DeviceDescription: ['', [Validators.required, this.noWhitespaceValidator()]],
      AppMode: [null],
      Site: [null],
      TerminalID: [null, Validators.required],
      ActiveBackgroundImage: [null, Validators.required],
      NoOrderBackgroundImage: [null, Validators.required]
    });
    this.subscription.add(this.formControls.AppMode.valueChanges.subscribe(appModeValue =>
    {
      this.sites = this.configurations.Sites.filter(item => item.AppMode === appModeValue);
    }));
    this.subscription.add(this.formControls.Site.valueChanges.subscribe(siteValue =>
    {
      this.terminals = this.configurations.Terminals.filter(item => item.SiteID === siteValue);
    }));
  }

  getConfigurations(): void
  {
    const request = new GetCustomerFacingDisplayConfigurationRequest();
    this.manageLoadingService.startLoading();
    this.customerFacingDisplayService.Configuration(request).pipe(finalize(() => this.manageLoadingService.finishLoading())).subscribe({
      next: (response: GetCustomerFacingDisplayConfigurationResponse) =>
      {
        this.configurations = response;
      },
      error: err => { },
      complete: () => { }
    });
  }

  onChangeUpload(type: number): void
  {
    const cfdImageFormats = '.jpg,.jpeg,.jpe,.jif,.jfif,.jfi,.png,.gif,.bmp,.dib,.tiff,.tif,.heif,.heic';
    const cfdImagePermittedFileTypes = ['PNG', 'JPG', 'JPEG'];
    const data: ImageUploaderModel = {
      round: false,
      maintainAspectRatio: false,
      aspectRatio: 4 / 2.5,
      acceptFormat: cfdImageFormats,
      permittedFileTypes: cfdImagePermittedFileTypes,
      maxSize: 512,
      origImage: type === BGCFDType.ActiveBackgroundImage ? this.formControls.ActiveBackgroundImage.value : this.formControls.NoOrderBackgroundImage.value,
      croppedImage: type === BGCFDType.ActiveBackgroundImage ? this.ActiveBackgroundImage : this.NoOrderBackgroundImage
    };
    const imageUploaderDialog = this.dialog.open(ImageUploaderComponent, {
      width: '400px',
      panelClass: ['o-dialog-md-fullscreen'],
      data,
    });
    imageUploaderDialog.afterClosed().subscribe(result =>
    {
      if (result)
      {
        if (type === BGCFDType.ActiveBackgroundImage)
        {
          this.ActiveBackgroundImage = result?.croppedImage;
          this.formControls.ActiveBackgroundImage.setValue(result?.origImage);
        } else
        {
          this.NoOrderBackgroundImage = result?.croppedImage;
          this.formControls.NoOrderBackgroundImage.setValue(result?.origImage);
        }
      }
    });
  }

  onSave(): void
  {
    const formData = new FormData();
    formData.append('DeviceDescription', this.formControls.DeviceDescription.value);
    formData.append('TerminalID', this.formControls.TerminalID.value);
    formData.append('ActiveBackgroundImage', this.formControls.ActiveBackgroundImage.value);
    formData.append('NoOrderBackgroundImage', this.formControls.NoOrderBackgroundImage.value);
    this.manageLoadingService.startLoading();
    this.customerFacingDisplayService.Create(formData).pipe(finalize(() => this.manageLoadingService.finishLoading())).subscribe({
      next: (response: PostCustomerFacingDisplayResponse) =>
      {
        this.generateSecretId(response);
      },
      error: err => { },
      complete: () => { }
    });
  };

  generateSecretId(data: PostCustomerFacingDisplayResponse): void
  {
    const dialog = this.dialog.open(NewSecretIdDialogComponent, {
      panelClass: ['o-dialog-sm-fullscreen', 'o-dialog-md-fullscreen', 'new-customer-facing-display-secret'],
      width: '460px',
      data: {
        data: data,
        isMobile: this.isMobile
      }
    });

    dialog.afterClosed().subscribe(result =>
    {
      this.router.navigate(['..'], { relativeTo: this.route });
    });
  }

  get formControls()
  {
    return this.form.controls;
  }

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }

}