import { ItemNode } from '../../../components/tree/tree.model';
export class GetManageRoleRequest
{
  public UserID: number;
}

export class GetManageRoleResponse
{
  public IsAdministrator: boolean;
  public GlobalRoles: Array<Role> = [];
  public Portals: Array<Portal> = [];
  public DefaultIdentifiers: Array<DefaultIdentifier>;
}

export class DefaultIdentifier
{
  DefaultDeviceClientIdentifierID?: number;
  DefaultSiteID?: number;
  TenantID: number;
}
export class Role
{
  public RoleID: number;
  public RoleName: string;
  public IsSelected: boolean = false;
}

export class Portal
{
  public PortalID: number;
  public TenantID: number;
  public PortalName: string;
  public SpecificRoles: Array<Role> = [];
  public Restaurants: Array<Site> = [];
  public ProShops: Array<Site> = [];
  public IsSelected: boolean = false;
  public RestaurantsTreeData: Array<ItemNode> = [];
  public ProShopsTreeData: Array<ItemNode> = [];
}

export class Site
{
  public SiteID: number;
  public SiteName: string;
  public SiteIsSelected: boolean = false;
  public Terminals: Terminal[];
  public DefaultSelected: boolean;
}

export class Terminal
{
  public TerminalID: string;
  public Description: string;
  public TerminalIsSelected: boolean = false;
}