import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyAutocompleteModule as MatAutocompleteModule } from '@angular/material/legacy-autocomplete';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCheckboxModule as MatCheckboxModule } from '@angular/material/legacy-checkbox';
import { MatDividerModule } from '@angular/material/divider';
import { MatExpansionModule } from '@angular/material/expansion';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacySelectModule as MatSelectModule } from '@angular/material/legacy-select';
import { MatStepperModule } from '@angular/material/stepper';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { LoadingModule, MultiChipModule, SnackBarModule } from '@tei/material';
import { TreeModule } from '../../../../components/tree/tree.module';
import { StaffUserService } from './../../../../@services/staff-user.service';
import { AccessPortalUserFinalizeComponent } from './access-portal-user-finalize/access-portal-user-finalize.component';
import { AccessPortalUserInitiateComponent } from './access-portal-user-initiate/access-portal-user-initiate.component';
import { AccessPortalUserComponent } from './access-portal-user.component';

const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule];
const MATERIAL_MODULES = [MatButtonModule, MatDividerModule, MatStepperModule, MatIconModule,
  MatFormFieldModule, MatCheckboxModule, MatAutocompleteModule, MatExpansionModule, MatInputModule, MatSelectModule];
const TEI_MATERIAL_MODULES = [LoadingModule, SnackBarModule, MultiChipModule];
const TEI_CORE_MODULES = [TeiTextTranslatePipeModule];
const ADMIN_CENTER_MODULES = [TreeModule];
const COMPONENTS = [AccessPortalUserComponent, AccessPortalUserInitiateComponent, AccessPortalUserFinalizeComponent];

@NgModule({
  declarations: [
    ...COMPONENTS
  ],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_CORE_MODULES,
    ...ADMIN_CENTER_MODULES
  ],
  exports: [
    ...COMPONENTS,
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_CORE_MODULES,
    ...ADMIN_CENTER_MODULES
  ],
  providers: [
    { provide: 'IStaffUserService', useClass: StaffUserService }
  ]
})
export class AccessPortalUserModule { }
