import { CommonModule } from "@angular/common";
import { CUSTOM_ELEMENTS_SCHEMA, NgModule } from "@angular/core";
import { FormsModule, ReactiveFormsModule } from "@angular/forms";
import { MatDialogModule } from "@angular/material/dialog";
import { MatDividerModule } from "@angular/material/divider";
import { MatIconModule } from "@angular/material/icon";
import { MatLegacyButtonModule as MatButtonModule } from "@angular/material/legacy-button";
import { MatLegacyFormFieldModule as MatFormFieldModule } from "@angular/material/legacy-form-field";
import { MatLegacyInputModule as MatInputModule } from "@angular/material/legacy-input";
import { MatLegacySelectModule as MatSelectModule } from "@angular/material/legacy-select";
import { TeiTextTranslatePipeModule } from "@tei/core-translate";
import { AvatarModule, BreadCrumbModule, LoadingModule, SettingsListModule , MultipleSelectChipsModule} from "@tei/material";
import { WebAliasService } from "../../../../../@services/web-alias.service";
import { AddEditPortalGroupAliasesComponent } from "./add-edit-portal-group-aliases.component";
import { ScreenshotModule } from "../../../../../components/screenshot/screenshot.module";
import { PortalGroupsService } from "../../../../../@services/portal-groups.service";
import { PortalService } from "../../../../../@services/portal.service";
import { MatLegacyCheckboxModule as MatCheckboxModule } from '@angular/material/legacy-checkbox';
import { MatLegacyChipsModule as MatChipsModule } from '@angular/material/legacy-chips';

const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule];
const MATERIAL_MODULES = [MatIconModule, MatDialogModule, MatButtonModule, MatSelectModule, MatDividerModule, MatInputModule, MatFormFieldModule, MatCheckboxModule, MatChipsModule];
const TEI_MATERIAL_MODULES = [BreadCrumbModule, SettingsListModule, LoadingModule, AvatarModule, MultipleSelectChipsModule];
const COMPONENTS = [AddEditPortalGroupAliasesComponent];
const TEI_CORE_MODULES = [TeiTextTranslatePipeModule];
const ADMIN_CENTER_MODULES = [ScreenshotModule];

@NgModule({
  declarations: [COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_CORE_MODULES,
    ...ADMIN_CENTER_MODULES
  ],
  providers: [
    { provide: 'IWebAliasService', useClass: WebAliasService },
    { provide: 'IPortalGroupsService', useClass: PortalGroupsService },
    { provide: 'IPortalService', useClass: PortalService }
  ],
  schemas: [
    CUSTOM_ELEMENTS_SCHEMA
  ]
})

export class AddEditPortalGroupAliasesModule { }
