import { Component, Inject, OnInit } from '@angular/core';
import { UntypedFormControl, UntypedFormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { BreadCrumb } from '@tei/material';
import { Observable, Subscription } from 'rxjs';
import { tap } from 'rxjs/operators';
import { IAdminPortalService } from '../../../../@intermediate/admin-portal.interface';
import { IHostAdminCenterHelperService } from '../../../../@intermediate/host-admin-center-helper.interface';
import { GetPortalAdvancedSettingsRequest, GetPortalAdvancedSettingsResponse } from '../../../../@intermediate/models/get-portal-advanced-settings.model';
import { portalGroupDetails } from '../../../../@intermediate/models/host-admin-center-helper.model';
import { CreatePortalForm } from '../../../../@intermediate/portal/post-portal.model';

@Component({
  selector: 'tei-host-admin-manage-portal-advanced-settings',
  templateUrl: './manage-portal-advanced-settings.component.html'
})
export class ManagePortalAdvancedSettingsComponent implements OnInit
{
  isLoading;
  form: CreatePortalForm;
  breadCrumbData: BreadCrumb[] = [];
  portalId: string;
  parentPortalGroupId: string;
  portalDetail: portalGroupDetails;
  subscription: Subscription = new Subscription();
  constructor(
    @Inject('IAdminPortalService') private adminPortalService: IAdminPortalService,
    @Inject('IHostAdminCenterHelperService') private hostAdminCenterHelperService: IHostAdminCenterHelperService,
    private translate: TranslateService,
    private router: Router,
    private activatedRoute: ActivatedRoute
  )
  {
  }

  portalAdvancedSettings$: Observable<GetPortalAdvancedSettingsResponse>;

  ngOnInit(): void
  {

    this.subscription.add(this.activatedRoute.parent.params.subscribe(params =>
    {
      this.parentPortalGroupId = params['portalGroupId'];
      this.portalId = params['portals'];
    }));
    this.subscription.add(this.hostAdminCenterHelperService.currentPortalGroupDetails.subscribe(portalDetail =>
    {
      if (portalDetail)
      {
        this.portalDetail = {
          parentPortalGroup: portalDetail.parentPortalGroup.PortalGroup,
          portal: portalDetail.portal
        };
        this.fillBreadCrump();
        this.getAdvancedSetting();
      }
    }));
  }

  getAdvancedSetting(): void
  {
    const request: GetPortalAdvancedSettingsRequest = {
      id: this.portalDetail.portal.PortalID
    };

    this.portalAdvancedSettings$ = this.adminPortalService.AdvancedSetting(request).pipe(
      tap((response) =>
      {
        this.form = new UntypedFormGroup(
          {
            PortalIdentifier: new UntypedFormControl(response?.PortalIdentifier),
            PortalID: new UntypedFormControl(response?.LegacyPortalID),
            PortalAbbreviation: new UntypedFormControl(response?.PortalAbbreviation),
            IsPlay: new UntypedFormControl(response?.IsPlay),
            DataSource: new UntypedFormControl(response?.DataSource),
            DataBase: new UntypedFormControl(response?.Database),
            UserName: new UntypedFormControl(response?.Username),
            Password: new UntypedFormControl(response?.Password)
          }
        );

        this.form.disable();
      })
    );
  }

  fillBreadCrump(): void
  {
    this.breadCrumbData = [];
    this.breadCrumbData = [
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.PORTAL_GROUPS.TITLE'),
        relativeUrl: '../../../../',
      },
      {
        title: `${ this.portalDetail.parentPortalGroup.PortalGroupName }`,
        relativeUrl: `../../../`,
      },
      {
        title: `${ this.portalDetail.portal.PortalName }`,
        relativeUrl: '../',
      },
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.ADVANCED_SETTINGS'),
        relativeUrl: '',
      }
    ];
  }

  onBack()
  {
    this.router.navigate(["../"], { relativeTo: this.activatedRoute });
  }
}
