import { Component, Inject, OnInit } from '@angular/core';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { Observable, Subject, concat, of } from 'rxjs';
import { catchError, debounceTime, delay, switchMap } from 'rxjs/operators';
import { PutTenantsChangeActivationRequest } from '../../../@intermediate/models/put-tenants-change-activation.model';
import { GetPortalGroupsRequest, PortalGroup } from '../../../@intermediate/portal-group/models/get-portal-groups.model';
import { IPortalGroupsService } from '../../../@intermediate/portal-group/portal-groups.interface';
import { ITenantsService } from '../../../@intermediate/tenants.interface';

@Component({
  selector: 'tei-host-admin-portal-group',
  templateUrl: './portal-group.component.html',
  styleUrls: ['./portal-group.component.scss']
})
export class PortalGroupComponent implements OnInit
{

  groupChipWithDisplayName = [
    { chipName: 'PortalGroupName', displayName: this.translate.instant('ADMIN_CENTER.GROUP.PORTAL_GROUPS.GROUP') },
  ];
  portalChipWithDisplayName = [
    { chipName: 'PortalName', displayName: this.translate.instant('ADMIN_CENTER.GROUP.PORTAL_GROUPS.PORTAL_NAME') }
  ];
  groupArray: Observable<any[]> = of([]);
  portalArray: Observable<any[]> = of([]);
  groupTypeaheadInput$ = new Array();
  portalTypeaheadInput$ = new Array();

  activeChipNameForSearch: string = "";
  selectedChipValues: { PortalGroupName: string; PortalName: string; } = {
    PortalGroupName: '',
    PortalName: ''
  };
  portalGroups: PortalGroup[];
  filteredPortalGroups: PortalGroup[];
  filteredPortalNames: FlattenPortalGroup[] = [];


  displayedColumns: string[] = ['group', 'portals', 'actions'];

  constructor(
    private dialog: MatDialog,
    @Inject('ITenantsService') private hostAdminTenantsService: ITenantsService,
    @Inject('IPortalGroupsService') private hostAdminPortalGroupsService: IPortalGroupsService,
    private router: Router,
    private activeRoute: ActivatedRoute,
    private translate: TranslateService
  )
  {
  }

  ngOnInit()
  {
    this.groupChipWithDisplayName.forEach(el =>
    {
      this.groupTypeaheadInput$.push(new Subject<string>());
    });

    this.portalChipWithDisplayName.forEach(el =>
    {
      this.portalTypeaheadInput$.push(new Subject<string>());
    });

    this.load();
  }

  load()
  {
    const request = new GetPortalGroupsRequest();

    this.hostAdminPortalGroupsService.Get(request).subscribe(res =>
    {
      this.portalGroups = res.PortalGroups.filter(portal => portal.IsActive);
      this.filteredPortalGroups = res.PortalGroups.filter(portal => portal.IsActive);

      res.PortalGroups.forEach(portalGroup =>
      {
        portalGroup.Portals.forEach(portal =>
        {
          this.filteredPortalNames.push({
            PortalGroupID: portalGroup.PortalGroupID,
            PortalID: portal.PortalID,
            PortalName: portal.PortalName
          });
        });
      });
    });
  }

  filterByGroupChipItem(item: { chipName: string, index: number; })
  {
    this.chipItem(item);

    this.groupArray[item.index] = concat(
      of([]), // default items
      this.groupTypeaheadInput$[item.index].pipe(
        debounceTime(1000),
        switchMap((searchTerm) =>
          this.getPortalGroupsByGroupChip(searchTerm as string, item.chipName).pipe(
            catchError(() => of([])),
          ))
      )
    );
  }

  filterByPortalChipItem(item: { chipName: string, index: number; })
  {
    this.chipItem(item);

    this.portalArray[item.index] = concat(
      of([]), // default items
      this.portalTypeaheadInput$[item.index].pipe(
        debounceTime(1000),
        switchMap((searchTerm) =>
          this.getPortalGroupsByPortalChip(searchTerm as string, item.chipName).pipe(
            catchError(() => of([])),
          ))
      )
    );
  }

  chipItemValues(selectedValues: string | string[])
  {
    this.selectedChipValues[this.activeChipNameForSearch] = selectedValues;
    this.filterPortalGroups(this.selectedChipValues);
  }
  chipItem(event: { chipName: string, index: number; })
  {
    this.activeChipNameForSearch = event.chipName;
  }
  removeItem(event: { chipName: string; })
  {
    this.selectedChipValues[event.chipName] = '';
    this.filterPortalGroups(this.selectedChipValues);
  }

  filterPortalGroups(selectedChipValues: { PortalGroupName: string; PortalName: string; })
  {
    this.filteredPortalGroups = this.portalGroups.filter(item =>
    {
      if (
        (selectedChipValues.PortalGroupName && item.PortalGroupName !== selectedChipValues.PortalGroupName) ||
        (selectedChipValues.PortalName && !item.Portals.some(portal => portal.PortalName === selectedChipValues.PortalName)
        )
      )
        return false;

      return true;
    });
  }

  getPortalGroupsByGroupChip(term: string = null, item: string): Observable<PortalGroup[]>
  {
    let items = this.portalGroups;
    if (term)
    {
      const lowerTerm = term.toLocaleLowerCase();
      items = items.filter(x =>
        x[item]?.toLocaleLowerCase().indexOf(lowerTerm) > -1
      );
    }
    return of(items).pipe(delay(200));
  }

  getPortalGroupsByPortalChip(term: string = null, item: string): Observable<FlattenPortalGroup[]>
  {
    let items = this.filteredPortalNames;
    if (term)
    {
      const lowerTerm = term.toLocaleLowerCase();
      items = items.filter(x =>
        x[item]?.toLocaleLowerCase().indexOf(lowerTerm) > -1
      );
    }
    return of(items).pipe(delay(200));
  }

  addNewGroup()
  {
    this.router.navigate(['./new'], { relativeTo: this.activeRoute });
  }

  goToManagePortalGroup(portalGroup: PortalGroup)
  {
    this.router.navigate(['./', portalGroup.PortalGroupID], { relativeTo: this.activeRoute });
  }

  deactivatePortalGroup(portalGroup: PortalGroup)
  {
    const request = new PutTenantsChangeActivationRequest();

    request.TenantID = portalGroup.TenantID;
    request.IsActive = false;
    this.hostAdminTenantsService.ChangeActivation(request).subscribe((res) =>
    {
      this.load();
    },
      (error) =>
      {
      }
    );
  }
}

class FlattenPortalGroup
{
  PortalGroupID: number;
  PortalID: number;
  PortalName: string;
}
