import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { DeletePortalGroupsRequest, DeletePortalGroupsResponse } from '../@intermediate/portal-group/models/delete-portal-groups.model';
import { GetPortalGroupByIDRequest, GetPortalGroupByIDResponse } from '../@intermediate/portal-group/models/get-portal-group-by-id.model';
import { GetPortalGroupsRequest, GetPortalGroupsResponse } from '../@intermediate/portal-group/models/get-portal-groups.model';
import { PostPortalGroupsRequest, PostPortalGroupsResponse } from '../@intermediate/portal-group/models/post-portal-groups.model';
import { PutPortalGroupsRequest, PutPortalGroupsResponse } from '../@intermediate/portal-group/models/put-portal-groups.model';
import { IPortalGroupsService } from '../@intermediate/portal-group/portal-groups.interface';


@Injectable({
  providedIn: 'root'
})
export class PortalGroupsService implements IPortalGroupsService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/PortalGroup';
  }

  Get(request: GetPortalGroupsRequest): Observable<GetPortalGroupsResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.get<GetPortalGroupsResponse>(apiUrl, { params: request as any });
  }

  GetByID(request: GetPortalGroupByIDRequest): Observable<GetPortalGroupByIDResponse>
  {
    const apiUrl = this.serviceUrl + '/GetByID';
    return this.http.get<GetPortalGroupByIDResponse>(apiUrl, { params: request as any });
  }

  Post(request: PostPortalGroupsRequest): Observable<PostPortalGroupsResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.post<PostPortalGroupsResponse>(apiUrl, request);
  }
  Put(request: PutPortalGroupsRequest): Observable<PutPortalGroupsResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.put<PutPortalGroupsResponse>(apiUrl, request);
  }
  Delete(request: DeletePortalGroupsRequest): Observable<DeletePortalGroupsResponse>
  {
    const options = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      }),
      body: request
    };
    const apiUrl = this.serviceUrl;
    return this.http.delete<DeletePortalGroupsResponse>(apiUrl, options);

  }
}

