import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatDividerModule } from '@angular/material/divider';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacyRadioModule as MatRadioModule } from '@angular/material/legacy-radio';
import { RoleTypesService } from '../../../@services/role-types.service';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { DesignationsService } from '../../../@services/designation.service';
import { AddEditRoleDialogComponent } from './add-edit-role-dialog.component';
import { PortalService } from '../../../@services/portal.service';
import { MatLegacySelectModule as MatSelectModule} from '@angular/material/legacy-select';
import { MatLegacyCheckboxModule as MatCheckboxModule } from '@angular/material/legacy-checkbox';

const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule];
const MATERIAL_MODULES = [MatInputModule, MatFormFieldModule, MatDividerModule, MatRadioModule, MatButtonModule, MatIconModule, MatSelectModule, MatCheckboxModule];
const COMPONENTS = [AddEditRoleDialogComponent];
const TEI_CORE_MODULES = [TeiTextTranslatePipeModule];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_CORE_MODULES
  ],
  exports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...COMPONENTS,
    ...TEI_CORE_MODULES
  ],
  providers: [
    { provide: 'IDesignationsService', useClass: DesignationsService },
    { provide: 'IRoleTypesService', useClass: RoleTypesService },
    { provide: 'IPortalService', useClass: PortalService }
  ],
})
export class AddEditRoleDialogModule { }
