import { COMMA, ENTER } from '@angular/cdk/keycodes';
import { Component, ElementRef, EventEmitter, Inject, Input, Output, SkipSelf, ViewChild } from '@angular/core';
import { UntypedFormArray } from '@angular/forms';
import { ErrorStateMatcher } from '@angular/material/core';
import { MatLegacyAutocompleteSelectedEvent as MatAutocompleteSelectedEvent } from '@angular/material/legacy-autocomplete';
import { MatLegacyChipInputEvent as MatChipInputEvent } from '@angular/material/legacy-chips';
import { MatStepper } from '@angular/material/stepper';
import { TranslateService } from '@ngx-translate/core';
import { ModelDrivenFormGroupControlValueAccessor, ngFormGroupControlValueAccessorProviders } from '@tei/core';
import { SnackBarService } from '@tei/material';
import { Observable } from 'rxjs';
import { Portal, Role } from '../../../../../@intermediate/staff-user/models/get-invite-user.model';
import { GetManageRoleResponse } from '../../../../../@intermediate/staff-user/models/get-manage-role.model';
import { PortalRoleFormArray, PortalRoleFormGroup, PostManageRoleRequest, PostManageRoleRequestForm, RoleFormArray, RoleFormGroup } from '../../../../../@intermediate/staff-user/models/post-manage-role.model';
import { IStaffUserService } from '../../../../../@intermediate/staff-user/staff-user.interface';

@Component({
  selector: 'tei-admin-admin-center-access-portal-user-initiate',
  templateUrl: './access-portal-user-initiate.component.html',
  styleUrls: ['./access-portal-user-initiate.component.scss'],
  providers: [ngFormGroupControlValueAccessorProviders(AccessPortalUserInitiateComponent)]
})
export class AccessPortalUserInitiateComponent extends ModelDrivenFormGroupControlValueAccessor
{
  @Input() stepper: MatStepper;
  @Input() model: GetManageRoleResponse;
  @Input() userID: number;
  @Input() form: PostManageRoleRequestForm;
  @Output() selectedPortalEmitter = new EventEmitter();
  @Output() closeEmitter = new EventEmitter();


  isLoading: boolean = false;

  readonly separatorKeysCodes = [ENTER, COMMA] as const;
  selectable = true;
  removable = true;

  filteredRoles: Observable<Array<Role>>;

  @ViewChild('roleInput') roleInput: ElementRef<HTMLInputElement>;

  constructor(
    //private formBuilder: FormBuilder,
    private snackBarService: SnackBarService,
    private translate: TranslateService,
    @Inject('IStaffUserService') private service: IStaffUserService,
    @SkipSelf() protected _errorStateMatcher: ErrorStateMatcher
  )
  {
    super(_errorStateMatcher);


  }

  get formControls()
  {
    return this.form?.controls;
  }

  onSave(): void
  {
    const isAdministrator = this.formControls?.isAdministrator?.value;
    const request = new PostManageRoleRequest();
    request.UserID = this.userID;
    request.IsAdministrator = isAdministrator;
    request.TenantIDs = this.form.controls.PortalRoles.value.filter(p => p.IsSelected)?.map(portal => portal.TenantID);
    request.PortalRoles = this.form.controls.PortalRoles.value?.filter(p => p.IsSelected).map(pr =>
    ({
      TenantID: pr.TenantID,
      RoleIDs: pr.Roles?.map(r => r.RoleID)
    }));
    request.DefaultIdentifiers = this.model.DefaultIdentifiers;

    if (!isAdministrator)
    {
      request.PortalGroupRoles = this.form.controls.PortalGroupRoles.value?.map(role => role.RoleID);
      this.portalRoles.controls.forEach(portal =>
      {
        portal.get('RestaurantTerminals').value.forEach(restaurant =>
        {
          request.Terminals.push(restaurant);
        });

        portal.get('SiteTerminals').value.forEach(site =>
        {
          request.Terminals.push(site);
        });

      });
    }

    request.UpdatedTenants = this.form.controls.PortalRoles.value?.filter(p => p.Updated)
      .map(pr => pr.TenantID);

    this.isLoading = true;
    this.service.PostManageRole(request).subscribe({
      next: () =>
      {
        this.isLoading = false;
        this.closeEmitter.emit();
        this.snackBarService.showSuccess(this.translate.instant("ADMIN_CENTER.MANAGE_STAFF_USER.SUCCESS_MESSAGE"), '', null, 'left');
      },
      error: () =>
      {
        this.isLoading = false;
        this.snackBarService.showSuccess(this.translate.instant("ADMIN_CENTER.MANAGE_STAFF_USER.ERROR_MESSAGE"), '', null, 'left');
      }
    });
  }

  onManage(portalID: number): void
  {
    const tenantID = this.model.Portals.filter(portal => portal.PortalID === portalID)[0].TenantID;
    const portal = {
      portalID: portalID,
      tenantID: tenantID
    };
    this.selectedPortalEmitter.emit(portal);
    this.stepper.next();
  }

  addRole(event: MatChipInputEvent): void
  {
    const role = this.model?.GlobalRoles?.find(x => x.RoleID === Number(event.value));
    // if (role)
    // {
    //   role.IsSelected = true;
    // }

    this.portalGroupRoles.push(new RoleFormGroup(role));

    // Clear the input value
    // event.chipInput?.clear();
    event.value = '';
    //this.filterRoles();
  }

  removeRole(role: RoleFormGroup, i: number): void
  {
    this.portalGroupRoles.removeAt(i);
  }

  selectedRole(event: MatAutocompleteSelectedEvent): void
  {
    const role = this.model?.GlobalRoles?.find(x => x.RoleID === Number(event.option.value));

    this.portalGroupRoles.push(new RoleFormGroup(role));
  }

  get portalGroupRoles(): RoleFormArray
  {
    return this.form.controls.PortalGroupRoles as UntypedFormArray;
  }

  get portalRoles(): PortalRoleFormArray
  {
    return this.form.controls.PortalRoles as UntypedFormArray;
  }

  deselectedRoles(): Array<Role>
  {
    return this.model?.GlobalRoles.filter(role => this.form.controls.PortalGroupRoles.value.findIndex(gr => gr.RoleID == role.RoleID) < 0);
  }

  get selectedRoles(): Array<Role>
  {
    return this.model?.GlobalRoles?.filter(r => r.IsSelected);
  }

  onCheckChange(event, portal: Portal): void
  {
    //portal.IsSelected = event.checked;
  }

  onAdministratorCheckChange(event): void
  {
    const isChecked = event.checked;
    if (isChecked)
    {
      this.applyForAdmin();
    }
  }

  applyForAdmin(): void
  {
    this.portalRoles.value.map((portal, index) =>
    {
      portal.IsSelected = true;
      this.portalRoles.at(index).patchValue(portal);
    });
  }

  getPortalSpecialRoles(portal: PortalRoleFormGroup): string
  {
    let result = "";
    portal?.Roles?.value.forEach(role =>
    {
      if (result)
      {
        result += ", ";
      }
      result += role.RoleName;
    });

    return result;
  }

  get PortalRoles()
  {
    return this.form.get('PortalRoles') as UntypedFormArray;
  }
}
