import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { AnalyticsComponent } from './analytics.component';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { BreadCrumbModule} from '@tei/material';
import { TranslateModule } from '@ngx-translate/core';
import { AnalyticsReportAccessModule } from '@tei/analytics-group-analytics';


const COMPONENTS = [AnalyticsComponent];
const BASE_MODULES = [CommonModule,];
const TEI_CORE_MODULES = [TeiTextTranslatePipeModule, TranslateModule];
const TEI_MATERIAL_MODULES = [BreadCrumbModule];
const TEI_ANALYTICS_MODULES =[AnalyticsReportAccessModule]
@NgModule({
  declarations: [
    ...COMPONENTS,

  ],
  imports: [
    ...BASE_MODULES,
    ...TEI_CORE_MODULES,
   ...TEI_MATERIAL_MODULES,
   ...TEI_ANALYTICS_MODULES,
  ],
  exports: [
    ...COMPONENTS,
  ],
})
export class AnalyticsModule { }
