import { COMMA, ENTER, SPACE } from '@angular/cdk/keycodes';
import { Component, ElementRef, EventEmitter, Inject, Input, OnChanges, OnInit, Output, SimpleChanges, SkipSelf, ViewChild } from '@angular/core';
import { UntypedFormArray, UntypedFormBuilder, UntypedFormControl } from '@angular/forms';
import { MatLegacyAutocompleteSelectedEvent as MatAutocompleteSelectedEvent } from '@angular/material/legacy-autocomplete';
import { MatLegacyChipInputEvent as MatChipInputEvent } from '@angular/material/legacy-chips';
import { ErrorStateMatcher } from '@angular/material/core';
import { MatStepper } from '@angular/material/stepper';
import { TranslateService } from '@ngx-translate/core';
import { ModelDrivenFormGroupControlValueAccessor, ngFormGroupControlValueAccessorProviders } from '@tei/core';
import { SnackBarService } from '@tei/material';
import { GetInviteUserResponse, InviteUserSelectedData, Portal, Role } from '../../../../../@intermediate/staff-user/models/get-invite-user.model';
import { PortalFormGroup, PostInviteUserRequest, PostInviteUserRequestForm, RoleFormArray, RoleFormGroup } from '../../../../../@intermediate/staff-user/models/post-invite-user.model';
import { IStaffUserService } from '../../../../../@intermediate/staff-user/staff-user.interface';
import { GetUserByEmailRequest } from '../../../../../@intermediate/user/models/get-user-by-email.model';
import { GetVerifyStaffEmailInPortalGroupResponse } from '../../../../../@intermediate/user/models/get-verify-staff-email-in-portal-group.model';
import { IUserService } from '../../../../../@intermediate/user/user.interface';

@Component({
  selector: 'tei-admin-admin-center-invite-users-initiate',
  templateUrl: './invite-users-initiate.component.html',
  styleUrls: ['./invite-users-initiate.component.scss'],
  providers: [ngFormGroupControlValueAccessorProviders(InviteUsersInitiateComponent)]
})
export class InviteUsersInitiateComponent extends ModelDrivenFormGroupControlValueAccessor implements OnInit, OnChanges
{
  isLoading: boolean = false;
  @Input() stepper: MatStepper;
  @Input() model: GetInviteUserResponse;
  @Output() onCloseEmitter = new EventEmitter<boolean>();
  @Input() form: PostInviteUserRequestForm;
  @Output() selectedInviteUserEmitter = new EventEmitter<InviteUserSelectedData>();
  emails: Array<string> = [];
  invalidEmails: Array<string> = [];
  readonly separatorKeysCodes = [ENTER, COMMA, SPACE] as const;
  selectable = true;
  removable = true;

  selectedRoles: Array<Role> = [];

  @ViewChild('roleInput') roleInput: ElementRef<HTMLInputElement>;

  constructor(
    private formBuilder: UntypedFormBuilder,
    private snackBarService: SnackBarService,
    private translateService: TranslateService,
    @Inject('IStaffUserService') private service: IStaffUserService,
    @Inject('IUserService') private userService: IUserService,
    @SkipSelf() protected _errorStateMatcher: ErrorStateMatcher
  )
  {
    super(_errorStateMatcher);
  }

  ngOnInit(): void
  {
    // this.form = new FormGroup({
    //   Emails: new FormArray([], Validators.email),
    //   PortalGroupRoles: new FormArray([]),
    //   isAdministrator: new FormControl(false),
    //   roles: new FormArray([]),
    //   portals: new FormArray([])
    // });
    //this.filterRoles();
  }

  ngOnChanges(changes: SimpleChanges): void
  {
    // if (changes.model)
    // {
    //   this.filterRoles();
    // }
  }

  get formControls()
  {
    return this.form.controls;
  }

  onNext(): void
  {
    if (this.formControls?.isAdministrator?.value)
    {
      const request = new PostInviteUserRequest();
      request.Emails = this.emails;
      request.IsAdministrator = true;
      request.TenantIDs = this.model.Portals.filter(x => x.IsSelected).map(x => x.TenantID);


      this.isLoading = true;
      this.service.PostInvite(request).subscribe({
        next: () =>
        {
          this.onCloseEmitter.emit(true);
          this.isLoading = false;
          this.snackBarService.showInfo(this.translateService.instant("ADMIN_CENTER.INVITE_USERS_INITIATE.SUCCESS_MESSAGE"), '', null, 'left');
        },
        error: () => this.isLoading = false
      });
    } else
    {
      const selectedData = new InviteUserSelectedData();
      selectedData.Emails = this.emails;
      selectedData.GlobalRoles = this.selectedRoles;
      selectedData.Portals = this.model.Portals.filter(x => x.IsSelected);

      if (this.Portals.length === 0)
      {
        this.snackBarService.showError(this.translateService.instant("ADMIN_CENTER.INVITE_USERS_INITIATE.PLEASE_SELECT_PORTAL"), '', null, 'left');
        return;
      }

      this.selectedInviteUserEmitter.emit(selectedData);

      this.stepper.next();
    }
  }

  addEmailChip(emails: Array<string>): void
  {
    //Check if email is already exists
    const newEmail = emails.filter(x => !this.emails.includes(x))[0];
    const emailToRemove = this.emails.filter(x => !emails.includes(x))[0];
    this.emails = [...emails];
    if (emailToRemove)
    {
      this.removeEmail(emailToRemove);
      const indexItem = this.invalidEmails.indexOf(emailToRemove);
      if (indexItem >= 0)
      {
        this.invalidEmails.splice(indexItem, 1);
        this.formControls.Emails.setErrors({ 'existsEmail': this.invalidEmails.length > 0 });
      }
    } else if (newEmail)
    {
      this.checkEmailExists(newEmail);
    }
  }

  checkEmailExists(email: string): void
  {
    const request = new GetUserByEmailRequest();
    request.Email = email;

    this.isLoading = true;
    this.userService.GetVerifyStaffEmailInPortalGroup(request).subscribe({
      next: (response: GetVerifyStaffEmailInPortalGroupResponse) =>
      {
        this.isLoading = false;
        if (response && response.UserID)
        {
          this.invalidEmails.push(email);
          this.formControls.Emails.setErrors({ 'existsEmail': true });
        }
        else
        {
          this.emailFormArray.push(new UntypedFormControl(email));
        }
      },
      error: () => this.isLoading = false
    });

  }

  addRole(event: MatChipInputEvent): void
  {
    if (event.value.trim() == '')
    {
      return;
    }
    const role = this.model?.GlobalRoles?.find(x => x.RoleID === Number(event.value));
    this.portalGroupRoles.push(new RoleFormGroup(role));
    this.selectedRoles.push(role);
    // Clear the input value
    // event.chipInput?.clear();
    event.value = '';
  }

  removeRole(i: number): void
  {
    this.portalGroupRoles.removeAt(i);
  }

  selectedRole(event: MatAutocompleteSelectedEvent): void
  {
    const role = this.model?.GlobalRoles?.find(x => x.RoleID === Number(event.option.value));
    const selectedPortalGroup = this.portalGroupFormArray.controls.find(p => p.value == role.RoleID);
    if (!selectedPortalGroup)
    {
      this.portalGroupRoles.push(new RoleFormGroup(role));
    }
    this.roleInput.nativeElement.value = '';
  }

  deselectedRoles(): Array<Role>
  {
    return this.model?.GlobalRoles.filter(role => this.form.controls.PortalGroupRoles.value.findIndex(gr => gr.RoleID == role.RoleID) < 0);
  }

  get nextButtonText(): string
  {
    return this.formIsValid ? (this.formControls?.isAdministrator?.value ? 'ADMIN_CENTER.INVITE_USERS_INITIATE.SEND_INVITE' : 'ADMIN_CENTER.INVITE_USERS_INITIATE.NEXT') : 'ADMIN_CENTER.INVITE_USERS_INITIATE.DETAILS_REQUIRED';
  }

  get formIsValid(): boolean
  {
    return this.Portals.controls.length > 0 && this.emailFormArray?.controls.length > 0 && !this.emailFormArray.hasError('existsEmail');
  }

  get invalidEmailsString(): string
  {
    return this.invalidEmails.join(', ');
  }

  onCheckChange(event, portal: Portal): void
  {
    if (event.checked)
    {
      this.Portals?.push(new PortalFormGroup(portal));
    }
    else
    {
      let index = this.Portals.controls.findIndex(p => p.value == portal.PortalID);
      this.Portals.removeAt(index);
    }
  }

  onAdministratorCheckChange(event): void
  {
    const isChecked = event.checked;

    this.Portals.clear();
    this.portalGroupRoles.clear();

    if (isChecked)
    {
      this.model.Portals.forEach(p =>
      {
        this.Portals?.push(new PortalFormGroup(p));
      });
    }
    this.model?.Portals.map(portal => portal.IsSelected = isChecked);
  }

  close(): void
  {
    this.onCloseEmitter.emit(false);
  }

  removeEmail(email)
  {
    const index = this.emailFormArray.value.indexOf(email);
    if (index >= 0)
    {
      this.emailFormArray.removeAt(index);
    }
  }

  addEmail(emails: string[])
  {
    emails.forEach(email =>
    {
      if (email.toString().trim() == '')
      {
        return;
      }
      const emailFormControl = this.emailFormArray.controls.find(e => e.value == email);

      if (!email)
      {
        this.checkEmailExists(email);
      }
    });
  }

  get emailFormArray()
  {
    return this.form.get('Emails') as UntypedFormArray;
  }

  get portalGroupFormArray()
  {
    return this.form.get('PortalGroupRoles') as UntypedFormArray;
  }

  get portalGroupRoles(): RoleFormArray
  {
    return this.form.controls.PortalGroupRoles as UntypedFormArray;
  }

  get Portals()
  {
    return this.form.controls.Portals as UntypedFormArray;
  }
}
