import { Component, Inject, OnInit } from '@angular/core';
import { UntypedFormControl, UntypedFormGroup, Validators } from '@angular/forms';
import { TranslateService } from '@ngx-translate/core';
import { ManageLoadingService } from '@tei/core';
import { BreadCrumb, SnackBarService } from '@tei/material';
import { Subscription } from 'rxjs';
import { finalize } from 'rxjs/operators';
import { IAdminPortalService } from '../../../@intermediate/admin-portal.interface';
import { IHostAdminCenterHelperService } from '../../../@intermediate/host-admin-center-helper.interface';
import { GetNoonPaymentRequest, GetNoonPaymentResponse, PEChannel } from '../../../@intermediate/models/get-noon-paymnet-setup.model';
import { portalGroupDetails } from '../../../@intermediate/models/host-admin-center-helper.model';
import { PostNoonPaymentRequest, PostNoonPaymentResponse } from '../../../@intermediate/models/post-noon-paymnet-setup.model';
import { PortalGroupByID } from '../../../@intermediate/portal-group/models/get-portal-group-by-id.model';

@Component({
  selector: 'tei-admin-admin-center-noon-payment-setting',
  templateUrl: './noon-payment-setting.component.html',
  styleUrls: ['./noon-payment-setting.component.scss']
})
export class NoonPaymentSettingComponent implements OnInit
{
  isLoading: boolean = false;
  portalId: string;
  parentPortalGroupId: string;
  parentPortalGroup: PortalGroupByID;
  subscription: Subscription = new Subscription();
  portalDetail: portalGroupDetails;
  breadCrumbData: BreadCrumb[] = [];

  settingForm: UntypedFormGroup = new UntypedFormGroup({
    BusinessID: new UntypedFormControl('', Validators.required),
    ApplicationName: new UntypedFormControl('', Validators.required),
    ApplicationKey: new UntypedFormControl('', Validators.required),
    ChannelID: new UntypedFormControl('', Validators.required),
    Category: new UntypedFormControl('', Validators.required),
    DemoAccount: new UntypedFormControl(false)
  });
  channels: PEChannel[];
  constructor(
    @Inject('IHostAdminCenterHelperService') private hostAdminCenterHelperService: IHostAdminCenterHelperService,
    @Inject('IAdminPortalService') private adminPortalService: IAdminPortalService,
    public loadingService: ManageLoadingService,
    private translate: TranslateService,
    private snackBar: SnackBarService
  ) { }

  ngOnInit(): void
  {
    this.subscription.add(this.hostAdminCenterHelperService.currentPortalGroupDetails.subscribe(portalDetail =>
    {
      if (portalDetail)
      {
        this.portalDetail = {
          parentPortalGroup: portalDetail.parentPortalGroup.PortalGroup,
          portal: portalDetail.portal
        };
        this.fillBreadCrump();
        this.isLoad();
      }
    }));
  }
  private async fillBreadCrump(): Promise<void>
  {
    this.breadCrumbData = [];
    this.breadCrumbData = [
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.PORTAL_GROUPS.TITLE'),
        relativeUrl: '../../../../../',
      },
      {
        title: `${ this.portalDetail.parentPortalGroup.PortalGroupName }`,
        relativeUrl: `../../../../`,
      },
      {
        title: `${ this.portalDetail.portal.PortalName }`,
        relativeUrl: '../../',
      },
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.PAYMENT_SETTINGS'),
        relativeUrl: '../',
      },
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.NOON_PAYMENTS_SETUP'),
        relativeUrl: '',
      }
    ];
  }

  isLoad(): void
  {
    const request: GetNoonPaymentRequest = {
      PortalID: this.portalDetail.portal.PortalID
    };

    this.loadingService.startLoading();
    this.adminPortalService.GetNoonPaymentSetup(request).pipe(finalize(() => this.loadingService.finishLoading())).subscribe({
      next: (response: GetNoonPaymentResponse) =>
      {
        this.channels = response.PEChannels;
        this.settingForm.patchValue({
          BusinessID: response.BusinessID,
          ApplicationName: response.ApplicationName,
          ApplicationKey: response.ApplicationKey,
          ChannelID: response.Channel,
          Category: response.Category,
          DemoAccount: response.DemoAccount
        });
      }
    });
  }

  saveSetting(): void
  {
    if (this.settingForm.invalid) { return; }
    const request: PostNoonPaymentRequest = {
      PortalID: this.portalDetail.portal.PortalID,
      BusinessID: this.settingForm.value.BusinessID,
      ApplicationName: this.settingForm.value.ApplicationName,
      ApplicationKey: this.settingForm.value.ApplicationKey,
      ChannelID: this.channels.filter(channel => channel.Name === this.settingForm.value.ChannelID)?.[0].ID,
      Category: this.settingForm.value.Category,
      DemoAccount: this.settingForm.value.DemoAccount,
    };

    this.loadingService.startLoading();
    this.adminPortalService.PostNoonPaymentSetup(request).pipe(finalize(() => this.loadingService.finishLoading())).subscribe({
      next: (response: PostNoonPaymentResponse) =>
      {
        this.snackBar.showSuccess(this.translate.instant('ADMIN_CENTER.PRODUCTS.SAVED_SUCCESSFULLY'), '', null, 'left');
      }
    });

  }
  ngOnDestroy(): void
  {
    this.subscription?.unsubscribe();
  }

}
