import { Component, Inject } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { BreadCrumb, Setting } from '@tei/material';
import { SecureNavigation } from '@tei/material-smart-components';
import { Subscription } from 'rxjs';
import { IHostAdminCenterHelperService } from '../../../@intermediate/host-admin-center-helper.interface';
import { ManagePortalTab, ManagePortalTabIdEnum } from '../../../@intermediate/models/get-portal.model';

@Component({
  selector: 'tei-admin-admin-center-range-machine-integration',
  templateUrl: './range-machine-integration.component.html',
  styleUrls: ['./range-machine-integration.component.css']
})
export class RangeMachineIntegrationComponent
{
  portalId: string;
  tabs: ManagePortalTab[] = [];
  selectedTab: ManagePortalTab;
  breadCrumbData: BreadCrumb[] = [];
  subscription: Subscription = new Subscription();

  constructor(
    @Inject('IHostAdminCenterHelperService') private hostAdminCenterHelperService: IHostAdminCenterHelperService,
    private translate: TranslateService)
  {

  }

  ngOnInit(): void
  {
    this.subscription.add(this.hostAdminCenterHelperService.currentPortalGroupDetails.subscribe(portalDetail =>
    {
      if (portalDetail)
      {
        this.fillBreadCrump(portalDetail);
      }
    }));
  }

  secureNavigation: SecureNavigation = {
    groupPermissionIdentifier: 'range-machine-integrations',
    sections: [
      {
        title: '',
        items: [
          {
            permissionIdentifier: 'range-machine-class-ids',
            link: ManagePortalTabIdEnum.RangeMachineClassIDS,
            title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.INTEGRATION.RANGE_MACHINE_CLASS_IDS'),
            description: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.INTEGRATION.RANGE_MACHINE_CLASS_IDS_DESCRIPTION')
          },
          {
            permissionIdentifier: 'select-pi-setup',
            link: ManagePortalTabIdEnum.SelectPiSetup,
            title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.INTEGRATION.SELECT_PI_SETUP'),
            description: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.INTEGRATION.SELECT_PI_SETUP_DESCRIPTION')
          },
          {
            permissionIdentifier: 'erange-setup',
            link: ManagePortalTabIdEnum.ERangeSetup,
            title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.INTEGRATION.ERANGE_SETUP'),
            description: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.INTEGRATION.ERANGE_SETUP_DESCRIPTION')
          }
        ]
      }
    ]
  };

  settings: Setting[] = [
    {
      link: ManagePortalTabIdEnum.GeneralSetup,

      title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.GENERAL_SETUP'),
      description: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.GENERAL_SETUP_DESCRIPTION')
    }
  ];

  fillBreadCrump(portalDetail): void
  {
    this.breadCrumbData = [
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.PORTAL_GROUPS.TITLE'),
        relativeUrl: '../../../../../',
      },
      {
        title: `${ portalDetail.parentPortalGroup.PortalGroup.PortalGroupName }`,
        relativeUrl: `../../../../`,
      },
      {
        title: `${ portalDetail.portal.PortalName }`,
        relativeUrl: '../../../',
      },
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.ACCESS'),
        relativeUrl: '../../',
      },
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.INTEGRATION.TITLE'),
        relativeUrl: '../',
      },
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.RANGEMACHINEINTEGRATION'),
        relativeUrl: '',
      }
    ];
  }

  ngOnDestroy(): void
  {
    this.subscription?.unsubscribe();
  }
}
