import { HttpClient } from "@angular/common/http";
import { Inject, Injectable } from "@angular/core";
import { IAPIBaseUrlProvider } from "@tei/core";
import { Observable } from "rxjs";
import { GetAnonymousUserPermissionRequest, GetAnonymousUserPermissionResponse } from "../@intermediate/user-permissions/models/get-anonymous.model";
import { PostAnonymousUserPermissionRequest, PostAnonymousUserPermissionResponse } from "../@intermediate/user-permissions/models/post-anonymous.model";
import { IUserPermissionService } from "../@intermediate/user-permissions/user-permissions.interface";

@Injectable()
export class UserPermissionService implements IUserPermissionService
{
  private readonly serviceUrl: string;

  constructor(
    @Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider,
    protected http: HttpClient
  )
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Admin/AdminCenter/UserPermissions';
  }

  public GetAnonymous(request: GetAnonymousUserPermissionRequest): Observable<GetAnonymousUserPermissionResponse>
  {
    const apiUrl = this.serviceUrl + '/Anonymous';
    return this.http.get<GetAnonymousUserPermissionResponse>(apiUrl, { params: request as any });
  }

  public PostAnonymous(request: PostAnonymousUserPermissionRequest): Observable<PostAnonymousUserPermissionResponse>
  {
    const apiUrl = this.serviceUrl + '/Anonymous';
    return this.http.post<PostAnonymousUserPermissionResponse>(apiUrl, request);
  }
}
