import { Component, EventEmitter, Inject, Input, OnInit, Output, SkipSelf } from '@angular/core';
import { UntypedFormArray, UntypedFormBuilder } from '@angular/forms';
import { MatLegacyChipInputEvent as MatChipInputEvent } from '@angular/material/legacy-chips';
import { ErrorStateMatcher } from '@angular/material/core';
import { MatStepper } from '@angular/material/stepper';
import { TranslateService } from '@ngx-translate/core';
import { ModelDrivenFormGroupControlValueAccessor } from '@tei/core';
import { SnackBarService } from '@tei/material';
import { GetInviteUserResponse, InviteUserSelectedData } from '../../../../../@intermediate/staff-user/models/get-invite-user.model';
import { PortalRole, PostInviteUserRequest, PostInviteUserRequestForm, PostInviteUserResponse } from '../../../../../@intermediate/staff-user/models/post-invite-user.model';
import { IStaffUserService } from '../../../../../@intermediate/staff-user/staff-user.interface';
import { ItemNode } from './../../../../../components/tree/tree.model';

@Component({
  selector: 'tei-admin-admin-center-invite-users-finalize',
  templateUrl: './invite-users-finalize.component.html',
  styleUrls: ['./invite-users-finalize.component.scss']
})
export class InviteUsersFinalizeComponent extends ModelDrivenFormGroupControlValueAccessor implements OnInit
{
  isLoading: boolean = false;
  @Input() inviteUserSelectedData: InviteUserSelectedData;
  @Input() stepper: MatStepper;
  @Input() form: PostInviteUserRequestForm;
  @Output() onCloseEmitter = new EventEmitter<boolean>();
  @Input() model: GetInviteUserResponse;
  treeData: Array<ItemNode> = [];



  constructor(
    private formBuilder: UntypedFormBuilder,
    @SkipSelf() protected _errorStateMatcher: ErrorStateMatcher,
    @Inject('IStaffUserService') private service: IStaffUserService,
    private translateService: TranslateService,
    private snackBarService: SnackBarService)
  {
    super(_errorStateMatcher);
  }

  ngOnInit(): void
  {
  }

  onSendInvite(): void
  {
    const request = new PostInviteUserRequest();
    request.Emails = (<UntypedFormArray>this.form.get('Emails')).controls.map(email => email.value);
    request.IsAdministrator = false;
    this.Portals.controls.forEach(portal =>
    {
      request.TenantIDs.push(portal.get('TenantID').value);
      let portalRole = new PortalRole();
      portalRole.TenantID = portal.get('TenantID').value;
      portalRole.Roles = portal.get('Roles').value.map(role => role.RoleID);
      request.PortalRoles.push(portalRole);
      portal.get('RestaurantTerminals').value.forEach(terminal =>
      {
        request.Terminals.push(terminal);
      });

      portal.get('SiteTerminals').value.forEach(site =>
      {
        request.Terminals.push(site);
      });
    });

    request.PortalGroupRoles = (<UntypedFormArray>this.form.get('PortalGroupRoles')).controls.map(portalGroupRole => portalGroupRole.value.RoleID);

    this.isLoading = true;
    this.service.PostInvite(request).subscribe({
      next: (response: PostInviteUserResponse) =>
      {
        this.isLoading = false;
        this.onCloseEmitter.emit(true);
        this.snackBarService.showInfo(this.translateService.instant("ADMIN_CENTER.INVITE_USERS_INITIATE.SUCCESS_MESSAGE"), '', null, 'left');
      },
      error: () => this.isLoading = false
    });
  }

  onGoBack(): void
  {
    this.stepper.previous();
  }

  onClose(): void
  {
    this.onCloseEmitter.emit(false);
  }

  addRole(event: MatChipInputEvent): void
  {
    // event.chipInput?.clear();
    event.value = '';
  }

  get Portals()
  {
    return this.form.controls.Portals as UntypedFormArray;
  }
}
