import { Component, Inject, OnInit } from '@angular/core';
import { UntypedFormControl, UntypedFormGroup } from '@angular/forms';
import { MatLegacyDialog as MatDialog, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { ActivatedRoute } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { BreadCrumb, ImgDialogData, SnackBarService, UploadIconComponent, UploadIconDialogData, UploadImageDialogData, UploadProfilePictureComponent } from '@tei/material';
import { base64ToFile } from 'ngx-image-cropper';
import { IAdminPortalGroupService } from '../../../../@intermediate/portal-group/admin-portal-groups.interface';
import { GetPortalGroupDetailRequest, GetPortalGroupDetailResponse } from '../../../../@intermediate/portal-group/models/get-portal-group-detail.model';
import { PostPortalGroupDetailRequest, PostPortalGroupDetailResponse } from '../../../../@intermediate/portal-group/models/post-portal-group-detail.model';
import { PostPortalGroupSettingsResetRequest, SettingNameEnum } from '../../../../@intermediate/portal-group/models/post-portal-group-settings-reset.model';

@Component({
  selector: 'tei-host-admin-edit-portal-group',
  templateUrl: './edit-portal-group.component.html',
  styleUrls: ['./edit-portal-group.component.scss']
})
export class EditPortalGroupComponent implements OnInit
{
  portalGroup: GetPortalGroupDetailResponse;
  uploadPhotoDialogRef: MatDialogRef<UploadProfilePictureComponent>;
  settingForm: UntypedFormGroup = new UntypedFormGroup({
    PortalGroupName: new UntypedFormControl(''),
    PortalGroupIdentifier: new UntypedFormControl({ value: '', disabled: true }),
    PortalGroupTitle: new UntypedFormControl(''),
    Timezone: new UntypedFormControl(''),
    Culture: new UntypedFormControl(''),
    defaultTheme: new UntypedFormControl('')
  });
  uploadIconDialogRef: MatDialogRef<UploadIconComponent>;
  fieldsNameEnum = SettingNameEnum;

  breadCrumbData: BreadCrumb[] = [];
  isLoading;

  constructor(
    @Inject('IAdminPortalGroupService') private hostAdminPortalGroupsService: IAdminPortalGroupService,
    private dialog: MatDialog,
    private translate: TranslateService,
    private snackBarService: SnackBarService,
    private activatedRoute: ActivatedRoute
  )
  {
  }

  ngOnInit(): void
  {
    this.load();
  }

  load(): void
  {
    const portalGroupId = this.activatedRoute.snapshot.params.portalGroupId;

    if (portalGroupId)
    {

      const request: GetPortalGroupDetailRequest = {
        ID: portalGroupId
      };

      this.hostAdminPortalGroupsService.Detail(request).subscribe(response =>
      {
        this.portalGroup = response;

        this.settingForm.setValue({
          PortalGroupName: response.PortalGroupName,
          PortalGroupIdentifier: response.PortalGroupIdentifier,
          PortalGroupTitle: response.Title,
          Timezone: response.CurrentTimeZone,
          Culture: response.CurrentCulture,
          defaultTheme: response.DefaultTheme
        });

        this.breadCrumbData = [
          {
            title: this.translate.instant('ADMIN_CENTER.GROUP.PORTAL_GROUPS.TITLE'),
            relativeUrl: '../../'
          },
          {
            title: `${ response.PortalGroupName }`,
            relativeUrl: '../'
          },
          {
            title: this.translate.instant('ADMIN_CENTER.GROUP.NEW_PORTAL_GROUP.PORTAL_GROUP_DETAILS'),
            relativeUrl: ''
          }
        ];
      });
    }
  }

  onChangeFavIcon(): void
  {
    const data: UploadIconDialogData = {
      disableDelete: !this.portalGroup?.[SettingNameEnum.FavIcon],
      onConfirm: (base64File) =>
      {
        this.uploadIconDialogRef.close();
        const result = new ImgDialogData();
        result.croppedImage = base64File;
        this.onChangeUploadPhoto(result, SettingNameEnum.FavIcon);
      },
      onDelete: () => this.onDeleteUploadPhoto(SettingNameEnum.FavIcon)
    };

    this.uploadIconDialogRef = this.dialog.open(UploadIconComponent, {
      maxWidth: '80vw',
      width: '390px',
      panelClass: ['o-dialog-user-profile-picture'],
      data
    });
  }

  onChangeDarkThemeImage(): void
  {
    this.onChangeImage(SettingNameEnum.DarkThemeLogo);
  }

  onChangeLightThemeImage(): void
  {
    this.onChangeImage(SettingNameEnum.LightThemeLogo);
  }

  onChangeLightThemeHeaderLogo(): void
  {
    this.onChangeImage(SettingNameEnum.LightThemeHeaderLogo);
  }

  onChangeDarkThemeHeaderLogo(): void
  {
    this.onChangeImage(SettingNameEnum.DarkThemeHeaderLogo);
  }

  onChangePortalAvatar(): void
  {
    this.onChangeImage(SettingNameEnum.Avatar);
  }

  onChangeImage(fieldName: SettingNameEnum): void
  {
    const maintainAspectRatio = fieldName === SettingNameEnum.LightThemeHeaderLogo ||
      fieldName === SettingNameEnum.DarkThemeHeaderLogo ||
      fieldName === SettingNameEnum.LightThemeLogo ||
      fieldName === SettingNameEnum.DarkThemeLogo;

    const portalImageFormats = '.png,.jpg,.jpeg';
    const portalImagePermittedFileTypes = ['PNG', 'JPG', 'JPEG'];

    const data: UploadImageDialogData = {
      round: false,
      ...(maintainAspectRatio && { maintainAspectRatio: false }),
      ...(fieldName === SettingNameEnum.Avatar && { aspectRatio: 4 / 2.5 }),
      ...(fieldName === SettingNameEnum.Avatar && { acceptFormat: portalImageFormats }),
      ...(fieldName === SettingNameEnum.Avatar && { permittedFileTypes: portalImagePermittedFileTypes }),
      ...(fieldName !== SettingNameEnum.Avatar && { maxSize: 200 }),
      ProfilePictureUrl: this.portalGroup?.[fieldName],
      onChangePicture: (result: ImgDialogData) => this.onChangeUploadPhoto(result, fieldName),
      onDeletePicture: () => this.onDeleteUploadPhoto(fieldName)
    };

    this.uploadPhotoDialogRef = this.dialog.open(UploadProfilePictureComponent, {
      maxWidth: '80vw',
      width: '390px',
      panelClass: ['o-dialog-user-profile-picture', 'o-rectangular-container'],
      data
    });

  }

  onChangeUploadPhoto(result: ImgDialogData, fieldName: SettingNameEnum): void
  {

    const request = this.createUpdateSettingRequest();
    const file = base64ToFile(result.croppedImage);
    (request as any)[fieldName] = file;

    const formData = this.convertBodyRequestToFormData(request);
    if (this.settingForm.invalid) { return; }
    this.hostAdminPortalGroupsService.UpdateDetail(formData).subscribe({
      next: (response: PostPortalGroupDetailResponse) =>
      {
        this.portalGroup[fieldName] = response[fieldName];
        if (this.uploadPhotoDialogRef)
        {
          this.uploadPhotoDialogRef.componentInstance.profilePictureUrl = response[fieldName];
        }
      },
      error: () =>
      {
      }
    });
  }

  onDeleteUploadPhoto(fieldName: SettingNameEnum): void
  {
    const request: PostPortalGroupSettingsResetRequest = {
      TenantID: this.portalGroup.TenantID,
      SettingName: fieldName
    };
    this.hostAdminPortalGroupsService.ResetSetting(request).subscribe({
      next: (response) =>
      {
        this.portalGroup[fieldName] = response.DefaultValue;
        if (this.uploadPhotoDialogRef)
        {
          this.uploadPhotoDialogRef.componentInstance.profilePictureUrl = response.DefaultValue;
        }
      },
      error: () =>
      {
      }
    });
  }

  save(): void
  {
    const request = this.createUpdateSettingRequest();

    const formData = this.convertBodyRequestToFormData(request);
    if (this.settingForm.invalid) { return; }
    this.hostAdminPortalGroupsService.UpdateDetail(formData).subscribe(
      {
        next: () =>
        {
          this.snackBarService.showSuccess(
            this.translate.instant("ADMIN_CENTER.GROUP.MANAGE_PORTAL.SETTINGS.SAVE_SUCCESS"),
            '', null, 'left');

        },
        error: () =>
        {
          this.snackBarService.showError(
            this.translate.instant("ADMIN_CENTER.GROUP.MANAGE_PORTAL.SETTINGS.SAVE_ERROR"),
            '', null, 'left');
        }
      });
  }

  createUpdateSettingRequest()
  {
    const request: PostPortalGroupDetailRequest = {
      PortalGroupID: this.portalGroup.PortalGroupID,
      TenantID: this.portalGroup.TenantID,
      PortalGroupName: this.settingForm.value.PortalGroupName,
      Title: this.settingForm.value.PortalGroupTitle,
      CurrentTimeZone: this.settingForm.value.Timezone,
      CurrentCulture: this.settingForm.value.Culture,
      DefaultTheme: this.settingForm.value.defaultTheme
    };
    return request;
  }

  convertBodyRequestToFormData(bodyRequest: { [key in string | number]: any; } = {}): FormData
  {
    const formData = new FormData();

    Object.keys(bodyRequest).forEach(key =>
    {

      if (bodyRequest[key] instanceof Blob)
      {
        const fileExtension = bodyRequest[key]?.type.split('/')?.[1] ?? 'jpg';
        const fileName = key + "." + (fileExtension === 'x-icon' ? 'ico' : fileExtension);
        formData.append(key, bodyRequest[key], fileName);
      }
      else
      {
        formData.append(key, bodyRequest[key].toString());
      }
    });

    return formData;
  }

  getErrorMessage(fieldName: string, fieldLabel: string): string
  {
    if (this.settingForm?.get(fieldName).hasError('required'))
    {
      return `${ fieldLabel } ${ this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL.SETTINGS.IS_REQUIRED') }`;
    }
    if (this.settingForm?.get(fieldName).hasError('pattern'))
    {
      return `${ fieldLabel } ${ this.translate.instant('SHELL.AUTH_PAGES.LOGIN.IS_NOT_VALID') }`;
    }
  }
}
