export class GetCustomerFacingDisplayConfigurationRequest
{
    DeviceID?: number;
}

export class GetCustomerFacingDisplayConfigurationResponse
{
    Terminals: Array<Terminal>;
    Sites: Array<Site>;
    AppModes: Array<AppMode>;
}

export class AppMode
{
    Title: string;
    Value: number;
}

export class Site
{
    Title: string;
    Value: number;
    AppMode: string;
}
export class Terminal
{
    Value: number;
    Title: string;
    SiteID: number;
    POSID: string;
}

export enum BGCFDType
{
    ActiveBackgroundImage,
    NoOrderBackgroundImage
}