import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";
import { CustomerFacingDisplaysAddComponent } from "./customer-facing-displays-add/customer-facing-displays-add.component";
import { CustomerFacingDisplaysEditComponent } from "./customer-facing-displays-edit/customer-facing-displays-edit.component";
import { CustomerFacingDisplaysComponent } from "./customer-facing-displays.component";

const routes: Routes = [
    {
        path: '',
        component: CustomerFacingDisplaysComponent,
        children: []
    },
    {
        path: 'add',
        component: CustomerFacingDisplaysAddComponent
    },
    {
        path: 'edit/:id',
        component: CustomerFacingDisplaysEditComponent
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule],
})
export class CustomerFacingDisplaysRoutingModule
{
}