import { Component, Input, Output, EventEmitter, OnInit } from "@angular/core";
import { FormControl, FormGroup } from "@angular/forms";
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { FormFactoEnum } from "../../@intermediate/models/get-tenant-web-alias-manifest-screenshot.model";
import { ManageLoadingService } from "@tei/core";
import { ManifestScreenShotsForm } from "../../@intermediate/models/get-web-alias.model";
import { ImageUploaderComponent, ImageUploaderModel } from '@tei/material';

@Component({
  selector: 'tei-host-admin-screenshot',
  templateUrl: './screenshot.component.html',
  styleUrls: ['./screenshot.component.scss']
})
export class ScreenshotComponent implements OnInit {
  @Input() form: FormGroup<ManifestScreenShotsForm>;
  @Input() screenshotIndex: number;
  @Input() isLastIndex: boolean;
  @Output() remove = new EventEmitter();
  @Output() add = new EventEmitter();
  imageurl: string;
  formFactors = [
    { name: 'Not set', value: FormFactoEnum.NotSet },
    { name: 'Narrow', value: FormFactoEnum.Narrow },
    { name: 'Wide', value: FormFactoEnum.Wide }
  ];

  constructor(
    public manageLoadingService: ManageLoadingService,
    private dialog: MatDialog) {
  }

  ngOnInit(): void {
    var ImageUrl = (this.form.get('ImageUrl') as FormControl);
    this.imageurl = ImageUrl?.value;
  }

  get formControls() {
    return this.form.controls;
  }

  get translationKey(): string {
    return `ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.SCREEN_SHOTS_${this.screenshotIndex + 1}`;
  }

  onChangeUpload(): void
  {
    const imageFormats = '.jpg,.jpeg,.jpe,.jif,.jfif,.jfi,.png,.gif,.bmp,.dib,.tiff,.tif,.heif,.heic';
    const imagePermittedFileTypes = ['JPG', 'JPEG', 'JPE', 'JFIF', 'PNG', 'GIF', 'BMP', 'TIFF', 'HEIC'];
    const data: ImageUploaderModel = {
      round: false,
      maintainAspectRatio: false,
      aspectRatio: 4 / 2.5,
      acceptFormat: imageFormats,
      permittedFileTypes: imagePermittedFileTypes,
      maxSize: 512,
      origImage: this.formControls.ImageFile.value,
      imageServerUrl: this.imageurl
    };
    const imageUploaderDialog = this.dialog.open(ImageUploaderComponent, {
      width: '400px',
      panelClass: ['o-dialog-md-fullscreen'],
      data,
    });
    imageUploaderDialog.afterClosed().subscribe(result =>
    {
      if (result)
      {
        this.formControls.ImageFile.setValue(result?.origImage);
        this.imageurl = result?.croppedImage;
        if(!this.imageurl)
        {
          this.formControls.ImageUID.setValue(null);
        }
      }
    });
  }

  removeScreenshot()
  {
    this.remove.emit();
  }

  addScreenShot()
  {
    this.add.emit();
  }
}
