import { Component, Inject, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { ResponsiveService } from '@tei/core';
import { BreadCrumb, SnackBarService } from '@tei/material';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { GeneralSettingsRequestForm, GetGeneralSettingsRequest, GetGeneralSettingsResponse, IGeneralSettingsService } from '../../../@intermediate/general-settings';

@Component({
  selector: 'tei-portal-admin-center-payment-engine-settings-general',
  templateUrl: './general-settings.component.html',
  styleUrls: ['./general-settings.component.scss']
})
export class GeneralSettingsComponent implements OnInit
{
  isMobile: boolean;
  breadCrumbData: BreadCrumb[] = [
    {
      title: this.translate.instant('ADMIN_CENTER.HOST.SETTINGS.PORTAL_ADMIN_CENTER'),
      relativeUrl: ''
    },
    {
      title: this.translate.instant('ADMIN_CENTER.HOST.SETTINGS.SETUP'),
      relativeUrl: '../../'
    },
    {
      title: this.translate.instant('ADMIN_CENTER.HOST.SETTINGS.PAYMENT_ENGINE'),
      relativeUrl: '../'
    },
    {
      title: this.translate.instant('ADMIN_CENTER.HOST.SETTINGS.GENERAL_SETTINGS'),
      relativeUrl: ''
    },
  ];
  isLoading = false;
  form$: Observable<GeneralSettingsRequestForm>;
  model: GetGeneralSettingsResponse;

  constructor(private translate: TranslateService, private snackBar: SnackBarService,
    @Inject('IGeneralSettingsService') private generalSettingsService: IGeneralSettingsService,
    private responsiveService: ResponsiveService,
  )
  { }

  ngOnInit()
  {
    this.isMobile = this.responsiveService.isMobile;
    this.form$ = this.generalSettingsService.Get(new GetGeneralSettingsRequest)
      .pipe(map((response) => new GeneralSettingsRequestForm(response)));

    this.generalSettingsService.Get(new GetGeneralSettingsRequest).subscribe(response =>
    {
      this.model = response;
    });
  }

  save(form: GeneralSettingsRequestForm)
  {
    this.isLoading = true;
    this.generalSettingsService.Post(form.value).subscribe(
      () =>
      {
        this.isLoading = false;
        this.snackBar.showSuccess(this.translate.instant('PAYMENT_ENGINE.GENERAL_SETTINGS.SETTINGS_SAVED'));
      },
      () =>
      {
        this.isLoading = false;
      });
  }

}
