export class GetRoleRequest
{
}

export class GetRolesResponse
{
  Roles: Role[];
}

export class Role
{
  RoleID: number;
  DesignationID: DesignationIDEnum;
  RoleName: string;
  RoleDescription: string;
  IsSystem?: boolean;
  RoleTypeID?: number;
  RoleTypeTitle?: string;
  PortalID?: number;
  IsReadOnly?: boolean;
  AutoAssign: boolean;
}

export enum DesignationIDEnum
{
  Anonymous = 5,
  Staff = 4,
  Member = 3,
  Public = 2
}
