import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetSurchargeSettingsRequest, GetSurchargeSettingsResponse, ISurchargeSettingsService, PostSurchargeSettingsRequest, PostSurchargeSettingsResponse } from '../@intermediate/surcharge-settings';

@Injectable({
  providedIn: 'root'
})

export class SurchargeSettingsService implements ISurchargeSettingsService
{
  private serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + "/Common/PaymentEngine/Settings/SurchargeSettings";
  }

  public Get(request: GetSurchargeSettingsRequest): Observable<GetSurchargeSettingsResponse>
  {
    const apiUrl = this.serviceUrl + "";
    return this.http.get<GetSurchargeSettingsResponse>(apiUrl, { params: <any>request });
  }

  public Post(request: PostSurchargeSettingsRequest): Observable<PostSurchargeSettingsResponse>
  {
    const apiUrl = this.serviceUrl + "";
    return this.http.post<PostSurchargeSettingsResponse>(apiUrl, request);
  }
}
