import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';

import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatIconModule } from "@angular/material/icon";
import { MatLegacyFormFieldModule as MatFormFieldModule } from "@angular/material/legacy-form-field";
import { MatLegacyInputModule as MatInputModule } from "@angular/material/legacy-input";
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { BreadCrumbModule, LoadingModule, SettingsListModule } from '@tei/material';
import { ButterCMSSetupService } from '../../@services/butter-cms-setup.service';
import { ManagePortalTABModule } from '../host-admin-center/manage-portal/manage-portal-tab/manage-portal-tab.module';
import { IntegrationsComponent } from '../integrations/integrations.component';
import { ButterCMSSetupComponent } from './butter-cmssetup/butter-cmssetup.component';
import { RangeMachineIntegrationComponent } from './range-machine-integration/range-machine-integration.component';

const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule];
const MATERIAL_MODULES = [MatInputModule, MatFormFieldModule, MatIconModule];
const TEI_MATERIAL_MODULES = [BreadCrumbModule, TeiTextTranslatePipeModule, LoadingModule, SettingsListModule];
const ADMIN_CENTER_MODULES = [ManagePortalTABModule];
const COMPONENTS = [IntegrationsComponent, ButterCMSSetupComponent, RangeMachineIntegrationComponent]
@NgModule({
  declarations: [
    COMPONENTS
  ],
  imports: [
     ...MATERIAL_MODULES,
    ...BASE_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...ADMIN_CENTER_MODULES
  ],
  providers: [{ provide: 'IButterCMSSetupService', useClass: ButterCMSSetupService },]
})
export class IntegrationsModule { }
