import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetTenantsRequest, GetTenantsResponse } from '../@intermediate/models/get-tenants.model';
import { PostTenantsAddTenantImageRequest, PostTenantsAddTenantImageResponse } from '../@intermediate/models/post-tenants-add-tenant-image.model';
import { PutTenantsChangeActivationRequest, PutTenantsChangeActivationResponse } from '../@intermediate/models/put-tenants-change-activation.model';
import { ITenantsService } from '../@intermediate/tenants.interface';


@Injectable({
  providedIn: 'root'
})
export class TenantsService implements ITenantsService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Tenants';
  }

  Get(request: GetTenantsRequest): Observable<GetTenantsResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.get<GetTenantsResponse>(apiUrl, { params: request as any });
  }

  AddTenantImage(request: PostTenantsAddTenantImageRequest): Observable<PostTenantsAddTenantImageResponse>
  {
    const apiUrl = this.serviceUrl + '/AddTenantImage';
    return this.http.post<PostTenantsAddTenantImageResponse>(apiUrl, request);
  }

  ChangeActivation(request: PutTenantsChangeActivationRequest): Observable<PutTenantsChangeActivationResponse>
  {
    const apiUrl = this.serviceUrl + '/ChangeActivation';
    return this.http.put<PutTenantsChangeActivationResponse>(apiUrl, request);
  }
}

