import { Component, Inject, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ResponsiveService } from '@tei/core';
import { BreadCrumb } from '@tei/material';
import { Observable, Subject, concat, of } from 'rxjs';
import { catchError, debounceTime, delay, switchMap } from 'rxjs/operators';
import { GetPortalRequest, Portal } from '../../../../@intermediate/models/get-portal.model';
import { PutTenantsChangeActivationRequest } from '../../../../@intermediate/models/put-tenants-change-activation.model';
import { GetPortalGroupByIDRequest, PortalGroupByID } from '../../../../@intermediate/portal-group/models/get-portal-group-by-id.model';
import { IPortalGroupsService } from '../../../../@intermediate/portal-group/portal-groups.interface';
import { IPortalService } from '../../../../@intermediate/portal.interface';
import { ITenantsService } from '../../../../@intermediate/tenants.interface';

@Component({
  selector: 'tei-host-admin-manage-portal-group-portals',
  templateUrl: './manage-portal-group-portals.component.html',
  styleUrls: ['./manage-portal-group-portals.component.scss']
})
export class ManagePortalGroupPortalsComponent implements OnInit
{

  portalGroup: PortalGroupByID;

  chipItemsWithDisplayName = [
    { chipName: 'PortalName', displayName: this.translate.instant('ADMIN_CENTER.GROUP.PORTAL_GROUPS.PORTAL_NAME') }
  ];
  quickSearchValue = {};
  items: Observable<any[]>;
  contactArray: Observable<any[]> = of([]);
  contactTypeaheadInput$ = new Array();

  activeChipNameForSearch: string = "";
  selectedChipValues: { [key: string]: any; } = {};
  portals: Portal[];
  filteredPortals: Portal[];
  displayedColumns: string[] = ['portals', 'aliases', 'actions'];
  breadCrumbData: BreadCrumb[] = [];

  constructor(
    @Inject('ITenantsService') private hostAdminTenantsService: ITenantsService,
    @Inject('IPortalService') private hostAdminPortalService: IPortalService,
    @Inject('IPortalGroupsService') private hostAdminPortalGroupsService: IPortalGroupsService,
    private router: Router,
    private activeRoute: ActivatedRoute,
    private translate: TranslateService)
  {
  }

  ngOnInit()
  {
    this.chipItemsWithDisplayName.forEach(el =>
    {
      this.contactTypeaheadInput$.push(new Subject<string>());
      this.selectedChipValues[el.chipName] = '';
    });

    this.load();
  }

  load()
  {
    const request = new GetPortalRequest();
    request.PortalGroupID = Number(this.activeRoute.snapshot.params.portalGroupId);

    this.hostAdminPortalService.Get(request).subscribe((res) =>
    {
      this.portals = res.Portals?.filter(portal => portal.IsActive);
      this.filteredPortals = res.Portals.filter(portal => portal.IsActive);
    },
      error =>
      {
      });

    const getPortalGroupRequest: GetPortalGroupByIDRequest = {
      PortalGroupID: this.activeRoute.snapshot.params.portalGroupId
    };

    this.hostAdminPortalGroupsService.GetByID(getPortalGroupRequest).subscribe(res =>
    {
      this.portalGroup = res.PortalGroup;
      this.breadCrumbData = [
        {
          title: this.translate.instant('ADMIN_CENTER.GROUP.PORTAL_GROUPS.TITLE'),
          relativeUrl: '../../'
        },
        {
          title: `${ this.portalGroup.PortalGroupName }`,
          relativeUrl: '../../'
        },
        {
          title: this.translate.instant("ADMIN_CENTER.GROUP.SETTINGS.PORTALS"),
          relativeUrl: ''
        }
      ];
    });
  }


  filterByChipItem(item: { chipName: string, index: number; })
  {
    this.contactArray[item.index] = concat(
      of([]), // default items
      this.contactTypeaheadInput$[item.index].pipe(
        debounceTime(1000),
        switchMap((searchTerm) =>
          this.getStaff(searchTerm as string, item.chipName).pipe(
            catchError(() => of([])),
          ))
      )
    );
  }

  chipItemValues(selectedValues: string | string[])
  {
    this.selectedChipValues[this.activeChipNameForSearch] = selectedValues;
    this.filterManagePortalGroups(this.selectedChipValues);
  }
  chipItem(event: { chipName: string, index: number; })
  {
    this.activeChipNameForSearch = event.chipName;
  }
  removeItem(event: { chipName: string; })
  {
    this.selectedChipValues[event.chipName] = '';
    this.filterManagePortalGroups(this.selectedChipValues);
  }

  filterManagePortalGroups(selectedChipValues: { [key: string]: any; })
  {
    this.filteredPortals = this.portals.filter(item =>
    {

      for (let key in selectedChipValues)
      {
        if (selectedChipValues[key] && item[key] !== selectedChipValues[key])
          return false;
      }

      return true;
    });
  }

  getStaff(term: string = null, item: string): Observable<Portal[]>
  {
    let items = this.portals;
    if (term)
    {
      const lowerTerm = term.toLocaleLowerCase();
      items = items.filter(x =>
        x[item].toLocaleLowerCase().indexOf(lowerTerm) > -1
      );
    }
    return of(items).pipe(delay(200));
  }

  addNewPortal()
  {
    this.router.navigate(['./new-portal'], { relativeTo: this.activeRoute });
  }

  goToManagePortal(portal)
  {
    this.router.navigate(['./', portal.PortalID], { relativeTo: this.activeRoute });
  }

  deactivatePortal(portal: Portal)
  {
    const request = new PutTenantsChangeActivationRequest();

    request.TenantID = portal.TenantID;
    request.IsActive = false;
    this.hostAdminTenantsService.ChangeActivation(request).subscribe((res) =>
    {
      this.load();
    },
      (error) =>
      {
      }
    );
  }
}
