export class GetPaymentDevicesRequest
{
    DeviceID: number;
    DeviceDescription: string;
    AppMode: number;
    IntegrationType: number;
    TerminalID: number;
    Header: string;
    SubHeader: string;
    SubscriberID: string;
    RecipientID: string;
    DeviceIdentifier: string;
    ClientIdentifierID: string;
    DeviceSetupUID: string;
    DeviceStatus: number;
    PageNumber: number;
    PageSize: number;
    PortalID: number;
}

export class GetPaymentDevicesResponse
{
    PaymentDevices: Array<PaymentDevice>;
    TotalCount: number;
}

export class PaymentDevice
{
    DeviceID: number;
    DeviceIdentifier: string;
    TenantID: number;
    ClientIdentifierID: string;
    DeviceDescription: string;
    DeviceType: number;
    DeviceSetupUID: string;
    MaskedDeviceSecretCode: string;
    DeviceStatus: PaymentDeviceStatus;
    DateCreated: string;
    CreatedBy: string;
    DateModified: string;
    ModifiedBy: string;
    AppMode: number;
    IntegrationType: number;
    TerminalID: string;
    Header: string;
    SubHeader: string;
    SubscriberID: string;
    RecipientID: string;
    PaymentDeviceDateCreated: string;
    PaymentDeviceCreatedBy: string;
    PaymentDeviceDateModified: string;
    PaymentDeviceModifiedBy: string;
    DeviceName: string;
    AdditionalInfo: string;
}


export enum PaymentDeviceStatus
{
    Created,
    Activated,
    Deactivated,
    Deleted,
    Updated
}

export class PaymentDevicesMetadata
{
    public readonly Columns: string[] = ['DeviceDescription', 'Identifier', 'Status', 'Actions'];
}