import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { MatDividerModule } from '@angular/material/divider';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyTableModule as MatTableModule } from '@angular/material/legacy-table';
import { RouterModule } from '@angular/router';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { BreadCrumbModule } from '@tei/material';
import { PortalGroupsService } from '../../../../@services/portal-groups.service';
import { PortalService } from '../../../../@services/portal.service';
import { WebAliasService } from '../../../../@services/web-alias.service';
import { ManagePortalGroupAliasesComponent } from './manage-portal-group-aliases.component';
import { AddEditPortalGroupAliasesModule } from './add-edit-portal-group-aliases/add-edit-portal-group-aliases.module';



const COMPONENTS = [ManagePortalGroupAliasesComponent];
const BASE_MODULES = [CommonModule, RouterModule];
const MATERIAL_MODULES = [MatCardModule, MatIconModule, MatButtonModule, MatDividerModule, MatTableModule];
const TEI_CORE_MODULES = [TeiTextTranslatePipeModule];
const TEI_MATERIAL_MODULES = [
  BreadCrumbModule
];
const ADMIN_CENTER_MODULES = [
  AddEditPortalGroupAliasesModule
];
@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_CORE_MODULES,
    ...ADMIN_CENTER_MODULES
  ],
  exports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...ADMIN_CENTER_MODULES,
    ...COMPONENTS
  ],
  providers: [
    { provide: 'IPortalService', useClass: PortalService },
    { provide: 'IPortalGroupsService', useClass: PortalGroupsService },
    { provide: 'IWebAliasService', useClass: WebAliasService }
  ]
})
export class ManagePortalGroupAliasesModule { }
