import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatDividerModule } from '@angular/material/divider';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyMenuModule as MatMenuModule } from '@angular/material/legacy-menu';
import { MatLegacyPaginatorModule as MatPaginatorModule } from '@angular/material/legacy-paginator';
import { MatLegacyTableModule as MatTableModule } from '@angular/material/legacy-table';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { AvatarModule, LoadingModule, QuickSearchFilterChipModule } from '@tei/material';
import { StaffUserService } from '../../../@services/staff-user.service';
import { StaffUserGeneralService } from './../../../@services/staff-user-general.service';
import { InviteUsersModule } from './invite-users/invite-users.module';
import { ManageStaffUserModule } from './manage-staff-user/manage-staff-user.module';
import { StaffUserComponent } from './staff-user.component';
import { MatLegacyTooltipModule as MatTooltipModule } from '@angular/material/legacy-tooltip';

const BASE_MODULES = [CommonModule];
const MATERIAL_MODULES = [MatIconModule, MatButtonModule, MatDividerModule, MatMenuModule, MatCardModule, MatTableModule, MatPaginatorModule, MatTooltipModule,];
const TEI_MATERIAL_MODULES = [QuickSearchFilterChipModule, AvatarModule, LoadingModule];
const TEI_CORE_MODULES = [TeiTextTranslatePipeModule];
const ADMIN_CENTER_MODULES = [InviteUsersModule, ManageStaffUserModule];
const COMPONENTS = [StaffUserComponent];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_CORE_MODULES,
    ...ADMIN_CENTER_MODULES
  ],
  exports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_CORE_MODULES,
    ...ADMIN_CENTER_MODULES
  ],
  providers: [StaffUserGeneralService,
    { provide: 'IStaffUserService', useClass: StaffUserService }
  ],
})
export class GroupStaffModule { }
