import { Component, Inject, OnDestroy, OnInit } from '@angular/core';
import { UntypedFormControl, UntypedFormGroup, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ManageLoadingService } from '@tei/core';
import { BreadCrumb, SnackBarService } from '@tei/material';
import { Subscription } from 'rxjs';
import { finalize } from 'rxjs/operators';
import { IAdminPortalService } from '../../../@intermediate/admin-portal.interface';
import { IHostAdminCenterHelperService } from '../../../@intermediate/host-admin-center-helper.interface';
import { GetPaymentGatewaySettingsRequest, GetPaymentGatewaySettingsResponse, PaymentGateway, gatewayCodeEnum } from '../../../@intermediate/models/get-payment-gateway-settings.model';
import { portalGroupDetails } from '../../../@intermediate/models/host-admin-center-helper.model';
import { PostPaymentGatewaySettingsRequest, PostPaymentGatewaySettingsResponse } from '../../../@intermediate/models/post-payment-gateway-settings.model';
import { PortalGroupByID } from '../../../@intermediate/portal-group/models/get-portal-group-by-id.model';

@Component({
  selector: 'tei-admin-admin-center-general-payment-setting',
  templateUrl: './general-payment-setting.component.html',
  styleUrls: ['./general-payment-setting.component.scss']
})
export class GeneralPaymentSettingComponent implements OnInit, OnDestroy
{
  isLoading: boolean = false;
  portalId: string;
  parentPortalGroupId: string;
  parentPortalGroup: PortalGroupByID;
  subscription: Subscription = new Subscription();
  portalDetail: portalGroupDetails;
  breadCrumbData: BreadCrumb[] = [];

  settingForm: UntypedFormGroup = new UntypedFormGroup({
    ACHQEnabled: new UntypedFormControl(''),
    Gateway: new UntypedFormControl('', Validators.required)
  });
  gateways: PaymentGateway[];
  hostSettingID: number;
  constructor(
    @Inject('IHostAdminCenterHelperService') private hostAdminCenterHelperService: IHostAdminCenterHelperService,
    @Inject('IAdminPortalService') private adminPortalService: IAdminPortalService,
    private activatedRoute: ActivatedRoute,
    private snackBar: SnackBarService,
    public loadingService: ManageLoadingService,
    private translate: TranslateService
  ) { }

  ngOnInit(): void
  {
    this.subscription.add(this.hostAdminCenterHelperService.currentPortalGroupDetails.subscribe(portalDetail =>
    {
      if (portalDetail)
      {
        this.portalDetail = {
          parentPortalGroup: portalDetail.parentPortalGroup.PortalGroup,
          portal: portalDetail.portal
        };
        this.fillBreadCrump(portalDetail);
        this.isLoad();
      }
    }));

    this.settingForm.get('Gateway').valueChanges.subscribe(changes =>
    {
      if (this.gateways.filter(gateway => gateway.GatewayID === changes)?.[0]?.GatewayCode === gatewayCodeEnum.Noon)
      {
        this.settingForm.get('ACHQEnabled').patchValue(false);
        this.settingForm.get('ACHQEnabled').disable();
      } else
      {
        this.settingForm.get('ACHQEnabled').enable();
      }
    });
  }

  fillBreadCrump(portalDetail): void
  {
    this.breadCrumbData = [
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.PORTAL_GROUPS.TITLE'),
        relativeUrl: '../../../../../',
      },
      {
        title: `${ portalDetail.parentPortalGroup.PortalGroup.PortalGroupName }`,
        relativeUrl: `../../../../`,
      },
      {
        title: `${ portalDetail.portal.PortalName }`,
        relativeUrl: '../../',
      },
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.PAYMENT_SETTINGS'),
        relativeUrl: '../',
      },
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.GENERAL_SETUP'),
        relativeUrl: '',
      }
    ];
  }

  isLoad(): void
  {
    const request: GetPaymentGatewaySettingsRequest = {
      PortalID: this.portalDetail.portal.PortalID
    };

    this.loadingService.startLoading();
    this.adminPortalService.GetPaymentGatewaySettings(request).pipe(finalize(() => this.loadingService.finishLoading())).subscribe({
      next: (response: GetPaymentGatewaySettingsResponse) =>
      {
        this.gateways = response.PaymentGateways;
        this.hostSettingID = response.HostSettingID;
        const converge = this.gateways.filter(gateway => gateway.GatewayCode === gatewayCodeEnum.Converge)?.[0]?.GatewayID;
        this.settingForm.patchValue({
          ACHQEnabled: response.IsACHQEnabled ?? false,
          Gateway: response.SelectedGateway?.GatewayID ?? converge
        });
      }
    });
  }

  saveSetting(): void
  {
    if (this.settingForm.invalid) { return; }
    const request: PostPaymentGatewaySettingsRequest = {
      PortalID: this.portalDetail.portal.PortalID,
      GatewayID: this.settingForm.getRawValue().Gateway,
      IsACHQEnabled: this.settingForm.getRawValue().ACHQEnabled,
      UserID: this.portalDetail.portal.TenantUID,
      HostSettingID: this.hostSettingID
    };

    if (this.hostSettingID === null)
    {
      delete request.HostSettingID;
    }
    this.loadingService.startLoading();
    this.adminPortalService.PostPaymentGatewaySettings(request).pipe(finalize(() => this.loadingService.finishLoading())).subscribe({
      next: (response: PostPaymentGatewaySettingsResponse) =>
      {
        if (response.Success)
        {
          this.snackBar.showSuccess(this.translate.instant('ADMIN_CENTER.PRODUCTS.SAVED_SUCCESSFULLY'), '', null, 'left');
          this.isLoad();
        }
      }
    });
  }
  ngOnDestroy(): void
  {
    this.subscription?.unsubscribe();
  }
}
