import { Component, Inject, OnInit } from '@angular/core';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { BreadCrumb, CardDialogComponent, ConfirmationDialogComponent, SnackBarService } from '@tei/material';
import { firstValueFrom, Subscription } from 'rxjs';
import { DeleteWebAliasRequest } from '../../../../@intermediate/models/delete-web-alias.model';
import { GetPortalDetailRequest, PortalDetail } from '../../../../@intermediate/models/get-portal.model';
import { TenantWebAlias } from '../../../../@intermediate/models/get-tenant-web-alias.model';
import { GetWebAliasRequest } from '../../../../@intermediate/models/get-web-alias.model';
import { GetPortalGroupByIDRequest, PortalGroupByID } from '../../../../@intermediate/portal-group/models/get-portal-group-by-id.model';
import { IPortalGroupsService } from '../../../../@intermediate/portal-group/portal-groups.interface';
import { IPortalService } from '../../../../@intermediate/portal.interface';
import { IWebAliasService } from '../../../../@intermediate/web-alias.interface';
import { ResponsiveService } from '@tei/core';

@Component({
  selector: 'tei-host-admin-manage-portal-group-aliases',
  templateUrl: './manage-portal-group-aliases.component.html',
  styleUrls: ['./manage-portal-group-aliases.component.scss']
})
export class ManagePortalGroupAliasesComponent implements OnInit
{
  portal: PortalDetail;
  portalGroup: PortalGroupByID;
  filteredWebAliases: TenantWebAlias[];
  displayedColumns: string[] = ['alias', 'isPrimary', 'actions'];
  breadCrumbData: BreadCrumb[];
  tenantID: number;
  subscription = new Subscription();
  isSmallScreen: boolean;

  constructor(
    @Inject('IWebAliasService') private hostAdminWebAliasService: IWebAliasService,
    @Inject('IPortalGroupsService') private hostAdminPortalGroupsService: IPortalGroupsService,
    @Inject('IPortalService') private hostAdminPortalService: IPortalService,
    private translate: TranslateService,
    private activatedRoute: ActivatedRoute,
    private dialog: MatDialog,
    private snackBarService: SnackBarService,
    private responsiveService: ResponsiveService,
    private router: Router,
    private activeRoute: ActivatedRoute
  )
  {
  }

  async ngOnInit(): Promise<void>
  {
    let routeParams = this.activatedRoute.snapshot.params;

    if (!this.activatedRoute.snapshot.params.portalGroupId)
    {
      routeParams = this.activatedRoute.parent.snapshot.params;
    }

    const request: GetPortalGroupByIDRequest = {
      PortalGroupID: routeParams.portalGroupId
    };

    const response = await firstValueFrom(this.hostAdminPortalGroupsService.GetByID(request));
    this.portalGroup = response.PortalGroup;

    this.tenantID = this.portalGroup.TenantID;

    const portalId = routeParams.id;

    if (portalId)
    {
      const request: GetPortalDetailRequest = {
        PortalID: portalId
      };

      this.portal = await firstValueFrom(this.hostAdminPortalService.GetByID(request));
      this.tenantID = this.portal.TenantID;

      this.breadCrumbData = [
        {
          title: this.translate.instant('ADMIN_CENTER.GROUP.PORTAL_GROUPS.TITLE'),
          relativeUrl: '../../../../',
        },
        {
          title: `${ this.portalGroup.PortalGroupName }`,
          relativeUrl: `../../../`,
        },
        {
          title: `${ this.portal.PortalName }`,
          relativeUrl: '../',
        },
        {
          title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.ALIASES'),
          relativeUrl: '',
        }
      ];
    } else
    {
      this.breadCrumbData = [
        {
          title: this.translate.instant('ADMIN_CENTER.GROUP.PORTAL_GROUPS.TITLE'),
          relativeUrl: '../../'
        },
        {
          title: `${ this.portalGroup.PortalGroupName }`,
          relativeUrl: '../'
        },
        {
          title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.ALIASES'),
          relativeUrl: ''
        }
      ];
    }

    this.load();
    this.subscribeToResponsiveMediaBreakpoint();
  }

  load(): void
  {
    const request: GetWebAliasRequest = {
      TenantID: this.tenantID
    };

    this.hostAdminWebAliasService.Get(request).subscribe((res) =>
    {
      this.filteredWebAliases = res.TenantWebAliases;
    },
      error =>
      {
      }
    );
  }

  private subscribeToResponsiveMediaBreakpoint(): void
  {
    const screenSize = this.responsiveService.mediaBreakpoint.getValue();
    this.isSmallScreen = screenSize === 'xs' || screenSize === 'md';
  }

  addNewAlias(): void
  {
    this.router.navigate(['./add'], { relativeTo: this.activeRoute });
  }

  editAlias(alias: TenantWebAlias): void
  {
    this.router.navigate(['./edit', alias.TenantWebAliasID], { relativeTo: this.activeRoute });
  }

  deleteAlias(alias: TenantWebAlias): void
  {
    const dialogRef = this.dialog.open(CardDialogComponent, {
      panelClass: 'o-confirm-dialog',
      data: {
        component: ConfirmationDialogComponent,
        dialogData: {
          title: 'ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.DELETE_ALIAS',
          content: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.DELETE_ALIAS_QUESTION', { AliasName: alias.WebAliasUrl }),
          submitButtonLabel: 'ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.DELETE_ALIAS',
          closeButtonLabel: 'ADMIN_CENTER.CANCEL'
        }
      },
      width: '350px',
    });
    dialogRef?.afterClosed().subscribe(result =>
    {
      if (result)
      {
        const request: DeleteWebAliasRequest = {
          TenantWebAliasID: alias.TenantWebAliasID
        };
        this.hostAdminWebAliasService.Delete(request).subscribe(() =>
        {
          this.filteredWebAliases = this.filteredWebAliases.filter(webAlias => webAlias.TenantWebAliasID !== alias.TenantWebAliasID);

          const successMessage = this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.DELETE_ALIAS_SUCCESS', { AliasName: alias.WebAliasUrl });
          this.snackBarService.showSuccess(successMessage, '', null, 'left');
        },
          error =>
          {
          }
        );
      }
    });
  };
}

