import { HttpClient } from "@angular/common/http";
import { Inject, Injectable } from "@angular/core";
import { IAPIBaseUrlProvider } from "@tei/core";
import { Observable } from "rxjs";
import { IAuthenticationSettingsService } from "../@intermediate/authentication-settings.interface";
import { GetEditableAuthenticationSettingsRequest, GetEditableAuthenticationSettingsResponse } from "../@intermediate/models/get-editable-authentication-settings.model";
import { PostAuthenticationSettingsRequest, PostAuthenticationSettingsResponse } from "../@intermediate/models/post-editable-authentication-settings.model";

@Injectable({
  providedIn: 'root'
})
export class AuthenticationSettingsService implements IAuthenticationSettingsService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/AuthenticationSettings';
  }

  public GetEditableSettings(request: GetEditableAuthenticationSettingsRequest): Observable<GetEditableAuthenticationSettingsResponse>
  {
    const apiUrl = this.serviceUrl + '/GetEditableSettings';
    return this.http.get<GetEditableAuthenticationSettingsResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostAuthenticationSettingsRequest): Observable<PostAuthenticationSettingsResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostAuthenticationSettingsResponse>(apiUrl, request);
  }
}
