import { ChangeDetectorRef, Component, EventEmitter, Input, Output } from '@angular/core';
import { ngFormGroupControlValueAccessorProviders } from '@tei/core';
import { GetManageRoleResponse } from '../../../../@intermediate/staff-user/models/get-manage-role.model';
import { PortalRoleFormGroup, PostManageRoleRequestForm } from '../../../../@intermediate/staff-user/models/post-manage-role.model';

@Component({
  selector: 'tei-admin-admin-center-access-portal-user',
  templateUrl: './access-portal-user.component.html',
  styleUrls: ['./access-portal-user.component.scss'],
  providers: [ngFormGroupControlValueAccessorProviders(AccessPortalUserComponent)]
})
export class AccessPortalUserComponent
{
  selectedTenantID: number;
  @Input() userID: number;
  @Input() model: GetManageRoleResponse;
  @Output() closeEmitter = new EventEmitter();
  @Input() form: PostManageRoleRequestForm;
  @Input() portalRoleForm: PortalRoleFormGroup;

  constructor(protected changeDetector: ChangeDetectorRef)
  {
  }

  ngOnInit(): void
  {
  }

  onManagePortal(portal): void
  {
    this.selectedTenantID = portal.tenantID;
    this.portalRoleForm = this.form.PortalRoles.controls.find(pr => pr.get("PortalID").value === portal.portalID) as PortalRoleFormGroup;
    this.changeDetector.detectChanges();
  }

  onClose(): void
  {
    this.closeEmitter.emit();
  }
}
