import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCheckboxModule as MatCheckboxModule } from '@angular/material/legacy-checkbox';
import { MatIconModule } from '@angular/material/icon';
import { MatTreeModule } from '@angular/material/tree';
import { TreeComponent } from './tree.component';

const COMPONENTS = [TreeComponent];
const BASE_MODULES = [CommonModule];
const MATERIAL_MODULES = [MatTreeModule, MatCheckboxModule, MatButtonModule, MatIconModule];

@NgModule({
  declarations: [
    ...COMPONENTS
  ],
  imports: [
    ...BASE_MODULES, ...MATERIAL_MODULES
  ],
  exports: [
    ...COMPONENTS, ...BASE_MODULES, ...MATERIAL_MODULES
  ]
})
export class TreeModule { }
