export class GetStaffUserRequest
{
}

export class GetStaffUserResponse
{
  public StaffUsers: Array<StaffUser> = [];
}

export class StaffUser
{
  public UserID: number;
  public UserName: string;
  public FirstName: string;
  public LastName: string;
  public Email: string;
  public IsSupperUser: boolean;
  public DateCreated: Date;
  public DateStatusChanged: Date;
  public UserPhoto: string;

  public StatusAvatar: string;
  public StatusTitle: string;
  public UserStatus: UserStatusEnum;
}

export enum UserStatusEnum
{
  Invited = 1,
  Joined = 2,
  NoAccess = 3,
  InviteCancelled = 4,
  ResendInvite = 5
}
