import { Component, Inject, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { MatLegacyPaginator as MatPaginator } from '@angular/material/legacy-paginator';
import { MatLegacyTableDataSource as MatTableDataSource } from '@angular/material/legacy-table';
import { TranslateService } from '@ngx-translate/core';
import { ResponsiveService, ngFormGroupControlValueAccessorProviders } from '@tei/core';
import { ChipItemModel, ConfirmationDialogComponent, QuickSearchFilterChipItem, SnackBarService } from '@tei/material';
import { Observable, Subject, Subscription, concat, of } from 'rxjs';
import { catchError, debounceTime, delay, filter, switchMap, tap } from 'rxjs/operators';
import { StaffUser } from '../../../@intermediate/models/get-staff-user.model';
import { IStaffUserService } from '../../../@intermediate/staff-user/staff-user.interface';
import { StaffUserGeneralService } from '../../../@services/staff-user-general.service';
import { GetStaffUserRequest, GetStaffUserResponse, UserStatusEnum } from './../../../@intermediate/models/get-staff-user.model';
import { RevokeAccessResponse } from './../../../@intermediate/staff-user/models/revoke-access.model';
import { InviteUsersComponent } from './invite-users/invite-users.component';
import { ManageStaffUserComponent } from './manage-staff-user/manage-staff-user.component';

@Component({
  selector: 'tei-admin-admin-center-tei-staff-user',
  templateUrl: './staff-user.component.html',
  styleUrls: ['./staff-user.component.scss'],
  providers: [ngFormGroupControlValueAccessorProviders(StaffUserComponent)]
})
export class StaffUserComponent implements OnInit, OnDestroy
{
  @ViewChild(MatPaginator) paginator: MatPaginator;
  isMobileView = false;
  minTermLengthUserStatusList = 0;
  subscription: Subscription = new Subscription();
  isLoading = false;
  chipItems: string[] = ['UserName', 'Email', 'UserStatus'];
  chipItemsWithDisplayName = [
    { chipName: 'UserName', displayName: this.translate.instant('ADMIN_CENTER.STAFF_USER.USERNAME') },
    { chipName: 'Email', displayName: this.translate.instant('ADMIN_CENTER.STAFF_USER.EMAIL') }
  ];
  dataSource: MatTableDataSource<StaffUser>;
  contactArray: Observable<any[]> = of([]);
  contactTypeAheadInput$ = [];
  staffUsers: Array<StaffUser> = [];
  filteredStaffUsers: Array<StaffUser> = [];
  displayedColumns: string[] = ['User', 'UserStatus', 'Actions', 'DotActions'];
  activeChipNameForSearch = '';
  selectedChipValues: { [key: string]: any; } = {
    UserName: '',
    Email: '',
    UserStatus: []
  };
  userStatusList = [];
  statusChipItemsWithDisplayName = [{ chipName: 'UserStatus', displayName: this.translate.instant('ADMIN_CENTER.STAFF_USER.STATUS') }];

  constructor(
    @Inject('IStaffUserService') private service: IStaffUserService,
    private staffUserGeneralService: StaffUserGeneralService,
    private dialog: MatDialog,
    private translate: TranslateService,
    private snackBarService: SnackBarService,
    private responsiveService: ResponsiveService,
  ) { }

  ngOnInit()
  {
    this.getMobileSize();
    this.userStatusList = Object.keys(UserStatusEnum).filter((v) => isNaN(Number(v)))
      .map((name) =>
      {
        const userStatus = UserStatusEnum[name as keyof typeof UserStatusEnum];
        return { UserStatus: this.statusTitle(userStatus), id: userStatus };
      });

    this.chipItems.forEach(() =>
    {
      this.contactTypeAheadInput$.push(new Subject<string>());
    });

    this.load();
  }

  getMobileSize(): void
  {
    this.subscription.add(this.responsiveService.mediaBreakpoint.subscribe(mediaBreakpoint =>
    {
      this.isMobileView = mediaBreakpoint === 'xs' || mediaBreakpoint === 'sm' || mediaBreakpoint === 'md';
    }));
  }

  load(): void
  {
    if (this.staffUsers?.length > 0)
    {
      this.staffUsers = [];
    }


    this.subscription.add(
      this.GetStaff()
    );
  }

  private GetStaff(request: GetStaffUserRequest = null)
  {
    this.isLoading = true;

    if (request === null)
    {
      request = new GetStaffUserRequest();
    }

    return this.GetStaffOb(request).subscribe({
      next: (response: GetStaffUserResponse) =>
      {
        this.staffUsers = response.StaffUsers;
        this.filterStaffUsers(this.selectedChipValues);
        this.isLoading = false;
        this.refreshDatasurce();
      },
      error: () => this.isLoading = false
    });
  }

  private GetStaffOb(request: GetStaffUserRequest = null): Observable<GetStaffUserResponse>
  {
    return this.service.Get(request);
  }

  filterByChipItem(item: QuickSearchFilterChipItem)
  {
    this.contactArray[item.index] = concat(
      of([]), // default items
      this.contactTypeAheadInput$[item.index].pipe(
        debounceTime(1000),
        switchMap((searchTerm) =>
          this.getStaff(searchTerm as string, item.chipName).pipe(
            catchError(() => of([])),
          ))
      )
    );
  }

  onFilterChange(chipItems: ChipItemModel[]): void
  {
    chipItems.forEach(item =>
    {
      this.updateSelectedChipValues(item);
    });
    this.filterStaffUsers(this.selectedChipValues);
  }

  updateSelectedChipValues(item: ChipItemModel)
  {
    if (item.chipName === 'UserStatus')
    {
      this.selectedChipValues[item.chipName] =
        Array.from(item.selectedItem).map(itemSelected => this.userStatusList.find(a => a.UserStatus === itemSelected).id);
    }
    else
    {

      this.selectedChipValues[item.chipName] = item.value;
    }
  }

  chipItem(event: { chipName: string, index: number; })
  {
    this.activeChipNameForSearch = event.chipName;
  }

  filterStaffUsers(selectedChipValues: { [key: string]: any; })
  {
    this.filteredStaffUsers = this.staffUsers.filter(item =>
    {
      for (const key in selectedChipValues)
      {
        if (key === 'UserStatus')
        {
          if (selectedChipValues[key].length > 0 && !selectedChipValues[key].includes(item[key]))
          {
            return false;
          }
        }
        else if (selectedChipValues[key] && item[key] !== selectedChipValues[key])
        {
          return false;
        }
      }
      return true;
    });
    this.refreshDatasurce();
  }

  getStaff(term: string = null, item: string): Observable<StaffUser[]>
  {
    let items = this.staffUsers;
    if (term)
    {
      const lowerTerm = term.toLocaleLowerCase();
      items = items.filter(x =>
        x[item]?.toLocaleLowerCase().indexOf(lowerTerm) > -1
      );
    }
    return of(items).pipe(delay(200));
  }

  getFullName(staffUser: StaffUser): string
  {
    return this.staffUserGeneralService.getFullName(staffUser.FirstName, staffUser.LastName, staffUser.UserName);
  }

  inviteUser(): void
  {
    const dialog = this.dialog.open(InviteUsersComponent, {
      panelClass: 'invite-users-modal',
      width: '500px'
    });

    this.subscription.add(dialog.afterClosed().subscribe((isChanged: boolean) =>
    {
      if (isChanged)
      {
        this.GetStaff();
      }
    }));
  }

  onDetails(user: StaffUser): void
  {
    this.dialog.open(ManageStaffUserComponent, {
      panelClass: ['manage-staff-user-modal', 'o-dialog-md-fullscreen'],
      width: '500px',
      data: user.UserID
    });
  }

  onCancelInvite(user: StaffUser)
  {
    const dialogData = {
      dialogData: {
        title: 'ADMIN_CENTER.STAFF_USER.CANCEL_INVITE',
        content: `Are you sure you want to cancel the invite for ${ user.Email }?`,
        submitButtonLabel: 'ADMIN_CENTER.STAFF_USER.CANCEL_INVITE',
        submitButtonColor: 'warn'
      }
    };

    const dialogRef = this.dialog.open(ConfirmationDialogComponent, {
      maxWidth: '18vw',
      minWidth: '320px',
      panelClass: 'o-dialog-change-preferences',
      data: dialogData
    });

    dialogRef.afterClosed().subscribe(result =>
    {
      if (result)
      {
        this.CancelInvite(user);
      }

    });

  }

  private CancelInvite(user: StaffUser)
  {
    this.service.PostCancelInvite({
      UserID: user.UserID
    }).subscribe(res =>
    {
      user.UserStatus = res.CurrentUserStatus;
      user.StatusTitle = res.StatusTitle;
      user.StatusAvatar = res.StatusAvatar;
    });
  }

  onResendInvite(user: StaffUser)
  {
    this.ResendInvite(user);
  }

  private ResendInvite(user: StaffUser)
  {
    this.isLoading = true;
    this.service.SendInviteStaffEmail({
      Username: user.UserName,
      UserID: user.UserID
    }).subscribe(
      {
        next: (res) =>
        {
          this.isLoading = false;
          this.snackBarService.showSuccess(`${ user.Email } ${ this.translate.instant("ADMIN_CENTER.STAFF_USER.INVITED_SUCCESSFULLY") }`, '', null, 'left');
          user.UserStatus = res.CurrentUserStatus;
          user.StatusTitle = res.StatusTitle;
          user.StatusAvatar = res.StatusAvatar;
        },
        error: () =>
        {
          this.isLoading = false;
          this.snackBarService.showSuccess(this.translate.instant("ADMIN_CENTER.STAFF_USER.INVITED_FAIL"), '', null, 'left');
        }
      });
  }

  onEdit(user: StaffUser): void
  {
  }

  onRevokeAccess(user: StaffUser): void
  {
    const dialogData = {
      dialogData: {
        title: 'ADMIN_CENTER.STAFF_USER.REVOKE_ACCESS',
        content: `Are you sure you want to revoke access for ${ user.FirstName } ${ user.LastName }? They will no longer be permitted to login.`,
        submitButtonLabel: 'ADMIN_CENTER.STAFF_USER.REVOKE_ACCESS',
        submitButtonColor: 'warn'
      }
    };

    const dialogRef = this.dialog.open(ConfirmationDialogComponent, {
      maxWidth: '18vw',
      minWidth: '320px',
      panelClass: 'o-dialog-change-preferences',
      data: dialogData
    });

    dialogRef.afterClosed()
      .pipe(
        filter(
          x => x === true
        ),
        switchMap(
          x => this.RevokeAccess(user)
        ),
        tap(
          x => this.GetStaff()
        ),
        tap(
          x => this.snackBarService.showSuccess(`${ this.translate.instant("ADMIN_CENTER.STAFF_USER.ACCESS_REVOKED") }`, '', null, 'left')
        )
      ).subscribe();
  }

  private RevokeAccess(user: StaffUser): Observable<RevokeAccessResponse>
  {
    return this.service.RevokeAccess({
      UserID: user.UserID
    });
  }

  statusIsJoined(userStatus: UserStatusEnum): boolean
  {
    return userStatus === UserStatusEnum.Joined;
  }

  statusIsInvited(userStatus: UserStatusEnum): boolean
  {
    return userStatus === UserStatusEnum.Invited;
  }

  statusIsNoAccess(userStatus: UserStatusEnum): boolean
  {
    return userStatus === UserStatusEnum.NoAccess;
  }

  statusIsResendInvite(userStatus: UserStatusEnum): boolean
  {
    return userStatus === UserStatusEnum.ResendInvite;
  }

  statusTitle(userStatus: UserStatusEnum): string
  {
    switch (userStatus)
    {
      case UserStatusEnum.Joined:
        return 'Joined';
      case UserStatusEnum.Invited:
        return 'Invited';
      case UserStatusEnum.NoAccess:
        return 'No access';
      case UserStatusEnum.InviteCancelled:
        return 'Cancelled';
      case UserStatusEnum.ResendInvite:
        return 'Invite Cancelled';
      default:
        return 'Unknown';
    }
  }

  ngOnDestroy(): void
  {
    this.subscription?.unsubscribe();
  }

  refreshDatasurce()
  {
    this.dataSource = new MatTableDataSource<StaffUser>(this.filteredStaffUsers);
    this.dataSource.paginator = this.paginator;
  }
}

