import { Component, Inject, OnDestroy, OnInit } from '@angular/core';
import { UntypedFormBuilder, UntypedFormGroup, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ManageLoadingService, ResponsiveService } from '@tei/core';
import { BreadCrumb, SnackBarService } from '@tei/material';
import { Observable, Subscription, finalize, forkJoin, map } from 'rxjs';
import { IAdminPortalService } from '../../../@intermediate/admin-portal.interface';
import { IHostAdminCenterHelperService } from '../../../@intermediate/host-admin-center-helper.interface';
import { GetAchqPaymentSettingsConfigRequest, GetAchqPaymentSettingsConfigResponse } from '../../../@intermediate/models/get-achq-payment-settings-config.model';
import { GetAchqPaymentSettingsRequest, GetAchqPaymentSettingsResponse } from '../../../@intermediate/models/get-achq-payment-settings.model';
import { portalGroupDetails } from '../../../@intermediate/models/host-admin-center-helper.model';
import { PostAchqPaymentSettingsRequest, PostAchqPaymentSettingsResponse } from '../../../@intermediate/models/post-achq-payment-settings.model';
import { PortalGroupByID } from '../../../@intermediate/portal-group/models/get-portal-group-by-id.model';

@Component({
  selector: 'tei-admin-admin-center-achq-payment-setting',
  templateUrl: './achq-payment-setting.component.html',
  styleUrls: ['./achq-payment-setting.component.scss']
})
export class AchqPaymentSettingComponent implements OnInit, OnDestroy
{
  isLoading: boolean = false;
  isMobile = false;
  portalId: string;
  parentPortalGroupId: string;
  parentPortalGroup: PortalGroupByID;
  subscription: Subscription = new Subscription();
  portalDetail: portalGroupDetails;
  breadCrumbData: BreadCrumb[] = [];
  achqConfigs: GetAchqPaymentSettingsConfigResponse;
  achqData: GetAchqPaymentSettingsResponse;
  form: UntypedFormGroup;
  constructor(@Inject('IHostAdminCenterHelperService') private hostAdminCenterHelperService: IHostAdminCenterHelperService,
    @Inject('IAdminPortalService') private adminPortalService: IAdminPortalService,
    private activatedRoute: ActivatedRoute,
    public loadingService: ManageLoadingService,
    private translate: TranslateService,
    private responsiveService: ResponsiveService,
    private fb: UntypedFormBuilder,
    private snackBar: SnackBarService)
  { }

  ngOnInit(): void
  {
    this.subscription.add(this.hostAdminCenterHelperService.currentPortalGroupDetails.subscribe(portalDetail =>
    {
      if (portalDetail)
      {
        this.portalDetail = {
          parentPortalGroup: portalDetail.parentPortalGroup.PortalGroup,
          portal: portalDetail.portal
        };
        this.fillBreadCrump();
      }
    }));
    this.getMobileSize();
    this.createForm();
    this.getData();
  }

  getMobileSize(): void
  {
    this.subscription.add(this.responsiveService.mediaBreakpoint.subscribe(mediaBreakpoint =>
    {
      this.isMobile = mediaBreakpoint === 'xs' || mediaBreakpoint === 'sm' || mediaBreakpoint === 'md';
    }));
  }

  getData(): void
  {
    this.subscription.add(this.activatedRoute.parent.params.subscribe(param =>
    {
      this.portalId = param['id'];
      this.loadingService.startLoading();
      forkJoin({ ashqData: this.getAchqPaymentSettings(param['id']), achqConfigs: this.getAchqPaymentSettingsConfigs() }).pipe(finalize(() => this.loadingService.finishLoading())).subscribe({
        next: (response =>
        {
          this.achqData = response.ashqData;
          this.achqConfigs = response.achqConfigs;
          this.fillAchqData();
        })
      });
    }));
  }

  getAchqPaymentSettingsConfigs(): Observable<GetAchqPaymentSettingsConfigResponse>
  {
    const request: GetAchqPaymentSettingsConfigRequest = {};
    return this.adminPortalService.AchqPaymentSettingsConfig(request).pipe(map((response: GetAchqPaymentSettingsConfigResponse) =>
    {
      return response;
    }));
  }

  getAchqPaymentSettings(portalId: number): Observable<GetAchqPaymentSettingsResponse>
  {
    const request: GetAchqPaymentSettingsRequest = {
      PortalID: portalId
    };
    return this.adminPortalService.AchqPaymentSettings(request).pipe(map((response: GetAchqPaymentSettingsResponse) =>
    {
      return response;
    }));
  }

  onSave(): void
  {
    const request: PostAchqPaymentSettingsRequest = { ...this.form.value, PortalID: parseInt(this.portalId) };
    this.loadingService.startLoading();
    this.adminPortalService.UpdateAchqPaymentSetting(request).pipe(finalize(() => this.loadingService.finishLoading())).subscribe({
      next: (response: PostAchqPaymentSettingsResponse) =>
      {
        if (response.Success) this.snackBar.showSuccess(this.translate.instant('ADMIN_CENTER.PRODUCTS.SAVED_SUCCESSFULLY'), '', null, 'left');
      }
    });
  }

  fillAchqData(): void
  {
    this.form.patchValue({
      IsEnabled: this.achqData.IsEnabled,
      SetupID: this.achqData.SetupID,
      ApiUrl: this.achqData.ApiUrl,
      ProviderID: this.achqData.ProviderID,
      ProviderGateID: this.achqData.ProviderGateID,
      ProviderGateKey: this.achqData.ProviderGateKey,
      MerchantID: this.achqData.MerchantID,
      MerchantGateID: this.achqData.MerchantGateID,
      MerchantGateKey: this.achqData.MerchantGateKey,
      TokenSource: this.achqData.TokenSource,
      SendEmailToCustomer: this.achqData.SendEmailToCustomer,
      RunExpressVerify: this.achqData.RunExpressVerify,
      SECCode: this.achqData.SECCode,
      CommandVersion: this.achqData.CommandVersion,
      DeliveryWindow: this.achqData.DeliveryWindow
    });
  }

  createForm(): void
  {
    this.form = this.fb.group({
      IsEnabled: [false],
      SetupID: ['', Validators.required],
      ApiUrl: ['', Validators.required],
      ProviderID: ['', Validators.required],
      ProviderGateID: ['', Validators.required],
      ProviderGateKey: ['', Validators.required],
      MerchantID: ['', Validators.required],
      MerchantGateID: ['', Validators.required],
      MerchantGateKey: ['', Validators.required],
      TokenSource: ['', Validators.required],
      SendEmailToCustomer: [false],
      RunExpressVerify: [false],
      SECCode: ['', Validators.required],
      CommandVersion: ['', Validators.required],
      DeliveryWindow: ['', Validators.required]
    });
  }

  fillBreadCrump(): void
  {
    this.breadCrumbData = [
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.PORTAL_GROUPS.TITLE'),
        relativeUrl: '../../../../../',
      },
      {
        title: `${ this.portalDetail.parentPortalGroup.PortalGroupName }`,
        relativeUrl: `../../../../`,
      },
      {
        title: `${ this.portalDetail.portal.PortalName }`,
        relativeUrl: '../../',
      },
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.PAYMENT_SETTINGS'),
        relativeUrl: '../',
      },
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.ACHQ_SETUP'),
        relativeUrl: '',
      }
    ];
  }

  ngOnDestroy(): void
  {
    this.subscription?.unsubscribe();
  }
}
