import { Component, Inject, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { BreadCrumb, SnackBarService } from '@tei/material';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { IGoogleAuthenticatorSettingService } from '../../@intermediate/google-authenticator-setting/google-authenticator-setting.interface';
import { GetGoogleAuthenticatorSettingRequest, GetGoogleAuthenticatorSettingResponse } from '../../@intermediate/google-authenticator-setting/models/get-google-authenticator-setting.model';
import { GoogleAuthenticatorSettingForm } from '../../@intermediate/google-authenticator-setting/models/post-google-authenticator-setting.model';
import { ResponsiveService } from '@tei/core';
@Component({
  selector: 'tei-admin-admin-center-google-authenticator-setting',
  templateUrl: './google-authenticator-setting.component.html',
  styleUrls: ['./google-authenticator-setting.component.scss'],
})
export class GoogleAuthenticatorSettingComponent implements OnInit
{
  breadCrumbData: BreadCrumb[] = [

    {
      title: this.translate.instant('ADMIN_CENTER.HOST.SETTINGS.PORTAL_ADMIN_CENTER'),
      relativeUrl: ''
    },
    {
      title: this.translate.instant('ADMIN_CENTER.SETUP'),
      relativeUrl: '../setup'
    },
    {
      title: 'Google Analytics',
      relativeUrl: ''
    },
  ];

  screenWidth: number;
  form$: Observable<GoogleAuthenticatorSettingForm>;
  model: GetGoogleAuthenticatorSettingResponse;
  isLoading = false;

  constructor(@Inject('IGoogleAuthenticatorSettingService') private googleAuthenticatorSettingService: IGoogleAuthenticatorSettingService, private translate: TranslateService
    , private snackBar: SnackBarService, private responsiveService: ResponsiveService) { }

  ngOnInit()
  {
    this.detectScreenSize();
    this.form$ = this.googleAuthenticatorSettingService.Get(new GetGoogleAuthenticatorSettingRequest)
      .pipe(map((response) => new GoogleAuthenticatorSettingForm(response)));


  }

  private detectScreenSize(): void
  {
    this.responsiveService.screenWidth.subscribe(screenSize =>
    {
      this.screenWidth = screenSize;
    });
  }

  save(form: GoogleAuthenticatorSettingForm)
  {
    this.isLoading = true;
    this.googleAuthenticatorSettingService.Post(form.value).subscribe(response =>
    {
      if (response)
      {
        this.isLoading = false;
        this.snackBar.showSuccess(this.translate.instant('ADMIN_CENTER.GOOGLE_ANALYTICS.SETTINGS_SAVED'));
      }
    });
  }

}
