import { Component, Inject, OnInit } from '@angular/core';
import { UntypedFormBuilder, Validators } from '@angular/forms';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { IFormBuilder, IFormGroup } from "@rxweb/types";
import { IDesignationsService } from '../../../@intermediate/designation.interface';
import { Designation, GetDesignationRequest } from '../../../@intermediate/models/get-designation.model';
import { GetPortalByCurrentTenantRequest, Portal } from '../../../@intermediate/models/get-portal-by-current-tenant.model';
import { GetRoleTypesRequest, RoleType } from '../../../@intermediate/models/get-role-types.model';
import { DesignationIDEnum, Role } from '../../../@intermediate/models/get-role.model';
import { IPortalService } from '../../../@intermediate/portal.interface';
import { IRoleTypesService } from '../../../@intermediate/role-types.interface';

@Component({
  selector: 'tei-admin-center-add-edit-role-dialog',
  templateUrl: './add-edit-role-dialog.component.html',
  styleUrls: ['./add-edit-role-dialog.component.scss']
})
export class AddEditRoleDialogComponent implements OnInit
{

  form: IFormGroup<Role>;
  formBuilder: IFormBuilder;
  designations: Partial<Designation[]> = [];
  roleTypes: Partial<RoleType[]> = [];
  defaultRoleTypeID: number;
  portals: Partial<Portal[]> = [];
  model: Role;
  editMode: boolean = false;
  _PORTAL_ROLE_ID: number = 2;

  constructor(
    @Inject('IDesignationsService') private designationService: IDesignationsService,
    @Inject('IRoleTypesService') private roleTypesService: IRoleTypesService,
    @Inject('IPortalService') private portalService: IPortalService,
    public dialogRef: MatDialogRef<AddEditRoleDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: AddEditRoleDialogDataModel,
    formBuilder: UntypedFormBuilder
  )
  {
    this.formBuilder = formBuilder;
    this.model = this.data.model;
  }

  ngOnInit(): void
  {
    this.editMode = !!this.model?.RoleID;

    const designationRequest: GetDesignationRequest = {};

    this.designationService.Get(designationRequest)
      .subscribe((res) =>
      {
        this.designations = this.AddDisablePropertyToDesignations(res.Designations);
      });

      const PortalRequest: GetPortalByCurrentTenantRequest = {};

      this.portalService.GetByCurrentTenant(PortalRequest)
        .subscribe((res) =>
        {
          this.portals = res.Portals;

          if(!this.editMode && this.portals.length === 1)
          {
            this.form.controls["PortalID"].setValue(this.portals[0].PortalID);
            this.form.controls["PortalID"].disable();
          }
        });

      const RoleTypesRequest: GetRoleTypesRequest = {};

      this.roleTypesService.Get(RoleTypesRequest)
        .subscribe((res) =>
        {
          this.roleTypes = this.AddDisablePropertyToRoleTypes(res.RoleTypes);
          this.defaultRoleTypeID = res.DefaultRoleTypeID;

          if(!this.editMode)
          {
            this.form.controls["RoleTypeID"].setValue(this.defaultRoleTypeID);
          }
        });

          this.form = this.formBuilder.group<Role>({
            RoleID: [this.model?.RoleID],
            DesignationID: [this.model?.DesignationID || DesignationIDEnum.Staff],
            RoleName: [this.model?.RoleName || "",[Validators.required, Validators.maxLength(30)]],
            RoleDescription: [this.model?.RoleDescription || ""],
            RoleTypeID: [this.model?.RoleTypeID || this.defaultRoleTypeID],
            RoleTypeTitle: [this.model?.RoleTypeTitle || ""],
            PortalID: [this.model?.PortalID || 0],
            AutoAssign: [this.model?.AutoAssign || false]
          });
  }

  AddDisablePropertyToDesignations(Designations: Designation[]): (Designation & { disabled: boolean; })[]
  {
    return Designations.map(item =>
    {
      return {
        ...item,
        disabled: this.editMode,
      };
    });
  }

  AddDisablePropertyToRoleTypes(roleTypes: RoleType[]): (RoleType & { disabled: boolean; })[]
  {
    return roleTypes.map(item =>
    {
      return {
        ...item,
        disabled: this.editMode || !item.IsAccessible,
      };
    });
  }

 closeDialog()
  {
    this.dialogRef.close();
  }

  confirmAddEditRole()
  {
    this.dialogRef.close(this.form.value);
  }

  IsPortalRole()
  {
    return this.form.controls["RoleTypeID"].value === this._PORTAL_ROLE_ID;
  }
}

export class AddEditRoleDialogDataModel
{
  model: Role;
  isStaffLevel: boolean;
}
