import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { DefaultLayoutModule } from '@tei/theme';
import { AdminCenterService } from './@services/admin-cneter.service';
import { AdminCenterRoutingModule } from './admin-center-routing.module';
import { AdminCenterComponent } from './admin-center.component';
import { CustomerFacingDisplaysModule } from './pages/customer-facing-displays/customer-facing-displays.module';
import { DefaultModule } from './pages/default/default.module';
import { GoogleAuthenticatorSettingModule } from './pages/google-authenticator-setting/google-authenticator-setting.module';
import { GroupAdminCenterModule } from './pages/group-admin-center/group-admin-center.module';
import { GroupStaffModule } from './pages/group-admin-center/staff-user/staff-user.module';
import { AnalyticsModule } from './pages/host-admin-center/analytics/analytics.module';
import { HostAdminCenterModule } from './pages/host-admin-center/host-admin-center.module';
import { ManagePortalGroupAliasesModule } from './pages/host-admin-center/manage-portal-group/manage-portal-group-aliases/manage-portal-group-aliases.module';
import { AddEditPortalDialogModule } from './pages/host-admin-center/manage-portal-group/manage-portal-group-portals/add-edit-portal/add-edit-portal.module';
import { ManagePortalGroupPortalsModule } from './pages/host-admin-center/manage-portal-group/manage-portal-group-portals/manage-portal-group-portals.module';
import { ManagePortalGroupSettingsModule } from './pages/host-admin-center/manage-portal-group/settings/manage-portal-group-settings.module';
import { ManagePortalModule } from './pages/host-admin-center/manage-portal/manage-portal.module';
import { AddPortalGroupModule } from './pages/host-admin-center/portal-group/add-portal-group/add-portal-group.module';
import { EditPortalGroupModule } from './pages/host-admin-center/portal-group/edit-portal-group/edit-portal-group.module';
import { PortalGroupModule } from './pages/host-admin-center/portal-group/portal-group.module';
import { PagesComponent } from './pages/pages.component';
import { PrintersSettingModule } from './pages/printers-setting/printers-setting.module';
import { RolesModule } from './pages/roles/roles.module';
import { PortalAdminCenterSettingsModule } from './pages/settings/portal-admin-center-settings.module';

const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule];
const MODULES = [
  RolesModule,
  DefaultLayoutModule,
  DefaultModule,
  HostAdminCenterModule,
  GroupAdminCenterModule,
  GroupStaffModule,
  PortalGroupModule,
  AddEditPortalDialogModule,
  EditPortalGroupModule,
  AddPortalGroupModule,
  ManagePortalGroupSettingsModule,
  ManagePortalGroupPortalsModule,
  ManagePortalGroupAliasesModule,
  PortalAdminCenterSettingsModule,
  GoogleAuthenticatorSettingModule,
  PrintersSettingModule,
  CustomerFacingDisplaysModule,
  ManagePortalModule,
  AnalyticsModule
];

const COMPONENTS = [PagesComponent, AdminCenterComponent];
const PIPES = [];

@NgModule({
  imports: [AdminCenterRoutingModule, ...BASE_MODULES, ...MODULES],
  exports: [...BASE_MODULES],
  declarations: [...COMPONENTS, ...PIPES],
  providers: [
    { provide: 'IAdminCenterService', useClass: AdminCenterService }
  ]
})
export class AdminCenterModule { }
