import { COMMA, ENTER } from '@angular/cdk/keycodes';
import { Component, ElementRef, Inject, Input, OnInit, ViewChild } from '@angular/core';
import { UntypedFormArray, UntypedFormGroup } from '@angular/forms';
import { MatLegacyAutocompleteSelectedEvent as MatAutocompleteSelectedEvent } from '@angular/material/legacy-autocomplete';
import { MatLegacyChipInputEvent as MatChipInputEvent } from '@angular/material/legacy-chips';
import { TranslateService } from '@ngx-translate/core';
import { ngFormGroupControlValueAccessorProviders } from '@tei/core';
import { GetInviteUserResponse, Portal, Role } from '../../../../../@intermediate/staff-user/models/get-invite-user.model';
import { GetPortalSettingRequest, GetPortalSettingResponse } from '../../../../../@intermediate/staff-user/models/get-portal-setting.model';
import { PortalFormArray, RoleFormArray, RoleFormGroup } from '../../../../../@intermediate/staff-user/models/post-invite-user.model';
import { IStaffUserService } from '../../../../../@intermediate/staff-user/staff-user.interface';
import { ItemNode } from '../../../../../components/tree/tree.model';

@Component({
  selector: 'tei-admin-admin-center-invite-users-portal-access',
  templateUrl: './invite-users-portal-access.component.html',
  styleUrls: ['./invite-users-portal-access.component.scss'],
  providers: [ngFormGroupControlValueAccessorProviders(InviteUsersPortalAccessComponent)]
})
export class InviteUsersPortalAccessComponent implements OnInit
{
  portal: Portal;
  @Input() portalIndex: number;
  @Input() form: UntypedFormGroup;
  @Input() model: GetInviteUserResponse;
  restaurantsTreeData: Array<ItemNode> = [];
  proShopsTreeData: Array<ItemNode> = [];
  isLoading = false;

  readonly separatorKeysCodes = [ENTER, COMMA] as const;
  selectable = true;
  removable = true;

  filteredRoles: Array<Role> = [];

  @ViewChild('roleInput') roleInput: ElementRef<HTMLInputElement>;

  constructor(
    private translateService: TranslateService,
    @Inject('IStaffUserService') private service: IStaffUserService
  ) { }

  ngOnInit(): void
  {
    // const portalId = this.form?.controls?.Portals[this.portalIndex]?.get('PortalID').value;
    const tenantId = this.form.controls.TenantID.value;
    this.portal = this.model?.Portals?.find(p => p.TenantID == tenantId);
  }

  addRole(event: MatChipInputEvent): void
  {
    const role = this.model?.GlobalRoles?.find(x => x.RoleID === Number(event.value));

    this.portalRoles.push(new RoleFormGroup(role));

    // Clear the input value
    // event.chipInput?.clear();
    event.value = '';
  }


  removeRole(i: number): void
  {
    this.portalRoles.removeAt(i);
  }

  selectedRole(event: MatAutocompleteSelectedEvent): void
  {
    const role = this.portal?.SpecificRoles?.find(x => x.RoleID === Number(event.option.value));

    this.portalRoles.push(new RoleFormGroup(role));
    this.roleInput.nativeElement.value = '';
  }

  // filterRoles(): void
  // {
  //   const filterValue = this.roleInput?.nativeElement?.value?.toLowerCase();
  //   // this.filteredRoles = this.portal?.SpecificRoles?.filter(role => !this.selectedRoles.some(y => y.RoleID === role.RoleID));

  //   // if (filterValue)
  //   // {
  //   //   this.filteredRoles = this.filteredRoles.filter(role => role.RoleName.toLowerCase().includes(filterValue));
  //   // }
  // }

  get portalGroupRoles(): RoleFormArray
  {
    return this.form.get("PortalGroupRoles") as UntypedFormArray;
  }

  get portalRoles(): PortalFormArray
  {
    return this.form.get("Roles") as PortalFormArray;
  }

  get siteTerminals(): UntypedFormArray
  {
    return this.form.get("SiteTerminals") as UntypedFormArray;
  }

  get restaurantTerminals(): UntypedFormArray
  {
    return this.form.get("RestaurantTerminals") as UntypedFormArray;
  }

  deselectedRoles(): Array<Role>
  {
    return this.portal.SpecificRoles.filter(role => this.portalRoles.value.findIndex(gr => gr.RoleID == role.RoleID) < 0);
  }

  fillRestaurantTreeData(portalSetting: GetPortalSettingResponse): Array<ItemNode>
  {
    let treeData = [];
    if (portalSetting?.Restaurants?.length > 0)
    {
      let restaurantItemNode = new ItemNode();
      restaurantItemNode.Item = {
        Id: "Restaurants",
        Key: "Restaurants",
        Title: this.translateService.instant("ADMIN_CENTER.INVITE_USERS_INITIATE.RESTAURANTS"),
        IsSelected: false
      };

      portalSetting?.Restaurants?.forEach(item =>
      {
        restaurantItemNode.Children.push(
          {
            Item: {
              Id: item.SiteID.toString(),
              Key: item.SiteID.toString(),
              Title: item.SiteName,
              IsSelected: false
            },
            Children: []
          });

        var insertedItem = restaurantItemNode.Children[restaurantItemNode.Children.length - 1];
        item.Terminals.forEach(terminal =>
        {
          insertedItem.Children.push(
            {
              Item: {
                Id: terminal.TerminalID,
                Key: terminal.TerminalID,
                Title: terminal.Description,
                IsSelected: false
              },
              Children: []
            });
        });
      });

      treeData.push(restaurantItemNode);
    }
    return treeData;
  }

  fillProShopTreeData(portalSetting: GetPortalSettingResponse): Array<ItemNode>
  {
    let treeData = [];

    if (portalSetting?.ProShops?.length > 0)
    {
      let retailItemNode = new ItemNode();

      retailItemNode.Item = {
        Id: "Retails",
        Key: "Retails",
        Title: this.translateService.instant("ADMIN_CENTER.INVITE_USERS_INITIATE.RETAILS"),
        IsSelected: false
      };

      portalSetting?.ProShops?.forEach(item =>
      {
        retailItemNode.Children.push(
          {
            Item: {
              Id: item.SiteID.toString(),
              Key: item.SiteID.toString(),
              Title: item.SiteName,
              IsSelected: false
            },
            Children: []
          });

        var insertedItem = retailItemNode.Children[retailItemNode.Children.length - 1];
        item.Terminals.forEach(terminal =>
        {
          insertedItem.Children.push(
            {
              Item: {
                Id: terminal.TerminalID,
                Key: terminal.TerminalID,
                Title: terminal.Description,
                IsSelected: false
              },
              Children: []
            });
        });
      });
      treeData.push(retailItemNode);
    }
    return treeData;
  }

  getPortalSetting(): void
  {
    if (this.restaurantsTreeData.length === 0 || this.proShopsTreeData.length === 0)
    {
      const request: GetPortalSettingRequest = {
        TenantId: this.portal.TenantID
      };
      this.isLoading = true;
      this.service.PortalSetting(request).subscribe({
        next: (response: GetPortalSettingResponse) =>
        {
          this.restaurantsTreeData = this.fillRestaurantTreeData(response);
          this.proShopsTreeData = this.fillProShopTreeData(response);
          this.isLoading = false;
        },
        error: err => this.isLoading = false,
        complete: () => this.isLoading = false,
      });
    }
  }
}
