import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { DeleteRoleRequest, DeleteRolesResponse } from '../@intermediate/models/delete-role.model';
import { GetRoleWithPermissionAccessRequest, GetRoleWithPermissionAccessResponse } from '../@intermediate/models/get-role-with-permission-access.model';
import { GetRoleRequest, GetRolesResponse } from '../@intermediate/models/get-role.model';
import { PostRoleRequest, PostRolesResponse } from '../@intermediate/models/post-role.model';
import { PutRoleRequest, PutRolesResponse } from '../@intermediate/models/put-role.model';
import { IRolesService } from "../@intermediate/role.interface";

@Injectable()
export class RolesService implements IRolesService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Admin/AdminCenter/Role';
  }

  public Get(request: GetRoleRequest): Observable<GetRolesResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.get<GetRolesResponse>(apiUrl, { params: request as any });
  }
  
  public GetRolesWithPermissionAccess(request: GetRoleWithPermissionAccessRequest): Observable<GetRoleWithPermissionAccessResponse>
  {
    const apiUrl = this.serviceUrl + "/GetRoleWithPermissionAccess";
    return this.http.get<GetRoleWithPermissionAccessResponse>(apiUrl, { params: request as any });
  }
  
  public Post(request: PostRoleRequest): Observable<PostRolesResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.post<PostRolesResponse>(apiUrl, request);
  }

  Put(request: PutRoleRequest): Observable<PutRolesResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.put<PutRolesResponse>(apiUrl, request);
  }

  public Delete(request: DeleteRoleRequest): Observable<DeleteRolesResponse>
  {
    const options = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      }),
      body: request
    };
    const apiUrl = this.serviceUrl;
    return this.http.delete<DeleteRolesResponse>(apiUrl, options);
  }
}
