import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { GroupAdminCenterComponent } from './group-admin-center.component';
import { StaffUserComponent } from './staff-user/staff-user.component';


const routes: Routes = [{
  path: '',
  component: GroupAdminCenterComponent,
  children: [
    {
      path: 'staff',
      component: StaffUserComponent
    },
  ]
}];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class GroupAdminCenterRoutingModule
{
}
