import { Component, Inject, Input, OnDestroy, OnInit } from '@angular/core';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { MatLegacyTab as MatTab } from '@angular/material/legacy-tabs';
import { Sort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ManageLoadingService, ResponsiveService } from '@tei/core';
import { CardDialogComponent, ConfirmationDialogComponent, SnackBarService } from '@tei/material';
import { firstValueFrom, Subscription } from 'rxjs';
import { DeleteRoleRequest } from '../../@intermediate/models/delete-role.model';
import { DesignationIDEnum, GetRoleRequest, Role } from '../../@intermediate/models/get-role.model';
import { PostRoleRequest } from '../../@intermediate/models/post-role.model';
import { PutRoleRequest } from '../../@intermediate/models/put-role.model';
import { IRolesService } from '../../@intermediate/role.interface';
import { AddEditRoleDialogComponent, AddEditRoleDialogDataModel } from './add-edit-role-dialog/add-edit-role-dialog.component';

@Component({
  selector: 'tei-admin-center-roles',
  templateUrl: './roles.component.html',
  styleUrls: ['./roles.component.scss']
})
export class RolesComponent implements OnInit, OnDestroy
{

  @Input() isStaffLevel: boolean = false;
  @Input() showTabFilters: boolean = true;
  @Input() selectedTab: DesignationIDEnum = DesignationIDEnum.Staff;

  roles: Role[] = [];
  filteredRoles: Role[];
  dataSource = new MatTableDataSource<Role>();
  displayedColumns: string[] = ['name', 'description', 'autoAssign', 'actions'];
  designationIDEnum = DesignationIDEnum;
  mediaBreakpoint: string;
  subscription: Subscription = new Subscription();
  lastSortValue: Sort;

  constructor(
    private router: Router,
    private activeRoute: ActivatedRoute,
    private responsiveService: ResponsiveService,
    private dialog: MatDialog,
    public manageLoadingService: ManageLoadingService,
    @Inject('IRolesService') private rolesService: IRolesService,
    private translate: TranslateService,
    private snackBar: SnackBarService
  )
  {
  }

  ngOnInit()
  {
    this.subscription.add(
      this.responsiveService.mediaBreakpoint.subscribe((mediaBreakpoint) =>
      {
        this.mediaBreakpoint = mediaBreakpoint;
      })
    );
    this.load().then();
  }

  async load()
  {
    const request = new GetRoleRequest();

    this.manageLoadingService.startLoading();
    try
    {
      const res = await firstValueFrom(this.rolesService.Get(request));
      this.roles = res.Roles;

      if (this.showTabFilters)
      {
        this.filterByChangeTab(this.selectedTab);
      }
      else
      {
        this.filteredRoles = res.Roles;
        this.dataSource.data = this.filteredRoles;
      }

    } catch (err)
    {
      console.log(err);
    } finally
    {
      this.manageLoadingService.finishLoading();
    }
  }

  onTabChange(tab: MatTab): void
  {
    this.selectedTab = +tab.ariaLabel;
    if (this.selectedTab !== this.designationIDEnum.Anonymous)
    {
      this.filterByChangeTab(this.selectedTab);
    }
  }

  filterByChangeTab(selectedDesignation: DesignationIDEnum)
  {
    this.filteredRoles = this.roles.filter(role => role.DesignationID === selectedDesignation);
    this.sortChanged(this.lastSortValue);
  }

  sortChanged(sortState?: Sort): void
  {
    this.lastSortValue = sortState;
    const tempRoles = JSON.parse(JSON.stringify(this.filteredRoles));
    if (sortState?.direction === 'asc')
    {
      this.dataSource.data = tempRoles.sort((a, b) => a.RoleName.localeCompare(b.RoleName));
    } else if (sortState?.direction === 'desc')
    {
      this.dataSource.data = tempRoles.sort((a, b) => b.RoleName.localeCompare(a.RoleName));
    } else
    {
      this.dataSource.data = tempRoles;
    }
  }

  addNewRole()
  {
    const data: AddEditRoleDialogDataModel = {
      model: null,
      isStaffLevel: this.isStaffLevel
    };

    const addPortalGroupDialogRef = this.dialog.open(CardDialogComponent, {
      panelClass: 'add-edit-role-dialog-wrapper',
      width: '400px',
      data: {
        component: AddEditRoleDialogComponent,
        ...data
      }
    });

    addPortalGroupDialogRef.afterClosed().subscribe(async (role: Role) =>
    {

      if (!role)
        return;

      const request: PostRoleRequest = {
        RoleDescription: role.RoleDescription,
        RoleName: role.RoleName,
        DesignationID: role.DesignationID,
        RoleTypeID: role.RoleTypeID,
        PortalID: role.PortalID,
        AutoAssign: role.AutoAssign
      };
      this.manageLoadingService.startLoading();
      try
      {
        await firstValueFrom(this.rolesService.Post(request));

        this.load().then();

      } catch (err)
      {
        console.log(err);
      }
      finally
      {
        this.manageLoadingService.finishLoading();
      }
    });
  }

  editRole(role: Role)
  {

    const data: AddEditRoleDialogDataModel = {
      model: role,
      isStaffLevel: this.isStaffLevel
    };

    const editPortalGroupDialogRef = this.dialog.open(CardDialogComponent, {
      panelClass: 'add-edit-role-dialog-wrapper',
      width: '400px',
      data: {
        component: AddEditRoleDialogComponent,
        ...data
      }
    });

    editPortalGroupDialogRef.afterClosed().subscribe(async (role: Role) =>
    {

      if (!role)
        return;

      const request: PutRoleRequest = role;
      this.manageLoadingService.startLoading();
      try
      {

        await firstValueFrom(this.rolesService.Put(request));

        this.load().then();

      } catch (err)
      {
        console.log(err);
      } finally
      {
        this.manageLoadingService.finishLoading();
      }
    });

  }

  deleteRole(role: Role)
  {
    const dialogRef = this.dialog.open(CardDialogComponent, {
      panelClass: 'o-confirm-dialog',
      data: {
        component: ConfirmationDialogComponent,
        dialogData: {
          title: 'ADMIN_CENTER.ROLES.DELETE_ROLE',
          content: this.translate.instant('ADMIN_CENTER.ROLES.DELETE_ROLE_QUESTION', { RoleName: role.RoleName }),
          submitButtonLabel: 'ADMIN_CENTER.ROLES.DELETE_ROLE',
          closeButtonLabel: 'ADMIN_CENTER.CANCEL'
        }
      },
      width: '350px',
    });

    dialogRef?.afterClosed().subscribe(async result =>
    {
      if (result)
      {
        const request: DeleteRoleRequest = {
          RoleID: role.RoleID
        };

        this.manageLoadingService.startLoading();
        try
        {
          await firstValueFrom(this.rolesService.Delete(request));

          this.load().then();
        } catch (err)
        {
          console.log(err);
        } finally
        {
          this.manageLoadingService.finishLoading();
        }

      }
    });
  }

  goToPermissions(role: Role): void
  {
    this.router.navigate(['./', role.RoleID, 'permissions'], { relativeTo: this.activeRoute });
  }

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }

}
