import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { BehaviorSubject, forkJoin } from 'rxjs';
import { IHostAdminCenterHelperService } from '../@intermediate/host-admin-center-helper.interface';
import { GetPortalDetailRequest, PortalDetail } from '../@intermediate/models/get-portal.model';
import { GetPortalGroupByIDRequest, GetPortalGroupByIDResponse } from '../@intermediate/portal-group/models/get-portal-group-by-id.model';
import { IPortalGroupsService } from '../@intermediate/portal-group/portal-groups.interface';
import { IPortalService } from '../@intermediate/portal.interface';

@Injectable({
  providedIn: 'root'
})
export class HostAdminCenterHelperService implements IHostAdminCenterHelperService
{
  constructor(
    @Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient,
    @Inject('IPortalGroupsService') private hostAdminPortalGroupsService: IPortalGroupsService,
    @Inject('IPortalService') private portalService: IPortalService)
  {
  }

  private portalGroupDetails = new BehaviorSubject<{ portal: PortalDetail, parentPortalGroup: GetPortalGroupByIDResponse; }>(null);
  currentPortalGroupDetails = this.portalGroupDetails.asObservable();

  update(portalId, parentPortalGroupId): void
  {
    const portalDetailRequest = new GetPortalDetailRequest();
    portalDetailRequest.PortalID = portalId;
    const parentPortalGroupRequest = new GetPortalGroupByIDRequest();
    parentPortalGroupRequest.PortalGroupID = parentPortalGroupId;

    forkJoin({
      portal: this.portalService.GetByID(portalDetailRequest),
      parentPortalGroup: this.hostAdminPortalGroupsService.GetByID(parentPortalGroupRequest),
    }).subscribe(result =>
    {
      this.portalGroupDetails.next(result);
    });


  }
}
