import { UntypedFormControl, UntypedFormGroup } from "@angular/forms";
import { GetGoogleAuthenticatorSettingResponse } from "./get-google-authenticator-setting.model";

export class PostGoogleAuthenticatorSettingRequest
{
  GoogleAnalyticsEnabled: boolean;
  GoogleAnalyticsTagID: string;
}

export class PostGoogleAuthenticatorSettingResponse
{

}

export class GoogleAuthenticatorSettingForm extends UntypedFormGroup
{
  constructor(value: GetGoogleAuthenticatorSettingResponse)
  {
    super({
      GoogleAnalyticsEnabled: new UntypedFormControl(value.GoogleAnalyticsEnabled),
      GoogleAnalyticsTagID: new UntypedFormControl(value.GoogleAnalyticsTagID),
    });
  }

  public value: PostGoogleAuthenticatorSettingRequest;

  public setValue(value: GetGoogleAuthenticatorSettingResponse)
  {
    super.setValue(value);
    return value;
  }
}
