import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetRoleTypesRequest, GetRoleTypesResponse } from '../@intermediate/models/get-role-types.model';
import { IRoleTypesService } from '../@intermediate/role-types.interface';

@Injectable()
export class RoleTypesService implements IRoleTypesService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Admin/AdminCenter/Role';
  }
  Get(request: GetRoleTypesRequest): Observable<GetRoleTypesResponse>
  {
    const apiUrl = this.serviceUrl + "/GetRoleTypes";
    return this.http.get<GetRoleTypesResponse>(apiUrl, { params: request as any });
  }
}
