import { UntypedFormArray, UntypedFormControl, UntypedFormGroup, Validators } from "@angular/forms";
import { Role } from "./get-invite-user.model";

export class PostInviteUserRequest
{
  public Emails: Array<string> = [];
  public IsAdministrator: boolean;
  public TenantIDs: Array<number> = [];
  public PortalGroupRoles: Array<number> = [];
  public PortalRoles: Array<PortalRole> = [];
  public Terminals: Array<number> = [];
}

export class PostInviteUserResponse
{
}

export class PortalRole
{
  TenantID: number;
  Roles: Array<number> = [];
}
export class PostInviteUserRequestForm extends UntypedFormGroup
{
  public readonly PortalGroupRoles: RoleFormArray;
  public readonly Portals: PortalFormArray;

  constructor()
  {
    super({
      Emails: new UntypedFormArray([], [Validators.email, Validators.required]),
      PortalGroupRoles: new UntypedFormArray([]),
      isAdministrator: new UntypedFormControl(false, Validators.requiredTrue),
      Portals: new PortalFormArray()
    });

    this.PortalGroupRoles = this.get('PortalGroupRoles') as RoleFormArray;
    this.Portals = this.get('Portals') as PortalFormArray;
  }
}

export class RoleFormArray extends UntypedFormArray
{
  constructor()
  {
    super([]);
  }

  public setValue(value: Role[]): void
  {
    this.clear();
    value.forEach(v =>
    {
      const Role = new RoleFormGroup(v);

      this.push(Role);
    });
  }
}

export class RoleFormGroup extends UntypedFormGroup
{
  constructor(role: Role)
  {
    super({
      RoleID: new UntypedFormControl(role.RoleID),
      RoleName: new UntypedFormControl(role.RoleName),
      IsSelected: new UntypedFormControl(role.IsSelected)
    });
  };
}

export class PortalFormArray extends UntypedFormArray
{
  constructor()
  {
    super([]);
  }

  public setValue(value: Portal[])
  {
    this.clear();
    value.forEach(v =>
    {

      const portal = new PortalFormGroup(v);
      portal.Roles.setValue(v.SpecificRoles);

      this.push(portal);
    });
  }
}

export class Portal
{
  public TenantID: number;
  public PortalName: string;
  public SpecificRoles: Array<Role> = [];
  public Restaurants: Array<Site> = [];
  public ProShops: Array<Site> = [];
  public IsSelected: boolean = false;
  public RestaurantsTreeData: Array<ItemNode> = [];
  public ProShopsTreeData: Array<ItemNode> = [];
}

export class PortalFormGroup extends UntypedFormGroup
{
  public readonly Roles: RoleFormArray;
  public readonly TenantID: UntypedFormControl;
  public readonly Restaurants: UntypedFormArray;
  public readonly Sites: UntypedFormArray;
  public readonly SiteTerminals: UntypedFormArray;
  public readonly RestaurantTerminals: UntypedFormArray;

  constructor(portal: Portal)
  {
    super({
      TenantID: new UntypedFormControl(portal.TenantID),
      Roles: new RoleFormArray(),
      Restaurants: new UntypedFormArray([]),
      Sites: new UntypedFormArray([]),
      SiteTerminals: new UntypedFormArray([]),
      RestaurantTerminals: new UntypedFormArray([])
    });

    this.Roles = this.get('Roles') as RoleFormArray;
  }

  public setValue(value: Portal)
  {
    this.patchValue({
      TenantID: value.TenantID,
    });

    this.Roles.clear();
    this.Roles.setValue(value.SpecificRoles);
  }
}

export class Site
{
  public SiteID: number;
  public SiteName: string;
  public SiteIsSelected: boolean = false;
}

export class ItemNode
{
  public Children: ItemNode[] = [];
  public Item: TreeNode;
}

export class TreeNode
{
  public Title: string;
  public Id: string;
  public Key: string;
  public IsSelected: boolean = false;
}
