import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetGeneralSettingsRequest, GetGeneralSettingsResponse, IGeneralSettingsService, PostGeneralSettingsRequest, PostGeneralSettingsResponse } from '../@intermediate/general-settings';

@Injectable({
  providedIn: 'root'
})

export class GeneralSettingsService implements IGeneralSettingsService
{
  private serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + "/Common/PaymentEngine/Settings";
  }

  public Get(request: GetGeneralSettingsRequest): Observable<GetGeneralSettingsResponse>
  {
    const apiUrl = this.serviceUrl + "";
    return this.http.get<GetGeneralSettingsResponse>(apiUrl, { params: <any>request });
  }

  public Post(request: PostGeneralSettingsRequest): Observable<PostGeneralSettingsResponse>
  {
    const apiUrl = this.serviceUrl + "";
    return this.http.post<PostGeneralSettingsResponse>(apiUrl, request);
  }
}
