import { FormControl, FormGroup, Validators } from '@angular/forms';
import { GetSurchargeSettingsResponse } from './get-surcharge-settings.model';
export class PostSurchargeSettingsRequest
{
  public EnableSurcharging: boolean;
  public SurchargeItemID: string;
  public SurchargeType: string;
  public SurchargeValue: number;
  public EnableECommerce: boolean;
  public EnableEvents: boolean;
  public EnableLodging: boolean;
  public EnableStatementPayments: boolean;
  public EnableTeeTime: boolean;
  public SurchargeDescription: string;
}

export class PostSurchargeSettingsResponse
{

}
export class SurchargeSettingsRequestForm extends FormGroup
{
  constructor(value: GetSurchargeSettingsResponse)
  {
    super({
      EnableSurcharging: new FormControl(value.EnableSurcharging),
      SurchargeItemID: new FormControl(value.SurchargeItemID, Validators.required),
      SurchargeType: new FormControl(value.SurchargeType),
      SurchargeValue: new FormControl(value.SurchargeValue, [Validators.required, Validators.min(0)]),
      EnableECommerce: new FormControl(value.EnableECommerce),
      EnableEvents: new FormControl(value.EnableEvents),
      EnableLodging: new FormControl(value.EnableLodging),
      EnableStatementPayments: new FormControl(value.EnableStatementPayments),
      EnableTeeTime: new FormControl(value.EnableTeeTime),
      SurchargeDescription: new FormControl(value.SurchargeDescription, Validators.required),
    });
  }

  public value: PostSurchargeSettingsRequest;

  public setValue(value: GetSurchargeSettingsResponse)
  {
    const request = new PostSurchargeSettingsRequest();
    super.setValue(request);
  }
}

