import { Component, Inject, OnInit } from '@angular/core';
import { UntypedFormBuilder, UntypedFormGroup } from '@angular/forms';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { PostCustomerFacingDisplayResponse } from '../../../@intermediate/models/post-customer-facing-display.model';

@Component({
  selector: 'tei-admin-admin-center-new-secret-id-dialog',
  templateUrl: './new-secret-id-dialog.component.html',
  styleUrls: ['./new-secret-id-dialog.component.scss']
})
export class NewSecretIdDialogComponent implements OnInit
{
  form: UntypedFormGroup;
  constructor(private dialogRef: MatDialogRef<NewSecretIdDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: { data: PostCustomerFacingDisplayResponse, isMobile: boolean; },
    private fb: UntypedFormBuilder)
  { }

  ngOnInit(): void
  {
    this.form = this.fb.group({
      DeviceSetupURI: [this.data.data.DeviceSetupURI],
      SecretID: [this.data.data.SecretID]
    });
  }

  close(): void
  {
    this.dialogRef.close();
  }

  get formControls()
  {
    return this.form.controls;
  }

}
