import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyCheckboxModule as MatCheckboxModule } from '@angular/material/legacy-checkbox';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacySelectModule as MatSelectModule } from '@angular/material/legacy-select';
import { RouterModule } from '@angular/router';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { ExpansionPanelModule, LoadingModule } from '@tei/material';
import { PermissionService } from '../../../@services/permission.service';
import { RolePermissionService } from '../../../@services/role-permission.service';
import { RolesService } from '../../../@services/role.service';
import { PermissionsComponent } from './permissions.component';
import { MatLegacyTooltipModule as MatTooltipModule } from '@angular/material/legacy-tooltip';

const BASE_MODULES = [CommonModule, RouterModule];
const MATERIAL_MODULES = [
  MatSelectModule,
  MatFormFieldModule,
  MatCardModule,
  MatCheckboxModule,
  MatIconModule,
  MatButtonModule,
  MatTooltipModule
];
const TEI_MATERIAL_MODULES = [ExpansionPanelModule, TeiTextTranslatePipeModule, LoadingModule];
const COMPONENTS = [PermissionsComponent];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES
  ],
  exports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...COMPONENTS
  ],
  providers: [
    { provide: 'IRolePermissionService', useClass: RolePermissionService },
    { provide: 'IPermissionService', useClass: PermissionService },
    { provide: 'IRolesService', useClass: RolesService }
  ],
})
export class PermissionsModule { }
