import { Component, Inject, OnDestroy, OnInit } from '@angular/core';
import { UntypedFormControl, UntypedFormGroup } from '@angular/forms';
import { MatLegacyDialog as MatDialog, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { ActivatedRoute } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ManageLoadingService } from '@tei/core';
import { BreadCrumb, ImgDialogData, SnackBarService, UploadIconComponent, UploadIconDialogData, UploadImageDialogData, UploadProfilePictureComponent } from '@tei/material';
import { base64ToFile } from 'ngx-image-cropper';
import { Subscription } from 'rxjs';
import { finalize } from 'rxjs/operators';
import { IAdminPortalService } from '../../../../@intermediate/admin-portal.interface';
import { IHostAdminCenterHelperService } from '../../../../@intermediate/host-admin-center-helper.interface';
import { GetPortalSettingsRequest, GetPortalSettingsResponse } from '../../../../@intermediate/models/get-portal-settings.model';
import { portalGroupDetails } from '../../../../@intermediate/models/host-admin-center-helper.model';
import { PostPortalSettingsResetRequest, SettingNameEnum } from '../../../../@intermediate/models/post-portal-settings-reset.model';
import { PostPortalSettingsRequest, PostPortalSettingsResponse } from '../../../../@intermediate/models/post-portal-settings.model';
import { TeiChipsModel } from '../../../../components/portal-settings-localization-chips/portal-settings-localization-chips.component';

@Component({
  selector: 'tei-host-admin-manage-portal-settings',
  templateUrl: './manage-portal-settings.component.html',
  styleUrls: ['./manage-portal-settings.component.scss']
})
export class ManagePortalSettingsComponent implements OnInit, OnDestroy
{
  portalSetting: GetPortalSettingsResponse;
  uploadPhotoDialogRef: MatDialogRef<UploadProfilePictureComponent>;
  settingForm: UntypedFormGroup = new UntypedFormGroup({
    portalName: new UntypedFormControl(''),
    portalTitle: new UntypedFormControl(''),
    timezone: new UntypedFormControl(''),
    defaultTheme: new UntypedFormControl(''),
    ContactEmail: new UntypedFormControl(''),
    ContactPhone: new UntypedFormControl(''),
    DefaultCallingCode: new UntypedFormControl(''),
    CurrentCurrency: new UntypedFormControl(''),
    PreferredCallingCode: new UntypedFormControl([]),
    CurrentCulture: new UntypedFormControl(''),
    SupportedCultures: new UntypedFormControl([]),
  });
  uploadIconDialogRef: MatDialogRef<UploadIconComponent>;
  fieldsNameEnum = SettingNameEnum;

  get formControls()
  {
    return this.settingForm?.controls;
  }
  teiChipData: TeiChipsModel = {
    allChips: [
      {
        id: 1,
        name: 'EN_CA',
        active: true
      },
      {
        id: 2,
        name: 'FR_CA',
        active: true
      }
    ]
  };

  preferredCallingCodeChipData: TeiChipsModel;
  supportedCulturesChipData: TeiChipsModel;
  breadCrumbData: BreadCrumb[] = [];
  portalDetail: portalGroupDetails;
  subscription: Subscription = new Subscription();

  constructor(
    @Inject('IAdminPortalService') private hostAdminPortalService: IAdminPortalService,
    private dialog: MatDialog,
    private translate: TranslateService,
    public loadingService: ManageLoadingService,
    private snackBarService: SnackBarService,
    @Inject('IHostAdminCenterHelperService') private hostAdminCenterHelperService: IHostAdminCenterHelperService,
    private activatedRoute: ActivatedRoute
  )
  {
  }

  ngOnInit(): void
  {
    this.subscription.add(this.hostAdminCenterHelperService.currentPortalGroupDetails.subscribe(portalDetail =>
    {
      if (portalDetail)
      {
        this.portalDetail = {
          parentPortalGroup: portalDetail.parentPortalGroup.PortalGroup,
          portal: portalDetail.portal
        };
        this.fillBreadCrump();
        this.load();
      }
    }));
  }

  private async fillBreadCrump(): Promise<void>
  {

    this.breadCrumbData = [];
    this.breadCrumbData = [
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.PORTAL_GROUPS.TITLE'),
        relativeUrl: '../../../../',
      },
      {
        title: `${ this.portalDetail.parentPortalGroup.PortalGroupName }`,
        relativeUrl: `../../../`,
      },
      {
        title: `${ this.portalDetail.portal.PortalName }`,
        relativeUrl: '../',
      },
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.PORTAL_DETAILS'),
        relativeUrl: '',
      }
    ];
  }

  load(): void
  {
    const request: GetPortalSettingsRequest = {
      PortalID: this.portalDetail.portal.PortalID
    };

    this.loadingService.startLoading();
    this.hostAdminPortalService.GetSetting(request).pipe(finalize(() => this.loadingService.finishLoading())).subscribe({
      next: (response: GetPortalSettingsResponse) =>
      {
        this.portalSetting = response;
        this.preferredCallingCodeChipData = {
          allChips: response.Countries.map(x => ({ id: x.TwoLetterISOCode, name: x.CountryName, active: true }))
        };
        this.supportedCulturesChipData = {
          allChips: response.Cultures.map(x => ({ id: x.ShortName, name: x.FullName, active: true }))
        };
        this.settingForm.setValue({
          portalName: response.Portal.Name,
          portalTitle: response.Portal.Settings.Title,
          timezone: response.Portal.Settings.CurrentTimeZone,
          defaultTheme: response.Portal.Settings.DefaultTheme,
          ContactEmail: response.Portal.Settings.ContactEmail ?? '',
          ContactPhone: response.Portal.Settings.ContactPhone ?? '',
          DefaultCallingCode: response.Portal.Settings.DefaultCallingCode ?? '',
          CurrentCurrency: response.Portal.Settings.CurrentCurrency ?? '',
          PreferredCallingCode: response.Portal.Settings.PreferredCallingCode.split(',') ?? [],
          CurrentCulture: response.Portal.Settings.CurrentCulture ?? '',
          SupportedCultures: response.Portal.Settings.SupportedCultures.split(',') ?? [],
        });
      }
    });
  }

  onChangeFavIcon(): void
  {
    const data: UploadIconDialogData = {
      disableDelete: !this.portalSetting.Portal.Settings[SettingNameEnum.FavIcon],
      onConfirm: (base64File) =>
      {
        this.uploadIconDialogRef.close();
        const result = new ImgDialogData();
        result.croppedImage = base64File;
        this.onChangeUploadPhoto(result, SettingNameEnum.FavIcon);
      },
      onDelete: () => this.onDeleteUploadPhoto(SettingNameEnum.FavIcon)
    };

    this.uploadIconDialogRef = this.dialog.open(UploadIconComponent, {
      maxWidth: '80vw',
      width: '390px',
      panelClass: ['o-dialog-user-profile-picture'],
      data
    });
  }

  onChangeDarkThemeImage(): void
  {
    this.onChangeImage(SettingNameEnum.DarkThemeLogo);
  }

  onChangeLightThemeImage(): void
  {
    this.onChangeImage(SettingNameEnum.LightThemeLogo);
  }

  onChangeLightThemeHeaderLogo(): void
  {
    this.onChangeImage(SettingNameEnum.LightThemeHeaderLogo);
  }

  onChangeDarkThemeHeaderLogo(): void
  {
    this.onChangeImage(SettingNameEnum.DarkThemeHeaderLogo);
  }

  onChangePortalAvatar(): void
  {
    this.onChangeImage(SettingNameEnum.PortalAvatar);
  }

  onChangeImage(fieldName: SettingNameEnum): void
  {
    const maintainAspectRatio = fieldName === SettingNameEnum.LightThemeHeaderLogo ||
      fieldName === SettingNameEnum.DarkThemeHeaderLogo ||
      fieldName === SettingNameEnum.LightThemeLogo ||
      fieldName === SettingNameEnum.DarkThemeLogo;

    const portalAvatarFormats = '.png,.jpg,.jpeg';
    const portalAvatarPermittedFileTypes = ['PNG', 'JPG', 'JPEG'];

    const data: UploadImageDialogData = {
      round: false,
      ...(maintainAspectRatio && { maintainAspectRatio: false }),
      ...(fieldName === SettingNameEnum.PortalAvatar && { aspectRatio: 4 / 2.5 }),
      ...(fieldName === SettingNameEnum.PortalAvatar && { acceptFormat: portalAvatarFormats }),
      ...(fieldName === SettingNameEnum.PortalAvatar && { permittedFileTypes: portalAvatarPermittedFileTypes }),
      ...(fieldName !== SettingNameEnum.PortalAvatar && { maxSize: 200 }),
      ProfilePictureUrl: this.portalSetting?.Portal.Settings[fieldName],
      onChangePicture: (result: ImgDialogData) => this.onChangeUploadPhoto(result, fieldName),
      onDeletePicture: () => this.onDeleteUploadPhoto(fieldName)
    };

    this.uploadPhotoDialogRef = this.dialog.open(UploadProfilePictureComponent, {
      maxWidth: '80vw',
      width: '390px',
      panelClass: ['o-dialog-user-profile-picture', 'o-rectangular-container'],
      data
    });

  }

  onChangeUploadPhoto(result: ImgDialogData, fieldName: SettingNameEnum): void
  {

    const request = this.createUpdateSettingRequest();
    const file = base64ToFile(result.croppedImage);
    (request as any)[fieldName] = file;

    const formData = this.convertBodyRequestToFormData(request);
    if (this.settingForm.invalid) { return; }

    this.loadingService.startLoading();
    this.hostAdminPortalService.UpdateSetting(formData).pipe(finalize(() => this.loadingService.finishLoading())).subscribe({
      next: (response: PostPortalSettingsResponse) =>
      {
        this.portalSetting.Portal.Settings[fieldName] = response.Portal.Settings[fieldName];
        if (this.uploadPhotoDialogRef)
        {
          this.uploadPhotoDialogRef.componentInstance.profilePictureUrl = response.Portal.Settings[fieldName];
        }
      },
      error: () =>
      {
      }
    });
  }

  onDeleteUploadPhoto(fieldName: SettingNameEnum): void
  {
    const request: PostPortalSettingsResetRequest = {
      TenantID: this.portalDetail.portal.TenantID,
      SettingName: fieldName
    };
    this.loadingService.startLoading();
    this.hostAdminPortalService.ResetSetting(request).pipe(finalize(() => this.loadingService.finishLoading())).subscribe({
      next: (response) =>
      {
        this.portalSetting.Portal.Settings[fieldName] = response.DefaultValue;
        if (this.uploadPhotoDialogRef)
        {
          this.uploadPhotoDialogRef.componentInstance.profilePictureUrl = response.DefaultValue;
        }
      },
      error: () =>
      {
      }
    });
  }

  saveSetting(): void
  {
    const request = this.createUpdateSettingRequest();

    const formData = this.convertBodyRequestToFormData(request);
    if (this.settingForm.invalid) { return; }
    this.loadingService.startLoading();
    this.hostAdminPortalService.UpdateSetting(formData).pipe(finalize(() => this.loadingService.finishLoading())).subscribe(
      {
        next: () =>
        {
          this.snackBarService.showSuccess(
            this.translate.instant("ADMIN_CENTER.GROUP.MANAGE_PORTAL.SETTINGS.SAVE_SUCCESS"),
            '', null, 'left');

        },
        error: () =>
        {
          this.snackBarService.showError(
            this.translate.instant("ADMIN_CENTER.GROUP.MANAGE_PORTAL.SETTINGS.SAVE_ERROR"),
            '', null, 'left');
        }
      });
  }

  createUpdateSettingRequest(): PostPortalSettingsRequest
  {
    const request: PostPortalSettingsRequest = {
      PortalID: this.portalDetail.portal.PortalID,
      TenantID: this.portalDetail.portal.TenantID,
      PortalName: this.settingForm.value.portalName,
      Title: this.settingForm.value.portalTitle,
      CurrentTimeZone: this.settingForm.value.timezone,
      DefaultTheme: this.settingForm.value.defaultTheme,
      ContactEmail: this.settingForm.value.ContactEmail,
      ContactPhone: this.settingForm.value.ContactPhone,
      DefaultCallingCode: this.settingForm.value.DefaultCallingCode,
      CurrentCurrency: this.settingForm.value.CurrentCurrency,
      PreferredCallingCode: this.settingForm.value.PreferredCallingCode.join(','),
      CurrentCulture: this.settingForm.value.CurrentCulture,
      SupportedCultures: this.settingForm.value.SupportedCultures.join(',')

    };
    return request;
  }

  convertBodyRequestToFormData(bodyRequest: { [key in string | number]: any; } = {}): FormData
  {
    const formData = new FormData();

    Object.keys(bodyRequest).forEach(key =>
    {

      if (bodyRequest[key] instanceof Blob)
      {
        const fileExtension = bodyRequest[key]?.type.split('/')?.[1] ?? 'jpg';
        const fileName = key + "." + (fileExtension === 'x-icon' ? 'ico' : fileExtension);
        formData.append(key, bodyRequest[key], fileName);
      }
      else
      {
        formData.append(key, bodyRequest[key].toString());
      }
    });

    return formData;
  }
  ngOnDestroy(): void
  {
    this.subscription?.unsubscribe();
  }
}
