import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetInviteUserRequest, GetInviteUserResponse } from '../@intermediate/staff-user/models/get-invite-user.model';
import { GetManageRoleRequest, GetManageRoleResponse } from '../@intermediate/staff-user/models/get-manage-role.model';
import { GetPortalSettingRequest, GetPortalSettingResponse } from '../@intermediate/staff-user/models/get-portal-setting.model';
import { PostCancelInviteRequest, PostCancelInviteResponse } from '../@intermediate/staff-user/models/post-cancel-invite-user.model';
import { PostInviteUserRequest, PostInviteUserResponse } from '../@intermediate/staff-user/models/post-invite-user.model';
import { PostManageRoleRequest, PostManageRoleResponse } from '../@intermediate/staff-user/models/post-manage-role.model';
import { RevokeAccessRequest, RevokeAccessResponse } from '../@intermediate/staff-user/models/revoke-access.model';
import { SendInviteStaffEmailRequest, SendInviteStaffEmailResponse } from '../@intermediate/staff-user/models/send-invite-staff-email.model';
import { IStaffUserService } from '../@intermediate/staff-user/staff-user.interface';
import { GetStaffUserRequest, GetStaffUserResponse } from './../@intermediate/models/get-staff-user.model';

@Injectable()
export class StaffUserService implements IStaffUserService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Admin/AdminCenter/StaffUser';
  }

  SendInviteStaffEmail(request: SendInviteStaffEmailRequest): Observable<SendInviteStaffEmailResponse>
  {
    const apiUrl = this.serviceUrl + '/SendInviteStaffEmail';
    return this.http.post<SendInviteStaffEmailResponse>(apiUrl, request);
  }

  PostCancelInvite(request: PostCancelInviteRequest): Observable<PostCancelInviteResponse>
  {
    const apiUrl = this.serviceUrl + '/PostCancelInvite';
    return this.http.post<PostCancelInviteResponse>(apiUrl, request);
  }

  RevokeAccess(request: RevokeAccessRequest): Observable<RevokeAccessResponse>
  {
    const apiUrl = this.serviceUrl + '/RevokeAccess';
    return this.http.post<RevokeAccessResponse>(apiUrl, request);
  }

  Get(request: GetStaffUserRequest): Observable<GetStaffUserResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.get<GetStaffUserResponse>(apiUrl, { params: request as any });
  }

  GetInvite(request: GetInviteUserRequest): Observable<GetInviteUserResponse>
  {
    const apiUrl = this.serviceUrl + '/Invite';
    return this.http.get<GetInviteUserResponse>(apiUrl, { params: request as any });
  }

  PostInvite(request: PostInviteUserRequest): Observable<PostInviteUserResponse>
  {
    const apiUrl = this.serviceUrl + '/Invite';
    return this.http.post<PostInviteUserResponse>(apiUrl, request);
  }

  GetManageRole(request: GetManageRoleRequest): Observable<GetManageRoleResponse>
  {
    const apiUrl = this.serviceUrl + '/ManageRole';
    return this.http.get<GetManageRoleResponse>(apiUrl, { params: request as any });
  }

  PostManageRole(request: PostManageRoleRequest): Observable<PostManageRoleResponse>
  {
    const apiUrl = this.serviceUrl + '/ManageRole';
    return this.http.post<PostManageRoleResponse>(apiUrl, request);
  }

  PortalSetting(request: GetPortalSettingRequest): Observable<GetPortalSettingResponse>
  {
    const apiUrl = this.serviceUrl + '/PortalSetting';
    return this.http.get<GetPortalSettingResponse>(apiUrl, { params: request as any });
  }
}

