import { Component, Inject, OnInit } from '@angular/core';
import { UntypedFormGroup } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ManageLoadingService } from '@tei/core';
import { BreadCrumb, SnackBarService } from '@tei/material';
import { Subscription, firstValueFrom } from 'rxjs';
import { finalize } from 'rxjs/operators';
import { IAuthenticationSettingsService } from '../../@intermediate/authentication-settings.interface';
import { IHostAdminCenterHelperService } from '../../@intermediate/host-admin-center-helper.interface';
import { GetEditableAuthenticationSettingsResponse } from '../../@intermediate/models/get-editable-authentication-settings.model';
import { portalGroupDetails } from '../../@intermediate/models/host-admin-center-helper.model';
import { PostAuthenticationSettingsRequest, PostAuthenticationSettingsRequestForm } from '../../@intermediate/models/post-editable-authentication-settings.model';

@Component({
  selector: 'tei-admin-center-authentication-setting',
  templateUrl: './authentication-setting.component.html',
  styleUrls: ['./authentication-setting.component.scss']
})
export class AuthenticationSettingComponent implements OnInit
{
  form: PostAuthenticationSettingsRequestForm;
  breadCrumbData: BreadCrumb[] = [];
  portalDetail: portalGroupDetails;
  subscription: Subscription = new Subscription();
  constructor(
    @Inject("IAuthenticationSettingsService") private authenticationSettingsService: IAuthenticationSettingsService,
    @Inject('IHostAdminCenterHelperService') private hostAdminCenterHelperService: IHostAdminCenterHelperService,
    private activatedRoute: ActivatedRoute,
    public loadingService: ManageLoadingService,
    private snackBarService: SnackBarService,
    private translate: TranslateService,
  ) { }

  ngOnInit(): void
  {
    this.form = new PostAuthenticationSettingsRequestForm();
    this.subscription.add(this.hostAdminCenterHelperService.currentPortalGroupDetails.subscribe(portalDetail =>
    {
      if (portalDetail)
      {
        this.portalDetail = {
          parentPortalGroup: portalDetail.parentPortalGroup.PortalGroup,
          portal: portalDetail.portal
        };
        this.fillBreadCrump();
        this.initData();
      }
    }));
  }

  fillBreadCrump(): void
  {
    this.breadCrumbData = [];
    this.breadCrumbData = [
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.PORTAL_GROUPS.TITLE'),
        relativeUrl: '../../../../',
      },
      {
        title: `${ this.portalDetail.parentPortalGroup.PortalGroupName }`,
        relativeUrl: `../../../`,
      },
      {
        title: `${ this.portalDetail.portal.PortalName }`,
        relativeUrl: '../',
      },
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.AUTHENTICATION'),
        relativeUrl: '',
      }
    ];
  }


  async initData(): Promise<void>
  {
    this.loadingService.startLoading();
    const response: GetEditableAuthenticationSettingsResponse = await firstValueFrom(this.authenticationSettingsService.GetEditableSettings({ PortalID: this.portalDetail.portal.PortalID }).pipe(finalize(() => this.loadingService.finishLoading())));
    this.form = new PostAuthenticationSettingsRequestForm();
    // this.form.value.PortalID = this.portalDetail.portal.PortalID;
    this.form.setValue(response);

  }

  async save(): Promise<void>
  {
    const request: PostAuthenticationSettingsRequest = {
      AllowActivation: this.form.value.AllowActivation,
      AllowMember: this.form.value.AllowMember,
      AllowPublic: this.form.value.AllowPublic,
      AllowRegistration: this.form.value.AllowRegistration,
      AllowSSOLogin: this.form.value.AllowSSOLogin,
      PasswordLessLogin: this.form.value.PasswordLessLogin,
      TokenStorageType: this.form.value.TokenStorageType,
      PortalID: this.portalDetail.portal.PortalID
    };

    this.loadingService.startLoading();
    try
    {
      await firstValueFrom(this.authenticationSettingsService.Post(request).pipe(finalize(() => this.loadingService.finishLoading())));
      this.snackBarService.showSuccess(this.translate.instant("ADMIN_CENTER.AUTHENTICATION.SUCCESS_MESSAGE"), '', null, 'left');
    } finally
    {
      this.initData();
    }
  }
}
