import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetLinkedSiteRequest, GetLinkedSiteResponse } from '../@intermediate/models/get-linked-sites.model';
import { GetPrinterCredentialByIdRequest, GetPrinterCredentialByIdResponse } from '../@intermediate/models/get-printer-credential-by-id.model';
import { GetRestaurantAndRetailSiteRequest, GetRestaurantAndRetailSiteResponse } from '../@intermediate/models/get-sites-model';
import { PostCreatePrinterCredentialRequest, PostCreatePrinterCredentialResponse } from '../@intermediate/models/post-create-printer-credential.model';
import { DeletePrinterCredentialRequest, DeletePrinterCredentialResponse } from '../@intermediate/models/post-delete-printer-credential.model';
import { PostResetPrinterCredentialRequest, PostResetPrinterCredentialResponse } from '../@intermediate/models/post-reset-printer-credential.model';
import { PutPrinterCredentialRequest, PutPrinterCredentialResponse } from '../@intermediate/models/put-printer-credential.model';
import { IPrinterCredentialService } from '../@intermediate/printer-credential.interface';

@Injectable({
  providedIn: 'root'
})
export class PrinterCredentialService implements IPrinterCredentialService
{
  private readonly serviceUrl: string;
  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/DeviceIntegration/PrinterCredential';
  }

  LinkedSites(request: GetLinkedSiteRequest): Observable<GetLinkedSiteResponse>
  {
    const apiUrl = this.serviceUrl + '/LinkedSites';
    return this.http.get<GetLinkedSiteResponse>(apiUrl);
  }

  Sites(request: GetRestaurantAndRetailSiteRequest): Observable<GetRestaurantAndRetailSiteResponse>
  {
    const apiUrl = this.serviceUrl + '/Sites';
    return this.http.get<GetRestaurantAndRetailSiteResponse>(apiUrl);
  }

  PrinterCredential(request: PostCreatePrinterCredentialRequest): Observable<PostCreatePrinterCredentialResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.post<PostCreatePrinterCredentialResponse>(apiUrl, request);
  }

  PrinterCredentialById(request: GetPrinterCredentialByIdRequest): Observable<GetPrinterCredentialByIdResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.get<GetPrinterCredentialByIdResponse>(apiUrl, { params: request as any });
  }

  Delete(request: DeletePrinterCredentialRequest): Observable<DeletePrinterCredentialResponse>
  {
    const options = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      }),
      body: request
    };
    const apiUrl = this.serviceUrl;
    return this.http.delete<DeletePrinterCredentialResponse>(apiUrl, options);
  }

  Reset(request: PostResetPrinterCredentialRequest): Observable<PostResetPrinterCredentialResponse>
  {
    const apiUrl = this.serviceUrl + '/Reset';
    return this.http.post<PostResetPrinterCredentialResponse>(apiUrl, request);
  }

  Edit(request: PutPrinterCredentialRequest): Observable<PutPrinterCredentialResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.put<PutPrinterCredentialResponse>(apiUrl, request);
  }
}
