import { TenantWebAliasApplicationResponse } from "./get-tenant-web-alias-application.model";
import { TenantWebAliasManifestCategoryResponse } from "./get-tenant-web-alias-manifest-category.model";
import { TenantWebAliasManifestIconResponse } from "./get-tenant-web-alias-manifest-icon.model";
import { TenantWebAliasManifestScreenshotResponse } from "./get-tenant-web-alias-manifest-screenshot.model";

export class GetWebAliasDetailRequest
{
  TenantWebAliasID: number;
}

export class GetWebAliasDetailResponse
{
  TenantWebAliasID: number;
  TenantID: number;
  WebAliasID: number;
  WebAliasUrl: string;
  IsPrimary: boolean;
  AliasTypeID: number;

  //Manifest
  ManifestName: string;
  ManifestShortName: string;
  ManifestThemeColor: string;
  ManifestBackgroundColor: string;
  ManifestDisplay: displayEnum;
  ManifestScope: string;
  ManifestStartUrl: string;
  ManifestDescription: string;
  ManifestOrientation: orientationEnum;
  ManifestIconsZipFile: Blob;

  ManifestIcons: TenantWebAliasManifestIconResponse[];
  Applications: TenantWebAliasApplicationResponse[];
  Screenshots: TenantWebAliasManifestScreenshotResponse[];
  ManifestCategories: TenantWebAliasManifestCategoryResponse[];
}

export class ManifestCategory
{
  ManifestCategoryID: number;
  Name: string;
}

export enum displayEnum
{
  fullscreen = 'fullscreen',
  standalone = 'standalone',
  minimal_ui = 'minimal-ui',
  browser = 'browser'
}

export enum orientationEnum
{
  natural = 'natural',
  landscape = 'landscape',
  portrait = 'portrait'
}

export enum formFactorsEnum
{
  not_set = 'not_set',
  narrow = 'narrow',
  wide = 'wide'
}
