import { Component, Inject, OnDestroy, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { BreadCrumb } from '@tei/material';
import { SecureNavigation } from '@tei/material-smart-components';
import { Subscription } from 'rxjs';
import { IHostAdminCenterHelperService } from '../../@intermediate/host-admin-center-helper.interface';
import { ManagePortalAccessTabIdEnum } from '../../@intermediate/models/get-access.model';
import { portalGroupDetails } from '../../@intermediate/models/host-admin-center-helper.model';

@Component({
  selector: 'tei-admin-admin-center-access-navigation',
  templateUrl: './access-navigation.component.html',
  styleUrls: ['./access-navigation.component.scss']
})
export class AccessNavigationComponent implements OnInit, OnDestroy
{

  breadCrumbData: BreadCrumb[] = [];
  portalDetail: portalGroupDetails;
  subscription: Subscription = new Subscription();
  parentPortalGroupId: string;

  secureNavigation: SecureNavigation = {
    groupPermissionIdentifier: 'access',
    sections: [
      {
        title: '',
        items: [
          {
            permissionIdentifier: 'analytics',
            link: ManagePortalAccessTabIdEnum.Analytics,
            title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.ANALYTICS'),
            description: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.ANALYTICS_DESCRIPTION')
          },
          {
            permissionIdentifier: 'integrations',
            link: ManagePortalAccessTabIdEnum.Integrations,
            title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.INTEGRATION.TITLE'),
            description: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.INTEGRATION.INTEGRATION_DESCRIPTION')
          },
          {
            permissionIdentifier: 'products',
            link: ManagePortalAccessTabIdEnum.Products,
            title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.Products'),
            description: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.Products_DESCRIPTION')
          }
        ]
      }
    ]
  };

  constructor(private translate: TranslateService,
    @Inject('IHostAdminCenterHelperService') private hostAdminCenterHelperService: IHostAdminCenterHelperService,
  )
  {

  }

  ngOnInit(): void
  {
    this.subscription.add(this.hostAdminCenterHelperService.currentPortalGroupDetails.subscribe(portalDetail =>
    {
      if (portalDetail)
      {
        this.fillBreadCrump(portalDetail);
      }
    }));
  }

  fillBreadCrump(portalDetail): void
  {
    this.breadCrumbData = [
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.PORTAL_GROUPS.TITLE'),
        relativeUrl: '../../../',
      },
      {
        title: `${ portalDetail.parentPortalGroup.PortalGroup.PortalGroupName }`,
        relativeUrl: `../../`,
      },
      {
        title: `${ portalDetail.portal.PortalName }`,
        relativeUrl: '../',
      },
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.ACCESS'),
        relativeUrl: '',
      }
    ];
  }

  ngOnDestroy(): void
  {
    this.subscription?.unsubscribe();
  }
}
