import { FormGroupDirective, NgForm, UntypedFormArray, UntypedFormControl, UntypedFormGroup } from "@angular/forms";
import { ErrorStateMatcher } from "@angular/material/core";
import { Portal, Role, Site } from "./get-manage-role.model";

export class PostManageRoleRequest
{
  public UserID: number;
  public IsAdministrator: boolean;
  public PortalRoles: Array<PortalRolePostRequest> = [];
  public TenantIDs: Array<number> = [];
  public PortalGroupRoles: Array<number> = [];
  public UpdatedTenants: Array<number>;
  public Terminals: Array<number> = [];
  public DefaultIdentifiers: Array<DefaultIdentifier>;
}

export class DefaultIdentifier
{
  DefaultDeviceClientIdentifierID?: number;
  DefaultSiteID?: number;
  TenantID: number;
}

export class PostManageRoleResponse
{
}

export class PortalRolePostRequest
{
  public TenantID: number;
  public RoleIDs: Array<number>;
}

export class PostManageRoleRequestForm extends UntypedFormGroup
{
  public readonly PortalGroupRoles: RoleFormArray;
  public readonly PortalRoles: PortalRoleFormArray;

  constructor()
  {
    super({
      isAdministrator: new UntypedFormControl(0),
      PortalGroupRoles: new RoleFormArray(),
      PortalRoles: new PortalRoleFormArray()
    });

    this.PortalGroupRoles = this.get('PortalGroupRoles') as RoleFormArray;
    this.PortalRoles = this.get('PortalRoles') as PortalRoleFormArray;
  }
}

export class RoleFormArray extends UntypedFormArray
{
  constructor()
  {
    super([]);
  }

  public setValue(value: Role[]): void
  {
    this.clear();
    value.forEach(v =>
    {
      const Role = new RoleFormGroup(v);

      this.push(Role);
    });
  }
}

export class PortalRoleFormArray extends UntypedFormArray
{
  constructor()
  {
    super([]);
  }

  public setValue(value: Portal[])
  {
    this.clear();
    value.forEach(v =>
    {

      const portalRole = new PortalRoleFormGroup(v);
      portalRole.Roles.setValue(v.SpecificRoles);
      this.push(portalRole);
    });
  }

}

export class PortalRoleFormGroup extends UntypedFormGroup
{
  public readonly Roles: RoleFormArray;

  constructor(portal: Portal)
  {
    super({
      PortalID: new UntypedFormControl(portal.PortalID),
      PortalName: new UntypedFormControl(portal.PortalName),
      IsSelected: new UntypedFormControl(portal.IsSelected),
      Roles: new RoleFormArray(),
      RestaurantTerminals: new SiteTerminalFormArray(portal.Restaurants),
      SiteTerminals: new SiteTerminalFormArray(portal.ProShops),
      TenantID: new UntypedFormControl(portal.TenantID),
      DefaultDevice: new UntypedFormControl(),
      DefaultSiteID: new UntypedFormControl(),
      Updated: new UntypedFormControl(false)
    });

    this.Roles = this.get('Roles') as RoleFormArray;
  }

  public setValue(value: Portal)
  {
    this.patchValue({
      PortalID: value.PortalID,
      TenantID: value.TenantID,
    });

    this.Roles.clear();
    this.Roles.setValue(value.SpecificRoles);
  }
}

export class RoleFormGroup extends UntypedFormGroup
{
  constructor(role: Role)
  {
    super({
      RoleID: new UntypedFormControl(role.RoleID),
      RoleName: new UntypedFormControl(role.RoleName),
      IsSelected: new UntypedFormControl(role.IsSelected)
    });
  };
}

export class SiteTerminalFormArray extends UntypedFormArray
{
  constructor(sites: Site[])
  {
    super([]);
    sites?.forEach(site =>
    {
      site.Terminals.filter(t => t.TerminalIsSelected == true).forEach(terminal =>
      {
        this.push(new UntypedFormControl(terminal.TerminalID));
      });
    });
  }

  public setValue(sites: Site[]): void
  {
    this.clear();
    sites?.forEach(site =>
    {
      site.Terminals.filter(t => t.TerminalIsSelected == true).forEach(terminal =>
      {
        this.push(new UntypedFormControl(terminal.TerminalID));
      });
    });
  }
}

/** Error when invalid control is dirty, touched, or submitted. */
export class PortalRoleErrorStateMatcher implements ErrorStateMatcher
{
  isErrorState(control: UntypedFormControl | null, form: FormGroupDirective | NgForm | null): boolean
  {
    const isSubmitted = form && form.submitted;
    return !!(control && control.invalid && (control.dirty || control.touched || isSubmitted));
  }
}