import { Component, Inject, OnInit } from '@angular/core';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { ActivatedRoute } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ManageLoadingService, ResponsiveService } from '@tei/core';
import { BreadCrumb, CardDialogComponent, ConfirmationDialogComponent, DialogDataModel, SnackBarService } from '@tei/material';
import { Subscription } from 'rxjs';
import { IHostAdminCenterHelperService } from '../../@intermediate/host-admin-center-helper.interface';
import { DeletePaymentDeviceRequest, DeletePaymentDeviceResponse } from '../../@intermediate/models/delete-payment-device.model';
import { GetPaymentDevicesRequest, GetPaymentDevicesResponse, PaymentDevice, PaymentDeviceStatus, PaymentDevicesMetadata } from '../../@intermediate/models/get-payment-devices.model';
import { portalGroupDetails } from '../../@intermediate/models/host-admin-center-helper.model';
import { PostPaymentDeviceResetRequest, PostPaymentDeviceResetResponse } from '../../@intermediate/models/post-payment-device-reset.model';
import { IPaymentDevicesService } from '../../@intermediate/payment-devices.interface';
import { NewPaymentDeviceComponent } from './new-payment-device/new-payment-device.component';
import { PaymentDeviceSecretIdComponent } from './payment-device-secret-id/payment-device-secret-id.component';

@Component({
  selector: 'tei-admin-admin-center-payment-devices',
  templateUrl: './payment-devices.component.html',
  styleUrls: ['./payment-devices.component.scss']
})
export class PaymentDevicesComponent implements OnInit
{
  paymentDeviceStatus = PaymentDeviceStatus;
  metadata = new PaymentDevicesMetadata();
  dataSource: Array<PaymentDevice>;
  isMobileView: boolean = false;
  subscription = new Subscription();
  PortalId: number;
  totalCount = 100;
  pageIndex = 0;
  pageSize = 10;
  breadCrumbData: BreadCrumb[] = [];
  parentPortalGroupId: string;
  portalDetail: portalGroupDetails;

  constructor(
    private responsiveService: ResponsiveService,
    @Inject('IPaymentDevicesService') private paymentDevicesService: IPaymentDevicesService,
    @Inject('IHostAdminCenterHelperService') private hostAdminCenterHelperService: IHostAdminCenterHelperService,
    private dialog: MatDialog,
    private snackBarService: SnackBarService,
    public manageLoadingService: ManageLoadingService,
    private translate: TranslateService,
    private activatedRoute: ActivatedRoute
  ) { }

  ngOnInit(): void
  {
    this.subscription.add(this.activatedRoute.parent.params.subscribe(params =>
    {
      this.parentPortalGroupId = params['portalGroupId'];
      this.PortalId = Number(params['id']);
    }));
    this.getMobileSize();
    this.subscription.add(this.hostAdminCenterHelperService.currentPortalGroupDetails.subscribe(portalDetail =>
    {
      if (portalDetail)
      {
        this.portalDetail = {
          parentPortalGroup: portalDetail.parentPortalGroup.PortalGroup,
          portal: portalDetail.portal
        };
        this.fillBreadCrump();
        this.load(this.portalDetail.portal.PortalID);
      }
    }));
  }

  fillBreadCrump(): void
  {
    this.breadCrumbData = [];
    this.breadCrumbData = [
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.PORTAL_GROUPS.TITLE'),
        relativeUrl: '../../../../',
      },
      {
        title: `${ this.portalDetail.parentPortalGroup.PortalGroupName }`,
        relativeUrl: `../../../`,
      },
      {
        title: `${ this.portalDetail.portal.PortalName }`,
        relativeUrl: '../',
      },
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.PAYMENT_DEVICES'),
        relativeUrl: '',
      }
    ];
  }

  getMobileSize(): void
  {
    this.subscription.add(this.responsiveService.mediaBreakpoint.subscribe(mediaBreakpoint =>
    {
      this.isMobileView = mediaBreakpoint === 'xs' || mediaBreakpoint === 'sm' || mediaBreakpoint === 'md';
    }));
  }

  load(portalId: number): void
  {
    const request = new GetPaymentDevicesRequest();
    request.PortalID = portalId;
    request.PageNumber = this.pageIndex + 1;
    request.PageSize = this.pageSize;
    this.manageLoadingService.startLoading();
    this.paymentDevicesService.GetPaymentDevices(request).subscribe({
      next: (response: GetPaymentDevicesResponse) =>
      {
        this.dataSource = response.PaymentDevices;
        this.totalCount = response.TotalCount;
        this.manageLoadingService.finishLoading();
      },
      error: err => this.manageLoadingService.finishLoading(),
      complete: () => this.manageLoadingService.finishLoading(),
    });
  }

  newDevice(): void
  {
    const dialog = this.dialog.open(NewPaymentDeviceComponent, {
      panelClass: ['o-dialog-sm-fullscreen', 'o-dialog-md-fullscreen', 'new-payment-device'],
      width: '420px',
      data: {
        PortalID: this.PortalId,
      }
    });
    dialog.afterClosed().subscribe(result =>
    {
      if (result)
      {
        this.load(this.PortalId);
      }
    });
  }

  onDeleteDialog(deviceId: number): void
  {
    const dialogRef = this.dialog.open(CardDialogComponent, {
      panelClass: 'o-confirm-dialog',
      data: {
        component: ConfirmationDialogComponent,
        dialogData: {
          title: 'ADMIN_CENTER.PAYMENT_DEVICES.DELETE_DEVICE',
          content: 'ADMIN_CENTER.PAYMENT_DEVICES.DELETE_DEVICE_QUESTION',
          submitButtonLabel: 'ADMIN_CENTER.PAYMENT_DEVICES.DELETE',
          submitButtonColor: 'warn',
          cancelButtonLabel: 'ADMIN_CENTER.PAYMENT_DEVICES.CANCEL'
        } as DialogDataModel
      },
      width: '350px',
    });
    dialogRef.afterClosed().subscribe(result =>
    {
      if (result)
      {
        this.onDelete(deviceId);
      }
    });
  }

  onDelete(deviceId: number): void
  {
    const request: DeletePaymentDeviceRequest = {
      DeviceID: deviceId,
      PortalID: this.PortalId
    };
    this.manageLoadingService.startLoading();
    this.paymentDevicesService.Delete(request).subscribe({
      next: (response: DeletePaymentDeviceResponse) =>
      {
        this.manageLoadingService.finishLoading();
        this.snackBarService.showSuccess(this.translate.instant('ADMIN_CENTER.PAYMENT_DEVICES.DELETE_SUCCESSFULLY'), '', null, 'left');
        this.load(this.PortalId);
      },
      error: err => this.manageLoadingService.finishLoading(),
      complete: () => this.manageLoadingService.finishLoading()
    });
  }

  onResetDialog(deviceId: number): void
  {
    const dialogRef = this.dialog.open(CardDialogComponent, {
      panelClass: 'o-confirm-dialog',
      data: {
        component: ConfirmationDialogComponent,
        dialogData: {
          title: 'ADMIN_CENTER.PAYMENT_DEVICES.RESET_DEVICE',
          content: 'ADMIN_CENTER.PAYMENT_DEVICES.RESET_DEVICE_QUESTION',
          submitButtonLabel: 'ADMIN_CENTER.PAYMENT_DEVICES.RESET',
          submitButtonColor: 'warn',
          cancelButtonLabel: 'ADMIN_CENTER.PAYMENT_DEVICES.CANCEL'
        } as DialogDataModel
      },
      width: '350px',
    });
    dialogRef.afterClosed().subscribe(result =>
    {
      if (result)
      {
        this.onReset(deviceId);
      }
    });
  }

  onReset(deviceId: number): void
  {
    const request: PostPaymentDeviceResetRequest = {
      DeviceID: deviceId,
      PortalID: this.PortalId,
    };
    this.manageLoadingService.startLoading();
    this.paymentDevicesService.Reset(request).subscribe({
      next: (response: PostPaymentDeviceResetResponse) =>
      {
        this.manageLoadingService.finishLoading();
        this.openSecretId(response.SecretID);
      },
      error: err => this.manageLoadingService.finishLoading(),
      complete: () => this.manageLoadingService.finishLoading()
    });
  }

  openSecretId(secretId: string): void
  {
    const dialog = this.dialog.open(PaymentDeviceSecretIdComponent, {
      panelClass: ['o-dialog-sm-fullscreen', 'o-dialog-md-fullscreen', 'new-payment-device'],
      width: '420px',
      data: {
        SecretID: secretId,
        isMobile: this.isMobileView
      }
    });
    dialog.afterClosed().subscribe(result =>
    {
      if (result)
      {
        this.load(this.PortalId);
      }
    });
  }

  handlePaginationEvent(event): void
  {
    this.pageIndex = event.pageIndex;
    this.pageSize = event.pageSize;
    this.load(this.PortalId);
  }
}
