import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetPermissionRequest, GetPermissionResponse } from '../@intermediate/models/get-permission.model';
import { IPermissionService } from '../@intermediate/permission.interface';

@Injectable()
export class PermissionService implements IPermissionService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Security/UserManagement/Permission';
  }

  Get(request: GetPermissionRequest): Observable<GetPermissionResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.get<GetPermissionResponse>(apiUrl, { params: request as any });
  }

}
