import { Component, Inject, OnInit } from '@angular/core';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { ngFormGroupControlValueAccessorProviders } from '@tei/core';
import { GetManageRoleRequest, GetManageRoleResponse, Portal, Role } from '../../../../@intermediate/staff-user/models/get-manage-role.model';
import { PortalRoleFormGroup, PostManageRoleRequestForm } from '../../../../@intermediate/staff-user/models/post-manage-role.model';
import { IStaffUserService } from '../../../../@intermediate/staff-user/staff-user.interface';
import { GetUserByIDRequest, GetUserByIDResponse } from '../../../../@intermediate/user/models/get-user-by-user-id.model';
import { IUserService } from '../../../../@intermediate/user/user.interface';
import { StaffUserGeneralService } from './../../../../@services/staff-user-general.service';

@Component({
  selector: 'tei-admin-admin-center-manage-staff-user',
  templateUrl: './manage-staff-user.component.html',
  styleUrls: ['./manage-staff-user.component.scss'],
  providers: [ngFormGroupControlValueAccessorProviders(ManageStaffUserComponent)]
})
export class ManageStaffUserComponent implements OnInit
{
  isLoading: boolean = false;
  model: GetManageRoleResponse;
  userInfo: GetUserByIDResponse;
  form: PostManageRoleRequestForm;
  portalRoleForm: PortalRoleFormGroup;

  constructor(@Inject('IStaffUserService') private service: IStaffUserService,
    @Inject('IUserService') private userService: IUserService,
    @Inject(MAT_DIALOG_DATA) public userId: number,
    private staffUserGeneralService: StaffUserGeneralService,
    private dialogRef: MatDialogRef<ManageStaffUserComponent>) { }

  ngOnInit(): void
  {
    this.form = new PostManageRoleRequestForm();

    this.loadUserInfo();
    this.load();
  }

  loadUserInfo(): void
  {
    if (this.userInfo)
    {
      this.userInfo = new GetUserByIDResponse();
    }

    const request = new GetUserByIDRequest();
    request.UserID = this.userId;
    this.isLoading = true;
    this.userService.GetById(request).subscribe({
      next: (response: GetUserByIDResponse) =>
      {
        this.isLoading = false;
        this.userInfo = response;
        this.userInfo.FullName = this.staffUserGeneralService.getFullName(this.userInfo.FirstName, this.userInfo.LastName, this.userInfo.UserName);
      },
      error: () => this.isLoading = false
    });
  }

  load(): void
  {
    if (this.model)
    {
      this.model = new GetManageRoleResponse();
    }
    const request = new GetManageRoleRequest();
    request.UserID = this.userId;

    this.isLoading = true;
    this.service.GetManageRole(request).subscribe({
      next: (response: GetManageRoleResponse) =>
      {
        this.model = response;
        this.model.Portals.map(portal => portal.RestaurantsTreeData = this.staffUserGeneralService.fillRestaurantTreeData(portal));
        this.model.Portals.map(portal => portal.ProShopsTreeData = this.staffUserGeneralService.fillSiteTreeData(portal));
        this.isLoading = false;

        this.fillControls(response);
      },
      error: () => this.isLoading = false
    });
  }

  onClose(): void
  {
    this.dialogRef.close();
  }

  fillControls(model: GetManageRoleResponse): void
  {
    this.fillRoles(model.GlobalRoles);
    this.fillPortalRoles(model.Portals);

    this.form.patchValue({
      isAdministrator: this.model?.IsAdministrator
    });
  }

  fillRoles(role: Role[])
  {
    const cloneRole = JSON.parse(JSON.stringify(role.filter(r => r.IsSelected)));
    this.form.PortalGroupRoles.setValue(cloneRole);
  }

  fillPortalRoles(portal: Portal[])
  {
    const clonePortal = JSON.parse(JSON.stringify(portal));

    portal.forEach(element =>
    {
      const sr = element.SpecificRoles.filter(r => r.IsSelected);
      clonePortal.find(s => s.PortalID === element.PortalID).SpecificRoles = JSON.parse(JSON.stringify(sr));
    });

    clonePortal.forEach(portal =>
    {
      portal.Restaurants = [];
      portal.ProShops = [];
    });

    this.form.PortalRoles.setValue(clonePortal);

    this.portalRoleForm = this.form.PortalRoles[0];
  }
}
