import { ClipboardModule } from '@angular/cdk/clipboard';
import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatDividerModule } from '@angular/material/divider';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacyMenuModule as MatMenuModule } from '@angular/material/legacy-menu';
import { MatLegacyPaginatorModule as MatPaginatorModule } from '@angular/material/legacy-paginator';
import { MatLegacySelectModule as MatSelectModule } from '@angular/material/legacy-select';
import { MatLegacyTableModule as MatTableModule } from '@angular/material/legacy-table';
import { MatStepperModule } from '@angular/material/stepper';
import { ResponsiveTableHeightModule } from '@tei/core';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { BreadCrumbModule, LoadingModule, SnackBarModule } from '@tei/material';
import { PaymentDevicesService } from '../../@services/payment-devices.service';
import { NewPaymentDeviceComponent } from './new-payment-device/new-payment-device.component';
import { PaymentDeviceSecretIdComponent } from './payment-device-secret-id/payment-device-secret-id.component';
import { PaymentDevicesComponent } from './payment-devices.component';

const COMPONENTS = [PaymentDevicesComponent];
const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule];
const MATERIAL_MODULES = [MatTableModule, MatButtonModule, MatMenuModule, MatDividerModule, MatFormFieldModule, MatInputModule, MatStepperModule, MatSelectModule, ClipboardModule, MatPaginatorModule];
const TEI_MATERIAL_MODULES = [LoadingModule, SnackBarModule, BreadCrumbModule];
const TEI_CORE_MODULES = [TeiTextTranslatePipeModule, ResponsiveTableHeightModule];


@NgModule({
  declarations: [...COMPONENTS, NewPaymentDeviceComponent, PaymentDeviceSecretIdComponent],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_CORE_MODULES
  ],
  exports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_CORE_MODULES,
    ...COMPONENTS
  ],
  providers: [{ provide: 'IPaymentDevicesService', useClass: PaymentDevicesService }],
})
export class PaymentDevicesModule { }
