import { Component, Inject, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Subscription } from 'rxjs';
import { IHostAdminCenterHelperService } from '../../../@intermediate/host-admin-center-helper.interface';

@Component({
  selector: 'tei-host-admin-manage-portal',
  templateUrl: './manage-portal.component.html',
  styleUrls: ['./manage-portal.component.scss']
})
export class ManagePortalComponent implements OnInit
{
  subscription: Subscription = new Subscription();
  constructor(
    @Inject('IHostAdminCenterHelperService') private hostAdminCenterHelperService: IHostAdminCenterHelperService,
    private activatedRoute: ActivatedRoute,
  )
  {
  }
  ngOnInit(): void
  {
    this.subscription.add(this.activatedRoute.params.subscribe(params =>
    {
      this.hostAdminCenterHelperService.update(params['id'], params['portalGroupId']);
    }));
  }

}
