import { Injectable } from "@angular/core";
import { AppIdentifierService, CategoryEnum, EventTypeEnum, GenericEventModel, GenericEventService } from "@tei/core";
import { IAdminCenterService } from "../@intermediate/admin-cneter.interface";

@Injectable()
export class AdminCenterService implements IAdminCenterService
{
  constructor(
    private genericEventService: GenericEventService,
    private appIdentifierService: AppIdentifierService)
  { }

  ChangeCurrentModuleToAdminCenter(): void
  {
    const moduleName = 'admin-admin-center';

    this.setAppSession(moduleName);
  }

  setAppSession(moduleName: string): void
  {
    const appSession = this.appIdentifierService.getAppSession();
    appSession.appIdentifier = moduleName;
    this.appIdentifierService.setAppSession(appSession);

    this.genericEventService.onChangeGenericEvent({
      module: { Category: CategoryEnum.Pos, Name: moduleName },
      eventType: EventTypeEnum.onChangeCurrentModule,
      data: {
        currentModule: moduleName
      }
    } as GenericEventModel);
  }
}
