import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyCheckboxModule as MatCheckboxModule } from '@angular/material/legacy-checkbox';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacySelectModule as MatSelectModule } from '@angular/material/legacy-select';
import { MatLegacySlideToggleModule as MatSlideToggleModule } from '@angular/material/legacy-slide-toggle';
import { RouterModule } from '@angular/router';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { BreadCrumbModule, LoadingModule } from '@tei/material';
import { GeneralSettingsService } from '../../../@services/general-settings.service';
import { AdminCenterQuillContainerModule } from '../../../components/quill-container/quill-container.module';
import { GeneralSettingsComponent } from './general-settings.component';
const BASE_MODULES = [
  CommonModule,
  FormsModule,
  ReactiveFormsModule,

];
const MATERIAL_MODULES = [
  MatCheckboxModule,
  MatSelectModule,
  MatSlideToggleModule,
  MatInputModule
];
const TEI_MATERIAL_MODULES = [
  BreadCrumbModule,
  LoadingModule,
  AdminCenterQuillContainerModule
];
const TEI_CORE_MODULES = [TeiTextTranslatePipeModule];
const COMPONENTS = [GeneralSettingsComponent];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    RouterModule.forChild([
      { path: '', component: GeneralSettingsComponent },
    ]),
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_CORE_MODULES
  ],
  providers: [{ provide: 'IGeneralSettingsService', useClass: GeneralSettingsService }],
})
export class GeneralSettingsModule { }
