import { Component, EventEmitter, Inject, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { BreadCrumb } from '@tei/material';
import { SecureNavigation } from '@tei/material-smart-components';
import { Subscription } from 'rxjs';
import { IHostAdminCenterHelperService } from '../../../../@intermediate/host-admin-center-helper.interface';
import { ManagePortalTab, ManagePortalTabIdEnum } from '../../../../@intermediate/models/get-portal.model';
import { portalGroupDetails } from '../../../../@intermediate/models/host-admin-center-helper.model';

@Component({
  selector: 'tei-admin-center-manage-portal-tab',
  templateUrl: './manage-portal-tab.component.html',
  styleUrls: ['./manage-portal-tab.component.scss']
})
export class ManagePortalTabComponent implements OnInit, OnDestroy
{
  @Input() tabs: ManagePortalTab[] = [];
  @Input() selectedTab: ManagePortalTab;
  @Output() selectedTabChanged = new EventEmitter<ManagePortalTab>();
  portalId: string;
  parentPortalGroupId: string;
  portalDetail: portalGroupDetails;
  ManagePortalTabIdEnum = ManagePortalTabIdEnum;
  breadCrumbData: BreadCrumb[] = [];

  secureNavigation: SecureNavigation = {
    groupPermissionIdentifier: 'portal',
    sections: [
      {
        title: '',
        items: [
          {
            permissionIdentifier: 'portal-details',
            link: ManagePortalTabIdEnum.Settings,
            title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.PORTAL_DETAILS'),
            description: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.PORTAL_DETAILS_DESCRIPTION')
          },
          {
            permissionIdentifier: 'access',
            link: ManagePortalTabIdEnum.Access,
            title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.ACCESS'),
            description: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.ACCESS_DESCRIPTION')
          },
          {
            permissionIdentifier: 'advanced-settings',
            link: ManagePortalTabIdEnum.AdvancedSettings,
            title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.ADVANCED_SETTINGS'),
            description: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.ADVANCED_SETTINGS_DESCRIPTION')
          },
          {
            permissionIdentifier: 'aliases',
            link: ManagePortalTabIdEnum.Aliases,
            title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.ALIASES'),
            description: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.ALIASES_DESCRIPTION')
          },
          {
            permissionIdentifier: 'authentication',
            link: ManagePortalTabIdEnum.Authentication,
            title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.AUTHENTICATION'),
            description: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.AUTHENTICATION_DESCRIPTION')
          },

          {
            permissionIdentifier: 'payment-devices',
            link: ManagePortalTabIdEnum.PaymentDevice,
            title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.PAYMENT_DEVICES'),
            description: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.PAYMENT_DEVICES_DESCRIPTION')
          },
          {
            permissionIdentifier: 'payment-settings',
            link: ManagePortalTabIdEnum.PaymentSettings,
            title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.PAYMENT_SETTINGS'),
            description: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.PAYMENT_SETTINGS_DESCRIPTION')
          },
          {
            permissionIdentifier: 'portal-setup-jobs',
            link: ManagePortalTabIdEnum.PortalSetupJobs,
            title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.SETUP_JOBS.TITLE'),
            description: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.SETUP_JOBS.DESCRIPTION')
          },
          {
            permissionIdentifier: 'email-settings',
            link: ManagePortalTabIdEnum.EmailSettings,
            title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.EMAIL_SETTINGS.TITLE'),
            description: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.EMAIL_SETTINGS.EMAIL_SETTINGS_DESCRIPTION')
          }
        ]
      }
    ]
  };

  subscription: Subscription = new Subscription();
  constructor(
    @Inject('IHostAdminCenterHelperService') private hostAdminCenterHelperService: IHostAdminCenterHelperService,
    private translate: TranslateService,
    private activatedRoute: ActivatedRoute,
  )
  {
  }

  ngOnInit(): void
  {
    this.subscription.add(this.hostAdminCenterHelperService.currentPortalGroupDetails.subscribe(portalDetail =>
    {
      if (portalDetail)
      {
        this.fillBreadCrump(portalDetail);
      }
    }));
  }

  fillBreadCrump(portalDetail): void
  {
    this.selectedTab = undefined;
    this.breadCrumbData = [];
    this.breadCrumbData = [
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.PORTAL_GROUPS.TITLE'),
        relativeUrl: '../../../',
      },
      {
        title: `${ portalDetail.parentPortalGroup.PortalGroup.PortalGroupName }`,
        relativeUrl: `../../`,
      },
      {
        title: `${ portalDetail.portal.PortalName }`,
        relativeUrl: '../',
      }
    ];
  }
  ngOnDestroy(): void
  {
    this.subscription?.unsubscribe();
  }
}
