import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetMyProfileSetupRequest, GetMyProfileSetupResponse } from '../@intermediate/portal-setup-jobs/models/get-my-profile-setup.model';
import { PostMyProfileSetupRequest, PostMyProfileSetupResponse } from '../@intermediate/portal-setup-jobs/models/post-my-profile-setup.model';
import { IPortalSetupJobsService } from '../@intermediate/portal-setup-jobs/portal-setup-jobs.interface';
import { GetEcommerceSetupRequest, GetEcommerceSetupResponse } from '../@intermediate/portal-setup-jobs/models/get-ecommerce-setup.model';
import { PostEcommerceSetupRequest, PostEcommerceSetupResponse } from '../@intermediate/portal-setup-jobs/models/post-ecommerce-setup.model';


@Injectable({
  providedIn: 'root'
})
export class PortalSetupJobsService implements IPortalSetupJobsService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Admin/AdminCenter/PortalSetupJobs';
  }

  GetMyProfileSetup(request: GetMyProfileSetupRequest): Observable<GetMyProfileSetupResponse>
  {
    const apiUrl = this.serviceUrl + '/MyProfileSetup';
    return this.http.get<GetMyProfileSetupResponse>(apiUrl, { params: request as any });
  }

  PostMyProfileSetup(request: PostMyProfileSetupRequest): Observable<PostMyProfileSetupResponse>
  {
    const apiUrl = this.serviceUrl + '/MyProfileSetup';
    return this.http.post<PostMyProfileSetupResponse>(apiUrl, request);
  }

  GetEcommerceSetup(request: GetEcommerceSetupRequest): Observable<GetEcommerceSetupResponse>
  {
    const apiUrl = this.serviceUrl + '/EcommerceSetup';
    return this.http.get<GetEcommerceSetupResponse>(apiUrl, { params: request as any });
  }

  PostEcommerceSetup(request: PostEcommerceSetupRequest): Observable<PostEcommerceSetupResponse>
  {
    const apiUrl = this.serviceUrl + '/EcommerceSetup';
    return this.http.post<PostEcommerceSetupResponse>(apiUrl, request);
  }
}

