import { Component, Inject, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { BreadCrumb, SnackBarService } from '@tei/material';
import { IAdminPortalService } from '../../../../../@intermediate/admin-portal.interface';
import { GetPortalGroupByIDRequest } from '../../../../../@intermediate/portal-group/models/get-portal-group-by-id.model';
import { IPortalGroupsService } from '../../../../../@intermediate/portal-group/portal-groups.interface';
import { CreatePortalForm, PostPortalRequest } from '../../../../../@intermediate/portal/post-portal.model';

@Component({
  selector: 'tei-host-admin-add-edit-portal-dialogue',
  templateUrl: './add-edit-portal.component.html',
  styleUrls: ['./add-edit-portal.component.scss']
})
export class AddEditPortalDialogComponent implements OnInit
{
  breadCrumbData: BreadCrumb[] = [];
  isLoading;
  form: CreatePortalForm;

  constructor(
    @Inject('IAdminPortalService') private hostAdminPortalService: IAdminPortalService,
    @Inject('IPortalGroupsService') private hostAdminPortalGroupsService: IPortalGroupsService,
    private translate: TranslateService,
    private snackBarService: SnackBarService,
    private router: Router,
    private activatedRoute: ActivatedRoute
  )
  {
  }

  ngOnInit()
  {
    this.form = new CreatePortalForm(Number(this.activatedRoute.snapshot.params.portalGroupId));

    const request: GetPortalGroupByIDRequest = {
      PortalGroupID: this.activatedRoute.snapshot.params.portalGroupId
    };

    this.hostAdminPortalGroupsService.GetByID(request).subscribe(res =>
    {
      this.breadCrumbData = [
        {
          title: this.translate.instant('ADMIN_CENTER.GROUP.PORTAL_GROUPS.TITLE'),
          relativeUrl: '../../../'
        },
        {
          title: `${ res.PortalGroup?.PortalGroupName }`,
          relativeUrl: '../../'
        },
        {
          title: this.translate.instant("ADMIN_CENTER.GROUP.SETTINGS.PORTALS"),
          relativeUrl: '../'
        },
        {
          title: this.translate.instant('ADMIN_CENTER.GROUP.NEW_PORTAL.NEW_PORTAL'),
          relativeUrl: ''
        }
      ];
    });


  }

  onSave()
  {
    const request: PostPortalRequest = this.form.value;

    this.hostAdminPortalService.Post(request).subscribe({
      next: () =>
      {
        this.snackBarService.showSuccess(this.translate.instant("ADMIN_CENTER.GROUP.NEW_PORTAL.SAVED_SUCCESSFULLY"));
        this.router.navigate(["../"], { relativeTo: this.activatedRoute });
      }
    });
  }
}
