import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { BreadCrumbModule, SelectChipModule, SettingsListModule } from '@tei/material';
import { HostAdminCenterHelperService } from '../../../@services/host-admin-center-helper.service';
import { PortalGroupsService } from '../../../@services/portal-groups.service';
import { PortalService } from '../../../@services/portal.service';
import { AuthenticationSettingModule } from '../../../components/authentication-setting/authentication-setting.module';
import { IntegrationsModule } from '../../integrations/integrations.module';
import { PaymentDevicesModule } from '../../payment-devices/payment-devices.module';
import { PaymentSettingsModule } from '../../payment-settings/payment-settings.module';
import { PortalSetupJobsModule } from '../../portal-setup-jobs/portal-setup-jobs.module';
import { PWAAppSetupModule } from "../../pwa-app-setup/pwa-app-setup.module";
import { ManagePortalGroupAliasesModule } from '../manage-portal-group/manage-portal-group-aliases/manage-portal-group-aliases.module';
import { ManagePortalProductsModule } from '../portal-group/manage-portal-products/manage-portal-products.module';
import { ManagePortalAdvancedSettingsModule } from './advanced-settings/manage-portal-advanced-settings.module';
import { ManagePortalRoutingModule } from './manage-portal-routing.module';
import { ManagePortalSettingsModule } from './manage-portal-settings/manage-portal-settings.module';
import { ManagePortalTABModule } from './manage-portal-tab/manage-portal-tab.module';
import { ManagePortalComponent } from './manage-portal.component';
import { EmailSettingsModule } from './email-settings/email-settings.module';


const BASE_MODULES = [CommonModule, RouterModule];
const TEI_MATERIAL_MODULES = [BreadCrumbModule, TeiTextTranslatePipeModule, SelectChipModule, SettingsListModule];
const ADMIN_CENTER_MODULES = [
  ManagePortalSettingsModule,
  ManagePortalAdvancedSettingsModule,
  ManagePortalGroupAliasesModule,
  ManagePortalProductsModule,
  ManagePortalTABModule,
  AuthenticationSettingModule,
  PaymentDevicesModule,
  PaymentSettingsModule,
  PWAAppSetupModule,
  PortalSetupJobsModule,
  IntegrationsModule,
  EmailSettingsModule
];
const ROUTING_MODULES = [ManagePortalRoutingModule];

@NgModule({
  declarations: [ManagePortalComponent],
  imports: [
    ...ROUTING_MODULES,
    ...BASE_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...ADMIN_CENTER_MODULES
  ],
  providers: [
    { provide: 'IPortalGroupsService', useClass: PortalGroupsService },
    { provide: 'IPortalService', useClass: PortalService },
    { provide: 'IHostAdminCenterHelperService', useClass: HostAdminCenterHelperService },
  ],
})
export class ManagePortalModule { }
