import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { IAdminPortalGroupService } from '../@intermediate/portal-group/admin-portal-groups.interface';
import { GetPortalGroupDetailRequest, GetPortalGroupDetailResponse } from '../@intermediate/portal-group/models/get-portal-group-detail.model';
import { PostPortalGroupDetailResponse } from '../@intermediate/portal-group/models/post-portal-group-detail.model';
import { PostPortalGroupSettingsResetRequest, PostPortalGroupSettingsResetResponse } from '../@intermediate/portal-group/models/post-portal-group-settings-reset.model';
import { PostPortalGroupsRequest, PostPortalGroupsResponse } from '../@intermediate/portal-group/models/post-portal-groups.model';
import { PutPortalGroupsRequest, PutPortalGroupsResponse } from '../@intermediate/portal-group/models/put-portal-groups.model';


@Injectable({
  providedIn: 'root'
})
export class AdminPortalGroupService implements IAdminPortalGroupService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Admin/AdminCenter/PortalGroup';
  }

  Post(request: PostPortalGroupsRequest): Observable<PostPortalGroupsResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.post<PostPortalGroupsResponse>(apiUrl, request);
  }

  Put(request: PutPortalGroupsRequest): Observable<PutPortalGroupsResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.put<PutPortalGroupsResponse>(apiUrl, request);
  }

  Detail(request: GetPortalGroupDetailRequest): Observable<GetPortalGroupDetailResponse>
  {
    const apiUrl = this.serviceUrl + '/Detail';
    return this.http.get<GetPortalGroupDetailResponse>(apiUrl, { params: request as any });
  }

  UpdateDetail(request: FormData): Observable<PostPortalGroupDetailResponse>
  {
    const apiUrl = this.serviceUrl + '/UpdateDetails';
    return this.http.post<PostPortalGroupDetailResponse>(apiUrl, request);
  }

  ResetSetting(request: PostPortalGroupSettingsResetRequest): Observable<PostPortalGroupSettingsResetResponse>
  {
    const apiUrl = this.serviceUrl + '/ResetSetting';
    return this.http.post<PostPortalGroupSettingsResetResponse>(apiUrl, request);
  }
}

