import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetPortalByCurrentTenantRequest, GetPortalByCurrentTenantResponse } from '../@intermediate/models/get-portal-by-current-tenant.model';
import { GetPortalDetailRequest, GetPortalRequest, GetPortalResponse, PortalDetail } from '../@intermediate/models/get-portal.model';
import { PutPortalRequest, PutPortalResponse } from '../@intermediate/models/put-portal.model';
import { IPortalService } from '../@intermediate/portal.interface';
import { PostPortalRequest, PostPortalResponse } from '../@intermediate/portal/post-portal.model';


@Injectable({
  providedIn: 'root'
})
export class PortalService implements IPortalService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Portal';
  }


  GetByID(request: GetPortalDetailRequest): Observable<PortalDetail>
  {
    const apiUrl = `${ this.serviceUrl }/${ request.PortalID }`;
    return this.http.get<PortalDetail>(apiUrl);
  }

  Get(request: GetPortalRequest): Observable<GetPortalResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.get<GetPortalResponse>(apiUrl, { params: request as any });
  }

  Post(request: PostPortalRequest): Observable<PostPortalResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.post<PostPortalResponse>(apiUrl, request);
  }
  Put(request: PutPortalRequest): Observable<PutPortalResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.put<PutPortalResponse>(apiUrl, request);
  }
  GetByCurrentTenant(request: GetPortalByCurrentTenantRequest): Observable<GetPortalByCurrentTenantResponse>
  {
    const apiUrl = this.serviceUrl + '/GetPortalByCurrentTenant';
    return this.http.get<GetPortalByCurrentTenantResponse>(apiUrl, { params: request as any });
  }
}

