import { Component, EventEmitter, Inject, Input, OnInit, Output } from '@angular/core';
import { UntypedFormBuilder, UntypedFormGroup } from '@angular/forms';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';

@Component({
  selector: 'tei-admin-admin-center-payment-device-secret-id',
  templateUrl: './payment-device-secret-id.component.html',
  styleUrls: ['./payment-device-secret-id.component.scss']
})
export class PaymentDeviceSecretIdComponent implements OnInit
{

  secretID: string;
  isMobileView = false;
  form: UntypedFormGroup;
  @Output() close = new EventEmitter();
  @Input() set secretId(value: string)
  {
    if (value)
    {
      this.form.patchValue({
        secretID: value
      });
    }
  };

  @Input() set isMobile(value: boolean)
  {
    this.isMobileView = value;
  };

  constructor(@Inject(MAT_DIALOG_DATA) public data: { SecretID: string, isMobile: boolean; }, private dialogRef: MatDialogRef<PaymentDeviceSecretIdComponent>, private fb: UntypedFormBuilder) { }

  ngOnInit(): void
  {
    this.createForm();
    if (this.data)
    {
      this.form.patchValue({
        secretID: this.data.SecretID
      });
    }
  }

  get formControls()
  {
    return this.form.controls;
  }

  createForm(): void
  {
    this.form = this.fb.group({
      secretID: [{ value: '', disabled: true }]
    });
  }

  onClose(): void
  {
    this.data ? this.dialogRef.close(true) : this.close.emit(true);
  }

}
