import { COMMA, ENTER } from '@angular/cdk/keycodes';
import { ChangeDetectorRef, Component, ElementRef, EventEmitter, Inject, Input, OnChanges, OnDestroy, OnInit, Output, SimpleChanges, ViewChild } from '@angular/core';
import { UntypedFormArray } from '@angular/forms';
import { MatLegacyAutocompleteSelectedEvent as MatAutocompleteSelectedEvent } from '@angular/material/legacy-autocomplete';
import { MatLegacyChipInputEvent as MatChipInputEvent } from '@angular/material/legacy-chips';
import { MatStepper } from '@angular/material/stepper';
import { TranslateService } from '@ngx-translate/core';
import { ngFormGroupControlValueAccessorProviders } from '@tei/core';
import { SnackBarService } from '@tei/material';
import { Observable, Subscription, map, startWith } from 'rxjs';
import { Site } from '../../../../../@intermediate/staff-user/models/get-invite-user.model';
import { GetManageRoleResponse, Portal, Role } from '../../../../../@intermediate/staff-user/models/get-manage-role.model';
import { GetPortalSettingRequest, GetPortalSettingResponse, PaymentDevice, SiteType } from '../../../../../@intermediate/staff-user/models/get-portal-setting.model';
import { PortalRoleErrorStateMatcher, PortalRoleFormArray, PortalRoleFormGroup, PostManageRoleRequest, PostManageRoleRequestForm, RoleFormArray, RoleFormGroup } from '../../../../../@intermediate/staff-user/models/post-manage-role.model';
import { IStaffUserService } from '../../../../../@intermediate/staff-user/staff-user.interface';
import { ItemNode } from '../../../../../components/tree/tree.model';

@Component({
  selector: 'tei-admin-admin-center-access-portal-user-finalize',
  templateUrl: './access-portal-user-finalize.component.html',
  styleUrls: ['./access-portal-user-finalize.component.scss'],
  providers: [ngFormGroupControlValueAccessorProviders(AccessPortalUserFinalizeComponent)]
})
export class AccessPortalUserFinalizeComponent implements OnInit, OnChanges, OnDestroy
{
  isLoading: boolean = false;
  @Input() stepper: MatStepper;
  @Input() selectedTenantID: number;
  @Input() userID: number;
  @Input() model: GetManageRoleResponse;
  @Input() form: PostManageRoleRequestForm;
  @Input() portalRoleForm: PortalRoleFormGroup;
  @Output() closeEmitter = new EventEmitter();
  matcher = new PortalRoleErrorStateMatcher();
  restaurantsTreeData: Array<ItemNode> = [];
  proShopsTreeData: Array<ItemNode> = [];
  portal: Portal;
  treeData: Array<ItemNode> = [];
  restaurants: Array<Site>;
  proShops: Array<Site>;
  allFilteredSites: Array<Site> = [];
  paymentDevices: Array<PaymentDevice>;
  subscription = new Subscription();
  readonly separatorKeysCodes = [ENTER, COMMA] as const;
  selectable = true;
  removable = true;
  filteredRoles: Array<Role> = [];
  filteredPaymentDeviceOptions: Observable<PaymentDevice[]>;
  filteredDeviceOptions: PaymentDevice[];
  dataModel = new Map<number, GetPortalSettingResponse>();
  @ViewChild('roleInput') roleInput: ElementRef<HTMLInputElement>;

  constructor(private snackBarService: SnackBarService,
    protected changeDetector: ChangeDetectorRef
    , private translate: TranslateService
    , @Inject('IStaffUserService') private service: IStaffUserService) { }

  ngOnInit(): void
  {
    this.handleDefaultDeviceChange();
    this.handleTerminalsChange();
  }

  private handleDefaultDeviceChange()
  {
    this.subscription.add(this.portalRoleForm.controls.DefaultDevice.valueChanges.subscribe(value =>
    {
      if (!value.DeviceClientIdentifierID)
      {
        this.portalRoleForm.controls.DefaultSiteID.setValue(null);
        this.portalRoleForm.controls.DefaultSiteID.disable();
      }
      else
      {
        this.portalRoleForm.controls.DefaultSiteID.enable();
      }
    }));
  }

  private handleTerminalsChange()
  {
    this.subscription.add(this.portalRoleForm.controls.RestaurantTerminals.valueChanges.subscribe(terminalId =>
    {
      this.updateAllFilteredSites();
    }));
    this.subscription.add(this.portalRoleForm.controls.SiteTerminals.valueChanges.subscribe(terminalId =>
    {
      this.updateAllFilteredSites();
    }));
  }

  private updateAllFilteredSites()
  {
    this.allFilteredSites = this.getFilterSites(this.portalRoleForm.controls.RestaurantTerminals.value, this.restaurants);
    this.allFilteredSites = this.allFilteredSites.concat(this.getFilterSites(this.portalRoleForm.controls.SiteTerminals.value, this.proShops));

    if (!this.allFilteredSites.some(a => a.SiteName === '-- None --'))
    {
      this.allFilteredSites.unshift({
        SiteID: null,
        SiteIsSelected: false,
        SiteName: '-- None --',
        Terminals: [],
        DefaultSelected: false,
      });
    }

    if (!this.allFilteredSites.some(site => site.SiteID === this.portalRoleForm.controls.DefaultSiteID.value))
    {
      this.portalRoleForm.controls.DefaultSiteID.setValue(null);
    }

  }

  getFilterSites(terminals: Array<number>, sites: Array<Site>): Array<Site>
  {
    return sites.filter(site => site.Terminals.some(terminal => terminals.includes(parseInt(terminal.TerminalID))));
  }

  ngOnChanges(changes: SimpleChanges): void
  {
    if (changes.selectedTenantID)
    {
      this.portal = this.model?.Portals?.find(p => p.TenantID === this.selectedTenantID);
      this.getPortalSetting(this.selectedTenantID);
    }
  }

  getPortalSetting(tenantId: number): void
  {
    if (this.dataModel.has(tenantId))
    {
      this.UpdateDataPerTenant(this.dataModel.get(tenantId), false);
    }
    else
    {
      const request: GetPortalSettingRequest = {
        TenantId: tenantId,
        UserID: this.userID
      };
      this.isLoading = true;
      this.service.PortalSetting(request).subscribe({
        next: (response: GetPortalSettingResponse) =>
        {
          this.isLoading = false;
          this.UpdateDataPerTenant(response, true);
          this.dataModel.set(tenantId, response);
        },
        error: err => this.isLoading = false,
        complete: () => this.isLoading = false,
      });
    }
  }

  onAutocompleteOpened(): void
  {
    this.filteredDeviceOptions = this.paymentDevices?.filter(item =>
      this.portalRoleForm?.get('DefaultDevice')?.value?.DeviceClientIdentifierID !== item.DeviceClientIdentifierID);
    this.changeDetector.detectChanges();
  }

  private UpdateDataPerTenant(response: GetPortalSettingResponse, updateForm: boolean)
  {
    this.restaurantsTreeData = this.fillRestaurantTreeData(response);
    this.proShopsTreeData = this.fillProShopTreeData(response);
    this.restaurants = response.Restaurants;
    this.proShops = response.ProShops;
    this.paymentDevices = response.PaymentDevices;

    if (!this.paymentDevices.some(a => a.DeviceDescription === '-- None --'))
    {
      this.paymentDevices.unshift({
        DefaultSelected: false,
        DeviceClientIdentifierID: null,
        DeviceDescription: '-- None --'
      });
    }

    this.filteredDeviceOptions = JSON.parse(JSON.stringify(this.paymentDevices));

    this.allFilteredSites = this.restaurants.filter(site => site.SiteIsSelected).concat(this.proShops.filter(site => site.SiteIsSelected));

    if (!this.allFilteredSites.some(a => a.SiteName === '-- None --'))
    {
      this.allFilteredSites.unshift({
        SiteID: null,
        SiteIsSelected: false,
        SiteName: '-- None --',
        Terminals: [],
        DefaultSelected: false,
      });
    }

    if (updateForm)
    {
      this.portalRoleForm.get("RestaurantTerminals").setValue(response.Restaurants);
      this.portalRoleForm.get("Updated").setValue(true);
      this.portalRoleForm.get("SiteTerminals").setValue(response.ProShops);
      const defaultIdentifier = this.model.DefaultIdentifiers.find(a => a.TenantID === this.selectedTenantID);
      if (defaultIdentifier)
      {
        const foundDevice = this.paymentDevices.find(device => device.DeviceClientIdentifierID === defaultIdentifier.DefaultDeviceClientIdentifierID);
        this.portalRoleForm.controls.DefaultDevice.setValue(foundDevice);
        this.portalRoleForm.controls.DefaultSiteID.setValue(defaultIdentifier.DefaultSiteID);
      }
      else
      {
        const foundDevice = this.paymentDevices.find(device => device.DeviceClientIdentifierID === null);
        this.portalRoleForm.controls.DefaultDevice.setValue(foundDevice);
        this.portalRoleForm.controls.DefaultSiteID.setValue(null);
        this.portalRoleForm.controls.DefaultSiteID.disable();
      }
    }

    this.filteredPaymentDeviceOptions = this.portalRoleForm.controls.DefaultDevice.valueChanges.pipe(
      startWith(''),
      map(value =>
      {
        const name = typeof value === 'string' ? value : value?.DeviceDescription;
        return name ? this._paymentFilter(name as string) : this.paymentDevices.slice();
      })
    );

    this.changeDetector.detectChanges();
  }

  private _paymentFilter(name: string): PaymentDevice[]
  {
    const filterValue = name.toLowerCase();

    return this.paymentDevices.filter(device => device.DeviceDescription.toLowerCase().includes(filterValue));
  }

  displayPaymentDevice(paymentDevice: PaymentDevice): string
  {
    return paymentDevice?.DeviceDescription ?? '';
  }

  fillRestaurantTreeData(portalSetting: GetPortalSettingResponse): Array<ItemNode>
  {
    let treeData = [];
    if (portalSetting?.Restaurants?.length > 0)
    {
      let restaurantItemNode = new ItemNode();
      restaurantItemNode.Item = {
        Id: SiteType.Restaurant,
        Key: "Restaurants",
        Title: this.translate.instant("ADMIN_CENTER.INVITE_USERS_INITIATE.RESTAURANTS"),
        IsSelected: false
      };

      portalSetting?.Restaurants?.forEach(item =>
      {
        restaurantItemNode.Children.push(
          {
            Item: {
              Id: `${SiteType.Restaurant}_${item.SiteID.toString()}`,
              Key: item.SiteID.toString(),
              Title: item.SiteName,
              IsSelected: false,
            },
            Children: []
          });

        var insertedItem = restaurantItemNode.Children[restaurantItemNode.Children.length - 1];
        item.Terminals.forEach(terminal =>
        {
          insertedItem.Children.push(
            {
              Item: {
                Id: terminal.TerminalID,
                Key: terminal.TerminalID,
                Title: terminal.Description,
                IsSelected: terminal.TerminalIsSelected
              },
              Children: []
            });
        });
      });

      treeData.push(restaurantItemNode);
    }
    return treeData;
  }

  fillProShopTreeData(portalSetting: GetPortalSettingResponse): Array<ItemNode>
  {
    let treeData = [];

    if (portalSetting?.ProShops?.length > 0)
    {
      let retailItemNode = new ItemNode();

      retailItemNode.Item = {
        Id: SiteType.ProShop,
        Key: "Retails",
        Title: this.translate.instant("ADMIN_CENTER.INVITE_USERS_INITIATE.RETAILS"),
        IsSelected: false
      };

      portalSetting?.ProShops?.forEach(item =>
      {
        retailItemNode.Children.push(
          {
            Item: {
              Id: `${SiteType.ProShop}_${item.SiteID.toString()}`,
              Key: item.SiteID.toString(),
              Title: item.SiteName,
              IsSelected: false,
            },
            Children: []
          });

        var insertedItem = retailItemNode.Children[retailItemNode.Children.length - 1];
        item.Terminals.forEach(terminal =>
        {
          insertedItem.Children.push(
            {
              Item: {
                Id: terminal.TerminalID,
                Key: terminal.TerminalID,
                Title: terminal.Description,
                IsSelected: terminal.TerminalIsSelected
              },
              Children: []
            });
        });
      });
      treeData.push(retailItemNode);
    }
    return treeData;
  }

  addRole(event: MatChipInputEvent): void
  {
    const selectedRole = this.portal.SpecificRoles.find(pr => pr.RoleID === Number(event.value));

    this.currentPortalRoles.push(new RoleFormGroup(selectedRole));

    // event.chipInput?.clear();
    event.value = '';
  }

  removeRole(role: PortalRoleFormGroup, i: number): void
  {
    this.currentPortalRoles.removeAt(i);
  }

  selectedRole(event: MatAutocompleteSelectedEvent): void
  {
    const selectedRole = this.portal.SpecificRoles.find(pr => pr.RoleID === Number(event.option.value));

    this.currentPortalRoles.push(new RoleFormGroup(selectedRole));
  }

  get selectedRoles(): Array<Role>
  {
    return this.portal?.SpecificRoles?.filter(r => r.IsSelected);
  }

  get formControls()
  {
    return this.form?.controls;
  }

  onBack(): void
  {
    this.updateDefaultIdentifiersModel();
    this.stepper.previous();
  }

  get currentPortalRoles(): RoleFormArray
  {
    return this.portalRoleForm?.controls?.Roles as UntypedFormArray;
  }

  get portalRoles(): PortalRoleFormArray
  {
    return this.form.controls.PortalRoles as UntypedFormArray;
  }

  deselectedRoles(): Array<Role>
  {
    return this.portal?.SpecificRoles.filter(role => this.portalRoleForm.controls.Roles.value.findIndex(gr => gr.RoleID == role.RoleID) < 0);
  }

  onSave(): void
  {
    this.updateDefaultIdentifiersModel();

    const isAdministrator = this.formControls?.isAdministrator?.value;
    const request = new PostManageRoleRequest();
    request.UserID = this.userID;
    request.IsAdministrator = isAdministrator;
    request.TenantIDs = this.form.controls.PortalRoles.value.filter(p => p.IsSelected)?.map(portal => portal.TenantID);
    request.PortalRoles = this.form.controls.PortalRoles.value?.filter(p => p.IsSelected).map(pr =>
    ({
      TenantID: pr.TenantID,
      RoleIDs: pr.Roles?.map(r => r.RoleID)
    }));
    request.DefaultIdentifiers = this.model.DefaultIdentifiers;

    if (!isAdministrator)
    {
      request.PortalGroupRoles = this.form.controls.PortalGroupRoles.value?.map(role => role.RoleID);
      this.portalRoles.controls.forEach(portal =>
      {
        portal.get('RestaurantTerminals').value.forEach(restaurant =>
        {
          request.Terminals.push(restaurant);
        });

        portal.get('SiteTerminals').value.forEach(site =>
        {
          request.Terminals.push(site);
        });
      });
    }

    request.UpdatedTenants = this.form.controls.PortalRoles.value?.filter(p => p.Updated)
      .map(pr => pr.TenantID);

    this.callManageRoleService(request);
  }

  updateDefaultIdentifiersModel()
  {
    const deviceID = this.portalRoleForm.value.DefaultDevice?.DeviceClientIdentifierID;
    const siteID = this.portalRoleForm.value.DefaultSiteID;
    if (deviceID)
    {
      const device = this.model.DefaultIdentifiers.find(device => device.TenantID === this.selectedTenantID);
      if (device)
      {
        device.DefaultDeviceClientIdentifierID = deviceID;
        device.DefaultSiteID = siteID;
      } else
      {
        this.model.DefaultIdentifiers.push({
          DefaultDeviceClientIdentifierID: deviceID,
          TenantID: this.selectedTenantID,
          DefaultSiteID: siteID
        });
      }
    } else
    {
      this.model.DefaultIdentifiers = this.model.DefaultIdentifiers.filter(a => a.TenantID != this.selectedTenantID);
    }
  }

  get siteTerminals(): UntypedFormArray
  {
    return this.portalRoleForm.get("SiteTerminals") as UntypedFormArray;
  }

  get restaurantTerminals(): UntypedFormArray
  {
    return this.portalRoleForm.get("RestaurantTerminals") as UntypedFormArray;
  }

  callManageRoleService(request: PostManageRoleRequest)
  {
    this.isLoading = true;
    this.service.PostManageRole(request).subscribe({
      next: () =>
      {
        this.isLoading = false;
        this.snackBarService.showSuccess(this.translate.instant("ADMIN_CENTER.MANAGE_STAFF_USER.SUCCESS_MESSAGE"), '', null, 'left');
      },
      error: () =>
      {
        this.isLoading = false;
        this.snackBarService.showSuccess(this.translate.instant("ADMIN_CENTER.MANAGE_STAFF_USER.ERROR_MESSAGE"), '', null, 'left');
      }
    });
  }

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }
}
