import { SelectionModel } from '@angular/cdk/collections';
import { FlatTreeControl } from '@angular/cdk/tree';
import { ChangeDetectorRef, Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges } from '@angular/core';
import { UntypedFormArray, UntypedFormControl } from '@angular/forms';
import { MatTreeFlatDataSource, MatTreeFlattener } from '@angular/material/tree';
import { ItemFlatNode, ItemNode } from './tree.model';

@Component({
  selector: 'tei-admin-admin-center-tree',
  templateUrl: './tree.component.html',
  styleUrls: ['./tree.component.scss']
})
export class TreeComponent implements OnChanges, OnInit
{
  @Input() treeData: Array<ItemNode>;
  @Output() selectedItemsEmitter = new EventEmitter<ItemFlatNode>();
  @Input() siteForm: UntypedFormArray;
  @Input() terminalForm: UntypedFormArray;

  /** Map from flat node to nested node. This helps us finding the nested node to be modified */
  flatNodeMap = new Map<ItemFlatNode, ItemNode>();

  /** Map from nested node to flattened node. This helps us to keep the same object for selection */
  nestedNodeMap = new Map<ItemNode, ItemFlatNode>();

  /** A selected parent node to be inserted */
  selectedParent: ItemFlatNode | null = null;

  treeControl: FlatTreeControl<ItemFlatNode>;

  treeFlattener: MatTreeFlattener<ItemNode, ItemFlatNode>;

  dataSource: MatTreeFlatDataSource<ItemNode, ItemFlatNode>;

  /** The selection for checklist */
  checklistSelection = new SelectionModel<ItemFlatNode>(true /* multiple */);


  constructor(protected changeDetector: ChangeDetectorRef)
  {
    this.treeFlattener = new MatTreeFlattener(this.transformer, this.getLevel, this.isExpandable, this.getChildren);
    this.treeControl = new FlatTreeControl<ItemFlatNode>(this.getLevel, this.isExpandable);
    this.dataSource = new MatTreeFlatDataSource(this.treeControl, this.treeFlattener);
  }

  ngOnInit(): void
  {
  }

  ngOnChanges(changes: SimpleChanges): void
  {
    if (this.treeData && (changes?.treeData || changes?.terminalForm))
    {
      this.dataSource.data = [];
      this.dataSource.data = this.treeData;
      this.selectShouldSelect();
      this.treeControl.expandAll();
    }
  }

  selectShouldSelect(): void
  {
    for (let i = 0; i < this.treeControl.dataNodes.length; i++)
    {
      if (
        (this.validNumber(this.treeControl.dataNodes[i]?.Item?.Id)
          && this.terminalForm?.value?.includes(Number(this.treeControl.dataNodes[i]?.Item?.Id))
        )
      )
      {
        this.itemSelectionToggle(this.treeControl.dataNodes[i], false);
      }
    }
  }

  get selectedKeys(): Array<string>
  {
    return this.checklistSelection.selected.filter(x => x.Level === 1).map(x => x.Item.Key);
  }

  getLevel = (node: ItemFlatNode) => node.Level;

  isExpandable = (node: ItemFlatNode) => node.Expandable;

  getChildren = (node: ItemNode): ItemNode[] => node.Children;

  hasChild = (_: number, _nodeData: ItemFlatNode) => _nodeData.Expandable;

  hasNoContent = (_: number, _nodeData: ItemFlatNode) => !_nodeData.Item;

  /**
     * Transformer to convert nested node to flat node. Record the nodes in maps for later use.
     */
  transformer = (node: ItemNode, level: number) =>
  {
    const existingNode = this.nestedNodeMap.get(node);
    const flatNode = existingNode && existingNode.Item.Key === node.Item.Key
      ? existingNode
      : new ItemFlatNode();
    flatNode.Item = node.Item;
    flatNode.Level = level;
    flatNode.Expandable = !!node.Children?.length;
    this.flatNodeMap.set(flatNode, node);
    this.nestedNodeMap.set(node, flatNode);
    return flatNode;
  };

  /** Whether all the descendants of the node are selected. */
  descendantsAllSelected(node: ItemFlatNode): boolean
  {
    const descendants = this.treeControl.getDescendants(node);
    const descAllSelected = descendants.length > 0 && descendants.every(child =>
    {
      return this.checklistSelection.isSelected(child);
    });
    return descAllSelected;
  }

  /** Whether part of the descendants are selected */
  descendantsPartiallySelected(node: ItemFlatNode): boolean
  {
    const descendants = this.treeControl.getDescendants(node);
    const result = descendants.some(child => this.checklistSelection.isSelected(child));
    return result && !this.descendantsAllSelected(node);
  }

  /** Toggle the item selection. Select/deselect all the descendants node */
  itemSelectionToggle(node: ItemFlatNode, shouldEmitSelected: boolean = true): void
  {
    this.checklistSelection.toggle(node);
    const descendants = this.treeControl.getDescendants(node);
    this.checklistSelection.isSelected(node)
      ? this.checklistSelection.select(...descendants)
      : this.checklistSelection.deselect(...descendants);

    // Force update for the parent
    descendants?.forEach(child => this.checklistSelection.isSelected(child));
    this.checkAllParentsSelection(node);
    if (shouldEmitSelected)
    {
      this.selectedItemsEmitter.emit(node);
    }
  }

  /** Toggle a leaf item selection. Check all the parents to see if they changed */
  leafItemSelectionToggle(node: ItemFlatNode): void
  {
    this.checklistSelection.toggle(node);
    this.checkAllParentsSelection(node);
    this.selectedItemsEmitter.emit(node);
  }

  /* Checks all the parents when a leaf node is selected/unselected */
  checkAllParentsSelection(node: ItemFlatNode): void
  {

    let parent: ItemFlatNode | null = this.getParentNode(node);
    while (parent)
    {
      this.checkRootNodeSelection(parent);
      parent = this.getParentNode(parent);
    }
  }

  /** Check root node checked state and change it accordingly */
  checkRootNodeSelection(node: ItemFlatNode): void
  {
    const nodeSelected = this.checklistSelection.isSelected(node);
    const descendants = this.treeControl.getDescendants(node);
    const descAllSelected = descendants.length > 0 && descendants.every(child =>
    {
      return this.checklistSelection.isSelected(child);
    });
    if (nodeSelected && !descAllSelected)
    {
      this.checklistSelection.deselect(node);
    } else if (!nodeSelected && descAllSelected)
    {
      this.checklistSelection.select(node);
    }
  }

  /* Get the parent node of a node */
  getParentNode(node: ItemFlatNode): ItemFlatNode | null
  {
    const currentLevel = this.getLevel(node);

    if (currentLevel < 1)
    {
      return null;
    }

    const startIndex = this.treeControl.dataNodes.indexOf(node) - 1;

    for (let i = startIndex; i >= 0; i--)
    {
      const currentNode = this.treeControl.dataNodes[i];

      if (this.getLevel(currentNode) < currentLevel)
      {
        return currentNode;
      }
    }
    return null;
  }

  onParentChanged(node, event: any)
  {
    if (node.Level == 0)
    {
      this.terminalForm.clear();

      if (event?.checked)
      {
        this.treeData[0].Children.forEach(site =>
        {
          this.addSiteTerminals(site);
        });
      }
    }
    else
    {
      if (event?.checked)
      {
        const selectedSite = this.treeData[0].Children.find(site => site.Item.Id == node.Item?.Id);
        this.addSiteTerminals(node);
      }
      else
      {
        const selectedSite = this.treeData[0].Children.find(site => site.Item.Id == node.Item?.Id);
        selectedSite.Children.forEach(terminal =>
        {
          const index = this.terminalForm.controls.findIndex(terminalId => terminalId.value == terminal.Item?.Id);
          this.terminalForm.removeAt(index);
        });

      }
    }
  }

  onChildChanged(node, event: any)
  {
    if (event?.checked)
    {
      this.terminalForm?.push(new UntypedFormControl(node.Item?.Id));
    }
    else
    {
      const index = this.terminalForm.controls.findIndex(terminalId => terminalId.value == node.Item?.Id);
      this.terminalForm.removeAt(index);
    }
  }

  addSiteTerminals(node)
  {
    const selectedSite = this.treeData[0].Children.find(site => site.Item.Id == node.Item?.Id);
    selectedSite.Children.forEach(terminal =>
    {
      const index = this.terminalForm.controls.findIndex(terminalId => terminalId.value == terminal.Item?.Id);
      if (index === -1)
      {
        this.terminalForm?.push(new UntypedFormControl(terminal.Item?.Id));
      }
    });
  }

  addTerminal(node)
  {
    const selectedSite = this.treeData[0].Children.find(site => site.Item.Id == node.Item?.Id);
    selectedSite.Children.forEach(terminal =>
    {
      const index = this.terminalForm.controls.findIndex(terminalId => terminalId.value == terminal.Item?.Id);
      if (index === -1)
      {
        this.terminalForm?.push(new UntypedFormControl(terminal.Item?.Id));
      }
    });
  }

  validNumber(value): boolean
  {
    if (!(value?.toString()) || (!Number(value) && Number(value) !== 0)) { return false; }
    return true;
  }
}
