export class GetPaymentGatewaySettingsRequest
{
  PortalID: number;
}

export class GetPaymentGatewaySettingsResponse
{
  PaymentGateways: PaymentGateway[];
  SelectedGateway: PaymentGateway;
  HostSettingID: number;
  IsACHQEnabled: boolean;
}


export class PaymentGateway
{
  GatewayID: number;
  GatewayCode: string;
  GatewayName: string;
  GatewayDescription: string;
  SortOrder: number;
}

export enum gatewayCodeEnum
{
  None = 'None',
  Converge = 'Converge',
  Noon = 'Noon'
}
