import { Component, Inject, OnDestroy, OnInit } from '@angular/core';
import { UntypedFormControl, UntypedFormGroup, Validators } from '@angular/forms';
import { TranslateService } from '@ngx-translate/core';
import { ManageLoadingService } from '@tei/core';
import { BreadCrumb, SnackBarService } from '@tei/material';
import { Subscription } from 'rxjs';
import { finalize } from 'rxjs/operators';
import { IAdminPortalService } from '../../../@intermediate/admin-portal.interface';
import { IHostAdminCenterHelperService } from '../../../@intermediate/host-admin-center-helper.interface';
import { Coverage, GetCoverageSetupRequest, GetCoverageSetupResponse } from '../../../@intermediate/models/get-coverage-setup.model';
import { portalGroupDetails } from '../../../@intermediate/models/host-admin-center-helper.model';
import { PostCoverageSetupRequest, PostCoverageSetupResponse } from '../../../@intermediate/models/post-coverage-setup.model';
import { PortalGroupByID } from '../../../@intermediate/portal-group/models/get-portal-group-by-id.model';

@Component({
  selector: 'tei-admin-admin-center-converge-settings',
  templateUrl: './converge-settings.component.html',
  styleUrls: ['./converge-settings.component.scss']
})
export class ConvergeSettingsComponent implements OnInit, OnDestroy
{
  isLoading: boolean = false;
  portalId: string;
  parentPortalGroupId: string;
  parentPortalGroup: PortalGroupByID;
  subscription: Subscription = new Subscription();
  portalDetail: portalGroupDetails;
  breadCrumbData: BreadCrumb[] = [];
  coverageSetup: Coverage;
  settingForm: UntypedFormGroup = new UntypedFormGroup({
    MerchantID: new UntypedFormControl('', Validators.required),
    ConvergePIN: new UntypedFormControl('', Validators.required),
    UserId: new UntypedFormControl('', Validators.required),
    DemoAccount: new UntypedFormControl(false)
  });
  constructor(
    @Inject('IHostAdminCenterHelperService') private hostAdminCenterHelperService: IHostAdminCenterHelperService,
    @Inject('IAdminPortalService') private adminPortalService: IAdminPortalService,
    public loadingService: ManageLoadingService,
    private translate: TranslateService,
    private snackBar: SnackBarService

  )
  {
  }

  ngOnInit(): void
  {
    this.subscription.add(this.hostAdminCenterHelperService.currentPortalGroupDetails.subscribe(portalDetail =>
    {
      if (portalDetail)
      {
        this.portalDetail = {
          parentPortalGroup: portalDetail.parentPortalGroup.PortalGroup,
          portal: portalDetail.portal
        };
        this.fillBreadCrump();
        this.isLoad();
      }
    }));
  }

  fillBreadCrump(): void
  {
    this.breadCrumbData = [];
    this.breadCrumbData = [
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.PORTAL_GROUPS.TITLE'),
        relativeUrl: '../../../../../',
      },
      {
        title: `${ this.portalDetail.parentPortalGroup.PortalGroupName }`,
        relativeUrl: `../../../../`,
      },
      {
        title: `${ this.portalDetail.portal.PortalName }`,
        relativeUrl: '../../',
      },
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.PAYMENT_SETTINGS'),
        relativeUrl: '../',
      },
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.ELAVON_CONVERGE_SETUP'),
        relativeUrl: '',
      }
    ];
  }

  isLoad(): void
  {
    const request: GetCoverageSetupRequest = {
      PortalID: this.portalDetail.portal.PortalID
    };

    this.loadingService.startLoading();
    this.adminPortalService.GetCoverageSetup(request).pipe(finalize(() => this.loadingService.finishLoading())).subscribe({
      next: (response: GetCoverageSetupResponse) =>
      {
        if (response.Coverages.length)
        {
          this.coverageSetup = response.Coverages[0];
          this.settingForm.patchValue({
            MerchantID: this.coverageSetup.MerchantID,
            ConvergePIN: this.coverageSetup.ConvergePIN,
            UserId: this.coverageSetup.CoverageUserId,
            DemoAccount: this.coverageSetup.DemoAccount
          });
        } else
        {
          this.settingForm.setValue({
            MerchantID: '',
            ConvergePIN: '',
            UserId: '',
            DemoAccount: false
          });
        }
      }
    });
  }

  saveSetting(): void
  {
    if (this.settingForm.invalid) { return; }
    const request: PostCoverageSetupRequest = {
      MerchantID: this.settingForm.value.MerchantID,
      ConvergePIN: this.settingForm.value.ConvergePIN,
      UserId: this.settingForm.value.UserId,
      DemoAccount: this.settingForm.value.DemoAccount,
      PortalID: this.portalDetail.portal.PortalID
    };

    this.loadingService.startLoading();
    this.adminPortalService.PostCoverageSetup(request).pipe(finalize(() => this.loadingService.finishLoading())).subscribe({
      next: (response: PostCoverageSetupResponse) =>
      {
        if (response.IsSuccess)
        {
          this.snackBar.showSuccess(this.translate.instant('ADMIN_CENTER.PRODUCTS.SAVED_SUCCESSFULLY'), '', null, 'left');
        }
      }
    });
  }

  ngOnDestroy(): void
  {
    this.subscription?.unsubscribe();
  }
}
