import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatDividerModule } from '@angular/material/divider';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyMenuModule as MatMenuModule } from '@angular/material/legacy-menu';
import { MatLegacyTableModule as MatTableModule } from '@angular/material/legacy-table';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { AvatarModule, QuickSearchFilterChipModule } from '@tei/material';
import { PortalGroupsService } from '../../../@services/portal-groups.service';
import { TenantsService } from '../../../@services/tenants.service';
import { PortalGroupComponent } from './portal-group.component';

const BASE_MODULES = [CommonModule];
const MATERIAL_MODULES = [MatIconModule, MatButtonModule, MatDividerModule, MatMenuModule, MatTableModule];
const TEI_MATERIAL_MODULES = [QuickSearchFilterChipModule, AvatarModule, TeiTextTranslatePipeModule];

@NgModule({
  declarations: [PortalGroupComponent],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES
  ],
  providers: [
    { provide: 'ITenantsService', useClass: TenantsService },
    { provide: 'IPortalGroupsService', useClass: PortalGroupsService }
  ],
})
export class PortalGroupModule { }
