import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { IButterCMSSetupService } from "../@intermediate/butter-cms-setup.interface";
import { GetButterCMSSettings } from "../@intermediate/models/get-butter-cms-settings.model";
import { PostButterCMSSettings } from "../@intermediate/models/post-butter-cms-settings.model";

@Injectable()
export class ButterCMSSetupService implements IButterCMSSetupService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Marketing/CMS/Admin/ButterCMSSettings';
  }

  get(portalID: number): Observable<GetButterCMSSettings>
  {
    const apiUrl = this.serviceUrl;
    return this.http.get<GetButterCMSSettings>(apiUrl, { params: { PortalID: portalID } });
  }
  post(input: PostButterCMSSettings): Observable<void>
  {
    const apiUrl = this.serviceUrl;
    return this.http.post<void>(apiUrl, input);
  }

}
