export class GetEcommerceSetupRequest
{
    PortalID: number;
}
export class GetEcommerceSetupResponse
{
    Status: EcommerceSetupStatusEnum;
    AutomationGroups: AutomationGroup[];
    Sites: Site[];
}

export enum EcommerceSetupStatusEnum
{
    NotSupported = -3,
    New = -2,
    Ongoing = -1,
    Failed = 0,
    Successful = 1
}

class AutomationGroup
{
    AutomationID: number;
    AutomationName: string;
}

class Site
{
    SiteID: string;
    SiteName: string;
    Terminals: PortalSetupJobsTerminal[];
}

export class PortalSetupJobsTerminal
{
    TerminalID: string;
    TerminalName: string;
}