import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { PostReservationAvailabilityResponse } from '@tei/booking-dining/lib/@intermediate/dining-book-table';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { ICustomerFacingDisplayService } from '../@intermediate/customer-facing-display.interface';
import { DeleteCustomerFacingDisplayRequest, DeleteCustomerFacingDisplayResponse } from '../@intermediate/models/delete-customer-facing-display.model';
import { GetCustomerFacingDisplayConfigurationRequest, GetCustomerFacingDisplayConfigurationResponse } from '../@intermediate/models/get-customer-facing-display-configuration.model';
import { PostCustomerFacingDisplayResponse } from '../@intermediate/models/post-customer-facing-display.model';
import { PostResetCustomerFacingDisplayRequest, PostResetCustomerFacingDisplayResponse } from '../@intermediate/models/post-reset-customer-facing-display.model';
import { PostCustomerFacingDisplaySearchRequest, PostCustomerFacingDisplaySearchResponse } from '../@intermediate/models/post-search-customer-facing-display.model';

@Injectable({
  providedIn: 'root'
})
export class CustomerFacingDisplayService implements ICustomerFacingDisplayService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/DeviceIntegration/CustomerFacingDisplay/';
  }

  Search(request: PostCustomerFacingDisplaySearchRequest): Observable<PostCustomerFacingDisplaySearchResponse>
  {
    const apiUrl = this.serviceUrl + 'Search';
    return this.http.post<PostCustomerFacingDisplaySearchResponse>(apiUrl, request);
  }

  Configuration(request: GetCustomerFacingDisplayConfigurationRequest): Observable<GetCustomerFacingDisplayConfigurationResponse>
  {
    const apiUrl = this.serviceUrl + 'Configuration';
    return this.http.get<GetCustomerFacingDisplayConfigurationResponse>(apiUrl, { params: request as any });
  }

  Create(request: FormData): Observable<PostCustomerFacingDisplayResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.post<PostCustomerFacingDisplayResponse>(apiUrl, request);
  }

  Edit(request: FormData): Observable<PostCustomerFacingDisplayResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.put<PostCustomerFacingDisplayResponse>(apiUrl, request);
  }

  Delete(request: DeleteCustomerFacingDisplayRequest): Observable<DeleteCustomerFacingDisplayResponse>
  {
    const options = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      }),
      body: request
    };
    const apiUrl = this.serviceUrl;
    return this.http.delete<DeleteCustomerFacingDisplayResponse>(apiUrl, options);
  }

  Reset(request: PostResetCustomerFacingDisplayRequest): Observable<PostReservationAvailabilityResponse>
  {
    const apiUrl = this.serviceUrl + 'Reset';
    return this.http.post<PostResetCustomerFacingDisplayResponse>(apiUrl, request);
  }
}
