import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { IGoogleAuthenticatorSettingService } from '../@intermediate/google-authenticator-setting/google-authenticator-setting.interface';
import { GetGoogleAuthenticatorSettingRequest, GetGoogleAuthenticatorSettingResponse } from '../@intermediate/google-authenticator-setting/models/get-google-authenticator-setting.model';
import { PostGoogleAuthenticatorSettingRequest, PostGoogleAuthenticatorSettingResponse } from '../@intermediate/google-authenticator-setting/models/post-google-authenticator-setting.model';

@Injectable()
export class GoogleAuthenticatorSettingService implements IGoogleAuthenticatorSettingService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Admin/AdminCenter/GoogleAnalyticsSettings';
  }

  Post(request: PostGoogleAuthenticatorSettingRequest): Observable<PostGoogleAuthenticatorSettingResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.post<PostGoogleAuthenticatorSettingResponse>(apiUrl, request);
  }

  Get(request: GetGoogleAuthenticatorSettingRequest): Observable<GetGoogleAuthenticatorSettingResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.get<GetGoogleAuthenticatorSettingResponse>(apiUrl, { params: request as any });
  }


}

