import { FormControl } from "@angular/forms";

export class GetPWAAppSetupRequest
{
  PortalID: number;
  TenantWebAliasID: number;
}

export class GetPWAAppSetupResponse
{
  TenantWebAliasID: number;
  ShortName: string;
  Name: string;
  Description: string;
  ThemeColor: string;
  BackgroundColor: string;
  Display: string;
  Orientation: string;
  StartUrl: string;
  Icon_72_Url: string;
  Icon_96_Url: string;
  Icon_128_Url: string;
  Icon_144_Url: string;
  Icon_152_Url: string;
  Icon_192_Url: string;
  Icon_384_Url: string;
  Icon_512_Url: string;
}

export class Icons
{
  Src: string;
  Sizes: string;
  Type: string;
  Purpose: string;
}

export enum displayEnum
{
  fullscreen = 'fullscreen',
  standalone = 'standalone',
  minimal_ui = 'minimal-ui',
  browser = 'browser'
}

export enum orientationEnum
{
  natural = 'natural',
  landscape = 'landscape',
  portrait = 'portrait'
}

export enum formFactorsEnum
{
  not_set = 'not_set',
  narrow = 'narrow',
  wide = 'wide'
}

export interface PWAAppSetupForm
{
  ShortName: FormControl<string>;
  Name: FormControl<string>;
  Description: FormControl<string>;
  ThemeColor: FormControl<string>;
  BackgroundColor: FormControl<string>;
  Display: FormControl<string>;
  Orientation: FormControl<string>;
  StartURL: FormControl<string>;
  Icon_72_Url: FormControl<string>;
  Icon_96_Url: FormControl<string>;
  Icon_128_Url: FormControl<string>;
  Icon_144_Url: FormControl<string>;
  Icon_152_Url: FormControl<string>;
  Icon_192_Url: FormControl<string>;
  Icon_384_Url: FormControl<string>;
  Icon_512_Url: FormControl<string>;
}
